import {
	CONTEXT_NAMESPACE,
	DATA_NAMESPACE,
	STATE_NAMESPACE
} from '../../../constants';
import {
	matchContextAppBindingSyntax,
	matchContextSessionBindingSyntax,
	matchStateBindingSyntax
} from '../../../utils';
import isUndefined from 'lodash/isUndefined';
import get from 'lodash/get';

const DATA_CHAIN_BINDING = 'DATA_CHAIN_BINDING';
const JSON_LITERAL = 'JSON_LITERAL';

export const generateJSONLiteral = (value) => {
	return {
		type: JSON_LITERAL,
		value
	};
};

export const resolveContextBinding = (
	value,
	contextBindings,
	dataBrokerInstances,
	appChromeSettings = {}
) => {
	const matchedContextSessionBinding = matchContextSessionBindingSyntax(value);
	if (matchedContextSessionBinding) {
		const { path } = matchedContextSessionBinding;
		return resolveDataBinding(
			`${DATA_NAMESPACE}.session.output${path || ''}`,
			dataBrokerInstances
		);
	}

	const matchedContextAppBinding = matchContextAppBindingSyntax(value);
	if (matchedContextAppBinding) {
		const { path } = matchedContextAppBinding;
		const resolvedValue = get(appChromeSettings, path, null);

		return {
			type: JSON_LITERAL,
			value: !isUndefined(resolvedValue) ? resolvedValue : null
		};
	}

	const bindingName = value.substring(`${CONTEXT_NAMESPACE}.props`.length + 1);
	const bindingNameSplitter = bindingName.split('.');
	const contextValue = contextBindings[bindingNameSplitter.shift()];
	const path = bindingNameSplitter.length
		? `.${bindingNameSplitter.join('.')}`
		: '';

	if (
		contextValue &&
		typeof contextValue === 'string' &&
		contextValue.startsWith(DATA_NAMESPACE)
	) {
		return resolveDataBinding(`${contextValue}${path}`, dataBrokerInstances);
	}

	return {
		type: JSON_LITERAL,
		value: contextValue
	};
};

export const resolveDataBinding = (value, dataBrokerInstances) => {
	const splitExpression = value.split('.');
	const dataBrokerInstanceId = splitExpression[1];
	const outputPath = splitExpression.length > 2 ? splitExpression.slice(2) : [];
	const address = dataBrokerInstances
		.filter(
			(dataBrokerInstance) =>
				dataBrokerInstance.definition.type !== 'CLIENT_STATE' &&
				!dataBrokerInstance.simpleMutationBroker
		)
		.findIndex((dbInstance) => dbInstance.id === dataBrokerInstanceId);

	return {
		type: DATA_CHAIN_BINDING,
		binding: {
			address: [`${address}`, ...outputPath]
		}
	};
};

export const resolveStateBinding = (value, clientStateData) => {
	const bindingValue = matchStateBindingSyntax(value);
	const resolvedValue = get(clientStateData, bindingValue.name, null);
	return {
		type: JSON_LITERAL,
		value: !isUndefined(resolvedValue) ? resolvedValue : null
	};
};

const resolveBinding = (
	property,
	dataBrokerInstances,
	contextBindingValues,
	clientStateData,
	appChromeSettings
) => {
	const { value } = property;
	if (!value) {
		return;
	}
	if (typeof value !== 'string') {
		return generateJSONLiteral(value);
	} else if (value.startsWith(CONTEXT_NAMESPACE)) {
		return resolveContextBinding(
			value,
			contextBindingValues,
			dataBrokerInstances,
			appChromeSettings
		);
	} else if (value.startsWith(DATA_NAMESPACE)) {
		return resolveDataBinding(value, dataBrokerInstances);
	} else if (value.startsWith(STATE_NAMESPACE)) {
		return resolveStateBinding(value, clientStateData);
	}
	return generateJSONLiteral(value);
};

/**
 * Transforms macroponent.data values to databroker runtime to execute on server
 * @param {} param0
 */
export const transformDataBrokerInstancesToDataBrokerRuntime = (
	dataBrokerInstances,
	includeDataBrokerId,
	contextBindingValues = {},
	clientStateData,
	appChromeSettings
) => {
	const resolvedDataBrokers = dataBrokerInstances
		.filter((dataBrokerInstance) => {
			return (
				dataBrokerInstance.definition.type !== 'CLIENT_STATE' &&
				!dataBrokerInstance.simpleMutationBroker
			);
		})
		.map((dataBrokerInstance) => {
			const dataBrokerDefinitionId = dataBrokerInstance.definition.id;

			const hydratedInputValues = Object.entries(
				dataBrokerInstance.properties
			).reduce((acc, [name, property]) => {
				acc[name] = resolveBinding(
					property,
					dataBrokerInstances,
					contextBindingValues,
					clientStateData,
					appChromeSettings
				);
				return acc;
			}, {});

			return {
				dataBrokerid: includeDataBrokerId ? dataBrokerInstance.id : undefined,
				definitionSysId: dataBrokerDefinitionId,
				inputValues: hydratedInputValues,
				type: dataBrokerInstance.definition.type
			};
		});
	if (resolvedDataBrokers.find((d, i) => findMaxReferenceAddress(d, i) > i)) {
		return orderDataBrokerChainBreadthFirst(resolvedDataBrokers);
	}
	return resolvedDataBrokers;
};

const orderDataBrokerChainBreadthFirst = (dataBrokerDependencyChain) => {
	const nodes = dataBrokerDependencyChain.map((dataBroker, idx) => ({
		currentPosition: idx,
		curChildIndexes: new Set(),
		newPosition: -1,
		isChild: false
	}));

	for (const index in dataBrokerDependencyChain) {
		Object.values(dataBrokerDependencyChain[index].inputValues)
			.filter((v) => !!v)
			.filter(({ type }) => type === DATA_CHAIN_BINDING)
			.forEach(({ binding }) => {
				const addr = binding.address[0];
				nodes[addr].curChildIndexes.add(index);
				nodes[index].isChild = true;
			});
	}

	let counter = 0;
	let toBeVisited = nodes.filter((n) => !n.isChild);
	toBeVisited.sort((n1, n2) => n1.currentPosition - n2.currentPosition);

	while (toBeVisited.length > 0) {
		const childIndexes = new Set();
		for (const node of toBeVisited) {
			if (node.newPosition > -1) {
				// TODO: Should throw exception. We are skipping this for now.
				return dataBrokerDependencyChain;
			}
			node.newPosition = counter++;
			if (node.curChildIndexes.size) {
				node.curChildIndexes.forEach((child) => childIndexes.add(child));
			}
		}
		toBeVisited = [...childIndexes]
			.sort((n1, n2) => n1 - n2)
			.map((i) => nodes[i]);
	}

	nodes.sort((n1, n2) => n1.newPosition - n2.newPosition);
	const ret = new Array(dataBrokerDependencyChain.length);
	if (nodes.find((node) => node.newPosition < 0)) {
		return dataBrokerDependencyChain;
	}
	for (const node of nodes) {
		ret[node.newPosition] = dataBrokerDependencyChain[node.currentPosition];
		node.curChildIndexes.forEach((childIdx) => {
			const dataBroker = dataBrokerDependencyChain[childIdx];
			for (const k in dataBroker.inputValues) {
				if (
					dataBroker.inputValues[k].type === DATA_CHAIN_BINDING &&
					dataBroker.inputValues[k].binding.address[0] ===
						`${node.currentPosition}`
				) {
					dataBroker.inputValues[k].binding.address[0] = `${node.newPosition}`;
				}
			}
		});
	}
	return ret;
};

const findMaxReferenceAddress = (d, address) => {
	return Object.values(d.inputValues)
		.filter((v) => !!v)
		.filter((v) => v.type === DATA_CHAIN_BINDING)
		.reduce((acc, c) => Math.max(acc, c.binding.address[0]), address);
};
