import { INSERT_MODAL } from './modalActions';
import { getTemplates } from '@devsnc/library-uxf';
import {
	createElementFromComponent,
	getAllElementIds
} from '../element/utils/element';
import createPageState from '../../models/createPageState';
import cloneDeep from 'lodash/cloneDeep';
import { layout } from './defaultCustomModalLayout';
import { isCustomModalComponent, isViewportModalComponent } from './utils';

export default {
	[INSERT_MODAL]: ({
		action: { payload },
		state: {
			rawComponentList,
			properties: { pageState },
			getDefaultLayout
		},
		updateProperties,
		dispatch
	}) => {
		const {
			modal: { id: selectedModalId }
		} = payload;
		const modalComponent = rawComponentList.find(
			(component) => component.id === selectedModalId
		);

		getTemplates([modalComponent.id]);

		/*
		   with the introduction of custom modals all the modals
		   are rendered using the element mount. This means the
		   out of the box modals will no longer render as expected
		   because their original tag is `now-uxf-page` and our
		   element mount tries to load that. Instead, what we want
		   is to load their macroponent because we need to pass it
		   certain properties (like button titles) and treat it like
		   a single component. Loading OOB modals using renderer
		   will break that model.

		   This approach also helps us remove a lot of async rendering
		   logic from the renderer which loads the modals using an
		   overlay
		*/
		if (
			!isCustomModalComponent(modalComponent) &&
			!isViewportModalComponent(modalComponent)
		) {
			modalComponent.tag = `macroponent-${modalComponent.id}`;
		}

		const newElement = createElementFromComponent(
			modalComponent,
			pageState.rootElement,
			true,
			getDefaultLayout()
		);
		const allElementIds = getAllElementIds(pageState.rootElement);
		newElement.initialize(allElementIds);

		if (isCustomModalComponent(modalComponent)) {
			newElement.layout = layout;
		}

		const newRootElement = cloneDeep(pageState.rootElement);

		newRootElement.children.push(newElement);

		updateProperties({
			pageState: createPageState(
				{
					rootElement: newRootElement
				},
				pageState
			)
		});

		dispatch('UIB_ELEMENT#ELEMENT_SELECTED', {
			type: 'element',
			elementId: newElement.id,
			elementInstanceName: newElement.instanceName,
			component: modalComponent
		});
	}
};
