import { Slot } from '../../../models';
import cuid from 'cuid';
import isEmpty from 'lodash/isEmpty';
import flatMap from 'lodash/flatMap';

const childrenKey = (type) => (type === 'row' ? 'columns' : 'rows');

export function createSimpleLayout(element) {
	const generatedSlotNames = element.children.reduce((acc, cur) => {
		acc[cur.id] = cuid();
		return acc;
	}, {});

	element.children.forEach((child) => {
		child.slot = new Slot({
			name: generatedSlotNames[child.id],
			parentElement: element
		});
	});

	const newLayout = {
		default: {
			type: 'rowColumn',
			root: {
				id: cuid(),
				type: 'column',
				size: 12,
				rows: element.children.map((child) => ({
					type: 'row',
					id: cuid(),
					slotName: generatedSlotNames[child.id]
				}))
			}
		}
	};

	element.layout = newLayout;
}

export function createSlotsForEveryElement(
	layout,
	slottedElements,
	injectedCuid = cuid
) {
	const claimedSlotNames = {};

	const checkNode = (layoutElement) => {
		if (!layoutElement.slotName) {
			const childrenType = childrenKey(layoutElement.type);
			if (!layoutElement[childrenType]) {
				return [layoutElement];
			}

			return [
				{
					...layoutElement,
					[childrenType]: flatMap(layoutElement[childrenType], checkNode)
				}
			];
		}

		const slotName = layoutElement.slotName;
		const elementsInSlot = slottedElements.filter(
			(slottedElement) => slottedElement.slotName === slotName
		);
		if (elementsInSlot.length <= 1) {
			return [layoutElement];
		}

		if (layoutElement.type === 'row') {
			return elementsInSlot.map((elementInSlot) => {
				const newSlotName = injectedCuid();
				claimedSlotNames[elementInSlot.elementId] = newSlotName;
				return {
					type: 'row',
					id: injectedCuid(),
					slotName: newSlotName
				};
			});
		} else {
			return [
				{
					type: 'column',
					size: layoutElement.size,
					divider: layoutElement.divider,
					id: layoutElement.id,
					rows: elementsInSlot.map((elementInSlot) => {
						const newSlotName = injectedCuid();
						claimedSlotNames[elementInSlot.elementId] = newSlotName;
						return {
							type: 'row',
							id: injectedCuid(),
							slotName: newSlotName
						};
					})
				}
			];
		}
	};

	let newLayoutRoot;
	try {
		newLayoutRoot = checkNode(layout.default.root)[0];
	} catch (e) {
		return {
			fixedLayout: null,
			convertedSlotNames: {}
		};
	}

	if (isEmpty(claimedSlotNames)) {
		return {
			fixedLayout: null,
			convertedSlotNames: {}
		};
	} else {
		const rootChildren = newLayoutRoot[childrenKey(newLayoutRoot.type)] || [];
		return {
			newLayoutRoot:
				rootChildren.length !== 1 ? newLayoutRoot : rootChildren[0],
			convertedSlotNames: claimedSlotNames
		};
	}
}
