import { createHttpEffect } from '@servicenow/ui-effect-http';

import {
	CONFIGURATION_COMPONENT_BY_INTERFACE_REQUESTED,
	CONFIGURATION_COMPONENT_BY_INTERFACE_SUCCEEDED,
	CONFIGURATION_COMPONENT_BY_INTERFACE_FAILED
} from './configurationComponentByInterfaceActions';

import { COMPONENT_SOURCES_REQUESTED } from '../componentSources/componentSourcesActions';

const configurationComponentByInterfaceRequested = createHttpEffect(
	'/api/now/uxf/config_components/by_interface',
	{
		method: 'GET',
		successActionType: CONFIGURATION_COMPONENT_BY_INTERFACE_SUCCEEDED,
		errorActionType: CONFIGURATION_COMPONENT_BY_INTERFACE_FAILED
	}
);

const configurationComponentByInterfaceSucceeded = ({
	action,
	dispatch,
	updateProperties
}) => {
	const configComponentsToLoad = Array.from(
		new Set(
			Object.values(action.payload.result).map((value) => {
				return value.componentId;
			})
		)
	);

	updateProperties({ interfaceConfigurationComponent: action.payload.result });

	dispatch(COMPONENT_SOURCES_REQUESTED, {
		configComponents: configComponentsToLoad
	});
};

const configurationComponentByInterfaceFailed = ({
	dispatch,
	updateProperties
}) => {
	updateProperties({ interfaceConfigurationComponent: {} });
	dispatch(COMPONENT_SOURCES_REQUESTED, {
		configComponents: []
	});
};

export default {
	[CONFIGURATION_COMPONENT_BY_INTERFACE_REQUESTED]: configurationComponentByInterfaceRequested,
	[CONFIGURATION_COMPONENT_BY_INTERFACE_SUCCEEDED]: configurationComponentByInterfaceSucceeded,
	[CONFIGURATION_COMPONENT_BY_INTERFACE_FAILED]: configurationComponentByInterfaceFailed
};
