import isNil from 'lodash/isNil';
import get from 'lodash/get';
import cloneDeep from 'lodash/cloneDeep';
import set from 'lodash/set';
import omit from 'lodash/omit';
import { getIsActive } from './pageStatus';
import { computeEventMappingTargetKeys } from './shared';
import {
	getUxValueFromShorthand,
	isUxValueModel,
	getShorthandFromUxValue,
	getVersionedUxValueFromShorthand
} from '../../../factories';
import { DATABROKER_OP, EVENT } from '../../elementEventMappings/constants';
import isEmpty from 'lodash/isEmpty';
import {
	ACTIVE_ROUTE,
	VIEWPORT_ROUTES,
	ACTIVE_SCREEN,
	REPEATED_VIEWPORT_TAB_DATA,
	VIEWPORT_NOW_ID
} from '../../element/reservedPropertyIds';
import convertAncestorEventMappingsToRelays from './convertAncestorEventMappingsToRelays';
import {
	BINDING_SHORTHAND_PATTERN,
	SCRIPT_SHORTHAND_PATTERN
} from '../../../factories/constants';

const isLegacy = (pageStateType) => pageStateType === 'landing';

const getIsHiddenValue = async (isHiddenValue) => {
	if (isUxValueModel(isHiddenValue)) {
		return isHiddenValue;
	}
	return await getVersionedUxValueFromShorthand(isHiddenValue);
};

const isValidBindingShorthand = (str) => {
	return typeof str === 'string' && !!str.match(BINDING_SHORTHAND_PATTERN);
};

const isScriptedTransform = (str) => {
	return typeof str === 'string' && !!str.match(SCRIPT_SHORTHAND_PATTERN);
};

const transformToNodeProperties = (properties, isLegacyPageState) => {
	return Object.keys(properties).reduce((acc, cur) => {
		if (properties[cur].type === 'json') {
			let value;
			try {
				value = JSON.parse(properties[cur].value);
			} catch (e) {
				value = properties[cur].value;
			}

			acc[cur] = value;
		} else {
			let value = get(properties[cur], 'value', '');
			if (
				value &&
				!isLegacyPageState &&
				!isValidBindingShorthand(value) &&
				!isScriptedTransform(value) &&
				get(properties[cur], 'translatable')
			) {
				value = `translate(${value})`;
			}
			acc[cur] = value;
		}
		return acc;
	}, {});
};

export const transformEventMappings = async (
	eventMappings,
	isLegacyPageState
) => {
	const newEventMappings = [];
	for (const { eventMappingId, eventApiName, targets } of eventMappings) {
		const transformedTargets = [];
		for (const target of targets) {
			const {
				targetKey,
				valueKey,
				idKey,
				nonTargetKeys
			} = computeEventMappingTargetKeys(target.type);

			if (!targetKey) {
				transformedTargets.push({ ...target, type: 'EVENT' });
				continue;
			}

			const eventMappingTargetValues = get(
				target,
				`${targetKey}.${valueKey}`,
				[]
			);

			const targetValuesMap = eventMappingTargetValues.reduce((acc, cur) => {
				acc[cur.columnName] = cur;
				return acc;
			}, {});

			const targetValues = transformToNodeProperties(
				targetValuesMap,
				isLegacyPageState
			);
			const targetIsScripted = !!target.clientTransformScript;
			const noTargetValuesExist = Object.values(targetValues).every(
				(val) => !val
			);

			const targetUxValues = await getVersionedUxValueFromShorthand(
				noTargetValuesExist && targetIsScripted
					? target.clientTransformScript
					: targetValues
			);

			const transformedEventMappingTarget = {
				...target,
				[targetKey]: {
					[idKey]: target[targetKey][idKey],
					[valueKey]: targetUxValues
				},
				...nonTargetKeys.reduce((acc, key) => {
					return { ...acc, [key]: null };
				}, {})
			};

			if (target.type === EVENT) {
				transformedEventMappingTarget[targetKey].apiName = target.event.apiName;
			}

			if (target.type === DATABROKER_OP) {
				transformedEventMappingTarget[targetKey].operationName =
					target.operation.operationName;
				transformedEventMappingTarget[targetKey].parentResourceId =
					target.operation.parentResourceId;
			}

			transformedTargets.push(transformedEventMappingTarget);
		}

		newEventMappings.push({
			eventMappingId,
			sourceEventApiName: eventApiName,
			targets: transformedTargets
		});
	}
	return newEventMappings;
};

export const getOverrides = (pageStateType) => async (layout, children) => {
	const hasLayout = layout && layout.default;
	const hasChildren = children && children.length > 0;
	if (!hasLayout && !hasChildren) {
		return;
	}

	const composition = [];
	if (Array.isArray(children)) {
		for (const child of children) {
			composition.push(
				await createCompositionNodeFromElement(pageStateType)(child)
			);
		}
	}

	return {
		layout,
		composition
	};
};

const createCompositionNodeFromElement = (pageStateType) => async (element) => {
	const {
		id,
		instanceName,
		slot,
		component,
		properties,
		eventMappings,
		styles,
		children,
		layout,
		isHidden
	} = element;

	const definition = {
		type: component.type,
		id: component.id
	};

	const isLegacyPageState = isLegacy(pageStateType);

	const nodeProperties = Object.entries(
		transformToNodeProperties(properties, isLegacyPageState)
	);

	const propertyValues = {};
	for (const [propertyName, propertyValue] of nodeProperties) {
		propertyValues[propertyName] = isLegacyPageState
			? propertyValue
			: await getVersionedUxValueFromShorthand(
					propertyValue,
					properties[propertyName].schema
			  );
	}

	const transformedEventMappings =
		eventMappings &&
		(await transformEventMappings(eventMappings, isLegacyPageState));

	const isHiddenValue = await getIsHiddenValue(isHidden);

	const compositionNode = {
		elementId: id,
		elementLabel: instanceName,
		slot: slot && slot.name,
		definition,
		styles,
		propertyValues,
		eventMappings: transformedEventMappings
	};

	if (!isLegacy(pageStateType)) {
		compositionNode.isHidden = isHiddenValue;
	}

	const overrides = await getOverrides(pageStateType)(layout, children);
	if (overrides) {
		compositionNode.overrides = overrides;
	}

	return compositionNode;
};

const createMacroponentFromElement = (pageStateType) => async (
	element,
	dataBrokerInstances,
	clientStateParameters,
	dispatchedEvents,
	handledEvents
) => {
	const {
		instanceName: name,
		id,
		layout,
		children,
		extendsSysId,
		macroponentProperties
	} = element;

	const composition = [];

	for (const child of children) {
		composition.push(
			await createCompositionNodeFromElement(pageStateType)(child)
		);
	}

	const isLegacyPageState = isLegacy(pageStateType);

	const data = [];
	for (const dataBrokerInstance of dataBrokerInstances) {
		const newInstance = {
			elementId: dataBrokerInstance.id,
			elementLabel: dataBrokerInstance.instanceName,
			readEvaluationMode: dataBrokerInstance.readEvaluationMode || 'EAGER',
			definition: omit(dataBrokerInstance.definition, 'invalid'),
			inputValues: Object.entries(
				transformToNodeProperties(
					dataBrokerInstance.properties,
					isLegacyPageState
				)
			).reduce((acc, [propertyName, propertyValue]) => {
				return {
					...acc,
					[propertyName]: isLegacy(pageStateType)
						? propertyValue
						: getUxValueFromShorthand(propertyValue)
				};
			}, {}),
			eventMappings: await transformEventMappings(
				dataBrokerInstance.eventMappings
			)
		};
		data.push(newInstance);
	}

	const stateProperties = clientStateParameters.map((param) => {
		let value = param.initialValue;
		if (param.type === 'json') {
			try {
				value = JSON.parse(param.initialValue);
			} catch (e) {
				value = param.initialValue;
			}
		}

		return {
			name: param.name,
			label: param.name,
			fieldType: param.type,
			valueType: param.type,
			initialValue: getUxValueFromShorthand(value),
			description: '',
			shape: ''
		};
	});

	return {
		composition,
		data,
		extendsSysId,
		id,
		layout,
		name,
		rootElement: {}, // TODO
		properties: macroponentProperties.filter(
			(prop) => prop.inherited === false
		),
		stateProperties,
		dispatchedEvents,
		handledEvents
	};
};

const getTabsFromComposition = (elComposition) => {
	const propsUxValueModel = get(elComposition, 'propertyValues.tabs', null);
	let tabs = null;

	if (isUxValueModel(propsUxValueModel)) {
		const shortHandVal = getShorthandFromUxValue(propsUxValueModel);
		tabs = isEmpty(shortHandVal) ? null : shortHandVal;
	}

	return tabs;
};

const removeViewportsTransientPropertiesFromComposition = (
	composition,
	mcp,
	viewportElementIds
) => {
	return composition.map((elComposition) => {
		const { overrides } = elComposition;

		if (
			viewportElementIds.includes(elComposition.elementId) &&
			getTabsFromComposition(elComposition) === null
		) {
			// these are all transient properties that
			// should not be saved back into the composition
			// of any viewport element

			delete elComposition.propertyValues[VIEWPORT_ROUTES];
			delete elComposition.propertyValues[ACTIVE_ROUTE];
			delete elComposition.propertyValues[ACTIVE_SCREEN];
			delete elComposition.propertyValues[REPEATED_VIEWPORT_TAB_DATA];
			// supporting tabset backward compatibility
			delete elComposition.propertyValues[VIEWPORT_NOW_ID];
		} else if (!isEmpty(overrides) && !isEmpty(overrides.composition)) {
			/*
				Elements such have containers can be made parents of any element
				and that would require us to recursively check the overrides
				and invoke this method
			 */
			const { composition } = overrides;
			elComposition.overrides.composition = removeViewportsTransientPropertiesFromComposition(
				composition,
				mcp,
				viewportElementIds
			);
		}
		return elComposition;
	});
};

export const removeViewportsTransientProperties = (
	macroponentInstance,
	viewportElementIds = {}
) => {
	const mcp = cloneDeep(macroponentInstance);

	const topLevelComposition = removeViewportsTransientPropertiesFromComposition(
		get(mcp, 'macroponentInstance.macroponent.composition', []),
		mcp,
		viewportElementIds
	);

	set(mcp, 'macroponentInstance.macroponent.composition', topLevelComposition);

	return mcp;
};

export const createMacroponentInstanceFromElement = (pageStateType) => async (
	element,
	dataBrokerInstances,
	clientStateParameters,
	dispatchedEvents,
	handledEvents,
	screenEventMappings = [],
	internalEventMappings = []
) => {
	const { macroponentProperties } = element;

	const macroponent = await createMacroponentFromElement(pageStateType)(
		element,
		dataBrokerInstances,
		clientStateParameters,
		dispatchedEvents,
		handledEvents
	);

	const transformedScreenEventMappings = await transformEventMappings(
		screenEventMappings
	);

	const transformInternalEventMappings = async (mappings) => {
		const transformedMappings = await transformEventMappings(mappings);
		return transformedMappings.reduce((acc, mapping) => {
			if (!acc[mapping.sourceEventApiName]) {
				acc[mapping.sourceEventApiName] = mapping.targets || [];
			} else {
				acc[mapping.sourceEventApiName] = [
					...acc[mapping.sourceEventApiName],
					...(mapping.targets || [])
				];
			}
			return acc;
		}, {});
	};
	const transformedInternalEventMappings = await transformInternalEventMappings(
		internalEventMappings
	);
	macroponent.internalEventMappings = transformedInternalEventMappings;

	return {
		macroponent,
		eventMappings: transformedScreenEventMappings,
		propertyValues: macroponentProperties.reduce((acc, prop) => {
			if (!isNil(prop.value)) {
				if (
					!isValidBindingShorthand(prop.value) &&
					get(prop, 'typeMetadata.translatable')
				) {
					acc[prop.name] = getUxValueFromShorthand(
						`translate(${prop.value})`,
						prop.typeMetadata
					);
				} else {
					acc[prop.name] = prop.value;
				}
			}
			return acc;
		}, {}),
		propertyTestValues: macroponentProperties.reduce((acc, prop) => {
			if (!isNil(prop.testValue)) {
				acc[prop.name] = prop.testValue;
			}
			return acc;
		}, {})
	};
};

export default async function transformPageStateToMacroponent(pageState) {
	let updatedPageState = cloneDeep(pageState);

	updatedPageState = convertAncestorEventMappingsToRelays(pageState);
	const {
		sysId: id,
		scope,
		type: pageStateType,
		rootElement,
		pageStatus,
		uiTypeParameters,
		dataBrokerInstances,
		clientStateParameters = [],
		dispatchedEvents,
		handledEvents,
		screenEventMappings
	} = updatedPageState;

	const macroponent = {
		id,
		scope,
		isActive: getIsActive(pageStatus),
		macroponentInstance: await createMacroponentInstanceFromElement(
			pageStateType
		)(
			rootElement,
			dataBrokerInstances,
			clientStateParameters,
			dispatchedEvents,
			handledEvents,
			screenEventMappings,
			rootElement.internalEventMappings
		)
	};

	if (isLegacy(pageStateType)) {
		const applicability =
			uiTypeParameters && uiTypeParameters.applicability
				? uiTypeParameters.applicability
				: {
						type: 'ANY',
						applicablePage: ''
				  };

		macroponent.applicability = applicability;
	}

	return macroponent;
}
