import { createHttpEffect } from '@servicenow/ui-effect-http';
import isEmpty from 'lodash/isEmpty';
import {
	PAGE_REQUESTED,
	PAGE_REQUESTED_STARTED,
	PAGE_REQUESTED_HTTP,
	PAGE_REQUEST_HTTP_STARTED,
	PAGE_REQUEST_HTTP_FAILED,
	PAGE_REQUEST_HTTP_SUCCEEDED,
	ISOLATED_EDIT_MODE_REQUESTED,
	ISOLATED_EDIT_MODE_REQUEST_SUCCEEDED,
	ISOLATED_EDIT_MODE_REQUEST_FAILED
} from './pageActions';
import { COMPONENT_LIST_REQUESTED } from '../componentList/componentListActions';
import processContextState, {
	convertToShorthand
} from '../contextState/contextStateProcessor';
import pageEndpointFromType from './utils/pageEndpointFromType';
import effectProxy from '../helpers/effectProxy';
import get from 'lodash/get';
const pageRequested = ({ state, dispatch, action }) => {
	const {
		properties: { pages }
	} = state;
	const {
		payload: { sysId, noDomain, isCreatingPage }
	} = action;
	!isCreatingPage &&
		dispatch(PAGE_REQUESTED_STARTED, {
			sysId,
			pages,
			noDomain
		});
};
const pageRequestedStarted = ({ dispatch, action, properties }) => {
	const {
		payload: { sysId, noDomain }
	} = action;
	dispatch(PAGE_REQUESTED_HTTP, {
		sysId,
		pagesEndpoint: pageEndpointFromType(properties.pageType),
		['sysparm_nodomain']: noDomain
	});
	dispatch(COMPONENT_LIST_REQUESTED, {
		pageType: properties.pageType
	});
};
export const pageRequestedHttp = createHttpEffect(
	'/api/now/uxframework/:pagesEndpoint/:sysId',
	{
		method: 'GET',
		pathParams: ['pagesEndpoint', 'sysId'],
		queryParams: ['sysparm_transaction_scope', 'sysparm_nodomain'],
		startActionType: PAGE_REQUEST_HTTP_STARTED,
		successActionType: PAGE_REQUEST_HTTP_SUCCEEDED,
		errorActionType: PAGE_REQUEST_HTTP_FAILED,
		batch: false
	}
);
const pageRequestStarted = ({ updateProperties }) =>
	updateProperties({ isLoadingPage: true });
const isolatedEditModeRequested = createHttpEffect(
	`/api/now/builder/viewports/parent/:parentScreenId/info/:parentElementId`,
	{
		method: 'GET',
		pathParams: ['parentScreenId', 'parentElementId'],
		successActionType: ISOLATED_EDIT_MODE_REQUEST_SUCCEEDED,
		errorActionType: ISOLATED_EDIT_MODE_REQUEST_FAILED,
		batch: false
	}
);
const isolatedEditModeRequestSucceeded = ({
	action: {
		payload: {
			result: { parentData, parentInfo }
		}
	},
	properties,
	updateState,
	updateProperties,
	dispatch,
	state
}) => {
	const { dataBrokerDefinitions } = properties;
	const elementProperties = get(parentInfo, 'elementProperties', {});
	if (dataBrokerDefinitions && !isEmpty(parentData)) {
		const propsToUpdate = processContextState({
			contextState: { parentData, parentInfo },
			updateState,
			properties
		});
		updateProperties(propsToUpdate);
		updateState({
			dataBrokerElements: {
				...state.dataBrokerElements,
				...propsToUpdate.ancestorDataBrokerTags,
				...propsToUpdate.dataBrokerTags
			}
		});
	} else {
		updateProperties({
			parentInfo: {
				...parentInfo,
				elementProperties: convertToShorthand(elementProperties)
			}
		});
	}
	dispatch('UIB_APP#RESET_ROUTE_STATUS', {}, { parentData });
};
export default {
	[PAGE_REQUESTED]: pageRequested,
	[PAGE_REQUESTED_STARTED]: pageRequestedStarted,
	[PAGE_REQUESTED_HTTP]: effectProxy(pageRequestedHttp),
	[PAGE_REQUEST_HTTP_STARTED]: pageRequestStarted,
	[ISOLATED_EDIT_MODE_REQUESTED]: isolatedEditModeRequested,
	[ISOLATED_EDIT_MODE_REQUEST_SUCCEEDED]: isolatedEditModeRequestSucceeded
};
