import isEmpty from 'lodash/isEmpty';
import flatten from 'lodash/flatten';
import get from 'lodash/get';
import { t } from 'sn-translate';
import { isUxValueModel, getShorthandFromUxValue } from '../../factories';
import { Property, MacroponentProperty } from '../../models';
import {
	VIEWPORT_ROUTES,
	ACTIVE_ROUTE,
	DEFAULT_ROUTE,
	ACTIVE_SCREEN,
	VIEWPORT_CONFIGURATION,
	REPEATED_VIEWPORT_TAB_DATA,
	DYNAMIC_VIEWPORT_TAB_DATA
} from '../element/reservedPropertyIds';
import { getTemplates } from './templateLoader';
import { getElementByProperty } from '../utils.js';
import reduceContextBindingsWithAncestors from '../pageProperties/utils/reduceContextBindingsWithAncestors';
import { resolveBinding } from '../../utils';
const LOADING_ERROR = 'loading-error';
const LOADING = 'loading';

const renderPlaceholder = (state, message = null) => {
	const node = document.createElement('now-empty-state');
	node.setAttribute('slot', 'viewport');
	node.setAttribute('type', state);
	node.setAttribute('body-text', message);
	return node;
};

const setViewportContent = (host, content) => {
	if (!host) {
		return;
	}

	if (!isEmpty(content.children)) {
		content.children[0].setAttribute('slot', 'viewport');
	}

	const range = document.createRange();
	range.selectNodeContents(host);
	range.deleteContents();
	range.insertNode(content);
	range.detach();
};

const resolveMacroponentConfig = (macroponentConfiguration, pageProperties) => {
	if (isEmpty(macroponentConfiguration)) {
		return {};
	}

	const mcpConfigAsProperties = Object.keys(macroponentConfiguration).map(
		(name) => {
			let value;
			if (isUxValueModel(macroponentConfiguration[name])) {
				value = getShorthandFromUxValue(macroponentConfiguration[name]);
			} else {
				value = macroponentConfiguration[name];
			}
			return new MacroponentProperty.create({
				name,
				value
			});
		},
		{}
	);

	const pagePropsAsObject = pageProperties.reduce((acc, cur) => {
		return {
			...acc,
			[cur.name]: cur.value
		};
	}, {});

	return reduceContextBindingsWithAncestors(
		{
			rootElement: {
				macroponentProperties: mcpConfigAsProperties
			}
		},
		pagePropsAsObject
	);
};

export const renderContents = async (host, mcp, pageProperties) => {
	if (!host) {
		return;
	}

	setViewportContent(host, renderPlaceholder(LOADING, t('Please wait')));

	const {
		macroponentSysId: mcpId,
		macroponentConfiguration: mcpConfiguration,
		fields
	} = mcp;

	const resolvedMacroponentConfig = resolveMacroponentConfig(
		mcpConfiguration,
		pageProperties
	);

	const fragmentGenerator = await getTemplates([mcpId]);
	const fragment =
		fragmentGenerator &&
		fragmentGenerator[mcpId] &&
		fragmentGenerator[mcpId]({
			...resolvedMacroponentConfig,
			...fields
		});

	if (fragment) {
		setViewportContent(host, fragment);
	} else {
		setViewportContent(
			host,
			renderPlaceholder(
				LOADING_ERROR,
				t('An error occurred while trying to load the viewport')
			)
		);
	}
};

export const renderEmptyState = (
	host,
	hasRoutes = true,
	message = t('Please create viewport routes.')
) => {
	if (!host) {
		return;
	}
	const emptyState = renderPlaceholder('empty', message);

	if (hasRoutes) {
		setViewportContent(host, emptyState);
	} else {
		if (!host.shadowRoot) {
			host.attachShadow({ mode: 'open' });
		}
		host.shadowRoot.innerHTML = '';
		host.shadowRoot.appendChild(emptyState);
	}
};

const getDefaultActiveRoute = (viewportRoutes, defaultRoute) => {
	const routes = flatten(Object.values(viewportRoutes));
	const defaultRouteInfo = routes.find(
		(route) => route.routeType === defaultRoute
	);

	if (!isEmpty(defaultRouteInfo)) {
		return get(
			viewportRoutes,
			// TODO: resolve this 0 to the right value once we have route variants
			// figured out
			`${defaultRouteInfo.routeType}[0]`,
			{}
		);
	} else {
		return routes.sort((a, b) => a.order - b.order)[0] || {};
	}
};

const getActiveRouteAndScreen = (
	elementId,
	state,
	viewportRoutes,
	defaultRoute,
	variantId
) => {
	const route = get(
		state,
		`activeViewportRoutes.${variantId}.${elementId}`,
		getDefaultActiveRoute(viewportRoutes, defaultRoute)
	);

	const screen = get(
		state,
		`activeViewportScreens.${variantId}.${elementId}`,
		get(route, `macroponents[0]`, {})
	);

	return {
		route,
		screen
	};
};

export const initializeDataDrivenViewportProperties = (dataSource, state) => {
	return {
		[REPEATED_VIEWPORT_TAB_DATA]: new Property({
			name: REPEATED_VIEWPORT_TAB_DATA,
			columnName: REPEATED_VIEWPORT_TAB_DATA,
			label: t('Data driven viewport'),
			type: 'string',
			value: dataSource
				? transformAndResolveDataSource(dataSource, state)
				: null
		}),
		[DYNAMIC_VIEWPORT_TAB_DATA]: new Property({
			name: DYNAMIC_VIEWPORT_TAB_DATA,
			columnName: DYNAMIC_VIEWPORT_TAB_DATA,
			label: t('Viewport data source'),
			type: 'object',
			value: dataSource
		})
	};
};

export const isDataDrivenTab = (route) => !isEmpty(route.fields);

const sortViewportRoutes = (viewportRoutes) => {
	// when you delete a viewport route, the first route in it gets set as a default and active route. As the panel items are already sorted, this will select the first route.
	// Todo: Remove this once backend sorts it before passing it to the frontend.
	const sortedViewportRoutes = {};
	const sortedRouteKeys = Object.keys(viewportRoutes).sort(
		(a, b) => viewportRoutes[a][0].order - viewportRoutes[b][0].order
	);
	sortedRouteKeys.forEach((elem) => {
		sortedViewportRoutes[elem] = viewportRoutes[elem];
	});
	return sortedViewportRoutes;
};

export const initializeViewportElementProperties = (
	elementId,
	viewportRoutes,
	defaultRoute,
	state = {},
	variantId = '',
	dataSource
) => {
	const { route, screen } = getActiveRouteAndScreen(
		elementId,
		state,
		viewportRoutes,
		defaultRoute,
		variantId
	);

	// Data driven TabSet/Viewport component needs the routeType as the id in the viewportRoutes
	const modifiedViewportRoutes = {};
	const viewportRoutesKeys = Object.keys(viewportRoutes);
	viewportRoutesKeys.forEach((elem) => {
		if (isDataDrivenTab(viewportRoutes[elem][0])) {
			const dataDrivenRouteType = get(
				viewportRoutes,
				`${elem}[0].routeType`,
				''
			);
			modifiedViewportRoutes[dataDrivenRouteType] = viewportRoutes[elem];
		} else {
			modifiedViewportRoutes[elem] = viewportRoutes[elem];
		}
	});
	const properties = {
		[VIEWPORT_ROUTES]: new Property({
			name: VIEWPORT_ROUTES,
			columnName: VIEWPORT_ROUTES,
			label: t('Viewport Routes'),
			type: 'array',
			value: sortViewportRoutes(modifiedViewportRoutes)
		}),
		[ACTIVE_ROUTE]: new Property({
			name: ACTIVE_ROUTE,
			columnName: ACTIVE_ROUTE,
			label: t('Active Route'),
			type: 'object',
			value: route
		}),
		[ACTIVE_SCREEN]: new Property({
			name: ACTIVE_SCREEN,
			columnName: ACTIVE_SCREEN,
			label: t('Active Screen'),
			type: 'object',
			value: screen
		}),
		...initializeDataDrivenViewportProperties(dataSource, state)
	};

	if (
		!isEmpty(defaultRoute) &&
		isDefaultRouteNotPresent(defaultRoute, viewportRoutes)
	) {
		// if the existing defaultRoute prop is no longer valid then update
		// it to be that of the lowest order route
		properties[DEFAULT_ROUTE] = new Property({
			name: DEFAULT_ROUTE,
			columnName: DEFAULT_ROUTE,
			label: t('Default Route'),
			type: 'string',
			value: route.routeType
		});
	}

	return properties;
};

const isDefaultRouteNotPresent = (defaultRoute, viewportRoutes) => {
	let routeNotPresent = true;
	Object.values(viewportRoutes).forEach((routes) => {
		// Todo: change this when we have route variants
		if (routeNotPresent) {
			const route = routes[0];
			if (route.routeType === defaultRoute) {
				routeNotPresent = false;
			}
		}
	});
	return routeNotPresent;
};

export const transformAndResolveDataSource = (dataSource, { properties }) => {
	const {
		pageState,
		ancestorPageProperties,
		dataBrokerInstances,
		ancestorDataBrokerInstances,
		ancestorElements,
		clientScriptApi,
		appChromeSettings
	} = properties;

	const contextBindings = reduceContextBindingsWithAncestors(
		pageState,
		ancestorPageProperties
	);
	const rootElement = get(pageState, 'rootElement', {});
	return resolveBinding(dataSource, {
		bindings: contextBindings,
		ancestorPageProperties,
		dataBrokerInstances,
		ancestorDataBrokerInstances,
		rootElement,
		ancestorElements,
		clientScriptApi,
		appChromeSettings
	});
};

export const isViewportComponent = (component) =>
	get(component, 'category', '') === 'viewport';

export const isViewportIsolatedEditingDisabled = (properties) => {
	const viewportConfigFromNowUi = properties[VIEWPORT_CONFIGURATION];
	if (viewportConfigFromNowUi) {
		return viewportConfigFromNowUi.disableIsolationEditing;
	}
	return false;
};

export const getViewportElementAndProperties = (pageState, elementId) => {
	const newRootElement = get(pageState, 'rootElement', {});
	const viewportElement = getElementByProperty(newRootElement, elementId, 'id');
	const viewportRoutes = get(
		viewportElement,
		'properties.viewportRoutes.value',
		{}
	);
	const activeRoute = get(viewportElement, 'properties.activeRoute.value', {});

	const defaultRoute = get(
		viewportElement,
		'properties.defaultRoute.value',
		''
	);

	const activeScreen = get(
		viewportElement,
		'properties.activeScreen.value',
		{}
	);

	// we want this to be undefined when the element is dropped on the stage
	const dynamicViewportTabData = get(
		viewportElement,
		`properties[${DYNAMIC_VIEWPORT_TAB_DATA}]`
	);

	const repeatedViewportTabData = get(
		viewportElement,
		`properties[${REPEATED_VIEWPORT_TAB_DATA}]`
	);

	return {
		viewportElement,
		viewportRoutes,
		activeRoute,
		defaultRoute,
		activeScreen,
		dynamicViewportTabData,
		repeatedViewportTabData
	};
};
