import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	PAGE_TEMPLATE_CREATE_REQUESTED,
	PAGE_TEMPLATE_CREATE_HTTP_REQUESTED,
	PAGE_TEMPLATE_CREATE_STARTED,
	PAGE_TEMPLATE_CREATE_FAILED,
	PAGE_TEMPLATE_CREATE_SUCCEEDED
} from './pageTemplatesActions';
import { TEMPLATES_REQUESTED } from '../templates/templatesActions';
import { MACROPONENT_CATEGORY, SCREEN_TEMPLATE_SYS_ID } from '../../constants';
import effectProxy from '../helpers/effectProxy';
import { throwError } from '../../utils';

const pageCreateRequested = ({ action, properties, dispatch }) => {
	const { name: label, layout } = action.payload;
	const { templateTypes } = properties;

	const pageType = (templateTypes.find((type) => type.name === 'page') || {})
		.id;

	dispatch(PAGE_TEMPLATE_CREATE_HTTP_REQUESTED, {
		data: {
			label,
			associatedTypes: [pageType],
			macroponentInstance: {
				macroponent: {
					name: label,
					category: MACROPONENT_CATEGORY,
					extendsSysId: SCREEN_TEMPLATE_SYS_ID,
					layout: JSON.parse(layout)
				}
			}
		}
	});
};

// TODO: figure out async and ui response
const pageTemplateCreateStarted = ({ updateProperties }) =>
	updateProperties({ isSavingTemplate: true });

const pageTemplateCreateSucceeded = ({ dispatch }) => {
	dispatch(TEMPLATES_REQUESTED);
};

const pageTemplateCreateFailed = () =>
	throwError('Failed to save page template');

export const pageTemplateCreateHttpEffect = createHttpEffect(
	'/api/now/builder/templates',
	{
		method: 'POST',
		dataParam: 'data',
		startActionType: PAGE_TEMPLATE_CREATE_STARTED,
		successActionType: PAGE_TEMPLATE_CREATE_SUCCEEDED,
		errorActionType: PAGE_TEMPLATE_CREATE_FAILED,
		batch: false
	}
);

export default {
	[PAGE_TEMPLATE_CREATE_REQUESTED]: pageCreateRequested,
	[PAGE_TEMPLATE_CREATE_HTTP_REQUESTED]: effectProxy(
		pageTemplateCreateHttpEffect
	),
	[PAGE_TEMPLATE_CREATE_STARTED]: pageTemplateCreateStarted,
	[PAGE_TEMPLATE_CREATE_FAILED]: pageTemplateCreateFailed,
	[PAGE_TEMPLATE_CREATE_SUCCEEDED]: pageTemplateCreateSucceeded
};
