import pick from 'lodash/pick';
import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';

const hostAllowList = [
	'flex',
	'align-self',
	'order',
	'flex-grow',
	'flex-shrink',
	'flex-basis',
	'grid-column-start',
	'grid-column-end',
	'grid-row-start',
	'grid-row-end',
	'grid-area',
	'justify-self',
	'place-self',
	'display',
	'width',
	'min-width',
	'max-width',
	'height',
	'min-height',
	'max-height',
	'margin',
	'margin-top',
	'margin-right',
	'margin-bottom',
	'margin-left'
];

const stripSlotStyles = (model = {}, layouRole = 'default') => {
	const defaultModel = get(model, layouRole, {}) || {};
	const items = defaultModel.items || [];

	return {
		...model,
		default: {
			...defaultModel,
			items: items.map((item) => {
				return {
					...item,
					styles: pick(item.styles || {}, hostAllowList)
				};
			})
		}
	};
};
const cssStringToObject = (css) => {
	/**
	 * Fun regex:
	 * (?:^|;) -- This is making sure the pattern either starts with the beginning of the string or the previous line has a semicolon
	 * \s* -- covering possible whitespace
	 * (\w[\w-]+) -- Looking for rules that start with a word character and then consists of word characters and '-'. This is captured to use as the rule
	 * \s* -- possible whitespace
	 * : -- a colon!
	 * \s* -- possible whitespace
	 * (?:\([^)]+\)|[^;])+) -- Capturing the rule's value. Anything that is in a parentheses or (the pipe character) anything that's not a semicolon
	 * \s* -- possible whitespace
	 * (?:;|$) making sure the end of the 'rule' is either the end of the input or a semicolon.
	 */

	const rgx = /\s*(\w[\w-]+)\s*:\s*((?:\([^)]+\)|[^;])+)\s*(?:;|$)/gm;
	const cssObj = {};
	let result = rgx.exec(css);

	while (result) {
		cssObj[result[1].trim()] = result[2].trim();
		result = rgx.exec(css);
	}

	return cssObj;
};

const HostCss = ({ cssObj, id }) => {
	if (isEmpty(cssObj)) {
		return null;
	}
	const filteredObj = pick(cssObj, hostAllowList);
	const css = `:host {${Object.entries(filteredObj)
		.map((arr) => arr.join(' : '))
		.join(';')}}`;

	return <style key={id} type="text/css" innerHTML={css} />;
};

const getHostCSS = (host) => {
	if (!host || !host.shadowRoot) {
		return {};
	}
	const tag = host.tagName;
	const root = host.shadowRoot;

	return [...root.styleSheets, ...root.adoptedStyleSheets]
		.map((styleSheet) => {
			try {
				return [...styleSheet.cssRules]
					.map((rule) => {
						const rxp = new RegExp(`\\:host\\(${tag}\\)`, 'i');
						const text = rule.cssText.replace(rxp, ':host');

						return text;
					})
					.filter((ruleText) => {
						const rxp = new RegExp(`:host\\s*[,{]`, 'i');

						return rxp.test(ruleText);
					})
					.reduce((acc, css) => {
						const regexForInsideBrackets = /^.+\{([^}]+)}.*$/m;
						const results = regexForInsideBrackets.exec(css) || [null, ''];
						const parsed = cssStringToObject(results[1]);

						return { ...acc, ...parsed };
					}, {});
			} catch (e) {
				console.error(
					'Access to stylesheet %s is denied. Ignoring...',
					styleSheet.href,
					e.message
				);
			}
		})
		.filter(Boolean)
		.reduce((acc, css) => {
			return { ...acc, ...css };
		}, {});
};

export { getHostCSS, HostCss, hostAllowList, stripSlotStyles };
