import cloneDeep from 'lodash/cloneDeep';
import createPageState from '../../../models/createPageState';
import createDefaultLayout, {
	DEFAULT_SLOT_NAME
} from '../utils/createDefaultLayout';

import {
	findElementById,
	getRootElementFromState,
	findParentElement,
	isOnlySlottedChild,
	deleteChildElement,
	elementUsesSNLayout
} from '../utils/element';

import {
	findLayoutElementIdFromSlotName,
	deleteElementFromLayout
} from '../utils/api/layout';

import { Element } from '../../../models';

const elementDeleted = ({ state, action, updateProperties }) => {
	const {
		properties: { pageState }
	} = state;

	const newPageState = cloneDeep(pageState);

	const { elementId: deletedElementId } = action.payload;

	const newRootElement = cloneDeep(getRootElementFromState(state));

	const onlySlottedChild = isOnlySlottedChild(deletedElementId, newRootElement);

	if (newRootElement.id === deletedElementId) {
		// Don't allow deleting the root element
		return;
	}

	const parentElement = findParentElement(newRootElement, deletedElementId);

	if (elementUsesSNLayout(parentElement)) {
		const detachedElementSlotName = findElementById(
			newRootElement,
			deletedElementId
		).slot.name;

		const layoutElementId = findLayoutElementIdFromSlotName({
			layout: parentElement.layout,
			slotName: detachedElementSlotName
		});

		if (onlySlottedChild) {
			parentElement.layout = deleteElementFromLayout({
				layout: parentElement.layout,
				id: layoutElementId
			});
		}
	}

	deleteChildElement(parentElement, deletedElementId);

	// Prevent the root element layout from being null
	if (newRootElement.layout === null) {
		newRootElement.layout = createDefaultLayout(DEFAULT_SLOT_NAME, 'landing');
	}
	updateProperties({
		pageState: createPageState(
			{
				rootElement: new Element(newRootElement)
			},
			newPageState
		)
	});
};

export { elementDeleted };
