import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	DATA_BROKER_DEFINITIONS_REQUESTED,
	DATA_BROKER_DEFINITIONS_REQUEST_STARTED,
	DATA_BROKER_DEFINITIONS_REQUEST_FAILED,
	DATA_BROKER_DEFINITIONS_REQUEST_SUCCEEDED
} from './dataBrokerDefinitionsActions';
import { throwError } from '../../../utils';
import effectProxy from '../../helpers/effectProxy';

export const dataBrokerDefinitionsRequested = createHttpEffect(
	'/api/now/uxf/databroker/definitions',
	{
		method: 'GET',
		startActionType: DATA_BROKER_DEFINITIONS_REQUEST_STARTED,
		successActionType: DATA_BROKER_DEFINITIONS_REQUEST_SUCCEEDED,
		errorActionType: DATA_BROKER_DEFINITIONS_REQUEST_FAILED
	}
);

const dataBrokerDefinitionsRequestStarted = ({ updateProperties }) =>
	updateProperties({
		dataBrokerDefinitions: [],
		dataBrokerOperations: []
	});

const dataBrokerDefinitionsRequestSucceeded = ({
	action,
	updateProperties
}) => {
	const dataBrokerDefinitions = action.payload.result.map((def) => ({
		...def,
		simpleMutationBroker: !def.hydrationSysId
	}));

	const dataBrokerOperations = dataBrokerDefinitions.reduce((acc, dbDef) => {
		const dbOperations = {
			definitionId: dbDef.sysId,
			operations: dbDef.operations
		};
		return acc.concat(dbOperations);
	}, []);

	updateProperties({
		dataBrokerDefinitions,
		dataBrokerOperations
	});
};

const dataBrokerDefinitionsRequestFailed = () => {
	throwError(`Failed to load data broker definitions`);
};

export default {
	[DATA_BROKER_DEFINITIONS_REQUESTED]: effectProxy(
		dataBrokerDefinitionsRequested
	),
	[DATA_BROKER_DEFINITIONS_REQUEST_STARTED]: dataBrokerDefinitionsRequestStarted,
	[DATA_BROKER_DEFINITIONS_REQUEST_FAILED]: dataBrokerDefinitionsRequestFailed,
	[DATA_BROKER_DEFINITIONS_REQUEST_SUCCEEDED]: dataBrokerDefinitionsRequestSucceeded
};
