import Model from './model';
import { seismicCuid } from '../utils';

export default class EventMappingTarget extends Model {
	constructor({
		targetId = '', // string
		type = '', // string
		declarativeAction = null, // object
		event = null, // object
		operation = null, // object
		clientScript = null,
		clientTransformScript = null // string
	}) {
		super();
		this.targetId = targetId || seismicCuid();
		this.type = type;
		this.declarativeAction = declarativeAction;
		this.event = event;
		this.operation = operation;
		this.clientScript = clientScript;
		this.clientTransformScript = clientTransformScript;
	}
}
