import { SET_DOMAIN_EXPANDED } from './domainScopeActions';
import { SN_UIB_APP_UPDATE_APP_ROUTE_STATE } from '../../constants';
import { PAGE_LIST_REQUESTED } from '../pageList/pageListActions';

const setExpandedDomainScopeHandler = ({ action, dispatch }) => {
	const { isDomainExpanded, appsSysId } = action.payload;

	dispatch(SN_UIB_APP_UPDATE_APP_ROUTE_STATE, {
		sysId: appsSysId,
		sysparm_query_no_domain: isDomainExpanded
	});
	dispatch(PAGE_LIST_REQUESTED, {
		appConfigId: appsSysId,
		sysparm_query_no_domain: isDomainExpanded
	});
};

export default {
	[SET_DOMAIN_EXPANDED]: setExpandedDomainScopeHandler
};
