import { CLIENT_TRANSFORM_SCRIPT, SCRIPT_SHORTHAND_PATTERN } from './constants';
import isUndefined from 'lodash/isUndefined';

export default function ClientTransformScriptFactory({
	model,
	shorthand
} = {}) {
	if (!shorthand && !model) {
		throw new TypeError(
			'Something went wrong initializing ClientTransformScriptFactory'
		);
	}

	// PRIVATE PROPERTIES
	const fType = CLIENT_TRANSFORM_SCRIPT;
	const fShorthand = !isUndefined(shorthand)
		? shorthand
		: getShorthandFromModel(model);
	const fModel = model || getModelFromShorthand(shorthand);

	function getShorthandFromModel(model) {
		return `javascript(${model.script.inlineScript})${
			model.script.apiVersion === '2.0.0' ? '!' : ''
		}`;
	}

	function getModelFromShorthand(shorthand) {
		const scriptMatcher = shorthand.match(SCRIPT_SHORTHAND_PATTERN);
		let resolvedScript = '',
			apiVersion = '1.0.0';
		if (scriptMatcher) {
			resolvedScript = scriptMatcher[1];
			apiVersion = scriptMatcher[2] ? '2.0.0' : '1.0.0';
		}
		return {
			type: fType,
			script: {
				apiVersion,
				inlineScript: resolvedScript
			}
		};
	}

	// PUBLIC API
	return {
		getShorthand() {
			return fShorthand;
		},

		getModel() {
			return fModel;
		}
	};
}
