import get from 'lodash/get';
import { getWithNullDefault, normalizeRowColumnSlots } from '../utils';
const LAYOUT_TYPE = 'layout';
const ELEMENT_TYPE = 'element';
const CONTAINER_TYPE = 'container';
const LEAF_TYPE = 'leaf';
const SLOT_TYPE = 'slot';
const ROW_TYPE = 'row';
const COLUMN_TYPE = 'column';
const childrenKeyLookup = {
	[ROW_TYPE]: 'columns',
	[COLUMN_TYPE]: 'rows'
};

const createNode = ({
	nodeType,
	type,
	children,
	id,
	slotStyles,
	slotName,
	size = null,
	layoutType
}) => {
	return {
		type,
		nodeType,
		children,
		slotStyles,
		id,
		layoutType,
		size,
		slotName
	};
};
const handlersByType = {
	rowColumn: rowColHandlers,
	default: defaultHandlers
};

function rowColHandlers() {
	const processLayoutTree = (
		layoutNode = {},
		elements = [],
		layoutType,
		parentId
	) => {
		if (!layoutNode.id) {
			return null;
		}
		const type = layoutNode.type === ROW_TYPE ? ROW_TYPE : COLUMN_TYPE;
		const nodeType = LAYOUT_TYPE;

		const childrenKey = childrenKeyLookup[type];

		const children = layoutNode.slotName
			? elements.filter(({ slotName }) => slotName === layoutNode.slotName)
			: (layoutNode[childrenKey] || []).map((node) =>
					processLayoutTree(node, elements, layoutType, parentId)
			  );
		return createNode({
			nodeType,
			slotName: get(layoutNode, 'slotName') || null,
			type,
			id: layoutNode.id,
			children,
			layoutType,
			size: layoutNode.size
		});
	};
	const processChildren = (
		children = [],
		elementLayout = {},
		layoutRole,
		parentId
	) => {
		const rootLayoutNode = elementLayout.root;
		const processedElements = children.map((element) => {
			const { children, id, layout = {} } = element;
			const layoutType = get(layout, `${layoutRole}.type`, null);
			const process = get(handlersByType, layoutType, handlersByType.default)()
				.processChildren;

			const nodeType = ELEMENT_TYPE;
			const type = get(layout, `${layoutRole}`, false)
				? CONTAINER_TYPE
				: LEAF_TYPE;

			return createNode({
				children: process(
					children,
					get(layout, `${layoutRole}`, {}) || {},
					layoutRole,
					parentId
				),
				id,
				type,
				nodeType,
				slotName: get(element, 'slot.name')
			});
		});

		return [
			processLayoutTree(
				rootLayoutNode,
				processedElements,
				elementLayout.type,
				parentId
			)
		].filter((node) => node);
	};
	return { processChildren };
}
function defaultHandlers() {
	const processLayoutTree = (layoutNode = {}, elements = [], parentId) => {
		const { items = [] } = layoutNode;
		return items.map((item) => {
			const { slotName, styles = {} } = item;

			const children = elements.filter(
				({ slotName }) => slotName === item.slotName
			);

			return createNode({
				children,
				id: `${parentId}_${slotName}`,
				type: LEAF_TYPE,
				nodeType: SLOT_TYPE,
				slotStyles: styles,
				layoutType: layoutNode.type,
				slotName
			});
		});
	};

	const processChildren = (
		children = [],
		elementLayout = {},
		layoutRole,
		parentId
	) => {
		const processedElements = children.map((element) => {
			const { children, id, layout = {} } = element;
			const layoutType = get(layout, `${layoutRole}.type`, null);
			const process = get(handlersByType, layoutType, handlersByType.default)()
				.processChildren;
			const nodeType = ELEMENT_TYPE;
			const slots = getWithNullDefault(
				normalizeRowColumnSlots(layout),
				`${layoutRole}.items`,
				[]
			);
			const type = [children, slots].some((arr) => arr.length > 0)
				? CONTAINER_TYPE
				: LEAF_TYPE;

			return createNode({
				children: process(
					children,
					get(layout, `${layoutRole}`, {}) || {},
					layoutRole,
					id
				),
				id,
				type,
				nodeType,
				layoutType: elementLayout.type,
				slotName: get(element, 'slot.name')
			});
		});

		return processLayoutTree(elementLayout, processedElements, parentId).filter(
			(node) => node
		);
	};
	return { processChildren };
}

const mergeTrees = (rootElement, layoutRole) => {
	const { children, id, layout } = rootElement;
	const layoutType = get(layout, `${layoutRole}.type`, null);
	const processChildren = get(
		handlersByType,
		layoutType,
		handlersByType.default
	)().processChildren;
	return createNode({
		children: processChildren(
			children,
			get(layout, `${layoutRole}`, {}),
			layoutRole,
			id
		),
		id,
		nodeType: ELEMENT_TYPE,
		layoutType,
		type: CONTAINER_TYPE
	});
};

export {
	LAYOUT_TYPE,
	ELEMENT_TYPE,
	CONTAINER_TYPE,
	LEAF_TYPE,
	ROW_TYPE,
	COLUMN_TYPE
};

export default class PageTreeModel {
	static attributes = {
		root: {
			default: {}
		}
	};
	// the role is being passed so we can soon support different layouts (breakpoints, etc) for the same page.
	constructor({ rootElement, layoutRole = 'default' }) {
		this.root = mergeTrees(rootElement, layoutRole);
	}
}
