import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import { ROOT_MACROPONENT_SOURCE_EVENTS } from './page/constants';

export default {
	/**
	 * List of available components
	 * @type {Array<Component>}
	 */
	components: {
		default: [],
		selectable: true
	},

	/**
	 * List of available data broker definitions
	 */
	dataBrokerDefinitions: {
		default: [],
		selectable: true
	},

	/**
	 * List of data broker instances
	 * @type {Array<DataBrokerInstance>}
	 */
	dataBrokerInstances: {
		default: [],
		selectable: true
	},

	/**
	 * Current properties available for every data broker instance in the pageState,
	 * including value set on the property and property metadata
	 * @type {Object<String, Array<Object>>}
	 */
	dataBrokerProperties: {
		default: {},
		selectable: true
	},

	/**
	 * List of all ancestor data broker instances
	 * @type {Array<DataBrokerInstance>}
	 */
	ancestorDataBrokerInstances: {
		default: [],
		selectable: true
	},

	/**
	 * List of data broker instances on the direct parent
	 * @type {Array<DataBrokerInstance>}
	 */
	parentDataBrokerInstances: {
		default: [],
		selectable: true
	},

	/**
	 * List of all page properties from ancestor tree
	 * @type {Object<String, String>}
	 */
	ancestorPageProperties: {
		default: {},
		selectable: true
	},

	/**
	 * List of page properties from the direct parent
	 * @type {Object<String, String>}
	 */
	parentPageProperties: {
		default: {}
	},

	/**
	 * List of all ancestor element IDs and their instance names and propertyValues
	 * @type {Object<String, Object>}
	 */
	ancestorElements: {
		default: {},
		selectable: true
	},

	/**
	 * List of element IDs and instance names and propertyValues from the direct parent
	 * @type {Object<String, String>}
	 */
	parentElements: {
		default: {},
		selectable: true
	},

	/**
	 * List of available handlers (type EVENT or DECLARATIVE_ACTION or DATABROKER_OP)
	 * @public
	 * @type {Array<EventHandler>}
	 */
	eventHandlers: {
		default: [],
		selectable: true
	},

	/**
	 * A nested tree of elements in the stage
	 */
	elementTree: {
		default: {},
		selectable: true
	},

	/**
	 * Current event mappings configured for every element in the pageState
	 * @type {Object<String, Array<Object>>}
	 */
	elementEventMappings: {
		default: {},
		selectable: true
	},

	/**
	 * Current event mappings configured for the screen
	 * @type {Object<String, Array<Object>>}
	 */
	screenEventMappings: {
		default: [],
		selectable: true
	},

	/**
	 * Current event mappings configured for data broker instances in the pageState
	 * @type {Object<string, Arra}
	 */
	dataBrokerEventMappings: {
		default: [],
		selectable: true
	},

	/*
	 * Current values for each event mapping
	 * @type {Object<String>, Array<Object>>}
	 */
	eventMappingHandlers: {
		default: {},
		selectable: true
	},

	/**
	 * Current events available for every element in the pageState
	 * @type {Object<String, Array<Object>>}
	 */
	elementEvents: {
		default: {},
		selectable: true
	},

	/**
	 * Current properties available for every element in the pageState, including
	 * value set on the property and property metadata
	 * @type {Object<String, Array<Object>>}
	 */
	elementProperties: {
		default: {},
		selectable: true
	},

	/**
	 * Current layout value for every element in the pageState, including
	 * @type {Object<String, Array<Object>>}
	 */
	elementLayouts: {
		default: {},
		selectable: true
	},

	/**
	 * Current style values for every element in the pageState, including
	 * @type {Object<String, Array<Object>>}
	 */
	elementStyles: {
		default: {},
		selectable: true
	},

	/**
	 * Current status of deleting a page
	 * @type {boolean}
	 */
	isDeletingPage: {
		default: false,
		selectable: true
	},

	/**
	 * Current environment middleware is being instantiated
	 */
	isLegacy: {
		default: null
	},

	/**
	 * Current status of loading available components
	 * @type {boolean}
	 */
	isLoadingComponents: {
		default: false,
		selectable: true
	},

	/*
	 * Current status of loading a page
	 * @type {boolean}
	 */
	isLoadingPage: {
		default: false,
		selectable: true
	},

	/**
	 * Current status of loading available pages
	 * @type {boolean}
	 */
	isLoadingPages: {
		default: false,
		selectable: true
	},

	/**
	 * Protection policy to prevent copying protected pages or macroponents
	 */
	isPageCopiable: {
		default: true,
		selectable: true
	},

	/**
	 * Read only flag evaluated from the page and all of the pages macroponents
	 */
	isPageReadOnly: {
		default: false,
		selectable: true
	},
	/**
	 * Whether there are unsaved changes to the pageState property
	/*
	 * Current status of loading a page
	 * @type {boolean}
	 */
	isPageStateDirty: {
		default: false,
		selectable: true
	},

	/**
	 * Current status of saving (creating or updating) a page
	 * @type {boolean}
	 */
	isSavingPage: {
		default: false,
		selectable: true
	},

	/**
	 * Current metadata of page, including page parameters, type, name, and access priveleges.
	 * @type {Object}
	 */
	pageMetadata: {
		default: null,
		selectable: true
	},

	/**
	 * List of available pages
	 * @type {Array<LandingPage>}
	 */
	pages: {
		default: [],
		selectable: true
	},

	/**
	 * Current page state model
	 * @type {Object}
	 */
	pageState: {
		default: null,
		selectable: true
	},

	/**
	 * Pristine copy of page state model
	 * @type {Object}
	 */
	pageStatePristine: {
		default: null,
		selectable: false
	},

	/**
	 * Current merged tree of page's elements and layouts model
	 * @type {Object}
	 */
	pageTree: {
		default: null,
		selectable: true
	},

	/**
	 * List of page properties (root macroponent properties)
	 */
	pageProperties: {
		default: [],
		selectable: true
	},

	/**
	 * Layout model for the current page
	 */
	pageLayout: {
		default: {},
		selectable: true
	},
	/**
	 * Is the layout model currently the default model
	 */
	isDefaultPageLayout: {
		default: false,
		selectable: true
	},
	/**
	 * List of all UIB Templates
	 */
	templates: {
		default: [],
		selectable: true
	},
	/**
	 * Types of templates
	 * @type {Array<TemplateType>}
	 */
	templateTypes: {
		default: [],
		selectable: false
	},
	/**
	 * Macroponent definitions for templates
	 * @type {Array<Template>}
	 */
	templatesDefinitions: {
		default: [],
		selectable: false
	},

	/**
	 * List of operations for all data broker resources
	 * @private
	 * @type {Array<Object>}
	 */
	dataBrokerOperations: {
		default: []
	},
	bindings: {
		default: [],
		selectable: true
	},

	/**
	 * List of all the client scripts available for the current screen
	 */
	clientScripts: {
		default: [],
		selectable: true
	},

	/**
	 * Pristine copy of the client scripts
	 * @type {Object[]}
	 */
	clientScriptsPristine: {
		default: [],
		selectable: false
	},

	/**
	 * Client scripts can be added or deleted
	 */
	isClientScriptsBusy: {
		default: false,
		selectable: true
	},
	/**
	 * Map of eventHandlers and elenents that trigger them
	 */
	eventHandlerElementMap: {
		default: {},
		selectable: true
	},
	theme: {
		default: '',
		selectable: true
	},
	/**
	 * List of all the client state parameters configured on the current screen
	 */
	clientStateParameters: {
		default: [],
		selectable: true
	},
	/**
	 * An object containing the current client state data, with binding values transformed
	 */
	clientStateData: {
		default: {},
		selectable: true
	},
	/**
	 * Macroponent dispatched events
	 */
	dispatchedEvents: {
		default: [],
		selectable: true
	},
	handledEvents: {
		default: [],
		selectable: true
	},
	/**
	 * Interface to configuration Component Map
	 * @type {object}
	 */
	interfaceConfigurationComponent: {
		default: {},
		selectable: true
	},
	/**
	 * Table updated flags for notifying when back end data changes (AMB)
	 * @type {object}
	 */
	updatedTables: {
		default: {},
		selectable: true
	},
	/** Client scripts that need to evaluate at design time will use this api */
	clientScriptApi: {
		default: {
			elements: null,
			data: null,
			context: null,
			state: null
		},
		selectable: true
	},
	clientScriptApiCompletions: {
		default: {
			clientScript: `interface api {
				elements: null;
				data: null;
				state: null;
				context: null;
				/** Set current client state values */
				  setState(name: string, state: object): void;
			  }`,
			clientTransformScript: `interface api {
				elements: null;
				data: null;
				state: null;
				context: null;
			  }`
		},
		selectable: true
	},
	parentInfo: {
		default: {},
		selectable: true
	},
	newElementDefaultEventHandlers: {
		default: {},
		selectable: false
	},
	pageType: {
		default: null,
		selectable: false
	},
	hiddenElements: {
		default: {},
		selectable: true
	},
	isDomainExpanded: {
		default: false,
		selectable: true
	},
	routeState: {
		default: {}
	},
	isIsolatedEditAndEmptyState: {
		default: false,
		computed({ properties: { routeState, pageState } }) {
			return (
				get(routeState, 'queryParams.isolatedEditMode', false) &&
				isEmpty(pageState)
			);
		}
	},
	internalEventMappings: {
		default: [],
		selectable: true
	},
	internalEventMappingsSourceEvents: {
		default: [],
		selectable: true,
		computed({ properties: { handledEvents } }) {
			return [...(handledEvents || []), ...ROOT_MACROPONENT_SOURCE_EVENTS];
		}
	},
	appChromeSettings: {
		default: {},
		selectable: true
	}
};
