import { createHttpEffect } from '@servicenow/ui-effect-http';
import { transformDataBrokerInstancesToDataBrokerRuntime } from './utils/transformDataBrokerInstancesToDataBrokerRuntime';
import {
	DATA_BROKER_RUNTIME_EXECUTE_REQUESTED,
	DATA_BROKER_RUNTIME_EXECUTE_HTTP_REQUESTED,
	DATA_BROKER_RUNTIME_EXECUTE_STARTED,
	DATA_BROKER_RUNTIME_EXECUTE_SUCCEEDED,
	DATA_BROKER_RUNTIME_EXECUTE_FAILED,
	DATA_BROKER_RUNTIME_RESET_AND_EXECUTE_REQUESTED
} from './dataBrokerRuntimeActions';
import { DATA_BROKER_DEFINITIONS_REQUESTED } from './dataBrokerDefinitions/dataBrokerDefinitionsActions';
import { UPDATED_TABLES_RESET_REQUESTED } from '../updatedTables/updatedTablesActions';
import reduceContextBindingsWithAncestors from '../pageProperties/utils/reduceContextBindingsWithAncestors';
import { throwError, matchBindingSyntax } from '../../utils';
import effectProxy from '../helpers/effectProxy';
import get from 'lodash/get';
import cloneDeep from 'lodash/cloneDeep';
import { DATA_NAMESPACE } from '../../constants';
import createPageState from '../../models/createPageState';
import { createDataBrokerInstancePropertiesFromDataBrokerDefinition } from '../utils';

export const dataBrokerRuntimeExecuteHttpEffect = createHttpEffect(
	'/api/now/uxf/databroker/exec',
	{
		method: 'POST',
		batch: false,
		pathParams: ['pageEndpoint'],
		dataParam: 'data',
		startActionType: DATA_BROKER_RUNTIME_EXECUTE_STARTED,
		successActionType: DATA_BROKER_RUNTIME_EXECUTE_SUCCEEDED,
		errorActionType: DATA_BROKER_RUNTIME_EXECUTE_FAILED
	}
);

const dataBrokerRuntimeExecuteRequested = ({ properties, dispatch }) => {
	const {
		pageState,
		ancestorDataBrokerInstances,
		ancestorPageProperties,
		clientStateData,
		parentDataBrokerInstances,
		isIsolatedEditAndEmptyState,
		appChromeSettings
	} = properties;
	const dataBrokerInstances = isIsolatedEditAndEmptyState
		? parentDataBrokerInstances
		: get(pageState, 'dataBrokerInstances', []);
	const ancestorDBkrIDs = new Set(
		ancestorDataBrokerInstances.map((dbr) => dbr.id)
	);
	dataBrokerInstances.forEach((dbkr) => {
		for (const p in dbkr.properties) {
			const val = dbkr.properties[p].value;
			const bindExpr = val && matchBindingSyntax(val);
			if (
				bindExpr &&
				bindExpr.namespace === DATA_NAMESPACE &&
				ancestorDBkrIDs.has(bindExpr.id)
			) {
				dbkr.properties[p].value = null;
			}
		}
	});

	const dataBrokerRuntime = transformDataBrokerInstancesToDataBrokerRuntime(
		[...ancestorDataBrokerInstances, ...dataBrokerInstances],
		false,
		reduceContextBindingsWithAncestors(pageState, ancestorPageProperties),
		clientStateData,
		appChromeSettings
	);
	if (dataBrokerRuntime.length) {
		dispatch(DATA_BROKER_RUNTIME_EXECUTE_HTTP_REQUESTED, {
			data: dataBrokerRuntime
		});
	}
};

const dataBrokerRuntimeExecuteSucceeeded = ({
	action,
	properties,
	updateState
}) => {
	const {
		pageState = {},
		ancestorDataBrokerInstances,
		ancestorPageProperties,
		clientStateData,
		parentDataBrokerInstances,
		isIsolatedEditAndEmptyState,
		appChromeSettings
	} = properties;
	const dataBrokerInstances = isIsolatedEditAndEmptyState
		? parentDataBrokerInstances
		: get(pageState, 'dataBrokerInstances', []);

	const dataBrokerChain = transformDataBrokerInstancesToDataBrokerRuntime(
		[...ancestorDataBrokerInstances, ...dataBrokerInstances],
		true,
		reduceContextBindingsWithAncestors(pageState, ancestorPageProperties),
		clientStateData,
		appChromeSettings
	);

	const dataBrokerData = dataBrokerChain
		.map((dataBrokerLink, index) => {
			return {
				dataBrokerid: dataBrokerLink.dataBrokerid,
				result: get(action.payload, `result[${index}].executionResult`, {}),
				errors: get(action.payload, `result[${index}].errors`),
				executionTime: get(action.payload, `result[${index}].executionTime`, -1)
			};
		})
		.reduce((acc, dataBrokerLink) => {
			acc[dataBrokerLink.dataBrokerid] = {
				result: dataBrokerLink.result,
				executionTime: dataBrokerLink.executionTime,
				errors: dataBrokerLink.errors
			};

			return acc;
		}, {});

	updateState({
		path: 'dataBrokerData',
		value: dataBrokerData,
		operation: 'set'
	});
};

const dataBrokerRuntimeExecuteStarted = () => {
	// eslint-disable-next-line no-console
	console.log(`UXF Message: Started to execute data broker instances`); // TODO: Remove
};

const dataBrokerRuntimeExecuteFailed = () => {
	throwError(`Failed to execute data broker instances`);
};

const dataBrokerRuntimeResetAndExecuteRequested = ({
	dispatch,
	action,
	properties,
	updateProperties
}) => {
	const { __data } = action.payload;
	const inputProps = get(__data, 'record.props', null);
	const outputProps = get(__data, 'record.output_schema', null);
	const sysId = get(__data, 'sys_id', null);
	const actionType = get(__data, 'action', null);
	const changedDataBrokerDefinition = properties.dataBrokerDefinitions.find(
		(def) => def.sysId === sysId
	);
	if (!changedDataBrokerDefinition && actionType !== 'entry') {
		return;
	}

	let newInputProps,
		newOutputProps,
		newDataBrokerInstances = properties.pageState.dataBrokerInstances;
	if (inputProps) {
		try {
			newInputProps = JSON.parse(inputProps.value);
			changedDataBrokerDefinition.inputParams = newInputProps;
		} catch (e) {
			throwError('Failed to parse new incoming data broker prop changes');
		}
		const newInstanceProps = createDataBrokerInstancePropertiesFromDataBrokerDefinition(
			changedDataBrokerDefinition,
			false
		);
		newDataBrokerInstances = newDataBrokerInstances.map((dbinst) => {
			if (dbinst.definition.id === sysId) {
				return { ...dbinst, properties: newInstanceProps };
			}
			return dbinst;
		});
	}

	if (outputProps) {
		if (!outputProps.value) {
			changedDataBrokerDefinition.outputSchema = [
				{
					fieldType: 'object',
					label: 'Output',
					mandatory: false,
					name: 'output',
					readOnly: true,
					selectable: false,
					valueType: 'object'
				}
			];
		} else {
			try {
				newOutputProps = JSON.parse(outputProps.value);
				changedDataBrokerDefinition.outputSchema = newOutputProps;
			} catch (e) {
				throwError('Failed to parse new incoming data broker prop changes');
			}
		}
		const newInstanceOutputProps = createDataBrokerInstancePropertiesFromDataBrokerDefinition(
			changedDataBrokerDefinition,
			true
		);
		newDataBrokerInstances = newDataBrokerInstances.map((dbinst) => {
			if (dbinst.definition.id === sysId) {
				return { ...dbinst, outputProperties: newInstanceOutputProps };
			}
			return dbinst;
		});
	}

	(inputProps || outputProps) &&
		updateProperties({
			pageState: createPageState(
				{
					dataBrokerInstances: newDataBrokerInstances
				},
				cloneDeep(properties.pageState)
			)
		});

	dispatch(UPDATED_TABLES_RESET_REQUESTED, { table: 'sys_ux_data_broker' });
	dispatch(DATA_BROKER_DEFINITIONS_REQUESTED);
	dispatch(DATA_BROKER_RUNTIME_EXECUTE_REQUESTED);
};

export default {
	[DATA_BROKER_RUNTIME_EXECUTE_REQUESTED]: dataBrokerRuntimeExecuteRequested,
	[DATA_BROKER_RUNTIME_EXECUTE_HTTP_REQUESTED]: effectProxy(
		dataBrokerRuntimeExecuteHttpEffect
	),
	[DATA_BROKER_RUNTIME_EXECUTE_SUCCEEDED]: dataBrokerRuntimeExecuteSucceeeded,
	[DATA_BROKER_RUNTIME_EXECUTE_STARTED]: dataBrokerRuntimeExecuteStarted,
	[DATA_BROKER_RUNTIME_EXECUTE_FAILED]: dataBrokerRuntimeExecuteFailed,
	[DATA_BROKER_RUNTIME_RESET_AND_EXECUTE_REQUESTED]: dataBrokerRuntimeResetAndExecuteRequested
};
