import {
	VALID_BINDING_TYPES_ENUM,
	BINDING_SHORTHAND_PATTERN
} from './constants';
import { CONTEXT_NAMESPACE } from '../constants';

export default function BindingFactory({
	shorthand = null, // string
	model = null // object
} = {}) {
	if (!shorthand && !model) {
		throw new TypeError('Something went wrong initializing BindingFactory');
	}

	const fType = shorthand ? getTypeFromShorthand(shorthand) : model.type;
	const fShortHand = shorthand || getShorthandFromModel(model);
	const fModel = model || getModelFromShorthand(shorthand);

	function getTypeFromShorthand(shorthand) {
		const shorthandMatcher = shorthand.match(BINDING_SHORTHAND_PATTERN);
		if (!shorthandMatcher) {
			throw new TypeError('Invalid expression was supplied to Binding');
		}

		return Object.entries(VALID_BINDING_TYPES_ENUM).find(
			([, prefix]) => prefix === shorthandMatcher[1]
		)[0];
	}

	function getShorthandFromModel(model) {
		if (!VALID_BINDING_TYPES_ENUM[model.type]) {
			throw new TypeError('Invalid binding model was suppied to Binding');
		}

		if (
			VALID_BINDING_TYPES_ENUM[model.type] === CONTEXT_NAMESPACE &&
			model.binding.category
		) {
			const expressionRoot = `${CONTEXT_NAMESPACE}.${model.binding.category}`;

			const path = model.binding.address.join('.');
			return path ? `${expressionRoot}.${path}` : expressionRoot;
		}

		return `${
			VALID_BINDING_TYPES_ENUM[model.type]
		}.${model.binding.address.join('.')}`;
	}

	function getModelFromShorthand(shorthand = '') {
		const shorthandMatcher = shorthand.match(BINDING_SHORTHAND_PATTERN);

		let address = [];
		if (shorthandMatcher && shorthandMatcher[1] === CONTEXT_NAMESPACE) {
			const parts = shorthandMatcher[2].split('.');
			parts.shift();
			const category = parts.shift();
			const address = parts;
			return {
				type: fType,
				binding: { category, address }
			};
		}
		if (shorthandMatcher) {
			const parts = shorthandMatcher[2].split('.');
			parts.shift();
			address = parts;
		}

		return {
			type: fType,
			binding: { address }
		};
	}

	// PUBLIC API
	return {
		getShorthand() {
			return fShortHand;
		},

		getModel() {
			return fModel;
		}
	};
}
