import cloneDeep from 'lodash/cloneDeep';
import set from 'lodash/set';
import get from 'lodash/get';

import { UIB_VIEWPORT_RESOLVE_DATA_SOURCE } from './actions';
import { getElementByProperty } from '../utils.js';
import reduceContextBindingsWithAncestors from '../pageProperties/utils/reduceContextBindingsWithAncestors';
import { resolveBinding } from '../../utils';

export default {
	[UIB_VIEWPORT_RESOLVE_DATA_SOURCE]: ({
		action,
		properties,
		updateProperties
	}) => {
		const { value, elementId, isolationMode } = action.payload;
		const {
			pageState,
			ancestorPageProperties,
			dataBrokerInstances,
			ancestorDataBrokerInstances,
			ancestorElements,
			clientScriptApi,
			parentInfo,
			parentDataBrokerInstances,
			bindings,
			appChromeSettings
		} = properties;

		const newPageState = cloneDeep(pageState);
		const newRootElement = get(newPageState, 'rootElement', {});
		if (isolationMode) {
			const newParentInfo = cloneDeep(parentInfo);
			const resolvedPropertiesBindings =
				resolveBinding(value, {
					bindings,
					ancestorPageProperties,
					dataBrokerInstances: parentDataBrokerInstances,
					ancestorDataBrokerInstances,
					rootElement: {},
					ancestorElements,
					clientScriptApi,
					appChromeSettings
				}) || null;
			set(newParentInfo, 'resolvedDataSource', resolvedPropertiesBindings);

			updateProperties({
				parentInfo: newParentInfo
			});
		} else {
			const parentElement = getElementByProperty(
				newRootElement,
				elementId,
				'id'
			);
			const contextBindings = reduceContextBindingsWithAncestors(
				pageState,
				ancestorPageProperties
			);
			const resolvedPropertiesBindings =
				resolveBinding(value, {
					bindings: contextBindings,
					ancestorPageProperties,
					dataBrokerInstances: isolationMode
						? parentDataBrokerInstances
						: dataBrokerInstances,
					ancestorDataBrokerInstances,
					newRootElement,
					ancestorElements,
					clientScriptApi,
					appChromeSettings
				}) || null;
			set(
				parentElement,
				'properties.repeatedTabData.value',
				resolvedPropertiesBindings
			);

			set(parentElement, 'properties.dynamicTabData.value', value);

			updateProperties({
				pageState: newPageState
			});
		}
	}
};
