import Model from './model';
import Property from './property';
import seismicifyIds from './utils/seismicifyIds';
import { removePrefixes } from '../factories/util';
export default class DataBrokerInstance extends Model {
	constructor({
		id = '',
		definition = {},
		instanceName = '',
		properties = {},
		selectableProps = '',
		hydrationSysId = null,
		clientStateDataBrokerTagName = '',
		eventMappings = [],
		outputProperties = {},
		dispatchedEvents = [],
		readEvaluationMode = 'EAGER'
	} = {}) {
		super();
		this.id = id;
		this.definition = definition;
		this.instanceName = instanceName;
		this.properties = this._setNewProperties(properties);
		this.selectableProps = selectableProps;
		this.simpleMutationBroker = !hydrationSysId;
		this.clientStateDataBrokerTagName = clientStateDataBrokerTagName;
		this.eventMappings = eventMappings;
		this.outputProperties = this._setNewProperties(outputProperties);
		this.dispatchedEvents = dispatchedEvents;
		this.readEvaluationMode = readEvaluationMode;
	}

	_setNewProperties(properties) {
		if (!properties) {
			return null;
		} else if (
			Object.entries(properties).every((entry) => entry[1] instanceof Property)
		) {
			return properties;
		}
		return Object.entries(properties).reduce((acc, [name, property]) => {
			acc[name] = new Property(property);
			return acc;
		}, {});
	}

	_highestMatchingIdIndex(definitionName, allIds) {
		const targetName = seismicifyIds(definitionName);
		let highest = 0;
		allIds.forEach((id) => {
			const matcher = id.match(new RegExp(`^${targetName}\\_(\\d+)*$`));
			if (matcher) {
				const index = Number(matcher[1]);
				highest = index > highest ? index : highest;
			}
		});
		return highest;
	}

	initialize(definitionName, allIds) {
		const highestMatchingInstanceIndex = this._highestMatchingIdIndex(
			definitionName,
			allIds
		);
		// Remove translation prefixes from the definition name.
		const rootName = removePrefixes(definitionName || '') || '';
		// Lower case the ID and remove characters other than alphanumeric, -, and _ to make it seismic-safe
		const targetName = seismicifyIds(rootName);
		this.id =
			highestMatchingInstanceIndex > 0
				? `${targetName}_${(highestMatchingInstanceIndex + 1).toString()}`
				: `${targetName}_1`;
		this.instanceName =
			highestMatchingInstanceIndex > 0
				? `${rootName} ${(highestMatchingInstanceIndex + 1).toString()}`
				: `${rootName} 1`;
	}
}
