import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	TEMPLATES_REQUESTED,
	TEMPLATES_REQUEST_STARTED,
	TEMPLATES_REQUEST_FAILED,
	TEMPLATES_REQUEST_SUCCEEDED
} from './templatesActions';
import { UIB_TEMPLATES_SCOPE_ID } from '../../constants';
import { throwError } from '../../utils';
import get from 'lodash/get';
import effectProxy from '../helpers/effectProxy';
import { memoizeGetDefaultLayout } from './utils/getDefaultLayout';
/*
 * handlers for fetching the templates & template types
 */

export const templatesRequested = createHttpEffect(
	`/api/now/builder/templates`,
	{
		method: 'GET',
		startActionType: TEMPLATES_REQUEST_STARTED,
		successActionType: TEMPLATES_REQUEST_SUCCEEDED,
		errorActionType: TEMPLATES_REQUEST_FAILED
	}
);

const requestStarted = ({ updateProperties }) => {
	updateProperties({
		templates: [],
		templatesDefinitions: []
	});
};

const requestSucceeded = ({ action, updateProperties, updateState }) => {
	const {
		result: { templates: templatesDefinitions, templateTypes }
	} = action.payload;

	const templates = templatesDefinitions
		.map(
			({
				id,
				scope,
				scopeName,
				order,
				label: name,
				image,
				macroponentInstance,
				associatedTypes
			}) => ({
				id,
				customized: scope !== UIB_TEMPLATES_SCOPE_ID,
				name,
				order,
				rootMacroponentId: get(macroponentInstance, 'rootMacroponentId'),
				image,
				scopeName,
				types: associatedTypes.map((typeId) => {
					const at = templateTypes.find((type) => type.id === typeId) || {};
					return at.name;
				})
			})
		)
		.sort((a, b) => a.order - b.order);
	const memoizedGetDefaultLayout = memoizeGetDefaultLayout(
		templatesDefinitions
	);
	updateProperties({
		templates,
		templateTypes,
		templatesDefinitions
	});
	updateState({
		getDefaultLayout: memoizedGetDefaultLayout
	});
};

const requestFailed = () => {
	throwError(`Failed to load templates`);
};

export default {
	[TEMPLATES_REQUESTED]: effectProxy(templatesRequested),
	[TEMPLATES_REQUEST_STARTED]: requestStarted,
	[TEMPLATES_REQUEST_FAILED]: requestFailed,
	[TEMPLATES_REQUEST_SUCCEEDED]: requestSucceeded
};
