import cloneDeep from 'lodash/cloneDeep';
import get from 'lodash/get';
import { PAGE_TEMPLATE_SELECTED } from './pageTemplatesActions';

import { UPDATE_LAYOUT_SUCCEEDED } from '../element/elementActions';

import createPageState from '../../models/createPageState';
import transformMacroponentToPageState from '../page/utils/transformMacroponentToPageState';
import reparentElementsToNewLayout from '../element/utils/reparentElementsToNewLayout';
import { Element } from '../../models';
import { normalizeRowColumnSlots } from '../../utils';

/**
 * handlers for uib-dispatched actions
 *
 * @param {Element} newRootElement
 * @param {PageStateModel} pageState
 * @returns {PageStateModel}
 */
function getPageState(newRootElement, pageState) {
	return createPageState(
		{
			rootElement: new Element({
				...pageState.rootElement,
				layout: newRootElement.layout,
				children: newRootElement.children,
				extendsSysId: newRootElement.extendsSysId
			})
		},
		pageState
	);
}

const addTemplateId = (templatePageState, templateId) => {
	const layout = get(templatePageState, 'rootElement.layout.default');
	layout.templateId = templateId;
	return layout;
};

const pageTemplateSelected = ({
	action,
	properties,
	dispatch,
	updateProperties,
	state
}) => {
	const {
		pageState,
		templatesDefinitions,
		dataBrokerDefinitions,
		eventHandlers
	} = properties;

	const { pageTemplateId } = action.payload;

	const selectedPageTemplate = templatesDefinitions.find(
		({ id }) => id === pageTemplateId
	);

	const selectedPageTemplateAsPageState = transformMacroponentToPageState(
		selectedPageTemplate,
		dataBrokerDefinitions,
		eventHandlers,
		[], // empty client state params array
		state
	);

	addTemplateId(selectedPageTemplateAsPageState, pageTemplateId);

	const reparentedComponents = reparentElementsToNewLayout(
		pageState.rootElement,
		normalizeRowColumnSlots(selectedPageTemplateAsPageState.rootElement.layout)
	);

	const children = [
		...selectedPageTemplateAsPageState.rootElement.children.filter(
			function removeExistingElements(element) {
				return !reparentedComponents.find((reparentedElement) => {
					return reparentedElement.id === element.id;
				});
			}
		),
		...reparentedComponents
	];
	const newRootElement = cloneDeep({
		...selectedPageTemplateAsPageState.rootElement,
		id: pageState.rootElement.id,
		instanceName: pageState.rootElement.instanceName,
		macroponentProperties: pageState.rootElement.macroponentProperties,
		children
	});

	if (reparentedComponents.length > 0) {
		dispatch(UPDATE_LAYOUT_SUCCEEDED, {
			components: reparentedComponents
		});
	}

	updateProperties({
		pageState: getPageState(newRootElement, pageState)
	});
};

export default {
	[PAGE_TEMPLATE_SELECTED]: pageTemplateSelected
};
