import {
	NEW_PAGE_VARIANT_CREATE_OVERRIDE_FAILED,
	NEW_PAGE_VARIANT_CREATE_OVERRIDE_REQUEST,
	NEW_PAGE_VARIANT_CREATE_OVERRIDE_STARTED,
	NEW_PAGE_VARIANT_CREATE_OVERRIDE_SUCCEEDED
} from './newPageActions';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import processContextState from '../contextState/contextStateProcessor';
import { getTemplates } from '@devsnc/library-uxf';
import { replacePageWithOverrideReceived } from './newPageUtils';
import cloneDeep from 'lodash/cloneDeep';

const newPageVariantOverrideSucceeded = ({
	updateProperties,
	updateState,
	action,
	properties,
	state
}) => {
	const { dataBrokerDefinitions, pages } = properties;

	const {
		result: {
			macroponentInstance: { rootMacroponentId },
			contextState
		}
	} = action.payload;

	const {
		ancestorDataBrokerInstances,
		parentDataBrokerInstances,
		ancestorPageProperties,
		parentPageProperties,
		ancestorElements,
		appChromeSettings,
		parentElements,
		parentEventHandlers,
		theme
	} = processContextState({
		contextState,
		updateState,
		properties: { dataBrokerDefinitions }
	});

	// Load component scripts dynamically
	// This is async, but shouldn't matter if the scripts are loaded after
	// the component tags have been rendered in the html
	if (rootMacroponentId) {
		getTemplates([rootMacroponentId]);
	}

	const {
		pageState: { sysId: variantId }
	} = properties;

	const [pageState, newPages] = replacePageWithOverrideReceived(
		action.payload.result,
		pages,
		dataBrokerDefinitions,
		parentEventHandlers,
		state,
		variantId
	);

	updateProperties({
		pages: newPages,
		ancestorDataBrokerInstances,
		parentDataBrokerInstances,
		ancestorPageProperties,
		parentPageProperties,
		appChromeSettings,
		ancestorElements,
		parentElements,
		theme,
		pageState,
		pageStatePristine: cloneDeep(pageState)
	});
};

export const generateOverrideCreateHttpEffect = ({
	successActionType,
	errorActionType,
	startActionType
}) =>
	createHttpEffect('/api/now/uxframework/screens/:sysId/override', {
		method: 'POST',
		pathParams: ['sysId'],
		dataParam: 'data',
		queryParams: ['sysparm_transaction_scope'],
		startActionType,
		successActionType,
		errorActionType,
		batch: false
	});

const newPageVariantCreateOverrideHttpEffect = generateOverrideCreateHttpEffect(
	{
		successActionType: NEW_PAGE_VARIANT_CREATE_OVERRIDE_SUCCEEDED,
		startActionType: NEW_PAGE_VARIANT_CREATE_OVERRIDE_STARTED,
		errorActionType: NEW_PAGE_VARIANT_CREATE_OVERRIDE_FAILED
	}
);

export default {
	[NEW_PAGE_VARIANT_CREATE_OVERRIDE_REQUEST]: newPageVariantCreateOverrideHttpEffect,
	[NEW_PAGE_VARIANT_CREATE_OVERRIDE_SUCCEEDED]: newPageVariantOverrideSucceeded
};
