import { tokenize } from 'esprima';

function stripEndQuotes(str) {
	if (
		(str.charAt(0) === '"' && str.charAt(str.length - 1) === '"') ||
		(str.charAt(0) === "'" && str.charAt(str.length - 1) === "'")
	) {
		return str.substr(1, str.length - 2);
	}
}

export default function createRequiredTranslations(scripts) {
	const tokens = tokenize(scripts);

	if (!tokens.some((token) => token.value === 'translate')) {
		return;
	}

	return tokens.reduce((acc, currValue, currIndex, sourceArray) => {
		if (
			currValue.value === 'translate' &&
			sourceArray[currIndex - 1].value === '.' &&
			sourceArray[currIndex - 2].value === 'helpers'
		) {
			acc.push({
				message: stripEndQuotes(sourceArray[currIndex + 2].value)
			});
		}
		return acc;
	}, []);
}
