import { DELETE_MODAL } from './modalActions';
import createPageState from '../../models/createPageState';
import cloneDeep from 'lodash/cloneDeep';

export default {
	[DELETE_MODAL]: ({
		action: { payload },
		state: {
			properties: { pageState }
		},
		updateProperties
	}) => {
		const { elementId } = payload;
		const newRootElement = cloneDeep(pageState.rootElement);

		newRootElement.children = newRootElement.children.filter(
			(c) => c.id !== elementId
		);

		updateProperties({
			pageState: createPageState(
				{
					rootElement: newRootElement
				},
				pageState
			)
		});
	}
};
