import Model from './model';

export default class ClientScript extends Model {
	constructor({
		id = '', //string
		name = '', // string,
		script = '', // string
		scope = null, // string
		includes = [] // array
	}) {
		super();
		this.id = id;
		this.name = name;
		this.script = script;
		this.includes = includes;
		this.scope = scope;
	}
}
