import {
	formatEventNameAndLabel,
	getInvalidEventMappingTargets,
	getReducedInvalidHandlerList,
	getReducedIncongruentHandlerList
} from '../../elementEventMappings/utils';
import {
	MCP_CONTRACT_EVENT_CREATED,
	MCP_CONTRACT_EVENT_UPDATED
} from '../../dispatchedEvents/dispatchedEventsActions';
import { PAGE_SAVE_HTTP_REQUESTED } from '../pageActions';

const dispatchMcpContractAction = (
	eventHandlers,
	actionType,
	rootMacroponentId,
	screenName,
	savePayload,
	saveMeta,
	dispatch,
	variantScope
) => {
	for (const eventHandler of eventHandlers) {
		const { name, label } = formatEventNameAndLabel(
			eventHandler,
			rootMacroponentId,
			screenName
		);

		const payloadToSend = {
			event: {
				event_name: name,
				label,
				props: JSON.stringify(
					eventHandler.payloadFields.map((field) => {
						return {
							name: field.name,
							fieldType: field.type,
							interfaceApiName: field.interfaceApiName,
							label: field.label
						};
					})
				)
			},
			sysparm_transaction_scope: variantScope
		};

		if (actionType === MCP_CONTRACT_EVENT_UPDATED) {
			payloadToSend.eventId = eventHandler.existingDispatchedEvent.id;
		}

		setTimeout(() => {
			dispatch(actionType, payloadToSend, {
				screenEventMappingHandler: eventHandler,
				savePayload,
				saveMeta,
				context: 'dispatchedEvents'
			});
		}, 100);
	}
};

export default function createDispatchedEvents({
	rootElement,
	rootMacroponentId,
	screenName,
	dataBrokerInstances,
	parentEventHandlers,
	dispatch,
	existingDispatchedEvents = [],
	savePayload,
	saveMeta,
	variantScope
}) {
	const invalidEventMappingTargets = getInvalidEventMappingTargets(
		rootElement,
		parentEventHandlers,
		dataBrokerInstances
	);
	const dispatchedEventsToCreate = getReducedInvalidHandlerList(
		invalidEventMappingTargets,
		existingDispatchedEvents,
		rootMacroponentId
	);

	const dispatchedEventsToUpdate = getReducedIncongruentHandlerList(
		invalidEventMappingTargets,
		existingDispatchedEvents,
		rootMacroponentId
	);

	if (dispatchedEventsToCreate && dispatchedEventsToCreate.length > 0) {
		dispatchMcpContractAction(
			dispatchedEventsToCreate,
			MCP_CONTRACT_EVENT_CREATED,
			rootMacroponentId,
			screenName,
			savePayload,
			saveMeta,
			dispatch,
			variantScope
		);
	} else if (dispatchedEventsToUpdate && dispatchedEventsToUpdate.length) {
		dispatchMcpContractAction(
			dispatchedEventsToUpdate,
			MCP_CONTRACT_EVENT_UPDATED,
			rootMacroponentId,
			screenName,
			savePayload,
			saveMeta,
			dispatch,
			variantScope
		);
	} else {
		// if there are no dispatched events to create, continue saving the page
		dispatch(PAGE_SAVE_HTTP_REQUESTED, { ...savePayload }, { ...saveMeta });
	}
}
