import Model from './model';
export default class EventHandler extends Model {
	constructor({
		id = '', // string
		apiName = '', // string
		label = '', // string
		type = '', // string
		sourceName = '', // string
		sourceId = '', // string
		name = '', // string
		payloadFields = [], // array
		ancestor = false, // boolean
		relay = false // boolean
	}) {
		super();
		this.id = id;
		this.apiName = apiName;
		this.label = label;
		this.type = type;
		this.sourceName = sourceName;
		this.sourceId = sourceId || id;
		this.name = name;
		this.payloadFields = payloadFields;
		this.ancestor = ancestor;
		this.relay = relay;
	}
}
