const ENDPOINT_TYPE_MAP = {
	landing: 'ws_landing_pages',
	'landing-page': 'ws_landing_pages',
	dashboard: 'screens'
};

const METADATA_ENDPOINT_TYPE_MAP = {
	landing: 'ws_landing_pages_metadata',
	'landing-page': 'ws_landing_pages'
};

export default function pageEndpointFromType(type, metadata = false) {
	return metadata
		? METADATA_ENDPOINT_TYPE_MAP[type] || 'screens'
		: ENDPOINT_TYPE_MAP[type] || 'screens';
}
