import set from 'lodash/set';
import cloneDeep from 'lodash/cloneDeep';
import { UIB_VIEWPORT_DEFAULT_CHANGED } from './actions';
import { getViewportElementAndProperties } from './utils.js';

export default {
	[UIB_VIEWPORT_DEFAULT_CHANGED]: ({
		action: {
			payload: {
				elementId,
				item: { id: newDefaultRoute }
			}
		},
		properties: { pageState },
		updateProperties
	}) => {
		const newPageState = cloneDeep(pageState);

		const {
			viewportElement,
			defaultRoute: currentDefaultRoute
		} = getViewportElementAndProperties(newPageState, elementId);

		if (currentDefaultRoute === newDefaultRoute) {
			return;
		}

		set(viewportElement, 'properties.defaultRoute.value', newDefaultRoute);

		updateProperties({
			pageState: newPageState
		});
	}
};
