import { PageStateModel } from '../../../models';
import isNil from 'lodash/isNil';
import cloneDeep from 'lodash/cloneDeep';
import merge from 'lodash/merge';
import { getIsActive, getPageStatus } from './pageStatus';

export const getMetadataUpdatePayload = (changes = {}) => {
	const updatePayload = {};

	['name', 'sysPolicy', 'order', 'screenCondition'].forEach((field) => {
		if (!isNil(changes[field])) {
			updatePayload[field] = changes[field];
		}
	});

	if (changes.pageStatus) {
		updatePayload.isActive = getIsActive(changes.pageStatus);
	}

	return updatePayload;
};

export const transformMetadataToPageState = (pageMetadata, pageState) => {
	const { name, sysPolicy, isActive, order, screenCondition } = pageMetadata;

	const clonePageState = cloneDeep(pageState);
	const updatedPageState = merge(clonePageState, {
		name,
		sysPolicy,
		pageStatus: getPageStatus(isActive),
		uiTypeParameters: {
			...clonePageState.uiTypeParameters,
			order,
			screenCondition
		},
		rootElement: {
			...clonePageState.rootElement,
			instanceName: name
		}
	});

	return new PageStateModel(updatedPageState);
};
