import {
	DECLARATIVE_ACTION,
	EVENT,
	DATABROKER_OP,
	CLIENT_SCRIPT
} from '../../elementEventMappings/constants';
import { isUxValueModel, getShorthandFromUxValue } from '../../../factories';
import cloneDeep from 'lodash/cloneDeep';
import isNull from 'lodash/isNull';
import get from 'lodash/get';
import { getProperty } from '../../componentList/utils';
import EventMappingTarget from '../../../models/eventMappingTarget';
import EventMapping from '../../../models/eventMapping';

export const computeEventMappingTargetKeys = (type) => {
	const isDeclarativeAction = type === DECLARATIVE_ACTION;
	const isEvent = type === EVENT;
	const isDataBrokerOp = type === DATABROKER_OP;
	const isClientScript = type === CLIENT_SCRIPT;
	return (
		(isDeclarativeAction && {
			targetKey: 'declarativeAction',
			valueKey: 'actionValues',
			idKey: 'sysId',
			nonTargetKeys: ['event', 'operation', 'clientScript']
		}) ||
		(isEvent && {
			targetKey: 'event',
			valueKey: 'payload',
			idKey: 'sysId',
			nonTargetKeys: ['declarativeAction', 'operation', 'clientScript']
		}) ||
		(isDataBrokerOp && {
			targetKey: 'operation',
			valueKey: 'payload',
			idKey: 'dataBrokerId',
			nonTargetKeys: ['declarativeAction', 'event', 'clientScript']
		}) ||
		(isClientScript && {
			targetKey: 'clientScript',
			valueKey: 'payload',
			idKey: 'sysId',
			nonTargetKeys: ['declarativeAction', 'event', 'operation']
		}) || {
			targetKey: null,
			valueKey: null,
			idKey: null,
			nonTargetKeys: []
		}
	);
};

export const coercePropertyValueToShorthand = (value, type) => {
	const shorthand = isUxValueModel(value)
		? getShorthandFromUxValue(value)
		: value;

	const updatedValue =
		type === 'json' && typeof shorthand === 'object' && !isNull(shorthand)
			? JSON.stringify(shorthand)
			: shorthand;

	return updatedValue;
};

export const getPayloadPropertiesFromHandlerDefinition = (
	eventMappingTargetValues,
	handlerTypeId,
	eventHandlers
) => {
	const [eventMappingTargetSysId, sourceId, operationName] = (
		handlerTypeId || ''
	).split(':');
	let matchingEventHandler;
	if (operationName) {
		matchingEventHandler = eventHandlers.find(
			(handler) =>
				handler.id === eventMappingTargetSysId &&
				handler.sourceId === sourceId &&
				handler.name === operationName
		);
	} else {
		matchingEventHandler = eventHandlers.find(
			(handler) => handler.id === eventMappingTargetSysId
		);
	}

	if (matchingEventHandler) {
		const matchingEventHandlerPayloadFields =
			// get payload fields from either an EVENT or DECLARATIVE_ACTION
			matchingEventHandler.payloadFields ||
			get(matchingEventHandler, 'payloadDefinition.fields', []);

		return matchingEventHandlerPayloadFields.map(
			({ name, columnName, label, interfaceApiName, type, typeMetadata }) => {
				const value = coercePropertyValueToShorthand(
					eventMappingTargetValues[name || columnName] || null,
					type
				);

				return getProperty({
					label,
					fieldType: type,
					defaultValue: value,
					name: name || columnName,
					readOnly: false,
					typeMetadata: typeMetadata,
					interfaceApiName: interfaceApiName,
					emptyStateBehavior: typeMetadata && typeMetadata.emptyStateBehavior
				});
			}
		);
	}

	return [];
};

export const getTransformedEventTarget = (
	target,
	eventHandlers,
	screenEventMappings = []
) => {
	const { targetId, type } = target;
	const { targetKey, valueKey, idKey } = computeEventMappingTargetKeys(type);

	let eventMappingTargetSysId = get(target, `${targetKey}.${idKey}`, null);
	let eventMappingTargetApiName = get(target, 'event.apiName', null);
	if (eventMappingTargetSysId === null && eventMappingTargetApiName) {
		const targetEventHandler = eventHandlers.find(
			(handler) => handler.apiName === eventMappingTargetApiName
		);
		if (targetEventHandler) {
			eventMappingTargetSysId = targetEventHandler.id;
		}
	}

	const relayMapping = screenEventMappings.find(
		(mapping) => mapping.eventApiName === eventMappingTargetApiName
	);
	let convertToDataBrokerOp = false;
	if (type === EVENT && relayMapping) {
		// if the handler is a relay event, convert it to display the ancestor handler in the UI
		const ancestorType = get(relayMapping, 'targets[0].type', EVENT);
		if (ancestorType === EVENT) {
			eventMappingTargetSysId = get(
				relayMapping,
				'targets[0].event.sysId',
				null
			);
			eventMappingTargetApiName = get(
				relayMapping,
				'targets[0].event.apiName',
				null
			);
		} else if (ancestorType === DATABROKER_OP) {
			convertToDataBrokerOp = true;
		}
	}
	const eventMappingTargetValues =
		get(target, `${targetKey}.${valueKey}.container`, null) ||
		get(target, `${targetKey}.${valueKey}.value`, {});

	const eventMappingTargetScript = get(
		target,
		`${targetKey}.${valueKey}.script`,
		null
	);

	const handlerTypeId = target.operation
		? `${eventMappingTargetSysId}:${target.operation.parentResourceId}:${target.operation.operationName}`
		: eventMappingTargetSysId;
	const eventMappingTargetProperties = getPayloadPropertiesFromHandlerDefinition(
		eventMappingTargetValues,
		handlerTypeId,
		eventHandlers
	);

	let clientTransformScript = null;
	if (eventMappingTargetScript) {
		clientTransformScript = getShorthandFromUxValue(
			target[targetKey][valueKey]
		);
	}
	const newEventMappingTarget = new EventMappingTarget({
		targetId,
		type,
		clientTransformScript,
		[targetKey]: {
			[idKey]: eventMappingTargetSysId,
			[valueKey]: eventMappingTargetProperties
		}
	});

	if (newEventMappingTarget.type === EVENT) {
		newEventMappingTarget[targetKey].apiName = eventMappingTargetApiName;
	}

	if (type === DATABROKER_OP) {
		newEventMappingTarget[targetKey].operationName =
			target.operation.operationName;
		newEventMappingTarget[targetKey].parentResourceId =
			target.operation.parentResourceId;
	}

	if (convertToDataBrokerOp) {
		// if the handler is a relay event to a data broker operation, convert it
		// to display the ancestor handler in the UI and conserve the configured payload fields
		newEventMappingTarget.type = DATABROKER_OP;
		const relayOperation = get(relayMapping, 'targets[0].operation', {});
		newEventMappingTarget.operation = cloneDeep(relayOperation);
		const handlerTypeId = `${relayOperation.dataBrokerId}:${relayOperation.parentResourceId}:${relayOperation.operationName}`;
		const convertedEventMappingTargetProperties = getPayloadPropertiesFromHandlerDefinition(
			eventMappingTargetValues,
			handlerTypeId,
			eventHandlers
		);
		newEventMappingTarget.operation.payload = convertedEventMappingTargetProperties;
		newEventMappingTarget.event = null;
	}

	return newEventMappingTarget;
};

export const generateEventMappings = (
	eventMappings = [],
	eventHandlers,
	dispatchedEvents,
	screenEventMappings = []
) => {
	return eventMappings.map((eventMapping) => {
		const eventMappingTargets = get(eventMapping, 'targets', []);
		const eventApiName = eventMapping.sourceEventApiName;
		const eventId = dispatchedEvents.reduce((acc, event) => {
			if (event.apiName === eventApiName) {
				return event.id;
			}
			return acc;
		}, null);

		const transformedTargets = eventMappingTargets.map((target) => {
			return getTransformedEventTarget(
				target,
				eventHandlers,
				screenEventMappings
			);
		});

		return new EventMapping({
			...eventMapping,
			eventId,
			eventApiName,
			targets: transformedTargets
		});
	});
};

export const replacePropValuesWithNewId = (
	properties,
	key,
	namespace,
	previousId,
	newId
) => {
	(properties || []).map((prop = {}) => {
		if (
			typeof prop[key] === 'string' &&
			prop[key].startsWith(`${namespace}.${previousId}`)
		) {
			prop[key] = prop[key].replace(
				`${namespace}.${previousId}`,
				`${namespace}.${newId}`
			);
		}
	});
};

export const removePropertyReferencesToPreviousId = (
	previousId,
	newId,
	elements = [],
	dataBrokers = [],
	stateParams = [],
	namespace
) => {
	// if a data broker instance or element ID is changed, check to see whether any
	// existing element properties reference the old ID through binding expressions.
	elements.map(({ properties = {} }) => {
		replacePropValuesWithNewId(
			Object.values(properties),
			'value',
			namespace,
			previousId,
			newId
		);
	});
	// then check to see whether any existing DB properties reference the old ID through binding expressions.
	dataBrokers.map(({ properties = {} }) => {
		replacePropValuesWithNewId(
			Object.values(properties),
			'value',
			namespace,
			previousId,
			newId
		);
	});
	// finally, check to see whether any existing state params reference the old ID through binding expressions.
	replacePropValuesWithNewId(
		stateParams,
		'initialValue',
		namespace,
		previousId,
		newId
	);
};

const reconfigureEventMappingTargets = (
	mappings,
	namespace,
	previousId,
	newId
) => {
	mappings.map((mapping) => {
		mapping.targets.map((target = {}) => {
			// if a data broker instance ID is changed, check to see whether an operation
			// from that broker is being used as a target in any existing event mappings.
			const instanceId = get(target, 'operation.parentResourceId', null);
			if (instanceId && instanceId === previousId) {
				target.operation.parentResourceId = newId;
			}
			// if a data broker instance or element ID is being changed, check to see whether any
			// existing event mapping target values reference the old ID through binding expressions.
			const { targetKey, valueKey } = computeEventMappingTargetKeys(
				target.type
			);
			const handlerValues = get(target, `${targetKey}.${valueKey}`, []);
			replacePropValuesWithNewId(
				handlerValues,
				'value',
				namespace,
				previousId,
				newId
			);
		});
	});
};

export const removeEventReferencesToPreviousId = (
	previousId,
	newId,
	elements = [],
	eventHandlers = [],
	dataBrokers = [],
	internalEventMappings = [],
	namespace
) => {
	elements.map((element) => {
		reconfigureEventMappingTargets(
			element.eventMappings || [],
			namespace,
			previousId,
			newId
		);
	});
	dataBrokers.map((broker) => {
		reconfigureEventMappingTargets(
			broker.eventMappings || [],
			namespace,
			previousId,
			newId
		);
	});
	reconfigureEventMappingTargets(
		internalEventMappings || [],
		namespace,
		previousId,
		newId
	);

	// if a data broker instance ID is changed, update event handler source IDs that reference the old ID.
	eventHandlers.map((eventHandler) => {
		const sourceId = get(eventHandler, 'sourceId', null);
		if (sourceId && sourceId === previousId) {
			eventHandler.sourceId = newId;
		}
	});
};
