import isUndefined from 'lodash/isUndefined';
import get from 'lodash/get';
import { TRANSLATION_LITERAL, TRANSLATE_SHORTHAND_PATTERN } from './constants';
import { removePrefixes } from './util';

export default function TranslationLiteralFactory({
	model, // object
	shorthand // any
} = {}) {
	if (isUndefined(shorthand) && !model) {
		throw new TypeError(
			'Something went wrong initializing TranslationLiteralFactory'
		);
	}

	const fShorthand = shorthand || getShorthandFromModel(model);
	const fModel = model || getModelFromShorthand(shorthand);

	function getShorthandFromModel(model) {
		return removePrefixes(get(model, 'value.message', ''));
	}

	function getModelFromShorthand(shorthand) {
		let message = '';
		if (typeof shorthand === 'string') {
			const scriptMatcher = shorthand.match(TRANSLATE_SHORTHAND_PATTERN);
			message = scriptMatcher ? scriptMatcher[1] : shorthand;
		} else {
			message = get(shorthand, 'value', '');
		}

		return {
			type: TRANSLATION_LITERAL,
			value: {
				message: removePrefixes(message),
				comment: ''
			}
		};
	}

	// PUBLIC API
	return {
		getShorthand() {
			return fShorthand;
		},

		getModel() {
			return fModel;
		}
	};
}
