import { seismicCuid } from '../utils.js';

/**
 * @typedef {Object} MacroponentProperty
 * @property {string} id
 * @property {string} name
 * @property {string} label
 * @property {string} fieldType
 * @property {string} typeMetadata
 * @property {string} valueType
 * @property {string} shape
 * @property {boolean} mandatory
 * @property {string} defaultValue
 * @property {string} description
 * @property {boolean} readOnly
 * @property {boolean} selectable
 * @property {boolean} inherited Whether this property comes from a macroponent that is extended by this macroponent.
 *                               This property can not be deleted or have it's name changed.
 * @property {string} value
 * @property {string} testValue
 */

/**
 * @description Provides reasonable defaults when constructing a macroponent property
 * @param {MacroponentProperty} config
 * @returns {MacroponentProperty}
 */
export const create = ({
	id = seismicCuid(),
	name = '',
	label,
	fieldType = 'string',
	typeMetadata,
	valueType = 'string',
	shape,
	mandatory = false,
	defaultValue,
	description = '',
	readOnly = false,
	selectable = false,
	inherited = false,
	value,
	testValue
} = {}) => ({
	id,
	name: name,
	label: label === undefined ? name : label,
	fieldType,
	typeMetadata,
	valueType,
	shape,
	mandatory,
	defaultValue,
	description,
	readOnly,
	selectable,
	inherited,
	value,
	testValue
});

/**
 * @description Provides reasonable defaults when constructing a macroponent property
 * @param {MacroponentProperty} newInstance
 * @param {MacroponentProperty} old
 * @returns {MacroponentProperty}
 */
export const update = (newInstance, old) => ({
	...old,
	...newInstance
});

export default {
	create,
	update
};
