function reduceProperties(properties) {
	const { testValues = {}, values = {} } = properties;
	const reducedProperties = Object.entries(testValues).reduce(
		(acc, [propName, propTestValue]) => ({
			...acc,
			[propName]: propTestValue
		}),
		{}
	);
	return Object.entries(values).reduce((acc, [propName, propValue]) => {
		return {
			...acc,
			[propName]: testValues[propName] || propValue
		};
	}, reducedProperties);
}

export default (result) => {
	const { parentProperties = {}, ancestorProperties = {} } = result || {};

	let componentBuilderProps = {};
	try {
		componentBuilderProps =
			JSON.parse(window.sessionStorage.getItem('cbProps')) || {};
	} catch (_) {
		// Do nothing
	}

	parentProperties.testValues = {
		...parentProperties.testValues,
		...componentBuilderProps
	};
	ancestorProperties.testValues = {
		...ancestorProperties.testValues,
		...componentBuilderProps
	};
	parentProperties.values = {
		...parentProperties.values,
		...componentBuilderProps
	};
	ancestorProperties.values = {
		...ancestorProperties.values,
		...componentBuilderProps
	};

	const aggregateProperties = {
		...ancestorProperties,
		...parentProperties
	};

	return {
		ancestorPageProperties: reduceProperties(aggregateProperties),
		parentPageProperties: reduceProperties(parentProperties)
	};
};
