import { seismicCuid } from '../../../utils';
import EventMapping from '../../../models/eventMapping';
import Property from '../../../models/property';
import EventMappingTarget from '../../../models/eventMappingTarget';
import { EVENT, DATABROKER_OP } from '../../elementEventMappings/constants';

const generatePayloadProperty = (payloadField) => {
	return new Property({
		name: payloadField.name,
		columnName: payloadField.name,
		label: payloadField.label,
		type: payloadField.type,
		value: `@payload.${payloadField.name}`
	});
};

export default function createScreenEventMapping(
	sourceEventId,
	sourceEventApiName,
	targetHandler
) {
	const { type, apiName, id, payloadFields, sourceId, name } = targetHandler;
	const eventMappingTarget = new EventMappingTarget({
		targetId: seismicCuid(),
		type
	});

	switch (type) {
		case EVENT:
			eventMappingTarget.event = {};
			eventMappingTarget.event.apiName = apiName;
			eventMappingTarget.event.sysId = id;
			eventMappingTarget.event.payload = payloadFields.map((payloadField) => {
				return generatePayloadProperty(payloadField);
			});
			break;
		case DATABROKER_OP:
			eventMappingTarget.operation = {};
			eventMappingTarget.operation.parentResourceId = sourceId;
			eventMappingTarget.operation.dataBrokerId = id;
			eventMappingTarget.operation.operationName = name;
			eventMappingTarget.operation.payload = payloadFields.map(
				(payloadField) => {
					return generatePayloadProperty(payloadField);
				}
			);
	}

	return new EventMapping({
		eventId: sourceEventId,
		eventApiName: sourceEventApiName,
		eventMappingId: seismicCuid(),
		targets: [eventMappingTarget]
	});
}
