import { seismicCuid } from '../../utils';

import { Element, EventMapping, EventMappingTarget } from '../../models';

import createPageState from '../../models/createPageState';

import {
	ELEMENT_EVENT_MAPPING_CREATED,
	ELEMENT_EVENT_MAPPING_HANDLER_CHANGED,
	ELEMENT_EVENT_MAPPING_HANDLER_CREATED,
	ELEMENT_EVENT_MAPPING_HANDLER_DELETED,
	ELEMENT_COMPLETE_EVENT_MAPPING_CREATED,
	SCREEN_EVENT_MAPPING_CREATED,
	SCREEN_EVENT_MAPPING_HANDLER_ADDED,
	SCREEN_EVENT_MAPPING_HANDLER_DELETED,
	SCREEN_EVENT_MAPPING_HANDLER_CHANGED,
	PAGE_INTERNAL_EVENT_MAPPING_CREATED,
	PAGE_INTERNAL_EVENT_MAPPING_HANDLER_CREATED,
	PAGE_INTERNAL_EVENT_MAPPING_HANDLER_DELETED,
	PAGE_INTERNAL_EVENT_MAPPING_HANDLER_CHANGED
} from './elementEventMappingsActions';
import cloneDeep from 'lodash/cloneDeep';
import reject from 'lodash/reject';
import {
	getNewRootElementAndElementToUpdate,
	createEventMapping,
	createEventMappingHandler,
	deleteEventMappingHandler,
	updateEventMappingHandler
} from './utils';

function getPageState(newRootElement, pageState) {
	return createPageState(
		{
			rootElement: new Element(newRootElement)
		},
		pageState
	);
}

const elementEventMappingCreated = ({
	action,
	properties,
	updateProperties
}) => {
	const { pageState } = properties;
	const { elementId, eventId, eventApiName } = action.payload;
	const {
		newRootElement,
		elementToUpdate
	} = getNewRootElementAndElementToUpdate(pageState, elementId);

	const elementEventMapping = createEventMapping(eventId, eventApiName);
	elementToUpdate.eventMappings.unshift(elementEventMapping);

	updateProperties({
		pageState: getPageState(newRootElement, pageState)
	});
};

const elementEventMappingHandlerCreated = ({
	action,
	properties,
	updateProperties
}) => {
	const { pageState } = properties;
	const { elementId, eventMappingId } = action.payload;
	const {
		newRootElement,
		elementToUpdate = {}
	} = getNewRootElementAndElementToUpdate(pageState, elementId);
	const { eventMappings = [] } = elementToUpdate;
	createEventMappingHandler(eventMappings, eventMappingId);

	updateProperties({
		pageState: getPageState(newRootElement, pageState)
	});
};

const elementEventMappingHandlerDeleted = ({
	action,
	properties,
	updateProperties
}) => {
	const { pageState } = properties;
	const { elementId, eventMappingId, handlerId } = action.payload;
	const {
		newRootElement,
		elementToUpdate = {}
	} = getNewRootElementAndElementToUpdate(pageState, elementId);

	const { eventMappings = [] } = elementToUpdate;
	deleteEventMappingHandler(
		elementToUpdate,
		eventMappings,
		eventMappingId,
		handlerId
	);

	updateProperties({
		pageState: getPageState(newRootElement, pageState)
	});
};

const eventMappingHandlerChanged = ({
	action,
	properties,
	updateProperties
}) => {
	const { pageState, eventHandlers, clientStateParameters = [] } = properties;
	const {
		elementId,
		eventMappingId,
		handlerId,
		handlerTypeId,
		handlerValueId = null,
		value = null,
		scripted
	} = action.payload;

	const {
		newRootElement,
		elementToUpdate
	} = getNewRootElementAndElementToUpdate(pageState, elementId);
	const eventMappingToUpdate =
		elementToUpdate.eventMappings.find(
			(eventMapping) => eventMapping.eventMappingId === eventMappingId
		) || {};

	const { targets = [] } = eventMappingToUpdate;
	const handlerToUpdate =
		targets.find((target) => target.targetId === handlerId) || null;
	if (handlerToUpdate) {
		updateEventMappingHandler(
			handlerToUpdate,
			eventHandlers,
			handlerTypeId,
			handlerValueId,
			value,
			scripted,
			clientStateParameters
		);
	}

	const newPageState = getPageState(newRootElement, pageState);
	updateProperties({
		pageState: newPageState
	});
};

const elementCompleteEventMappingCreated = ({
	action,
	properties,
	updateProperties
}) => {
	const { pageState } = properties;
	const {
		elementId,
		eventId,
		eventApiName,
		eventMappingTarget
	} = action.payload;

	const {
		newRootElement,
		elementToUpdate
	} = getNewRootElementAndElementToUpdate(pageState, elementId);

	const elementEventMapping = new EventMapping({
		eventMappingId: seismicCuid(),
		eventId,
		eventApiName,
		targets: [new EventMappingTarget(eventMappingTarget)]
	});

	elementToUpdate.eventMappings.unshift(elementEventMapping);

	updateProperties({
		pageState: getPageState(newRootElement, pageState)
	});
};

const screenEventMappingCreated = ({
	action,
	properties,
	updateProperties
}) => {
	const { pageState } = properties;
	const { eventId, eventApiName } = action.payload;

	const newScreenEventMapping = createEventMapping(eventId, eventApiName);
	const existingScreenEventMappings = cloneDeep(pageState.screenEventMappings);
	existingScreenEventMappings.unshift(newScreenEventMapping);

	const newPageState = createPageState(
		{
			screenEventMappings: existingScreenEventMappings
		},
		pageState
	);

	updateProperties({
		pageState: newPageState
	});
};

const screenEventMappingHandlerAdded = ({
	action,
	properties,
	updateProperties
}) => {
	const { pageState } = properties;
	const { eventMappingId } = action.payload;

	const clonedScreenEventMappings = cloneDeep(
		pageState.screenEventMappings || []
	);
	createEventMappingHandler(clonedScreenEventMappings, eventMappingId);

	const newPageState = createPageState(
		{
			screenEventMappings: clonedScreenEventMappings
		},
		cloneDeep(pageState)
	);

	updateProperties({
		pageState: newPageState
	});
};

const screenEventMappingHandlerDeleted = ({
	action,
	properties,
	updateProperties
}) => {
	const { eventMappingId, handlerId } = action.payload;
	const clonedScreenEventMappings = cloneDeep(
		properties.pageState.screenEventMappings
	);
	const eventMappingToUpdate = clonedScreenEventMappings.find(
		(eventMapping) => eventMapping.eventMappingId === eventMappingId
	);

	eventMappingToUpdate.targets = reject(eventMappingToUpdate.targets, {
		targetId: handlerId
	});

	let newScreenEventMappings = clonedScreenEventMappings;
	// if there was only one target for this event mapping, delete the whole mapping
	if (eventMappingToUpdate.targets.length === 0) {
		newScreenEventMappings = reject(clonedScreenEventMappings, {
			eventMappingId: eventMappingId
		});
	}

	const newPageState = createPageState(
		{
			screenEventMappings: newScreenEventMappings
		},
		cloneDeep(properties.pageState)
	);

	updateProperties({
		pageState: newPageState
	});
};

const screenEventMappingHandlerChanged = ({
	action,
	properties,
	updateProperties
}) => {
	const { pageState, eventHandlers, clientStateParameters = [] } = properties;
	const {
		eventMappingId,
		handlerId,
		handlerTypeId,
		handlerValueId = null,
		value = null,
		scripted
	} = action.payload;
	const clonedScreenEventMappings = cloneDeep(pageState.screenEventMappings);
	const eventMappingToUpdate =
		clonedScreenEventMappings.find(
			(eventMapping) => eventMapping.eventMappingId === eventMappingId
		) || {};

	const { targets = [] } = eventMappingToUpdate;
	const handlerToUpdate =
		targets.find((target) => target.targetId === handlerId) || null;
	if (handlerToUpdate) {
		updateEventMappingHandler(
			handlerToUpdate,
			eventHandlers,
			handlerTypeId,
			handlerValueId,
			value,
			scripted,
			clientStateParameters
		);
	}

	const newPageState = createPageState(
		{ screenEventMappings: clonedScreenEventMappings },
		cloneDeep(pageState)
	);

	updateProperties({
		pageState: newPageState
	});
};

const internalEventMappingCreated = ({
	action,
	properties,
	updateProperties
}) => {
	const { pageState } = properties;
	const { eventId, eventApiName } = action.payload || {};

	const newInternalEventMapping = createEventMapping(eventId, eventApiName);
	const newRootElement = cloneDeep(pageState.rootElement) || {};
	const existingInternalEventMappings =
		newRootElement.internalEventMappings || [];
	existingInternalEventMappings.unshift(newInternalEventMapping);

	updateProperties({
		pageState: createPageState({ rootElement: newRootElement }, pageState)
	});
};

const internalEventMappingHandlerCreated = ({
	action,
	properties,
	updateProperties
}) => {
	const { pageState } = properties;
	const { eventMappingId } = action.payload || {};

	const newRootElement = cloneDeep(pageState.rootElement) || {};
	const existingInternalEventMappings =
		newRootElement.internalEventMappings || [];
	createEventMappingHandler(existingInternalEventMappings, eventMappingId);

	updateProperties({
		pageState: createPageState({ rootElement: newRootElement }, pageState)
	});
};

const internalEventMappingHandlerDeleted = ({
	action,
	properties,
	updateProperties
}) => {
	const { pageState } = properties;
	const { eventMappingId, handlerId } = action.payload || {};

	const newRootElement = cloneDeep(pageState.rootElement) || {};
	const existingInternalEventMappings =
		newRootElement.internalEventMappings || [];
	const eventMappingToUpdate =
		existingInternalEventMappings.find(
			(eventMapping) => eventMapping.eventMappingId === eventMappingId
		) || {};
	eventMappingToUpdate.targets = reject(eventMappingToUpdate.targets, {
		targetId: handlerId
	});

	// if there was only one target for this event mapping, delete the whole mapping
	if (eventMappingToUpdate.targets.length === 0) {
		newRootElement.internalEventMappings = reject(
			existingInternalEventMappings,
			{
				eventMappingId: eventMappingId
			}
		);
	}

	updateProperties({
		pageState: createPageState({ rootElement: newRootElement }, pageState)
	});
};

const internalEventMappingHandlerChanged = ({
	action,
	properties,
	updateProperties
}) => {
	const {
		eventHandlers = [],
		pageState = {},
		clientStateParameters = []
	} = properties;
	const {
		eventMappingId,
		handlerId,
		handlerTypeId,
		handlerValueId = null,
		value = null,
		scripted
	} = action.payload || {};

	const newRootElement = cloneDeep(pageState.rootElement) || {};
	const existingInternalEventMappings =
		newRootElement.internalEventMappings || [];
	const eventMappingToUpdate =
		existingInternalEventMappings.find(
			(eventMapping) => eventMapping.eventMappingId === eventMappingId
		) || {};

	const { targets = [] } = eventMappingToUpdate;
	const handlerToUpdate =
		targets.find((target) => target.targetId === handlerId) || null;
	if (handlerToUpdate) {
		updateEventMappingHandler(
			handlerToUpdate,
			eventHandlers,
			handlerTypeId,
			handlerValueId,
			value,
			scripted,
			clientStateParameters
		);
	}

	updateProperties({
		pageState: createPageState({ rootElement: newRootElement }, pageState)
	});
};

export default {
	[ELEMENT_EVENT_MAPPING_CREATED]: elementEventMappingCreated,
	[ELEMENT_EVENT_MAPPING_HANDLER_CHANGED]: eventMappingHandlerChanged,
	[ELEMENT_EVENT_MAPPING_HANDLER_CREATED]: elementEventMappingHandlerCreated,
	[ELEMENT_EVENT_MAPPING_HANDLER_DELETED]: elementEventMappingHandlerDeleted,
	[ELEMENT_COMPLETE_EVENT_MAPPING_CREATED]: elementCompleteEventMappingCreated,
	[SCREEN_EVENT_MAPPING_CREATED]: screenEventMappingCreated,
	[SCREEN_EVENT_MAPPING_HANDLER_ADDED]: screenEventMappingHandlerAdded,
	[SCREEN_EVENT_MAPPING_HANDLER_DELETED]: screenEventMappingHandlerDeleted,
	[SCREEN_EVENT_MAPPING_HANDLER_CHANGED]: screenEventMappingHandlerChanged,
	[PAGE_INTERNAL_EVENT_MAPPING_CREATED]: internalEventMappingCreated,
	[PAGE_INTERNAL_EVENT_MAPPING_HANDLER_CREATED]: internalEventMappingHandlerCreated,
	[PAGE_INTERNAL_EVENT_MAPPING_HANDLER_DELETED]: internalEventMappingHandlerDeleted,
	[PAGE_INTERNAL_EVENT_MAPPING_HANDLER_CHANGED]: internalEventMappingHandlerChanged
};
