import cloneDeep from 'lodash/cloneDeep';
import cuid from 'cuid';

import createPageState from '../../../models/createPageState';

import {
	findElementById,
	findElements,
	findParentElement,
	getRootElementFromState,
	deleteChildElement
} from '../utils/element';

import { Element } from '../../../models';

import {
	RESTORE_ELEMENT_SUCCEEDED,
	RELOCATE_ELEMENT_SUCCEEDED,
	DETACH_ELEMENT_SUCCEEDED,
	DETACHED_ELEMENT_RESTORED
} from '../elementActions';

import { flattenLayout, findLayoutElementById } from '../utils/layout';

import {
	deleteElementFromLayout,
	placeElementOnLayout
} from '../utils/api/layout';

const elementDetached = ({
	state,
	action,
	updateProperties,
	updateState,
	dispatch
}) => {
	const { elementId: detachedElementId } = action.payload;
	const {
		properties: { pageState }
	} = state;

	const newRootElement = cloneDeep(getRootElementFromState(state));

	const parentElement = findParentElement(newRootElement, detachedElementId);

	const flattenedLayout = flattenLayout(parentElement.layout.default.root);

	const detachedElement = findElementById(newRootElement, detachedElementId);
	const detachedElements = findElements(
		parentElement,
		(elm) => elm.slot && elm.slot.name === detachedElement.slot.name
	);

	const detachedLayoutNode = Object.values(flattenedLayout).find(
		(node) => node.slotName === detachedElement.slot.name
	);

	const previousLayout = parentElement.layout;
	parentElement.layout = deleteElementFromLayout({
		layout: previousLayout,
		id: detachedLayoutNode.id
	});

	updateProperties({
		pageState: createPageState(
			{
				rootElement: new Element(newRootElement)
			},
			pageState
		)
	});

	dispatch(DETACH_ELEMENT_SUCCEEDED, { elementId: detachedElementId });
	updateState({
		path: 'detachedElementData',
		operation: 'set',
		value: { previousLayout, detachedElements, detachedElementId }
	});
};

const detachedElementRelocated = ({
	state,
	action,
	updateState,
	updateProperties,
	dispatch
}) => {
	const {
		parentId,
		direction,
		adjacentLayoutElementId,
		slotName
	} = action.payload;

	const {
		detachedElementData: { detachedElements, detachedElementId },
		properties: { pageState }
	} = state;

	const newRootElement = cloneDeep(getRootElementFromState(state));
	const parentElement = findElementById(newRootElement, parentId);

	const layoutElementExists = !!findLayoutElementById(
		detachedElementId,
		parentElement.layout
	);

	if (layoutElementExists) {
		return dispatch(DETACHED_ELEMENT_RESTORED);
	}

	let detachedElementParent;
	detachedElements.length &&
		(detachedElementParent = findParentElement(
			newRootElement,
			detachedElements[0].id
		));

	if (detachedElementParent && detachedElementParent.id !== parentElement.id) {
		detachedElements.forEach((detachedElement) =>
			deleteChildElement(detachedElementParent, detachedElement.id)
		);
		detachedElements.forEach((detachedElement) =>
			parentElement.children.push(detachedElement)
		);
	}

	const movedElement = detachedElements.find(
		(el) => el.id === detachedElementId
	);
	const slotToPlaceElementIn = slotName || cuid();

	movedElement.slot.name = slotToPlaceElementIn;
	const newLayout = placeElementOnLayout({
		hasChildren: parentElement.children && parentElement.children.length > 0,
		adjacentId: adjacentLayoutElementId,
		slotName: slotToPlaceElementIn,
		layout: parentElement.layout,
		direction
	});
	const newElement = findElementById(newRootElement, detachedElementId);

	newElement.slot.name = slotToPlaceElementIn;
	parentElement.layout = newLayout;

	updateProperties({
		pageState: createPageState(
			{
				rootElement: new Element(newRootElement)
			},
			pageState
		)
	});
	dispatch(RELOCATE_ELEMENT_SUCCEEDED, { elementId: detachedElementId });

	updateState({
		path: 'detachedElementData',
		operation: 'set',
		value: null
	});
};

const detachedElementRestored = ({
	state,
	updateProperties,
	updateState,
	dispatch
}) => {
	const {
		detachedElementData: { detachedElements, previousLayout },
		properties: { pageState }
	} = state;

	const newRootElement = cloneDeep(getRootElementFromState(state));

	const parentElement = findParentElement(
		newRootElement,
		detachedElements[0].id
	);
	parentElement.layout = previousLayout;
	dispatch(RESTORE_ELEMENT_SUCCEEDED, {
		elementIds: detachedElements.map((el) => el.id)
	});

	updateProperties({
		pageState: createPageState(
			{
				rootElement: new Element(newRootElement)
			},
			pageState
		)
	});

	updateState({
		path: 'detachedElementData',
		operation: 'set',
		value: null
	});
};

export { elementDetached, detachedElementRelocated, detachedElementRestored };
