import PageStateModel from '../../models/pageState';

export const updatePageState = (pageState, updatedProperties) => {
	const { name, pageStatus, ...uiTypeParameters } = updatedProperties;
	return new PageStateModel({
		...pageState,
		name,
		pageStatus,
		uiTypeParameters: {
			...pageState.uiTypeParameters,
			...uiTypeParameters
		}
	});
};
