import {
	PAGE_SAVE_REQUESTED,
	PAGE_UPDATE_REQUESTED,
	PAGE_SAVE_HTTP_REQUESTED
} from './pageActions';
import transformPageStateToMacroponent, {
	removeViewportsTransientProperties
} from '../page/utils/transformPageStateToMacroponent';
import pageEndpointFromType from './utils/pageEndpointFromType';
import createDispatchedEvents from './utils/resolveInvalidEventMappingTargets';

const pageSaveRequestedHandler = ({ action, properties, dispatch }) => {
	const { pageStateModel } = action.payload || {};
	const pageState = pageStateModel || properties.pageState || {};
	const parentEventHandlers = pageState.eventHandlers.filter(
		(handler) => handler.ancestor === true
	);

	createDispatchedEvents({
		rootElement: pageState.rootElement,
		rootMacroponentId: pageState.rootElement.id,
		screenName: pageState.name,
		dataBrokerInstances: pageState.dataBrokerInstances,
		parentEventHandlers,
		dispatch,
		existingDispatchedEvents: pageState.dispatchedEvents,
		savePayload: action.payload,
		saveMeta: action.meta,
		variantScope: pageState.scope
	});
};

const pageSaveHandler = async ({ action, properties, dispatch }) => {
	const { pageStateModel } = action.payload || {};
	const pageState = pageStateModel || properties.pageState;
	let macroponentInstance = await transformPageStateToMacroponent(pageState);

	macroponentInstance = removeViewportsTransientProperties(
		macroponentInstance,
		pageState.viewportElementIds
	);

	const { forceActivate, forceDeactivate } = action.payload || {};
	if (forceActivate === true) {
		macroponentInstance.isActive = true;
	} else if (forceDeactivate === true) {
		macroponentInstance.isActive = false;
	}
	dispatch(
		PAGE_UPDATE_REQUESTED,
		{
			pageEndpoint: pageEndpointFromType(pageState.type),
			sysId: macroponentInstance.id,
			sysparm_transaction_scope: macroponentInstance.scope,
			data: macroponentInstance
		},
		{ forceActivate, forceDeactivate, ...action.meta }
	);
};

export default {
	[PAGE_SAVE_REQUESTED]: pageSaveRequestedHandler,
	[PAGE_SAVE_HTTP_REQUESTED]: pageSaveHandler
};
