import cloneDeep from 'lodash/cloneDeep';
import find from 'lodash/find';
import get from 'lodash/get';
import reject from 'lodash/reject';

import { getComponentsByTagNames } from '@devsnc/library-uxf';

import { seismicCuid } from '../../../utils';
import { DataBrokerInstance, Element, ElementEvent } from '../../../models';
import createPageState from '../../../models/createPageState';

import {
	DATA_BROKER_INSTANCE_CREATED,
	DATA_BROKER_INSTANCE_DELETED,
	DATA_BROKER_INSTANCE_PROPERTY_UPDATED,
	DATA_BROKER_INSTANCE_METADATA_UPDATE_REQUESTED,
	DATA_BROKER_INSTANCE_ID_CHANGED,
	DATA_BROKER_INSTANCE_EVALUATION_MODE_UPDATED,
	DATA_BROKER_INSTANCE_EVENT_MAPPING_CREATED,
	DATA_BROKER_INSTANCE_EVENT_MAPPING_HANDLER_CREATED,
	DATA_BROKER_INSTANCE_EVENT_MAPPING_HANDLER_CHANGED,
	DATA_BROKER_INSTANCE_EVENT_MAPPING_HANDLER_DELETED
} from './dataBrokerInstancesActions';

import { DATA_NAMESPACE } from '../../../constants';
import { createDataBrokerRuntimeElement } from '../dataBrokerFactory';

import createElementArray from '../../element/utils/createElementArray';
import { DATABROKER_OP } from '../../elementEventMappings/constants';
import {
	createEventMapping,
	createEventMappingHandler,
	updateEventMappingHandler,
	deleteEventMappingHandler
} from '../../elementEventMappings/utils';
import {
	addAllEventHandlers,
	createDataBrokerInstancePropertiesFromDataBrokerDefinition
} from '../../utils';
import {
	removeEventReferencesToPreviousId,
	removePropertyReferencesToPreviousId
} from '../../page/utils/shared';

function createDataBrokerFromDefinition(
	dataBrokerDefinitions,
	dataBrokerDefinitionId,
	dataBrokerInstances,
	ancestorDataBrokerInstances = [],
	descendentDataBrokerInstances = []
) {
	const dataBrokerDefinition = find(dataBrokerDefinitions, {
		sysId: dataBrokerDefinitionId
	});

	const allDataBrokerInstanceIds = [
		...dataBrokerInstances,
		...ancestorDataBrokerInstances,
		...descendentDataBrokerInstances.map((dbi) => ({ id: dbi.elementId }))
	].map((dbi) => dbi.id);

	const {
		name,
		type,
		selectableProps,
		hydrationSysId,
		clientStateDataBrokerTagName,
		dispatchedEvents = [],
		eventMappings
	} = dataBrokerDefinition;

	const newDataBrokerInstance = new DataBrokerInstance({
		id: seismicCuid(),
		instanceName: name,
		definition: {
			id: dataBrokerDefinitionId,
			type
		},
		selectableProps: selectableProps || [],
		hydrationSysId: hydrationSysId || null,
		clientStateDataBrokerTagName: clientStateDataBrokerTagName,
		properties: hydrationSysId
			? createDataBrokerInstancePropertiesFromDataBrokerDefinition(
					dataBrokerDefinition,
					false
			  )
			: [],
		outputProperties: createDataBrokerInstancePropertiesFromDataBrokerDefinition(
			dataBrokerDefinition,
			true
		),
		dispatchedEvents: dispatchedEvents.map(
			(ev) => new ElementEvent({ ...ev, id: ev.apiName })
		),
		eventMappings: eventMappings || []
	});
	newDataBrokerInstance.initialize(name, allDataBrokerInstanceIds);
	return newDataBrokerInstance;
}

function dataBrokerInstanceCreated({
	action,
	properties,
	updateProperties,
	updateState
}) {
	const {
		dataBrokerDefinitions,
		pageState,
		ancestorDataBrokerInstances = []
	} = properties;
	const {
		dataBrokerDefinitionId,
		dataBrokerInstance,
		descendentDataBrokers: descendentDataBrokerInstances
	} = action.payload;

	const newDataBrokerInstances = cloneDeep(pageState.dataBrokerInstances);
	const newEventHandlers = cloneDeep(pageState.eventHandlers);

	const dataBrokerInstanceToAdd =
		dataBrokerInstance ||
		createDataBrokerFromDefinition(
			dataBrokerDefinitions,
			dataBrokerDefinitionId,
			pageState.dataBrokerInstances,
			ancestorDataBrokerInstances,
			descendentDataBrokerInstances
		);

	newDataBrokerInstances.push(dataBrokerInstanceToAdd);

	if (dataBrokerInstanceToAdd.definition.type !== 'CLIENT_STATE') {
		const dataBrokerTag = createDataBrokerRuntimeElement(
			dataBrokerInstanceToAdd
		);

		updateState({
			path: `dataBrokerElements.${dataBrokerInstanceToAdd.id}`,
			value: dataBrokerTag,
			operation: 'set'
		});
	} else {
		getComponentsByTagNames([
			dataBrokerInstanceToAdd.clientStateDataBrokerTagName
		]);
	}

	const updatedEventHandlers = addAllEventHandlers(
		newEventHandlers,
		false, // These handlers are from local contexts
		[dataBrokerInstanceToAdd],
		dataBrokerDefinitions
	);

	updateProperties({
		pageState: createPageState(
			{
				dataBrokerInstances: newDataBrokerInstances,
				eventHandlers: updatedEventHandlers
			},
			pageState
		)
	});
}

const dataBrokerInstanceDeleted = ({
	action,
	properties,
	updateProperties
}) => {
	const { pageState } = properties;
	const { dataBrokerId } = action.payload;

	const newDataBrokerInstances = cloneDeep(pageState.dataBrokerInstances);
	const newEventHandlers = cloneDeep(pageState.eventHandlers);

	const newRootElement = cloneDeep(pageState.rootElement);
	const elements = createElementArray(newRootElement, (element) => ({
		eventMappings: element.eventMappings
	}));
	// remove any event mapping targets which reference the data broker instance we are deleting
	elements.forEach((element) => {
		element.eventMappings.forEach((eventMapping) => {
			eventMapping.targets = eventMapping.targets.filter((target) => {
				if (
					target.type === DATABROKER_OP &&
					target.operation.dataBrokerId === dataBrokerId
				) {
					return false;
				}
				return true;
			});
		});
	});
	// remove any event handlers which reference the data broker instance we are deleting
	const updatedEventHandlers = reject(newEventHandlers, {
		sourceId: dataBrokerId
	});
	// remove the data broker instance
	const updatedDataBrokerInstances = reject(newDataBrokerInstances, {
		id: dataBrokerId
	});

	updateProperties({
		pageState: createPageState(
			{
				rootElement: newRootElement,
				dataBrokerInstances: updatedDataBrokerInstances,
				eventHandlers: updatedEventHandlers
			},
			pageState
		)
	});
};

const dataBrokerInstancePropertyUpdated = ({
	action,
	properties,
	updateProperties
}) => {
	const { pageState, dataBrokerProperties = {} } = properties;
	const { dataBrokerId, propertyId, value } = action.payload;

	const newDataBrokerInstances = cloneDeep(pageState.dataBrokerInstances);

	const dataBrokerInstanceToUpdate =
		find(newDataBrokerInstances, {
			id: dataBrokerId
		}) || {};

	const dataBrokerInstancePropertyToUpdate =
		find(dataBrokerInstanceToUpdate.properties, {
			columnName: propertyId
		}) || {};

	const dataBrokerInstanceProperties = dataBrokerProperties[dataBrokerId] || [];
	const updateProp =
		find(dataBrokerInstanceProperties, { id: propertyId }) || {};

	if (updateProp.onChange) {
		updateProp.onChange(dataBrokerInstanceToUpdate);
	}

	dataBrokerInstancePropertyToUpdate.value = value;

	updateProperties({
		pageState: createPageState(
			{
				dataBrokerInstances: newDataBrokerInstances
			},
			pageState
		)
	});
};

const dataBrokerMetadataUpdatedHandler = ({
	action,
	properties,
	updateProperties,
	dispatch,
	updateState
}) => {
	const { pageState = {} } = properties;
	const {
		clientStateParameters = [],
		dataBrokerInstances = [],
		eventHandlers = [],
		rootElement = {}
	} = pageState;

	const { previousId, changes = {} } = action.payload;
	const newDataBrokerInstances = cloneDeep(dataBrokerInstances);
	const dataBrokerInstanceToUpdate = find(newDataBrokerInstances, {
		id: previousId
	});

	const newRootElement = cloneDeep(rootElement);
	const { internalEventMappings = [] } = newRootElement || {};
	const elements = createElementArray(newRootElement, (element) => element);
	const newEventHandlers = cloneDeep(eventHandlers);
	const newClientStateParameters = cloneDeep(clientStateParameters);

	if (changes.instanceName) {
		dataBrokerInstanceToUpdate.instanceName = changes.instanceName;
		// if a data broker instance name is changed, update event handlers that reference the old name.
		newEventHandlers.map((eventHandler) => {
			const sourceId = get(eventHandler, 'sourceId', null);
			if (sourceId === dataBrokerInstanceToUpdate.id) {
				eventHandler.sourceName = changes.instanceName;
			}
		});
	}

	if (changes.id) {
		dataBrokerInstanceToUpdate.id = changes.id;
		removePropertyReferencesToPreviousId(
			previousId,
			changes.id,
			elements,
			newDataBrokerInstances,
			newClientStateParameters,
			DATA_NAMESPACE
		);
		removeEventReferencesToPreviousId(
			previousId,
			changes.id,
			elements,
			newEventHandlers,
			newDataBrokerInstances,
			internalEventMappings,
			DATA_NAMESPACE
		);

		const dataBrokerTag = createDataBrokerRuntimeElement(
			dataBrokerInstanceToUpdate
		);
		updateState({
			path: `dataBrokerElements.${dataBrokerInstanceToUpdate.id}`,
			value: dataBrokerTag,
			operation: 'set'
		});

		dispatch(DATA_BROKER_INSTANCE_ID_CHANGED, { id: changes.id });
	}

	updateProperties({
		pageState: createPageState(
			{
				dataBrokerInstances: newDataBrokerInstances,
				clientStateParameters: newClientStateParameters,
				eventHandlers: newEventHandlers,
				rootElement: new Element(newRootElement)
			},
			pageState
		)
	});
};

const dataBrokerInstanceEvaluationModeChanged = ({
	action,
	properties,
	updateProperties
}) => {
	const { dataBrokerId, value } = action.payload || {};
	const { pageState } = properties;

	const newDataBrokerInstances = cloneDeep(pageState.dataBrokerInstances);
	const dataBrokerInstanceToUpdate = find(newDataBrokerInstances, {
		id: dataBrokerId
	});
	dataBrokerInstanceToUpdate.readEvaluationMode = value;

	updateProperties({
		pageState: createPageState(
			{ dataBrokerInstances: newDataBrokerInstances },
			pageState
		)
	});
};

const dataBrokerEventMappingCreated = ({
	action,
	properties,
	updateProperties
}) => {
	const { dataBrokerId, eventApiName } = action.payload || {};
	const { pageState = {} } = properties;
	const newDataBrokerInstances = cloneDeep(pageState.dataBrokerInstances || []);
	const dataBrokerInstanceToUpdate = newDataBrokerInstances.find(
		(dbInstance) => dbInstance.id === dataBrokerId
	);

	if (dataBrokerInstanceToUpdate) {
		const dataBrokerEventMapping = createEventMapping(
			eventApiName,
			eventApiName
		);
		dataBrokerInstanceToUpdate.eventMappings.unshift(dataBrokerEventMapping);
	}

	updateProperties({
		pageState: createPageState(
			{ dataBrokerInstances: newDataBrokerInstances },
			pageState
		)
	});
};

const dataBrokerEventMappingHandlerCreated = ({
	action,
	properties,
	updateProperties
}) => {
	const { dataBrokerId, eventMappingId } = action.payload || {};
	const { pageState = {} } = properties;
	const newDataBrokerInstances = cloneDeep(pageState.dataBrokerInstances || []);
	const dataBrokerInstanceToUpdate =
		newDataBrokerInstances.find(
			(dbInstance) => dbInstance.id === dataBrokerId
		) || {};

	const { eventMappings = [] } = dataBrokerInstanceToUpdate;
	createEventMappingHandler(eventMappings, eventMappingId);

	updateProperties({
		pageState: createPageState(
			{ dataBrokerInstances: newDataBrokerInstances },
			pageState
		)
	});
};

const dataBrokerEventMappingHandlerChanged = ({
	action,
	properties,
	updateProperties
}) => {
	const {
		dataBrokerId,
		eventMappingId,
		handlerId,
		handlerTypeId = null,
		handlerValueId = null,
		value = null,
		scripted = null
	} = action.payload || {};
	const {
		eventHandlers = [],
		pageState = {},
		clientStateParameters = []
	} = properties;
	const newDataBrokerInstances = cloneDeep(pageState.dataBrokerInstances || []);
	const dataBrokerInstanceToUpdate =
		newDataBrokerInstances.find(
			(dbInstance) => dbInstance.id === dataBrokerId
		) || {};
	const { eventMappings = [] } = dataBrokerInstanceToUpdate;
	const eventMappingToUpdate =
		eventMappings.find(
			(eventMapping) => eventMapping.eventMappingId === eventMappingId
		) || {};
	const { targets = [] } = eventMappingToUpdate;

	const handlerToUpdate =
		targets.find((target) => target.targetId === handlerId) || null;
	if (handlerToUpdate) {
		updateEventMappingHandler(
			handlerToUpdate,
			eventHandlers,
			handlerTypeId,
			handlerValueId,
			value,
			scripted,
			clientStateParameters
		);
	}

	updateProperties({
		pageState: createPageState(
			{ dataBrokerInstances: newDataBrokerInstances },
			pageState
		)
	});
};

const dataBrokerEventMappingHandlerDeleted = ({
	action,
	properties,
	updateProperties
}) => {
	const { dataBrokerId, eventMappingId, handlerId } = action.payload || {};
	const { pageState = {} } = properties;
	const newDataBrokerInstances = cloneDeep(pageState.dataBrokerInstances || []);
	const dataBrokerInstanceToUpdate =
		newDataBrokerInstances.find(
			(dbInstance) => dbInstance.id === dataBrokerId
		) || {};
	const { eventMappings = [] } = dataBrokerInstanceToUpdate;

	deleteEventMappingHandler(
		dataBrokerInstanceToUpdate,
		eventMappings,
		eventMappingId,
		handlerId
	);
	updateProperties({
		pageState: createPageState(
			{ dataBrokerInstances: newDataBrokerInstances },
			pageState
		)
	});
};

export default {
	[DATA_BROKER_INSTANCE_CREATED]: dataBrokerInstanceCreated,
	[DATA_BROKER_INSTANCE_DELETED]: dataBrokerInstanceDeleted,
	[DATA_BROKER_INSTANCE_PROPERTY_UPDATED]: dataBrokerInstancePropertyUpdated,
	[DATA_BROKER_INSTANCE_METADATA_UPDATE_REQUESTED]: dataBrokerMetadataUpdatedHandler,
	[DATA_BROKER_INSTANCE_EVALUATION_MODE_UPDATED]: dataBrokerInstanceEvaluationModeChanged,
	[DATA_BROKER_INSTANCE_EVENT_MAPPING_CREATED]: dataBrokerEventMappingCreated,
	[DATA_BROKER_INSTANCE_EVENT_MAPPING_HANDLER_CREATED]: dataBrokerEventMappingHandlerCreated,
	[DATA_BROKER_INSTANCE_EVENT_MAPPING_HANDLER_CHANGED]: dataBrokerEventMappingHandlerChanged,
	[DATA_BROKER_INSTANCE_EVENT_MAPPING_HANDLER_DELETED]: dataBrokerEventMappingHandlerDeleted
};
