import { createAmbSubscriptionEffect } from '@servicenow/ui-effect-amb';
import set from 'lodash/set';
import { encodeFilterForAmb } from './utils';
import { AMB_SUBSCRIPTION_REQUESTED } from './ambSubscribersActions';

const MESSAGE_UPDATE_ACTION_ROOT = 'SN_UXF#AMB_MESSAGE_RECEIVED';
const SUBSCRIPTION_SUCCEEDED_ACTION_ROOT = 'SN_UXF#AMB_SUBSCRIPTION_SUCCEEDED';
const SUBSCRIPTION_FAILED_ACTION_ROOT = 'SN_UXF#AMB_SUBSCRIPTION_FAILED';
const UNSUBSCRIBE_SUCCEEDED_ACTION_ROOT = 'SN_UXF#AMB_UNSUBSCRIBE_SUCCEEDED';

const ambSubscriptionRequestedHandler = {
	effect: () => null,
	interceptors: [
		(context) => {
			const {
				payload: { table, filter }
			} = context.coeffects.action;
			const targetTable = table || null;
			const targetFilter = encodeFilterForAmb(filter) || null;
			if (!targetTable || !targetFilter) {
				return context;
			}
			set(context.coeffects.action, 'payload.filter', targetFilter);

			const messageReceivedActionType = `${MESSAGE_UPDATE_ACTION_ROOT}:${targetTable}`;
			const subscriptionSucceededActionType = `${SUBSCRIPTION_SUCCEEDED_ACTION_ROOT}:${targetTable}`;
			const subscriptionFailedActionType = `${SUBSCRIPTION_FAILED_ACTION_ROOT}:${targetTable}`;
			const unsubscribeSucceededActionType = `${UNSUBSCRIBE_SUCCEEDED_ACTION_ROOT}:${targetTable}`;

			const channel = '/rw/default/:table/:filter';
			const options = {
				subscribeSucceededActionType: subscriptionSucceededActionType,
				subscribeFailedActionType: subscriptionFailedActionType,
				unsubscribeSucceededActionType: unsubscribeSucceededActionType,
				messageReceivedActionType: messageReceivedActionType
			};
			const ambEffect = createAmbSubscriptionEffect(channel, options);

			return {
				...context,
				effects: [
					{
						...ambEffect,
						args: [...ambEffect.args, context.coeffects]
					}
				]
			};
		}
	]
};

export default {
	[AMB_SUBSCRIPTION_REQUESTED]: ambSubscriptionRequestedHandler
};
