import cuid from 'cuid';
import get from 'lodash/get';

const toProportion = (nums) => {
	const sum = nums.reduce((s, n) => s + n, 0);
	return nums.map((n) => n / sum);
};
const toScale = (n) => [n * 12, (n * 12) % 1];

const processor = ({ remainder, numbers }, [num, rem]) => {
	let newRemainder = remainder;
	const newNumbers = [];

	if (num < 1) {
		return {
			remainder: newRemainder - (rem - 1),
			numbers: [...numbers, Math.ceil(num)]
		};
	}

	if (rem !== 0) {
		if (newRemainder + rem > 0.98) {
			newNumbers.push(Math.ceil(num));

			newRemainder -= 1 - rem;
		} else {
			newNumbers.push(Math.floor(num));
			newRemainder += rem;
		}
		return {
			remainder: newRemainder,
			numbers: [...numbers, ...newNumbers]
		};
	}

	return {
		remainder,
		numbers: [...numbers, num]
	};
};

const updateProportions = (nums) =>
	toProportion(nums)
		.map(toScale)
		.reduce(processor, { remainder: 0, numbers: [] }).numbers;

const updateProportionForNewSize = (size, nums) => {
	const prop = 12 / size / 12;
	const share = prop / nums.length;

	const processed = toProportion(nums).map(
		(n) => n - share * (n / (1 / nums.length))
	);
	return [...processed, prop]
		.map(toScale)
		.reduce(processor, { remainder: 0, numbers: [] }).numbers;
};
const getUpdatedSizesForNew = (arr, len) => {
	const updates = updateProportionForNewSize(
		len,
		arr.map((col) => parseInt(col.size))
	);

	return {
		updatedSizes: updates
			.slice(0, -1)
			.reduce((acc, num, i) => ({ ...acc, [arr[i].id]: num.toString() }), {}),
		newSize: updates.slice(-1)[0].toString()
	};
};
export const normalizeSizes = (array) => {
	const newCol = array.find((col) => !col.size);
	if (newCol) {
		const { updatedSizes, newSize } = getUpdatedSizesForNew(
			array.filter((col) => col.id !== newCol.id),
			array.length
		);
		return array.map((col) => {
			if (col.size) {
				col.size = updatedSizes[col.id];
			} else {
				col.size = newSize;
			}
			return col;
		});
	}
	return updateProportions(array.map((col) => parseInt(col.size))).map(
		(size, i) => {
			array[i].size = size.toString();
			return array[i];
		}
	);
};
export const flattenLayout = (
	layoutPart,
	flattenedParts = {},
	parent = 'root'
) => {
	function flattenChildren(parts, childType) {
		let newParts = parts;
		for (const i in layoutPart[childType]) {
			const childLayoutPart = layoutPart[childType][i];
			newParts = flattenLayout(childLayoutPart, newParts, layoutPart.id);
		}
		return newParts;
	}

	let childType;
	if (layoutPart.rows) {
		childType = 'rows';
	} else if (layoutPart.columns) {
		childType = 'columns';
	}
	flattenedParts[layoutPart.id] = {
		parent: parent || 'root',
		type: layoutPart.type,
		id: layoutPart.id
	};
	if (layoutPart.slotName) {
		flattenedParts[layoutPart.id].slotName = layoutPart.slotName;
	}
	if (flattenedParts[layoutPart.id].type === 'column') {
		flattenedParts[layoutPart.id].size = layoutPart.size;
	}
	if (!childType) {
		return flattenedParts;
	}
	if (childType === 'rows' && layoutPart.rows.length) {
		flattenedParts[layoutPart.id].rows = layoutPart.rows.map((row) => row.id);
	} else if (childType === 'columns' && layoutPart.columns.length) {
		flattenedParts[layoutPart.id].columns = layoutPart.columns.map(
			(column) => column.id
		);
	}

	return flattenChildren(flattenedParts, childType);
};

export const treeifyLayout = (flattenedLayout) => {
	function populateChildren(type, flattenedLayout, key) {
		flattenedLayout[key][type] = flattenedLayout[key][type].map((id) => {
			delete flattenedLayout[id].parent;
			return flattenedLayout[id];
		});
	}

	const ids = Object.keys(flattenedLayout);
	let currentKey, currentLayoutPart;
	for (const i in ids) {
		currentKey = ids[i];
		currentLayoutPart = flattenedLayout[currentKey];
		if (currentLayoutPart.rows) {
			populateChildren('rows', flattenedLayout, currentKey);
		} else if (currentLayoutPart.columns) {
			populateChildren('columns', flattenedLayout, currentKey);
		}
	}
	return {
		default: {
			type: 'rowColumn',
			root: Object.entries(flattenedLayout).reduce((acc, [key, val]) => {
				if (val.parent === 'root') {
					delete flattenedLayout[key].parent;
					return flattenedLayout[key];
				}
				return acc;
			}, null)
		}
	};
};

export const findLayoutElementByChild = (flattenedLayout, childId) => {
	return Object.values(flattenedLayout).find((layoutElement) => {
		const childCollectionType = getCollectionKey(
			getChildType(layoutElement.type)
		);
		if (!isLeaf(layoutElement)) {
			return layoutElement[childCollectionType].find((id) => id === childId);
		}
		return false;
	});
};

const findLayoutElementsWithSingleChildLayout = (flattenedLayout) => {
	const ret = [];
	Object.keys(flattenedLayout).forEach((id) => {
		const parentElement = flattenedLayout[id];
		const childCollectionKey = getCollectionKey(
			getChildType(parentElement.type)
		);
		if (
			parentElement[childCollectionKey] &&
			parentElement[childCollectionKey].length === 1
		) {
			const soloChildId = parentElement[childCollectionKey][0];
			!isLeaf(flattenedLayout[soloChildId]) && ret.push(parentElement);
		}
	});
	return ret;
};

export const spliceInNewChild = (
	childCollection,
	adjacentId,
	targetId,
	direction
) => {
	const ret = [...childCollection];
	const adjacentIdIndex = ret.indexOf(adjacentId);
	ret.splice(
		direction.toLowerCase() === 'left' || direction.toLowerCase() === 'top'
			? adjacentIdIndex
			: adjacentIdIndex + 1,
		0,
		targetId
	);
	return ret;
};

const spliceOutChild = (layoutParent, id) => {
	const childType = getChildType(layoutParent.type);
	const collectionKey = getCollectionKey(childType);
	const childCollection = layoutParent[collectionKey];
	childCollection.splice(childCollection.indexOf(id), 1);
	return childCollection;
};

export const replaceExistingChild = (childCollection, oldId, newId) => {
	return childCollection.map((child) => (child === oldId ? newId : child));
};

export const getChildType = (parentType) => {
	return parentType === 'column' ? 'row' : 'column';
};

export const getCollectionKey = (type) => {
	return `${type}s`;
};

const insertParallelLeaf = (
	layoutParent,
	flattenedLayout,
	childCollectionType,
	existingId,
	newSlotName,
	direction
) => {
	const childCollectionKey = getCollectionKey(childCollectionType);
	const newId = cuid();
	const newChildren = spliceInNewChild(
		layoutParent[childCollectionKey],
		existingId,
		newId,
		direction
	);
	flattenedLayout[layoutParent.id][childCollectionKey] = newChildren;
	flattenedLayout[newId] = {
		parent: layoutParent.id,
		type: childCollectionType,
		slotName: newSlotName,
		id: newId
	};
	return flattenedLayout;
};

const insertPerpendicularToLeaf = (
	layoutParent,
	flattenedLayout,
	childCollectionType,
	existingId,
	newSlotName,
	direction
) => {
	const childCollectionKey = getCollectionKey(childCollectionType);
	const newLayoutElementType = getChildType(childCollectionType);
	const newLayoutElementCollectionKey = getCollectionKey(newLayoutElementType);
	const newLeafId = cuid();
	const newContainerId = cuid();
	const newChildren = replaceExistingChild(
		layoutParent[childCollectionKey],
		existingId,
		newContainerId
	);
	flattenedLayout[layoutParent.id][childCollectionKey] = newChildren;
	const newContainer = {
		parent: layoutParent.id,
		id: newContainerId,
		type: childCollectionType,
		[newLayoutElementCollectionKey]: spliceInNewChild(
			[existingId],
			existingId,
			newLeafId,
			direction
		)
	};
	flattenedLayout[newContainerId] = newContainer;
	flattenedLayout[existingId].parent = newContainerId;

	flattenedLayout[newLeafId] = {
		parent: newContainerId,
		type: newLayoutElementType,
		slotName: newSlotName,
		id: newLeafId
	};
	flattenedLayout[existingId].type = newLayoutElementType;
	if (newLayoutElementType !== 'column') {
		delete flattenedLayout[existingId].size;
	}
	return flattenedLayout;
};

const insertPerpendicularToLayout = (
	flattenedLayout,
	existingId,
	newSlotName,
	direction
) => {
	const collectionType = getChildType(flattenedLayout[existingId].type);
	const collectionKey = getCollectionKey(collectionType);
	const children = [...flattenedLayout[existingId][collectionKey]];
	const siblingId =
		direction.toLowerCase() === 'right' || direction.toLowerCase() === 'bottom'
			? children.pop()
			: children.shift();
	return insertParallelLeaf(
		flattenedLayout[existingId],
		flattenedLayout,
		collectionType,
		siblingId,
		newSlotName,
		direction
	);
};

const insertParallelToRoot = (flattenedLayout, newSlotName, direction) => {
	const newLeafId = cuid();
	const newRootId = cuid();
	const existingRoot = getRoot(flattenedLayout);
	const existingRootType = existingRoot.type;
	existingRoot.parent = newRootId;
	const newRootType = getChildType(existingRootType);
	const newRootCollectionKey = getCollectionKey(existingRootType);
	const newRoot = {
		parent: 'root',
		id: newRootId,
		type: newRootType,
		[newRootCollectionKey]: [existingRoot.id]
	};
	const newLayoutElement = {
		parent: newRootId,
		type: existingRootType,
		id: newLeafId,
		slotName: newSlotName
	};
	if (
		direction.toLowerCase() === 'right' ||
		direction.toLowerCase() === 'bottom'
	) {
		newRoot[newRootCollectionKey].push(newLeafId);
	} else {
		newRoot[newRootCollectionKey].unshift(newLeafId);
	}
	flattenedLayout[newRootId] = newRoot;
	flattenedLayout[newLeafId] = newLayoutElement;
	return flattenedLayout;
};

export const isLeaf = (layoutElement) => {
	let hasChildren = false;
	for (const key of Object.keys(layoutElement)) {
		if (Array.isArray(layoutElement[key]) && layoutElement[key].length) {
			hasChildren = true;
			break;
		}
	}
	return !hasChildren;
};

export const getRoot = (flattenedLayout) => {
	const ids = Object.keys(flattenedLayout);
	for (const id of ids) {
		if (flattenedLayout[id].parent === 'root') {
			return flattenedLayout[id];
		}
	}
};

export const isParallel = (direction, layoutType) => {
	return (
		((direction.toLowerCase() === 'right' ||
			direction.toLowerCase() === 'left') &&
			layoutType === 'column') ||
		((direction.toLowerCase() === 'top' ||
			direction.toLowerCase() === 'bottom') &&
			layoutType === 'row')
	);
};

export const findLayoutElementById = (id, unflattenedLayout) => {
	const lookup = {
		column: 'rows',
		row: 'columns'
	};
	let items = [unflattenedLayout.default.root];
	let found = null;
	while (!found && items.length > 0) {
		const item = items.shift();
		if (item.id === id) {
			found = item;
		} else {
			items = items.concat(get(item, lookup[item.type], []));
		}
	}

	return found;
};

const promoteAndDeleteElement = (
	flattenedLayout,
	deletedElementParent,
	collectionKey,
	id,
	deletedElementParentId,
	deletedElementGrandParentId
) => {
	const soloLayoutChildId = deletedElementParent[collectionKey].filter(
		(childId) => childId !== id
	)[0];
	const currentSoloLayoutChildCollectionKey = getCollectionKey(
		getChildType(flattenedLayout[soloLayoutChildId].type)
	);
	if (
		flattenedLayout[soloLayoutChildId][currentSoloLayoutChildCollectionKey] &&
		!flattenedLayout[soloLayoutChildId][currentSoloLayoutChildCollectionKey]
			.length
	) {
		delete flattenedLayout[soloLayoutChildId][
			currentSoloLayoutChildCollectionKey
		];
	}
	flattenedLayout[soloLayoutChildId].type =
		flattenedLayout[deletedElementParentId].type;
	flattenedLayout[soloLayoutChildId].parent =
		flattenedLayout[deletedElementParentId].parent;
	flattenedLayout[soloLayoutChildId].type === 'row' &&
		delete flattenedLayout[soloLayoutChildId].size;
	const grandParentCollectionKey = getCollectionKey(
		getChildType(flattenedLayout[deletedElementGrandParentId].type)
	);
	const newGrandParentChildren = replaceExistingChild(
		flattenedLayout[deletedElementGrandParentId][grandParentCollectionKey],
		deletedElementParentId,
		soloLayoutChildId
	);
	flattenedLayout[deletedElementGrandParentId][
		grandParentCollectionKey
	] = newGrandParentChildren;
	delete flattenedLayout[deletedElementParentId];
	delete flattenedLayout[id];
	return flattenedLayout;
};

const insertPerpendicularToSingleLeaf = (
	flattenedLayout,
	layoutElementParent,
	childCollectionType,
	childCollectionKey,
	adjacentId,
	targetSlotName,
	direction
) => {
	const newLeafId = cuid();
	flattenedLayout[newLeafId] = {
		parent: layoutElementParent.slotName,
		id: newLeafId,
		slotName: targetSlotName
	};
	childCollectionType === 'row' && delete layoutElementParent.size;
	const newChildCollectionKey =
		childCollectionKey === 'columns' ? 'rows' : 'columns';
	const newChildCollection = spliceInNewChild(
		layoutElementParent[childCollectionKey],
		adjacentId,
		newLeafId,
		direction
	);
	layoutElementParent[newChildCollectionKey] = newChildCollection;
	const newChildType = layoutElementParent.type;
	newChildCollection.forEach((childId) => {
		newChildType === 'row' && delete flattenedLayout[childId].size;
		flattenedLayout[childId].type = newChildType;
	});
	delete layoutElementParent[childCollectionKey];
	layoutElementParent.type = childCollectionType;

	flattenedLayout[layoutElementParent.id] = layoutElementParent;
	return flattenedLayout;
};

const promoteSingltonAtRoot = (flattenedLayout, layoutParent) => {
	const childCollectionKey = getCollectionKey(getChildType(layoutParent.type));
	const singletonLayoutElement =
		flattenedLayout[layoutParent[childCollectionKey][0]];
	const singletonChildCollectionKey = getCollectionKey(
		getChildType(singletonLayoutElement.type)
	);
	const singletonLayoutElementChildIds =
		singletonLayoutElement[singletonChildCollectionKey];
	singletonLayoutElementChildIds.forEach((singletonChildId) => {
		flattenedLayout[singletonChildId].parent = layoutParent.id;
	});
	layoutParent.type = layoutParent.type === 'column' ? 'row' : 'column';
	layoutParent.type === 'row' && delete layoutParent.size;
	layoutParent[childCollectionKey] = singletonLayoutElementChildIds;
	layoutParent[childCollectionKey === 'columns' ? 'rows' : 'columns'] =
		layoutParent[childCollectionKey];
	delete layoutParent[childCollectionKey];
	flattenedLayout[layoutParent.id] = layoutParent;
	delete flattenedLayout[singletonLayoutElement.id];
	return flattenedLayout;
};

export const findIncongruentLayoutElement = (flattenedLayout) => {
	const ids = Object.keys(flattenedLayout);
	for (const id of ids) {
		if (flattenedLayout[id][`${flattenedLayout[id].type}s`]) {
			return flattenedLayout[id];
		}
	}
	return false;
};

const removeIncongruentLayoutElement = (
	flattenedLayout,
	illegalLayoutElement
) => {
	const illegalElementParent = flattenedLayout[illegalLayoutElement.parent];
	const illegalCollectionKey = getCollectionKey(illegalLayoutElement.type);
	illegalLayoutElement[illegalCollectionKey].forEach(
		(id) => (flattenedLayout[id].parent = illegalElementParent.id)
	);
	const mergedChildCollection = [
		...illegalElementParent[illegalCollectionKey],
		...illegalLayoutElement[illegalCollectionKey]
	].filter((id) => id !== illegalLayoutElement.id);
	illegalElementParent[illegalCollectionKey] = mergedChildCollection;
	flattenedLayout[illegalElementParent.id] = illegalElementParent;
	delete flattenedLayout[illegalLayoutElement.id];
	return flattenedLayout;
};

export const applyNewSizes = (flattenedLayout) => {
	const ids = Object.keys(flattenedLayout);
	for (const id of ids) {
		const layoutElement = flattenedLayout[id];
		if (layoutElement.parent === 'root' && layoutElement.type === 'column') {
			flattenedLayout[layoutElement.id].size = '12';
		}
		if (layoutElement.columns && layoutElement.columns.length) {
			const cols = layoutElement.columns.map(
				(columnId) => flattenedLayout[columnId]
			);
			normalizeSizes(cols);
		}
	}
	return flattenedLayout;
};

export const insertAndGetUpdatedLayout = (
	flattenedLayout,
	direction,
	adjacentId,
	targetSlotName
) => {
	const layoutElementParent = findLayoutElementByChild(
		flattenedLayout,
		adjacentId
	);

	if (layoutElementParent) {
		const childCollectionType = getChildType(layoutElementParent.type);
		if (isParallel(direction, childCollectionType)) {
			return applyNewSizes(
				insertParallelLeaf(
					layoutElementParent,
					flattenedLayout,
					childCollectionType,
					adjacentId,
					targetSlotName,
					direction
				)
			);
		} else if (!isParallel(direction, childCollectionType)) {
			const childCollectionKey = getCollectionKey(childCollectionType);
			if (
				layoutElementParent.parent === 'root' &&
				layoutElementParent[childCollectionKey].length === 1
			) {
				return applyNewSizes(
					insertPerpendicularToSingleLeaf(
						flattenedLayout,
						layoutElementParent,
						childCollectionType,
						childCollectionKey,
						adjacentId,
						targetSlotName,
						direction
					)
				);
			} else if (isLeaf(flattenedLayout[adjacentId])) {
				return applyNewSizes(
					insertPerpendicularToLeaf(
						layoutElementParent,
						flattenedLayout,
						childCollectionType,
						adjacentId,
						targetSlotName,
						direction
					)
				);
			} else {
				return applyNewSizes(
					insertPerpendicularToLayout(
						flattenedLayout,
						adjacentId,
						targetSlotName,
						direction
					)
				);
			}
		}
	} else {
		const rootLayoutElement = getRoot(flattenedLayout);

		if (
			rootLayoutElement.type === 'row' &&
			rootLayoutElement.columns.length === 1
		) {
			if (
				direction.toLowerCase() === 'bottom' ||
				direction.toLowerCase() === 'top'
			) {
				const childCollectionKey = getCollectionKey(
					getChildType(rootLayoutElement.type)
				);
				insertPerpendicularToSingleLeaf(
					flattenedLayout,
					rootLayoutElement,
					'column',
					childCollectionKey,
					rootLayoutElement.columns[0],
					targetSlotName,
					direction
				);
			} else {
				insertParallelLeaf(
					rootLayoutElement,
					flattenedLayout,
					'column',
					rootLayoutElement.columns[0],
					targetSlotName,
					direction
				);
			}
		} else if (isParallel(direction, rootLayoutElement.type)) {
			return applyNewSizes(
				insertParallelToRoot(flattenedLayout, targetSlotName, direction)
			);
		} else {
			return applyNewSizes(
				insertPerpendicularToLayout(
					flattenedLayout,
					adjacentId,
					targetSlotName,
					direction
				)
			);
		}
	}
	return applyNewSizes(flattenedLayout);
};

export const deleteAndGetUpdatedLayout = (flattenedLayout, id) => {
	const deletedElementParent = findLayoutElementByChild(flattenedLayout, id);
	const collectionKey = getCollectionKey(
		getChildType(deletedElementParent.type)
	);
	const childCollection = deletedElementParent[collectionKey];
	const deletedElementParentId = deletedElementParent.id;
	const deletedElementGrandParentId =
		flattenedLayout[deletedElementParentId].parent;
	if (childCollection.length > 2 || deletedElementGrandParentId === 'root') {
		flattenedLayout[deletedElementParentId][collectionKey] = spliceOutChild(
			deletedElementParent,
			id
		);
		delete flattenedLayout[id];
		const layoutParentsWithSingleChildLayout = findLayoutElementsWithSingleChildLayout(
			flattenedLayout
		);
		layoutParentsWithSingleChildLayout.forEach((layoutParent) => {
			if (layoutParent.parent === 'root') {
				return applyNewSizes(
					promoteSingltonAtRoot(flattenedLayout, layoutParent)
				);
			}
		});
	} else {
		const newFlattenedLayout = promoteAndDeleteElement(
			flattenedLayout,
			deletedElementParent,
			collectionKey,
			id,
			deletedElementParentId,
			deletedElementGrandParentId
		);
		const illegalLayoutElement = findIncongruentLayoutElement(
			newFlattenedLayout
		);
		if (illegalLayoutElement) {
			return applyNewSizes(
				removeIncongruentLayoutElement(newFlattenedLayout, illegalLayoutElement)
			);
		}
		return applyNewSizes(newFlattenedLayout);
	}
	return applyNewSizes(flattenedLayout);
};

function addIdsToLayoutElment(layoutElement) {
	layoutElement.id = layoutElement.id || cuid();

	if (layoutElement.slotName) {
		return;
	}

	const childAccessor = layoutElement.type === 'row' ? 'columns' : 'rows';

	for (const childLayoutElement of layoutElement[childAccessor]) {
		addIdsToLayoutElment(childLayoutElement);
	}
}

export const addIdsToLayout = (layout) => {
	if (!layout) {
		return;
	}

	if (layout.default) {
		const { root } = layout.default;

		addIdsToLayoutElment(root);
	}
};
export const getClosestNeighbor = (array, index, side, dir) => {
	const MIN_SIZE = 1;
	const getSide = {
		LEFT() {
			return array.slice(0, index).reverse();
		},
		RIGHT() {
			return array.slice(index + 1);
		}
	};

	const neighbors = getSide[side.toUpperCase()]();
	if (dir === 'EXPAND') {
		return neighbors.filter((neighbor) => neighbor.size > MIN_SIZE)[0] || null;
	}
	return neighbors.sort((a, b) => a.size - b.size)[0] || null;
};
const childrenKey = (type) => (type === 'row' ? 'columns' : 'rows');
const traverseChildren = (children, { label, value }) => {
	if (!children) {
		return null;
	}

	const results = children
		.map((child) => {
			if (child[label] === value) {
				return children;
			}
			const key = childrenKey(child.type);
			const items = child[key];
			if (!items) {
				return null;
			}
			return traverseChildren(items, { label, value });
		})
		.filter((child) => child);

	return results[0];
};
export const getResizedRow = (layout, { label, value }) => {
	const { root } = layout.default;
	const children = root[childrenKey(root.type)];
	return traverseChildren(children, { label, value }) || [];
};
