import { actionTypes } from '@servicenow/ui-core';
import { snUXFActions } from '../../sn-uxf-builder-middleware';

const componentDomReadyHandler = ({ dispatch }) => {
	dispatch(snUXFActions.AMB_SUBSCRIPTION_REQUESTED, {
		table: 'sys_ux_data_broker',
		filter: 'sys_idISNOTEMPTY',
		subscribe: true
	});
};

export default {
	[actionTypes.COMPONENT_DOM_READY]: componentDomReadyHandler
};
