import { createHttpEffect } from '@servicenow/ui-effect-http';
import get from 'lodash/get';
import cloneDeep from 'lodash/cloneDeep';
import {
	NEW_PAGE_SAVED,
	NEW_PAGE_SAVE_REQUEST,
	NEW_PAGE_SAVE_STARTED,
	NEW_PAGE_SAVE_SUCCEEDED,
	NEW_PAGE_SAVE_FAILED
} from './newPageActions';
import createPageState from '../../models/createPageState';
import { generateUpdatedMacroponentProperties } from './utils';

const newPageSave = ({ action, dispatch }) => {
	const {
		routeType,
		pageName,
		parameters,
		fields,
		icon,
		order
	} = action.payload.data;
	dispatch(
		NEW_PAGE_SAVE_REQUEST,
		{
			...action.payload,
			data: {
				routeType,
				name: pageName,
				parameters: parameters
					? Object.values(parameters).map((p) => ({
							name: p.name
					  }))
					: parameters,
				fields,
				icon,
				order
			}
		},
		action.meta
	);
};

const newPageSaveStarted = ({ updateProperties }) => {
	updateProperties({ isSavingPage: true });
};
const newPageSaveSucceeded = ({ action, properties, updateProperties }) => {
	const { pageState } = properties;
	const {
		rootElement: { macroponentProperties }
	} = pageState;

	const variants = get(action.payload, 'result.variants', []);
	const currentlyLoadedScreen = variants.find(
		(variant) => variant.id === pageState.sysId
	);
	if (!currentlyLoadedScreen) {
		return updateProperties({ isSavingPage: false, isLoadingPage: false });
	}

	const oldPageState = cloneDeep(pageState);
	const newPageState = createPageState(
		{
			rootElement: {
				...oldPageState.rootElement,
				macroponentProperties: generateUpdatedMacroponentProperties(
					currentlyLoadedScreen,
					macroponentProperties
				)
			}
		},
		oldPageState
	);
	updateProperties({
		pageState: newPageState,
		pageStatePristine: newPageState,
		isSavingPage: false,
		isLoadingPage: false
	});
};
const newPageSaveFailed = ({ updateProperties }) => {
	updateProperties({ isSavingPage: false, isLoadingPage: false });
};

export default {
	[NEW_PAGE_SAVED]: newPageSave,
	[NEW_PAGE_SAVE_REQUEST]: createHttpEffect(
		'/api/now/builder/apps/:appId/pages/:pageId',
		{
			method: 'PATCH',
			pathParams: ['appId', 'pageId'],
			dataParam: 'data',
			queryParams: ['sysparm_transaction_scope'],
			startActionType: NEW_PAGE_SAVE_STARTED,
			successActionType: NEW_PAGE_SAVE_SUCCEEDED,
			errorActionType: NEW_PAGE_SAVE_FAILED,
			batch: false
		}
	),
	[NEW_PAGE_SAVE_STARTED]: newPageSaveStarted,
	[NEW_PAGE_SAVE_SUCCEEDED]: newPageSaveSucceeded,
	[NEW_PAGE_SAVE_FAILED]: newPageSaveFailed
};
