import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	METADATA_UPDATE_REQUESTED,
	METADATA_UPDATE_HTTP_REQUESTED,
	METADATA_UPDATE_STARTED,
	METADATA_UPDATE_FAILED,
	METADATA_UPDATE_SUCCEEDED
} from './pageActions';
import {
	transformMetadataToPageState,
	getMetadataUpdatePayload
} from './utils/transformMetadata';
import pageEndpointFromType from './utils/pageEndpointFromType';
import { getPageStatus } from './utils/pageStatus';

export const metadataUpdateHttpEffect = createHttpEffect(
	'/api/now/uxframework/:pageEndpoint/:pageSysId',
	{
		method: 'PATCH',
		pathParams: ['pageEndpoint', 'pageSysId'],
		queryParams: ['sysparm_transaction_scope'],
		dataParam: 'data',
		startActionType: METADATA_UPDATE_STARTED,
		successActionType: METADATA_UPDATE_SUCCEEDED,
		errorActionType: METADATA_UPDATE_FAILED,
		batch: false
	}
);

const metadataUpdateRequested = ({ action, dispatch, properties }) => {
	const { pageToUpdate } = action.payload;
	const {
		pageState: {
			rootElement: { id: macroponentId }
		}
	} = properties;

	const { type, sysId, scope } = pageToUpdate
		? pageToUpdate
		: properties.pageState;

	const options = {
		pageEndpoint: pageEndpointFromType(`${type}`, true),
		pageSysId: sysId,
		sysparm_transaction_scope: scope,
		data: {
			...getMetadataUpdatePayload(action.payload)
		}
	};

	if (type === 'landing') {
		options.data.macroponentId = macroponentId;
	}

	dispatch(METADATA_UPDATE_HTTP_REQUESTED, options, {
		updatedPageSysId: sysId
	});
};

const metadataUpdateStarted = ({ updateProperties }) => {
	updateProperties({ isSavingPage: true });
};

const metadataUpdateSucceeded = ({ action, updateProperties, properties }) => {
	// In the case where the updated page is not the current page,
	// The page object should be correctly updated and pageState should not be updated
	const {
		meta: { updatedPageSysId }
	} = action;

	if (updatedPageSysId === properties.pageState.sysId) {
		const pageState = transformMetadataToPageState(
			action.payload.result,
			properties.pageState
		);

		// update any changed meta in pristine; preserve differences elsewhere
		const pageStatePristine = transformMetadataToPageState(
			action.payload.result,
			properties.pageStatePristine
		);

		const pages = properties.pages.map((page) => {
			if (page.sysId !== pageState.sysId) {
				return page;
			}

			return {
				...page,
				name: pageState.name,
				pageStatus: pageState.pageStatus
			};
		});

		updateProperties({
			pageState,
			pageStatePristine,
			pages,
			isSavingPage: false
		});
	} else {
		const {
			name,
			sysPolicy,
			isActive,
			order,
			screenCondition
		} = action.payload.result;
		const pages = properties.pages.map((page) => {
			if (page.sysId !== updatedPageSysId) {
				return page;
			}

			return {
				...page,
				name,
				sysPolicy,
				pageStatus: getPageStatus(isActive),
				order,
				screenCondition
			};
		});

		updateProperties({
			pages,
			isSavingPage: false
		});
	}
};

const metadataUpdateFailed = ({ updateProperties }) => {
	updateProperties({ isSavingPage: false });
};

export default {
	[METADATA_UPDATE_REQUESTED]: metadataUpdateRequested,
	[METADATA_UPDATE_HTTP_REQUESTED]: metadataUpdateHttpEffect,
	[METADATA_UPDATE_STARTED]: metadataUpdateStarted,
	[METADATA_UPDATE_FAILED]: metadataUpdateFailed,
	[METADATA_UPDATE_SUCCEEDED]: metadataUpdateSucceeded
};
