import { Property } from '../../models';
import isNull from 'lodash/isNull';
import isNil from 'lodash/isNil';

export const defaultEmptyStateBehavior = {
	condition_string: '',
	field_list: '',
	table_name: '',
	boolean: false,
	choice: null,
	field: '',
	html: '',
	json: null,
	number: null,
	reference: '',
	string: '',
	icon: ''
};

//convert UXF constants to JS types
const emptyStateBehaviorMap = {
	NULL: null,
	UNDEFINED: undefined,
	EMPTY_STRING: '',
	EMPTY_OBJ: {},
	EMPTY_ARRAY: []
};

/**
 * @typedef EmptyStateBehavior
 * @property {*} value
 */
/**
 * converts UXF constants to JS types if any are supplied, otherwise uses defaults for empty state behavior
 * @param {string} emptyStateBehavior
 * @param {string} type
 * @return {EmptyStateBehavior}
 */
const getEmptyStateBehavior = (emptyStateBehavior, type) => {
	const emptyStateValue =
		!isNil(emptyStateBehavior) &&
		emptyStateBehaviorMap.hasOwnProperty(emptyStateBehavior)
			? emptyStateBehaviorMap[emptyStateBehavior]
			: defaultEmptyStateBehavior[type];

	return { value: emptyStateValue };
};

export function getProperty({
	label,
	fieldType: type,
	defaultValue,
	name: columnName,
	readOnly,
	order,
	typeMetadata = {},
	interfaceApiName,
	selectable = false,
	description = ''
}) {
	const typeMetadataObj =
		isNull(typeMetadata) || typeof typeMetadata !== 'object'
			? {}
			: typeMetadata;

	const {
		choices = [],
		reference = null,
		referenceFilter = null,
		table = null,
		emptyStateBehavior = null,
		variant = null,
		translatable = false,
		schema
	} = typeMetadataObj;

	// handle the casting of string true/false to boolean true/false
	const processedDefaultValue =
		type === 'True/False' ? defaultValue === 'true' : defaultValue;

	return new Property({
		name: columnName,
		choices,
		columnName,
		label,
		table,
		type,
		value: processedDefaultValue,
		reference,
		referenceFilter,
		readOnly,
		order,
		interfaceApiName,
		emptyStateBehavior: getEmptyStateBehavior(emptyStateBehavior, type),
		selectable,
		description,
		defaultValue,
		variant,
		translatable,
		schema
	});
}
