export const isCustomModalComponent = (component) =>
	component.tag === 'now-uxf-page-container';

export const isViewportModalComponent = (component) => {
	return component.tag === 'now-uxf-modal-viewport';
};

const allowedModalSizes = ['sm', 'md', 'lg', 'fullscreen'];
export const getModalSize = (size) =>
	allowedModalSizes.includes(size) ? size : 'md';
