import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	PAGE_CREATE_REQUESTED,
	PAGE_CREATE_HTTP_REQUESTED,
	PAGE_CREATE_STARTED,
	PAGE_CREATE_FAILED,
	PAGE_CREATE_SUCCEEDED,
	PAGE_DATA_RECEIVED
} from './pageActions';

import {
	setupDefaultProperties,
	createLandingPage,
	createScreen,
	pageCreateStarted,
	pageCreateSucceeded,
	pageCreateFailed
} from './pageCreatedUtils';

import { COMPONENT_LIST_REQUESTED } from '../componentList/componentListActions';
import effectProxy from '../helpers/effectProxy';

const pageCreateRequested = ({ action, dispatch, properties }) => {
	const {
		applicability: { screenTypeId }
	} = action.payload;

	const { pageType } = properties;

	const { screenTypes } = properties;
	const { defaultProperties, defaultPropertyValues } = setupDefaultProperties(
		screenTypes,
		screenTypeId
	);

	switch (pageType) {
		case 'landing-page':
			createLandingPage(
				action.payload,
				[],
				{},
				defaultProperties,
				defaultPropertyValues,
				dispatch,
				PAGE_CREATE_HTTP_REQUESTED
			);
			break;
		default:
			createScreen(
				action.payload,
				[],
				{},
				defaultProperties,
				defaultPropertyValues,
				dispatch,
				PAGE_CREATE_HTTP_REQUESTED
			);
	}

	dispatch(COMPONENT_LIST_REQUESTED, { pageType });
	dispatch(PAGE_DATA_RECEIVED);
};

export const pageCreateHttpEffect = createHttpEffect(
	'/api/now/uxframework/:pageEndpoint',
	{
		method: 'POST',
		pathParams: ['pageEndpoint'],
		queryParams: ['sysparm_transaction_scope'],
		dataParam: 'data',
		startActionType: PAGE_CREATE_STARTED,
		successActionType: PAGE_CREATE_SUCCEEDED,
		errorActionType: PAGE_CREATE_FAILED,
		batch: false
	}
);

export default {
	[PAGE_CREATE_REQUESTED]: pageCreateRequested,
	[PAGE_CREATE_HTTP_REQUESTED]: effectProxy(pageCreateHttpEffect),
	[PAGE_CREATE_STARTED]: pageCreateStarted,
	[PAGE_CREATE_FAILED]: pageCreateFailed,
	[PAGE_CREATE_SUCCEEDED]: pageCreateSucceeded
};
