import Model from './model';
import Slot from './slot';
import Property from './property';
import Layout from './layout/layout';
import Component from './component';
import EventMapping from './eventMapping';
import seismicifyIds from './utils/seismicifyIds';
import { removePrefixes } from '../factories/util';

export default class Element extends Model {
	constructor({
		id = '', // string
		instanceName = '', // string
		slot = null, // Slot
		layout = null, // object
		component = null, // Component,
		properties = {}, // object,
		macroponentProperties = [],
		propertyValues = {},
		propertyTestValues = {},
		eventMappings = [], // Array<EventMapping>
		children = [], // Array<Element>
		listenTo = [],
		extendsSysId = null, // string
		error = null,
		styles = {},
		isModal = false,
		isModalDescendant = false,
		internalEventMappings = {},
		isHidden = '', // string
		scope = null,
		scopeName = null,
		sysPolicy = null
	} = {}) {
		super();
		this.id = id;
		this.instanceName = instanceName || this._getDefaultInstanceName(component);
		this.component = this._setNewComponent(component);
		this.properties = this._setNewProperties(properties);
		this.macroponentProperties = macroponentProperties;
		this.propertyValues = propertyValues;
		this.propertyTestValues = propertyTestValues;
		this.eventMappings = this._setNewEventMappings(eventMappings);
		this.children = this._setNewChildren(children);
		this.slot = this._setNewSlot(slot);
		this.layout = this._setNewLayout(layout);
		this.listenTo = listenTo;
		this.extendsSysId = extendsSysId;
		this.styles = styles;
		this.error = error;
		this.isModal = isModal;
		this.isModalDescendant = isModalDescendant;
		this.internalEventMappings = internalEventMappings;
		this.isHidden = isHidden;
		this.scope = scope;
		this.scopeName = scopeName;
		this.sysPolicy = sysPolicy;
	}

	_getDefaultInstanceName(component) {
		if (!component || typeof component !== 'object') {
			return '';
		}
		return component.name || '';
	}

	_setNewProperties(properties) {
		if (!properties) {
			return null;
		} else if (
			Object.entries(properties).every((entry) => entry[1] instanceof Property)
		) {
			return properties;
		}
		return Object.entries(properties).reduce((acc, [name, property]) => {
			acc[name] = new Property(property);
			return acc;
		}, {});
	}

	_setNewEventMappings(eventMappings) {
		if (!Array.isArray(eventMappings)) {
			return [];
		} else if (
			eventMappings.every((mapping) => mapping instanceof EventMapping)
		) {
			return eventMappings;
		}
		return eventMappings.map((mapping) => new EventMapping(mapping));
	}

	_setNewChildren(children) {
		if (!Array.isArray(children)) {
			return [];
		} else if (children.every((child) => child instanceof Element)) {
			return children;
		}
		return children.map((child) => new Element(child));
	}

	_setNewSlot(slot) {
		if (slot instanceof Slot) {
			return slot;
		} else if (slot !== null) {
			return new Slot(slot);
		}
		return null;
	}

	_setNewLayout(layout) {
		if (layout instanceof Layout) {
			return layout;
		} else if (layout !== null) {
			return new Layout(layout);
		}
		return null;
	}

	_setNewComponent(component) {
		if (component instanceof Component) {
			return component;
		} else if (component !== null) {
			return new Component(component);
		}
		return null;
	}

	_highestMatchingIdIndex(component, allIds) {
		const targetName = seismicifyIds(component.name);
		let highest = 0;
		allIds.forEach((id) => {
			const matcher = id.match(new RegExp(`^${targetName}\\_(\\d+)*$`));
			if (matcher) {
				const index = Number(matcher[1]);
				highest = index > highest ? index : highest;
			}
		});
		return highest;
	}

	initialize(allIds) {
		const highestMatchingInstanceIndex = this._highestMatchingIdIndex(
			this.component,
			allIds
		);
		// Remove translation prefixes from the definition name.
		const rootName = removePrefixes(this.component.name || '') || '';
		// Lower case the ID and remove characters other than alphanumeric, -, and _ to make it seismic-safe
		const targetName = seismicifyIds(rootName);
		if (this.component.category !== 'viewport') {
			this.id =
				highestMatchingInstanceIndex > 0
					? `${targetName}_${(highestMatchingInstanceIndex + 1).toString()}`
					: `${targetName}_1`;
		}
		this.instanceName =
			highestMatchingInstanceIndex > 0
				? `${rootName} ${(highestMatchingInstanceIndex + 1).toString()}`
				: `${rootName} 1`;
	}
}
