import { ELEMENT_STYLES_UPDATED } from './elementActions';
import { Element } from '../../models';
import { findElementById } from './utils/element';
import createPageState from '../../models/createPageState';
import cloneDeep from 'lodash/cloneDeep';

const elementStylesUpdated = ({ action, properties, updateProperties }) => {
	const { pageState } = properties;
	const { elementId, styles } = action.payload;
	const newRootElement = cloneDeep(pageState.rootElement);
	const element = findElementById(newRootElement, elementId);
	element.styles = styles;
	updateProperties({
		pageState: createPageState(
			{
				rootElement: new Element(newRootElement)
			},
			pageState
		)
	});
};

export default {
	[ELEMENT_STYLES_UPDATED]: elementStylesUpdated
};
