import get from 'lodash/get';
import { create as createMcpProperty } from '../../models/macroponentProperty';

export const generateUpdatedMacroponentProperties = (
	screen,
	macroponentProperties = []
) => {
	const updatedMcpDefinitions = get(
		screen,
		'macroponentInstance.macroponentDefinitions',
		{}
	);
	const rootMcpId = get(screen, 'macroponentInstance.rootMacroponentId', null);
	const rootMcpHydratedPropertyValues =
		get(screen, 'macroponentInstance.propertyValues', {}) || {};
	const rootMcpHydratedPropertyTestValues =
		get(screen, 'macroponentInstance.propertyTestValues', {}) || {};
	const rootMcpDef = updatedMcpDefinitions[rootMcpId];
	const newProperties =
		rootMcpDef.properties
			.filter(
				({ label }) =>
					!macroponentProperties.find(
						({ label: existingLabel }) => label === existingLabel
					)
			)
			.map((prop) => {
				const newProp = createMcpProperty(prop);
				newProp.value =
					rootMcpHydratedPropertyValues[prop.name] ||
					rootMcpHydratedPropertyTestValues[prop.name] ||
					'';
				return newProp;
			}) || [];
	return [...newProperties, ...macroponentProperties];
};
