import getLayoutType from './utils/getLayoutType';
import {
	COMPONENT_PLACED,
	COMPONENT_PLACED_SUCCEEDED,
	ELEMENT_DELETED,
	ELEMENT_DETACHED,
	DETACHED_ELEMENT_RELOCATED,
	DETACHED_ELEMENT_RESTORED,
	ELEMENT_IS_HIDDEN_TEST_VALUE_CHANGED,
	COMPONENT_EDIT_REQUESTED
} from './elementActions';
import { ELEMENT_COMPLETE_EVENT_MAPPING_CREATED } from '../elementEventMappings/elementEventMappingsActions';
import { throwError } from '../../utils';
import { getElementTree, getHiddenElements } from '../utils';
import { ELEMENT_INSTANCE_NAME, IS_HIDDEN } from './reservedPropertyIds';
import get from 'lodash/get';

import { findParentElement } from './utils/element';
import createElementArray from './utils/createElementArray';

import {
	componentPlaced,
	elementDetached,
	detachedElementRelocated,
	detachedElementRestored,
	elementDeleted
} from './rowColumnHandlers';
import {
	componentPlaced as defaultComponentPlaced,
	elementDetached as defaultElementDetached,
	detachedElementRelocated as defaultDetachedElementRelocated,
	detachedElementRestored as defaultDetachedElementRestored,
	elementDeleted as defaultElementDeleted,
	componentCreatedInSlot,
	componentMovedToSlot
} from './defaultLayoutElementHandlers';

const elementCreatedLookup = {
	rowColumn: componentPlaced,
	fallback: defaultComponentPlaced
};

const elementDetachedLookup = {
	rowColumn: elementDetached,
	fallback: defaultElementDetached
};

const elementRelocatedLookup = {
	rowColumn: detachedElementRelocated,
	fallback: defaultDetachedElementRelocated
};

const elementDeletedLookup = {
	rowColumn: elementDeleted,
	fallback: defaultElementDeleted
};

const elementRestoredLookup = {
	rowColumn: detachedElementRestored,
	fallback: defaultDetachedElementRestored
};
const createdInSlot = (coeffects) => {
	return !!get(coeffects, 'action.payload.elementId', false);
};
const elementCreatedHandler = (coeffects) => {
	const {
		pageState: { rootElement }
	} = coeffects.properties;

	const { parentId, isLegacy = false } = coeffects.action.payload;
	const type = isLegacy
		? 'rowColumn'
		: getLayoutType(rootElement, parentId, 'default');
	const actionFn = get(
		elementCreatedLookup,
		type,
		elementCreatedLookup.fallback
	);
	if (createdInSlot(coeffects)) {
		return componentCreatedInSlot(coeffects);
	}

	actionFn(coeffects);
};

const elementDetachedHandler = (coeffects) => {
	const {
		pageState: { rootElement }
	} = coeffects.properties;
	const { elementId } = coeffects.action.payload;
	const parentElement = findParentElement(rootElement, elementId);

	if (!parentElement) {
		throwError('Cannot detach root element');
	}

	const type = getLayoutType(rootElement, parentElement.id, 'default');
	const actionFn = get(
		elementDetachedLookup,
		type,
		elementDetachedLookup.fallback
	);
	actionFn(coeffects);
};

const elementRelocatedHandler = (coeffects) => {
	const {
		pageState: { rootElement }
	} = coeffects.properties;
	const { parentId } = coeffects.action.payload;
	const type = getLayoutType(rootElement, parentId, 'default');

	if (createdInSlot(coeffects)) {
		return componentMovedToSlot(coeffects);
	}
	const actionFn = get(
		elementRelocatedLookup,
		type,
		elementRelocatedLookup.fallback
	);
	actionFn(coeffects);
};

const elementRestoredHandler = (coeffects) => {
	const { state, properties, updateState } = coeffects;
	const {
		pageState: { rootElement }
	} = properties;
	const {
		detachedElementData: { detachedElements, previousLayout }
	} = state;

	if (!previousLayout || !detachedElements || detachedElements.length < 1) {
		updateState({
			path: 'detachedElementData',
			operation: 'set',
			value: null
		});

		return;
	}

	const parentElement = findParentElement(rootElement, detachedElements[0].id);

	const type = getLayoutType(rootElement, parentElement.id, 'default');
	const actionFn = get(
		elementRestoredLookup,
		type,
		elementRestoredLookup.fallback
	);
	actionFn(coeffects);
};

const elementDeleteConfirmedHandler = (coeffects) => {
	const {
		pageState: { rootElement }
	} = coeffects.properties;
	const { elementId, deletedElementId } = coeffects.action.payload;
	const parentElement = findParentElement(
		rootElement,
		elementId || deletedElementId
	);
	const type = getLayoutType(rootElement, parentElement.id, 'default');
	const actionFn = get(
		elementDeletedLookup,
		type,
		elementDeletedLookup.fallback
	);
	const { action } = coeffects;
	actionFn({
		...coeffects,
		action: {
			...action,
			payload: {
				elementId: elementId || deletedElementId
			}
		}
	});
};

const elementCreatedSucceededHandler = ({ state, action, dispatch }) => {
	const { elementId, componentId } = action.payload;
	const {
		rawComponentList,
		properties: { newElementDefaultEventHandlers }
	} = state;
	const component = rawComponentList.find(
		(component) => component.id === componentId
	);
	const defaultEventHandler =
		component && newElementDefaultEventHandlers[component.tag];

	if (defaultEventHandler) {
		const eventHandler = component.dispatchedEvents.find(({ eventName }) => {
			return eventName === defaultEventHandler.eventName;
		});

		if (eventHandler) {
			dispatch(ELEMENT_COMPLETE_EVENT_MAPPING_CREATED, {
				elementId,
				eventId: eventHandler.id,
				eventApiName: eventHandler.apiName,
				eventMappingTarget: defaultEventHandler.eventMappingTarget
			});
		}
	}
	dispatch('UIB_APP#EMPTY_SLOT_OR_ELEMENT_SELECTED', {
		type: 'element',
		elementId,
		component
	});
};

const elementIsHiddenTestValueChanged = ({
	action,
	properties,
	updateProperties
}) => {
	let newHiddenElements;
	if (action.payload.value === 'true' || action.payload.value === 'false') {
		newHiddenElements = {
			...properties.hiddenElements,
			[action.payload.elementId]: action.payload.value === 'true'
		};
	} else {
		const elementArr = createElementArray(
			properties.pageState.rootElement,
			(el) => ({
				id: el.id,
				isHidden: el.isHidden
			})
		);
		newHiddenElements = getHiddenElements(
			elementArr,
			properties.clientScriptApi
		);
	}

	const newElementTree = getElementTree(
		properties.pageState.rootElement,
		newHiddenElements
	);

	updateProperties({
		hiddenElements: newHiddenElements,
		elementTree: newElementTree
	});
};

const componentEditRequested = ({ action, properties }) => {
	const { clientScriptApi, elementProperties } = properties;
	const targetProperties = get(elementProperties, action.payload.elementId, []);
	const propertiesToCache = targetProperties.reduce((acc, { id, value }) => {
		let parsedValue = value;
		try {
			if (typeof value === 'string') {
				const checkValue = JSON.parse(value);
				typeof checkValue === 'object' && (parsedValue = checkValue);
			}
		} catch (_) {
			// do nothing
		}
		if (id === ELEMENT_INSTANCE_NAME || id === IS_HIDDEN) {
			return acc;
		}

		const valueToUse =
			typeof parsedValue === 'string' && value.startsWith('@')
				? get(clientScriptApi, value.slice(1), null)
				: parsedValue;

		return {
			...acc,
			[id]: valueToUse
		};
	}, {});

	try {
		sessionStorage.setItem('cbProps', JSON.stringify(propertiesToCache));
	} catch (_) {
		// do nothing
	}
};

export default {
	[COMPONENT_PLACED]: elementCreatedHandler,
	[COMPONENT_PLACED_SUCCEEDED]: elementCreatedSucceededHandler,
	[ELEMENT_DELETED]: elementDeleteConfirmedHandler,
	[ELEMENT_DETACHED]: elementDetachedHandler,
	[DETACHED_ELEMENT_RELOCATED]: elementRelocatedHandler,
	[DETACHED_ELEMENT_RESTORED]: elementRestoredHandler,
	[ELEMENT_IS_HIDDEN_TEST_VALUE_CHANGED]: elementIsHiddenTestValueChanged,
	[COMPONENT_EDIT_REQUESTED]: componentEditRequested
};
