import cloneDeep from 'lodash/cloneDeep';
import get from 'lodash/get';
import {
	ELEMENT_EXPLODED,
	ELEMENT_EXPLODED_MACROPONENT_SUCCEEDED,
	ELEMENT_EXPLODED_MACROPONENT_REQUESTED,
	ELEMENT_EXPLODED_SUBROUTE_COPY_REQUESTED,
	ELEMENT_EXPLODED_SUBROUTE_COPY_SUCCEEDED,
	ELEMENT_EXPLODED_UPDATE_PAGE_WITH_MACROPONENT_CONTENTS
} from './elementActions';

import { PAGE_LIST_REQUESTED } from '../pageList/pageListActions';

import { DATA_BROKER_INSTANCE_CREATED } from '../dataBrokers/dataBrokerInstances/dataBrokerInstancesActions';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	findElementById,
	findElements,
	getAllElementIds,
	findParentElement
} from './utils/element';
import { createDataBrokerInstances } from '../utils';
import {
	createElementFromMacroponent,
	transformSubroutesToViewportConfig
} from '../page/utils/transformMacroponentToPageState';
import { Element } from '../../models';
import createPageState from '../../models/createPageState';
import { SN_UIB_APP_UPDATE_APP_ROUTE_STATE } from '../../constants';

const explodedMacroponentRequested = createHttpEffect(
	'/api/now/uxframework/macroponents/:macroponentId',
	{
		method: 'GET',
		pathParams: ['macroponentId'],
		successActionType: ELEMENT_EXPLODED_MACROPONENT_SUCCEEDED,
		batch: false
	}
);

const subroutesCopyRequested = createHttpEffect(
	'/api/now/builder/subroutes/copy/to/variants/:variantSysId',
	{
		method: 'POST',
		dataParam: 'subroutes',
		pathParams: ['variantSysId'],
		successActionType: ELEMENT_EXPLODED_SUBROUTE_COPY_SUCCEEDED,
		batch: false
	}
);

const elementExplodedHandler = (coeffects) => {
	const {
		dispatch,
		action: {
			payload: { elementId }
		},
		properties: { pageState }
	} = coeffects;

	const explodedElement = findElementById(pageState.rootElement, elementId);

	// TODO: Right now, we invoke the explosion as soon as we have the macroponent definition.
	// Instead, we want to show a confirmation modal that shows the user what they will
	// be adding to the page if they proceed.
	dispatch(
		ELEMENT_EXPLODED_MACROPONENT_REQUESTED,
		{
			macroponentId: explodedElement.component.id
		},
		{
			explodedElementId: elementId
		}
	);
};

const updateBinding = (
	propertyValue,
	originalContextProperties,
	entityIdMap
) => {
	// TODO: This would be improved by treating these as UxValues instead of doing string
	// wizardry

	if (typeof propertyValue !== 'string') {
		return propertyValue;
	}

	if (propertyValue.startsWith('@context.props')) {
		const propBindingName = propertyValue.substring('@context.props.'.length);
		return originalContextProperties[propBindingName || null];
	} else if (propertyValue.startsWith('@data')) {
		const truncated = propertyValue.substring('@data.'.length);

		const dbInstanceId = truncated.substr(0, truncated.indexOf('.'));
		const remainder = truncated.substr(truncated.indexOf('.'));
		return `@data.${entityIdMap.data[dbInstanceId]}${remainder}`;
	}

	return propertyValue;
};

const createDataBokerInstances = (arg) => {
	const {
		data,
		dataBrokerDefinitions,
		originalContextProperties,
		entityIdMap,
		dispatch
	} = arg;

	const dataBrokerIdMap = {};
	const newDataBrokers = createDataBrokerInstances(data, dataBrokerDefinitions);

	newDataBrokers.forEach((dbInstance) => {
		const newDbInstanceId = entityIdMap.data[dbInstance.id];
		dataBrokerIdMap[dbInstance.id] = dbInstance.id = newDbInstanceId;

		Object.keys(dbInstance.properties).forEach((propertyName) => {
			const property = dbInstance.properties[propertyName];
			if (property.value) {
				property.value = updateBinding(
					property.value,
					originalContextProperties,
					entityIdMap
				);
			}
		});

		dispatch(DATA_BROKER_INSTANCE_CREATED, {
			dataBrokerInstance: dbInstance
		});
	});
};

const generateIdMap = (pageState, newExplodedElement, data) => {
	const idMap = {
		data: {},
		elements: {},
		state: {}
	};

	const uniqueIdMapGenerator = (originalIds, currentIds, prefix) => {
		return originalIds.reduce((acc, cur) => {
			let newId = `${prefix}_${cur}`;

			if (currentIds.includes(newId)) {
				let counter = 1;
				while (currentIds.includes(`${newId}_${counter}`)) {
					counter += 1;
				}
				newId += `_${counter}`;
			}

			currentIds.push(newId);
			acc[cur] = newId;
			return acc;
		}, {});
	};

	// Elements
	const currentElementIds = getAllElementIds(pageState.rootElement);
	const explodedElementIds = findElements(newExplodedElement, () => true)
		.map((element) => element.id)
		.filter((id) => id !== newExplodedElement.id);

	idMap.elements = uniqueIdMapGenerator(
		explodedElementIds,
		currentElementIds,
		newExplodedElement.id
	);
	idMap.elements[newExplodedElement.id] = newExplodedElement.id;

	// Data broker instances
	const currentDataBrokerInstanceIds = pageState.dataBrokerInstances.map(
		(instance) => instance.id
	);
	const explodedDataBrokerIds = data.map((instance) => instance.elementId);
	idMap.data = uniqueIdMapGenerator(
		explodedDataBrokerIds,
		currentDataBrokerInstanceIds,
		newExplodedElement.id
	);

	// TODO: State

	return idMap;
};

const subroutesCopySucceeded = ({ state, action, dispatch }) => {
	const {
		entityIdMap,
		originalContextProperties,
		macroponent,
		explodedElementId,
		componentDependencies,
		macroponentDependencies
	} = action.meta;

	const { result } = action.payload;

	const newExplodedElement = createElementFromMacroponent(
		macroponent,
		{},
		{},
		componentDependencies,
		macroponentDependencies,
		[],
		null,
		transformSubroutesToViewportConfig(result, entityIdMap.elements),
		state
	);

	// Look for empty slot references on children of container.
	// If empty, default it
	newExplodedElement.children.forEach((child) => {
		if (!child.slot.name) {
			child.slot.name = 'Main';
		}
	});

	newExplodedElement.id = explodedElementId;
	newExplodedElement.component.id = macroponent.extendsSysId;

	dispatch(ELEMENT_EXPLODED_UPDATE_PAGE_WITH_MACROPONENT_CONTENTS, {
		newExplodedElement,
		entityIdMap,
		originalContextProperties,
		macroponent,
		explodedElementId
	});
};

const explodedMacroponentRequestSucceeded = (coeffects) => {
	const {
		action: {
			meta: { explodedElementId },
			payload: {
				result: {
					macroponent,
					macroponentDependencies,
					componentDependencies,
					subroutes
				}
			}
		},
		properties: { pageState },
		state,
		dispatch
	} = coeffects;

	const originalExplodedElement = findElementById(
		pageState.rootElement,
		explodedElementId
	);

	const originalContextProperties = Object.keys(
		originalExplodedElement.properties
	).reduce((acc, cur) => {
		acc[cur] = originalExplodedElement.properties[cur].value;
		return acc;
	}, {});

	const newExplodedElement = createElementFromMacroponent(
		macroponent,
		{},
		{},
		componentDependencies,
		macroponentDependencies,
		[],
		null,
		{},
		state
	);
	newExplodedElement.id = explodedElementId;
	newExplodedElement.component.id = macroponent.extendsSysId;

	// Look for empty slot references on children of container.
	// If empty, default it
	newExplodedElement.children.forEach((child) => {
		if (!child.slot.name) {
			child.slot.name = 'Main';
		}
	});

	// First thing we do is figure out what all the new ID names are going to be for our
	// migrated content, in order to prevent collisions with things that already exist.
	// We need this first, so we can update references to these things as we migrate all our
	// different types of content.
	const entityIdMap = generateIdMap(
		pageState,
		newExplodedElement,
		macroponent.data
	);

	if (subroutes && subroutes.length > 0) {
		return dispatch(
			ELEMENT_EXPLODED_SUBROUTE_COPY_REQUESTED,
			{
				subroutes: subroutes.map((subroute) => {
					const currentParentCompositionElementId =
						subroute.parentCompositionElementId;
					return {
						...subroute,
						parentCompositionElementId:
							entityIdMap.elements[currentParentCompositionElementId]
					};
				}),
				variantSysId: pageState.sysId
			},
			{
				macroponent,
				macroponentDependencies,
				componentDependencies,
				explodedElementId,
				newExplodedElement,
				entityIdMap,
				originalContextProperties
			}
		);
	}

	dispatch(ELEMENT_EXPLODED_UPDATE_PAGE_WITH_MACROPONENT_CONTENTS, {
		newExplodedElement,
		entityIdMap,
		originalContextProperties,
		macroponent,
		explodedElementId
	});
};

const updatePageStateWithMacroponentContents = ({
	action,
	updateProperties,
	properties,
	dispatch
}) => {
	const {
		newExplodedElement,
		entityIdMap,
		originalContextProperties,
		macroponent,
		explodedElementId
	} = action.payload;
	const { pageState, dataBrokerDefinitions } = properties;

	// Create all our fresh new data brokers with the help of our DB creation event handling.
	createDataBokerInstances({
		data: macroponent.data || [],
		dataBrokerDefinitions,
		entityIdMap,
		originalContextProperties,
		dispatch
	});

	// Update the IDs of our new elements, and update their properties if they need to be updated
	// to reference new values.
	findElements(newExplodedElement, () => true).forEach((element) => {
		element.id = entityIdMap.elements[element.id];

		// Apply the old property values to any elements that bind to @context.props
		Object.keys(element.properties).forEach((propertyName) => {
			const property = element.properties[propertyName];
			if (property.value) {
				property.value = updateBinding(
					property.value,
					originalContextProperties,
					entityIdMap
				);
			}
		});
	});

	// Finally, update page state with our newly generated element, and replace the old element
	const newRootElement = cloneDeep(pageState.rootElement);
	const parentElement = findParentElement(newRootElement, explodedElementId);
	const elementIndex = parentElement.children.findIndex(
		(elem) => elem.id === explodedElementId
	);
	newExplodedElement.slot = parentElement.children[elementIndex].slot;
	newExplodedElement.instanceName =
		parentElement.children[elementIndex].instanceName;
	parentElement.children[elementIndex] = newExplodedElement;

	const newPageState = createPageState(
		{
			rootElement: new Element(newRootElement)
		},
		pageState
	);

	updateProperties({
		pageState: newPageState
	});

	const appConfig = get(pageState, 'uiTypeParameters.appConfig', '');
	const keyToUse = appConfig ? 'appConfigId' : 'macroponentSysId';
	const sysIdToUse =
		appConfig || get(pageState, 'uiTypeParameters.parentMacroponent', null);
	dispatch(
		PAGE_LIST_REQUESTED,
		{ [keyToUse]: sysIdToUse },
		{ isExplosion: true }
	);
	dispatch(SN_UIB_APP_UPDATE_APP_ROUTE_STATE, { sysId: sysIdToUse });
};

export default {
	[ELEMENT_EXPLODED]: elementExplodedHandler,
	[ELEMENT_EXPLODED_MACROPONENT_REQUESTED]: explodedMacroponentRequested,
	[ELEMENT_EXPLODED_MACROPONENT_SUCCEEDED]: explodedMacroponentRequestSucceeded,
	[ELEMENT_EXPLODED_SUBROUTE_COPY_REQUESTED]: subroutesCopyRequested,
	[ELEMENT_EXPLODED_SUBROUTE_COPY_SUCCEEDED]: subroutesCopySucceeded,
	[ELEMENT_EXPLODED_UPDATE_PAGE_WITH_MACROPONENT_CONTENTS]: updatePageStateWithMacroponentContents
};
