import cloneDeep from 'lodash/cloneDeep';
import reject from 'lodash/reject';
import isEmpty from 'lodash/isEmpty';
import omit from 'lodash/omit';
import { Element, Property } from '../../models';
import createPageState from '../../models/createPageState';
import * as MacroponentProperty from '../../models/macroponentProperty';
import {
	PAGE_PROPERTIES_CHANGED,
	PAGE_PROPERTIES_CREATED,
	PAGE_PROPERTIES_DELETED
} from './pagePropertiesActions';

function getPageState(newRootElement, pageState) {
	return createPageState(
		{
			rootElement: new Element(newRootElement)
		},
		pageState
	);
}

function resolveValue(value, testValue, fieldType) {
	const valueToUse = testValue || value;
	switch (fieldType) {
		case 'boolean':
			return valueToUse || false;
		case 'number':
			return Number(valueToUse) || 0;
		case 'json':
			try {
				return JSON.parse(valueToUse);
			} catch (_) {
				return valueToUse;
			}
		default:
			return valueToUse;
	}
}

const pagePropertiesChanged = ({ action, properties, updateProperties }) => {
	const { pageState } = properties;
	const newRootElement = cloneDeep(pageState.rootElement);
	const {
		macroponentProperties,
		properties: elementProperties
	} = newRootElement;
	const {
		pagePropertyId: id,
		value,
		testValue,
		name,
		fieldType,
		description,
		translatable
	} = action.payload;
	const propertyToUpdate = macroponentProperties.find(
		(pageProp) => pageProp.id === id
	);

	if (propertyToUpdate === undefined) {
		return;
	}

	const propertyToUpdateTypeMetadata = propertyToUpdate.typeMetadata || {};
	propertyToUpdate.name = name;
	// TODO: keep "label" in sync with name, for now; could become human-readable string
	propertyToUpdate.label = name;
	propertyToUpdate.value = resolveValue(value, null, fieldType);
	propertyToUpdate.testValue = resolveValue(null, testValue, fieldType);
	propertyToUpdate.fieldType = fieldType;
	propertyToUpdate.description = description || '';
	if (translatable) {
		propertyToUpdate.typeMetadata = {
			...propertyToUpdateTypeMetadata,
			translatable
		};
	} else if (translatable === false && !isEmpty(propertyToUpdateTypeMetadata)) {
		propertyToUpdate.typeMetadata = omit(
			propertyToUpdateTypeMetadata,
			'translatable'
		);
	}

	if (elementProperties[name] === undefined) {
		elementProperties[name] = new Property({ name });
	}
	elementProperties[name].value = resolveValue(value, testValue, fieldType);
	elementProperties[name].type = fieldType;

	updateProperties({
		pageState: getPageState(newRootElement, pageState)
	});
};

const pagePropertiesCreated = ({ action, properties, updateProperties }) => {
	const { payload = {} } = action;
	const { name, fieldType, description, translatable } = payload;
	const { pageState } = properties;
	const newRootElement = cloneDeep(pageState.rootElement);

	const typeMetadata = translatable ? { translatable } : undefined;

	const newMacroProp = MacroponentProperty.create({
		name,
		fieldType,
		description: description || '',
		typeMetadata
	});
	newRootElement.macroponentProperties.unshift(newMacroProp);
	newRootElement.properties[newMacroProp.name] = new Property({
		name: newMacroProp.name
	});

	updateProperties({
		pageState: getPageState(newRootElement, pageState)
	});
};

const pagePropertiesDeleted = ({ action, properties, updateProperties }) => {
	const { pageState } = properties;
	const newRootElement = cloneDeep(pageState.rootElement);
	const { pagePropertyId: id } = action.payload;

	newRootElement.macroponentProperties = reject(
		newRootElement.macroponentProperties,
		{ id }
	);

	updateProperties({
		pageState: getPageState(newRootElement, pageState)
	});
};

export default {
	[PAGE_PROPERTIES_CHANGED]: pagePropertiesChanged,
	[PAGE_PROPERTIES_CREATED]: pagePropertiesCreated,
	[PAGE_PROPERTIES_DELETED]: pagePropertiesDeleted
};
