import { createHttpEffect } from '@servicenow/ui-effect-http';
import _get from 'lodash/get';
import cloneDeep from 'lodash/cloneDeep';
import ClientScript from '../../models/clientScript';
import EventHandler from '../../models/eventHandler';
import createElementArray from '../element/utils/createElementArray';
import createPageState from '../../models/createPageState';
import createRequiredTranslations from './utils';

import {
	CLIENT_SCRIPTS_SCRIPT_CHANGED,
	CLIENT_SCRIPTS_SCRIPT_CREATED,
	CLIENT_SCRIPTS_SCRIPT_DELETED,
	CLIENT_SCRIPTS_CREATE_REQUESTED,
	CLIENT_SCRIPTS_CREATE_SUCCEEDED,
	CLIENT_SCRIPTS_DELETE_REQUESTED,
	CLIENT_SCRIPTS_DELETE_SUCCEEDED,
	CLIENT_SCRIPTS_UPDATE_REQUESTED,
	CLIENT_SCRIPTS_UPDATE_HTTP_REQUESTED,
	CLIENT_SCRIPTS_UPDATE_SUCCEEDED,
	CLIENT_SCRIPTS_CREATE_STARTED,
	CLIENT_SCRIPTS_DELETE_STARTED
} from './clientScriptsActions';

import { processLinting } from '../../utils';

import { ELEMENT_EVENT_MAPPING_HANDLER_DELETED } from '../elementEventMappings/elementEventMappingsActions';

import { CLIENT_SCRIPT } from '../elementEventMappings/constants';
const DEFAULT_SCRIPT_NAME = 'New client script';
const matchExpr = new RegExp(`^${DEFAULT_SCRIPT_NAME} (\\d+)$`);

const clientScriptsScriptCreated = ({ properties, dispatch }) => {
	const scope = _get(properties, 'pageState.scope', null);

	const newClientScripts = properties.clientScripts.map(
		(script) => new ClientScript(script)
	);
	const defaultScriptNameCount =
		1 +
		newClientScripts.reduce((count, clientScript) => {
			const matches = clientScript.name.match(matchExpr);
			if (matches) {
				return Math.max(parseInt(matches[1], 10), count);
			}
			return count;
		}, 0);
	const name = `${DEFAULT_SCRIPT_NAME} ${defaultScriptNameCount}`;
	const newClientScript = {
		name,
		script: `/**
 * @param {params} params
 * @param {api} params.api
 * @param {any} params.event
 * @param {any} params.imports
 */
function handler({api, event, helpers, imports}) {\n}`,
		scope,
		includes: []
	};

	dispatch(CLIENT_SCRIPTS_CREATE_REQUESTED, {
		screenId: properties.pageState.sysId,
		data: [newClientScript]
	});
};

const clientScriptsScriptDeleted = ({
	action,
	updateProperties,
	properties,
	dispatch
}) => {
	const filteredScripts = [
		...properties.clientScripts.filter((clientScript) => {
			return clientScript.id !== action.payload.id;
		})
	];

	const filteredEventHandlers = [
		...properties.eventHandlers.filter((eventHandler) => {
			return eventHandler.id !== action.payload.id;
		})
	];

	const elementArr = createElementArray(
		properties.pageState.rootElement,
		(element) => ({
			id: element.id,
			eventMappings: element.eventMappings
		})
	);

	elementArr.forEach((element) => {
		element.eventMappings.forEach((eventMapping) => {
			eventMapping.targets.forEach((target) => {
				if (
					target.clientScript &&
					target.clientScript.sysId === action.payload.id
				) {
					dispatch(ELEMENT_EVENT_MAPPING_HANDLER_DELETED, {
						elementId: element.id,
						eventMappingId: eventMapping.eventMappingId,
						handlerId: target.targetId
					});
				}
			});
		});
	});

	const newPageState = createPageState(
		{ eventHandlers: filteredEventHandlers },
		cloneDeep(properties.pageState)
	);

	updateProperties({
		clientScripts: filteredScripts,
		pageState: newPageState
	});

	dispatch(CLIENT_SCRIPTS_DELETE_REQUESTED, { scriptId: action.payload.id });
};

const clientScriptsScriptChanged = ({
	action,
	properties,
	updateProperties
}) => {
	const { code, name, id, includes } = action.payload;

	const newPageState = createPageState(
		{
			eventHandlers: properties.eventHandlers.map((eventHandler) => {
				const returnValue = eventHandler;

				if (eventHandler.id === id) {
					if (typeof name === 'string') {
						returnValue.name = name;
						returnValue.label = name;
					}
					if (Array.isArray(includes)) {
						returnValue.includes = includes;
					}
				}
				return returnValue;
			})
		},
		cloneDeep(properties.pageState)
	);

	updateProperties({
		clientScripts: properties.clientScripts.map((clientScript) => {
			const returnValue = clientScript;

			if (clientScript.id === id) {
				if (typeof name === 'string') {
					returnValue.name = name;
				}
				if (typeof code === 'string') {
					returnValue.script = code;
				}
				if (Array.isArray(includes)) {
					returnValue.includes = includes;
				}
			}
			return returnValue;
		}),
		pageState: newPageState
	});
};

const clientScriptsCreateRequested = createHttpEffect(
	'/api/now/uxframework/client_scripts/:screenId',
	{
		method: 'POST',
		batch: false,
		pathParams: ['screenId'],
		queryParams: ['sysparm_transaction_scope'],
		dataParam: 'data',
		successActionType: CLIENT_SCRIPTS_CREATE_SUCCEEDED,
		startActionType: CLIENT_SCRIPTS_CREATE_STARTED
	}
);
const clientScriptsCreateStarted = ({ updateProperties }) => {
	updateProperties({ isClientScriptsBusy: true });
};

const clientScriptsCreateSucceeded = ({
	properties,
	action: {
		payload: { result }
	},
	updateProperties
}) => {
	const newClientScript = new ClientScript(result[0]);
	const newClientScripts = [newClientScript, ...properties.clientScripts];

	const newEventHandler = new EventHandler({
		id: newClientScript.id,
		label: newClientScript.name,
		type: CLIENT_SCRIPT,
		sourceName: 'Client script',
		sourceId: newClientScript.id,
		name: newClientScript.name,
		payloadFields: []
	});

	updateProperties({
		isClientScriptsBusy: false,
		clientScripts: newClientScripts,
		eventHandlers: [...properties.eventHandlers, newEventHandler]
	});
};

const clientScriptsDeleteRequested = createHttpEffect(
	'/api/now/uxframework/client_scripts/:scriptId',
	{
		method: 'DELETE',
		batch: false,
		pathParams: ['scriptId'],
		successActionType: CLIENT_SCRIPTS_DELETE_SUCCEEDED,
		startActionType: CLIENT_SCRIPTS_DELETE_STARTED
	}
);

const clientScriptsDeleteStarted = ({ updateProperties }) =>
	updateProperties({ isClientScriptsBusy: true });

const clientScriptsDeleteSucceeded = ({ updateProperties }) =>
	updateProperties({ isClientScriptsBusy: false });

const clientScriptsUpdateHttpEffect = createHttpEffect(
	'/api/now/uxframework/client_scripts',
	{
		method: 'PATCH',
		batch: false,
		successActionType: CLIENT_SCRIPTS_UPDATE_SUCCEEDED,
		dataParam: 'data'
	}
);

const clientScriptsUpdateRequested = async ({ properties, dispatch }) => {
	const { clientScripts } = properties;

	const clientScriptsToUpdate = [];
	for (const clientScript of clientScripts) {
		const { script } = clientScript;
		const errors = (await processLinting([script]))[0];
		clientScriptsToUpdate.push({
			...clientScript,
			hasError: errors.length > 0,
			requiredTranslations: createRequiredTranslations(script)
		});
	}

	dispatch(CLIENT_SCRIPTS_UPDATE_HTTP_REQUESTED, {
		data: clientScriptsToUpdate
	});
};

const clientScriptsUpdateSucceeded = () => null;

export default {
	[CLIENT_SCRIPTS_SCRIPT_CHANGED]: clientScriptsScriptChanged,
	[CLIENT_SCRIPTS_SCRIPT_CREATED]: clientScriptsScriptCreated,
	[CLIENT_SCRIPTS_SCRIPT_DELETED]: clientScriptsScriptDeleted,
	[CLIENT_SCRIPTS_CREATE_REQUESTED]: clientScriptsCreateRequested,
	[CLIENT_SCRIPTS_CREATE_STARTED]: clientScriptsCreateStarted,
	[CLIENT_SCRIPTS_CREATE_SUCCEEDED]: clientScriptsCreateSucceeded,
	[CLIENT_SCRIPTS_DELETE_REQUESTED]: clientScriptsDeleteRequested,
	[CLIENT_SCRIPTS_DELETE_STARTED]: clientScriptsDeleteStarted,
	[CLIENT_SCRIPTS_DELETE_SUCCEEDED]: clientScriptsDeleteSucceeded,
	[CLIENT_SCRIPTS_UPDATE_REQUESTED]: clientScriptsUpdateRequested,
	[CLIENT_SCRIPTS_UPDATE_HTTP_REQUESTED]: clientScriptsUpdateHttpEffect,
	[CLIENT_SCRIPTS_UPDATE_SUCCEEDED]: clientScriptsUpdateSucceeded
};
