import { createCustomElement } from '@servicenow/ui-core';

/**
 * Function that creates Seismic elements for each ancestor element
 * @param {object} ancestorElements
 * @return {void}
 */
const createRuntimeElement = (propertyValues, tagName) => {
	createCustomElement(tagName, {
		properties: Object.keys(propertyValues).reduce((acc, propName) => {
			return {
				...acc,
				[propName]: {
					default: null,
					selectable: true
				}
			};
		}, {})
	});
};

/**
 * Function that generates Seismic elements for an ancestor element for selectable state
 * @param {object} ancestorElements
 * @return {object} Tagnames of all the runtime elementa that were created by elementId
 */
export const createAncestorElementsRuntimeElements = (ancestorElements) => {
	return Object.entries(ancestorElements)
		.map(([elementId, { propertyValues }]) => {
			// Lower case the tagname and remove characters other than alphanumeric, -, and _ to make it seismic-safe
			const tagName = `ancestor-element-${elementId
				.replace(/[^\w]/g, '')
				.toLowerCase()}`;
			createRuntimeElement(propertyValues, tagName);
			return { elementId, tagName };
		})
		.reduce((acc, { elementId, tagName }) => {
			return {
				...acc,
				[elementId]: tagName
			};
		}, {});
};
