import isNull from 'lodash/isNull';
import reduce from 'lodash/reduce';
import get from 'lodash/get';
import { default as BindingFactory } from './binding';
import { default as JSONLiteralFactory } from './json-literal';
import { default as ListContainerFactory } from './list-container';
import { default as MapContainerFactory } from './map-container';
import { default as ClientTransformScriptFactory } from './client-transform-script';
import { default as VersionedClientTransformScriptFactory } from './versioned-client-transform-script';
import { default as TranslationLiteralFactory } from './translation-literal';
import { isSchemaTranslatable } from './util';

import {
	JSON_LITERAL,
	LIST_CONTAINER,
	MAP_CONTAINER,
	CLIENT_TRANSFORM_SCRIPT,
	VALID_BINDING_TYPES_ENUM,
	BINDING_SHORTHAND_PATTERN,
	SCRIPT_SHORTHAND_PATTERN,
	TRANSLATE_SHORTHAND_PATTERN,
	TRANSLATION_LITERAL
} from './constants';

function isValidBindingType(type, types = {}) {
	return type in types;
}

function isValidClientTransformScriptType(type, validType) {
	return type === validType;
}

function isValidContainerType(type, types = []) {
	return types.indexOf(type) !== -1;
}

function isJSONLiteralType(type, jsonLiteralType) {
	return type === jsonLiteralType;
}

function isTranslationLiteralType(type) {
	return type === TRANSLATION_LITERAL;
}

function isValidBindingShorthand(str) {
	return !!str.match(BINDING_SHORTHAND_PATTERN);
}

function isValidClientTransformScriptShortHand(str) {
	return !!str.match(SCRIPT_SHORTHAND_PATTERN);
}

function isMapContainerShorthand(obj) {
	return Object.values(obj).reduce((hasBinding, val) => {
		if (hasBinding) {
			return true;
		}
		if (val === null) {
			return false;
		}
		switch (typeof val) {
			case 'boolean':
			case 'number':
			case 'undefined':
				return false;
			case 'string':
				if (
					isValidBindingShorthand(val) ||
					isValidClientTransformScriptShortHand(val) ||
					val.match(TRANSLATE_SHORTHAND_PATTERN)
				) {
					return true;
				}
				return false;
			case 'object':
				return isMapContainerShorthand(val);
		}
	}, false);
}

function isListContainerShorthand(arr) {
	return arr.reduce((hasBinding, val) => {
		if (hasBinding) {
			return true;
		}
		if (val === null) {
			return false;
		}
		switch (typeof val) {
			case 'boolean':
			case 'number':
			case 'undefined':
				return false;
			case 'string':
				if (
					isValidBindingShorthand(val) ||
					isValidClientTransformScriptShortHand(val)
				) {
					return true;
				}
				return false;
			case 'object':
				if (Array.isArray(val)) {
					return isListContainerShorthand(val);
				}
				return isMapContainerShorthand(val);
		}
	}, false);
}

function isStringTranslatable(value, schema) {
	const { type, translatable, oneOf } = schema || {};
	if (value.match(TRANSLATE_SHORTHAND_PATTERN)) {
		return true;
	} else if (!isNull(type) && type === 'string' && translatable) {
		return true;
	} else if (
		reduce(
			oneOf,
			(acc, val) =>
				get(val, 'type') === 'string' && get(val, 'translatable') ? true : acc,
			false
		)
	) {
		return true;
	}
	return false;
}

function getUxValueFactoryFromShorthand(value, schema) {
	const isObject = typeof value === 'object' && !isNull(value);
	const isArrayObject = isObject && Array.isArray(value);
	const isTranslatableShorthand =
		typeof value === 'string' && isStringTranslatable(value, schema);
	const isBindingShorthand =
		typeof value === 'string' && value.match(BINDING_SHORTHAND_PATTERN);
	const isClientTransformScriptShorthand =
		typeof value === 'string' && value.match(SCRIPT_SHORTHAND_PATTERN);

	if (isObject && schema && isSchemaTranslatable(schema)) {
		return isArrayObject ? ListContainerFactory : MapContainerFactory;
	} else if (isArrayObject) {
		return isListContainerShorthand(value)
			? ListContainerFactory
			: JSONLiteralFactory;
	} else if (isObject) {
		return isTranslationLiteralType(value.type)
			? TranslationLiteralFactory
			: isMapContainerShorthand(value)
			? MapContainerFactory
			: JSONLiteralFactory;
	} else if (isBindingShorthand) {
		return BindingFactory;
	} else if (isClientTransformScriptShorthand) {
		return ClientTransformScriptFactory;
	} else if (isTranslatableShorthand) {
		return TranslationLiteralFactory;
	} else {
		return JSONLiteralFactory;
	}
}

function getUxValueFactoryFromType(type) {
	switch (type) {
		case JSON_LITERAL:
			return JSONLiteralFactory;
		case MAP_CONTAINER:
			return MapContainerFactory;
		case LIST_CONTAINER:
			return ListContainerFactory;
		case CLIENT_TRANSFORM_SCRIPT:
			return ClientTransformScriptFactory;
		case TRANSLATION_LITERAL:
			return TranslationLiteralFactory;
		default:
			if (type in VALID_BINDING_TYPES_ENUM) {
				return BindingFactory;
			} else {
				throw new TypeError('invalid UXValue model supplied');
			}
	}
}

export const isUxValueModel = (obj) => {
	if (typeof obj !== 'object' || obj === null || !obj.type) {
		return false;
	}
	return (
		isValidBindingType(obj.type, VALID_BINDING_TYPES_ENUM) ||
		isValidClientTransformScriptType(obj.type, CLIENT_TRANSFORM_SCRIPT) ||
		isValidContainerType(obj.type, [MAP_CONTAINER, LIST_CONTAINER]) ||
		isJSONLiteralType(obj.type, JSON_LITERAL) ||
		isTranslationLiteralType(obj.type)
	);
};

export const getUxValueFromShorthand = (shorthand = null, schema = null) => {
	const Factory = getUxValueFactoryFromShorthand(shorthand, schema);
	return Factory({ shorthand, schema }).getModel();
};

export const getVersionedUxValueFromShorthand = async (
	shorthand = null,
	schema = null
) => {
	const Factory = getUxValueFactoryFromShorthand(shorthand, schema);
	if (Factory.name === 'ClientTransformScriptFactory') {
		const ScriptFactory = await VersionedClientTransformScriptFactory({
			shorthand,
			schema
		});
		return ScriptFactory.getModel();
	}
	return Factory({ shorthand, schema }).getModel();
};

export const getShorthandFromUxValue = (model = null, schema = null) => {
	if (!model) {
		return model;
	} else {
		const Factory = getUxValueFactoryFromType(model.type);
		return Factory({ model, schema }).getShorthand();
	}
};
