import {
	CONTEXT_NAMESPACE,
	ELEMENT_NAMESPACE,
	DATA_NAMESPACE,
	PAYLOAD_NAMESPACE,
	STATE_NAMESPACE
} from '../constants';

export const JSON_LITERAL = 'JSON_LITERAL';
export const MAP_CONTAINER = 'MAP_CONTAINER';
export const LIST_CONTAINER = 'LIST_CONTAINER';
export const CLIENT_TRANSFORM_SCRIPT = 'CLIENT_TRANSFORM_SCRIPT';
export const TRANSLATION_LITERAL = 'TRANSLATION_LITERAL';

export const VALID_BINDING_TYPES_ENUM = {
	ELEMENT_BINDING: ELEMENT_NAMESPACE,
	CONTEXT_BINDING: CONTEXT_NAMESPACE,
	DATA_OUTPUT_BINDING: DATA_NAMESPACE,
	EVENT_PAYLOAD_BINDING: PAYLOAD_NAMESPACE,
	STATE_BINDING: STATE_NAMESPACE
};
export const BINDING_SHORTHAND_PATTERN = new RegExp(
	`^(${Object.values(VALID_BINDING_TYPES_ENUM).join('|')})((\\.[a-zA-z0-9]+)+)$`
);

export const SCRIPT_SHORTHAND_PATTERN = new RegExp(
	`^javascript\\(([\\n\\S\\d\\W\\w]*)\\)(\\!)?$`
);

export const TRANSLATE_SHORTHAND_PATTERN = new RegExp(
	`^translate\\(([\\n\\S\\d\\W\\w]*)\\)$`
);
