import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	NEW_PAGE_DELETE_REQUEST,
	NEW_PAGE_DELETE_REQUEST_HTTP,
	NEW_PAGE_DELETE_STARTED,
	NEW_PAGE_DELETE_SUCCEEDED,
	NEW_PAGE_DELETE_FAILED
} from './newPageActions';
import { pathParamsFromPayload } from './newPageUtils';

const newPageDeleteRequestEffect = createHttpEffect(
	'/api/now/builder/apps/:appId/pages/:pageSysId',
	{
		method: 'DELETE',
		pathParams: ['appId', 'pageSysId'],
		startActionType: NEW_PAGE_DELETE_STARTED,
		successActionType: NEW_PAGE_DELETE_SUCCEEDED,
		errorActionType: NEW_PAGE_DELETE_FAILED
	}
);

const newPageDeleteRequestHandler = ({ action, dispatch }) => {
	dispatch(
		NEW_PAGE_DELETE_REQUEST_HTTP,
		action.payload,
		pathParamsFromPayload(action.payload)
	);
};

const newPageDeleteStarted = ({ updateProperties }) => {
	updateProperties({ isSavingPage: true });
};

const newPageDeleteSucceeded = ({ updateProperties, dispatch }) => {
	dispatch('SN_UXF#PAGE_STATE_CLEARED');
	updateProperties({ isSavingPage: true });
};

const newPageDeleteFailed = ({ updateProperties }) => {
	updateProperties({ isSavingPage: false, isLoadingPage: false });
};

export default {
	[NEW_PAGE_DELETE_REQUEST]: newPageDeleteRequestHandler,
	[NEW_PAGE_DELETE_REQUEST_HTTP]: newPageDeleteRequestEffect,
	[NEW_PAGE_DELETE_STARTED]: newPageDeleteStarted,
	[NEW_PAGE_DELETE_SUCCEEDED]: newPageDeleteSucceeded,
	[NEW_PAGE_DELETE_FAILED]: newPageDeleteFailed
};
