import Model from './model';

export default class EventMapping extends Model {
	constructor({
		eventMappingId = '', // string
		eventId = '', // string
		eventApiName = '', // string
		targets = [] // array
	}) {
		super();
		this.eventMappingId = eventMappingId;
		this.eventId = eventId;
		this.eventApiName = eventApiName;
		this.targets = targets;
	}
}
