import cloneDeep from 'lodash/cloneDeep';
import createPageState from '../../../models/createPageState';
import get from 'lodash/get';
import {
	findElementById,
	findElements,
	findParentElement,
	getRootElementFromState,
	deleteChildElement,
	createElementFromComponent,
	elementUsesSNLayout,
	getAllElementIds
} from '../utils/element';
import { Element } from '../../../models';
import Slot from '../../../models/slot';
import isEqual from 'lodash/isEqual';
import { getTemplates } from '@devsnc/library-uxf';
import { throwError, normalizeRowColumnSlots } from '../../../utils';

import {
	RELOCATE_ELEMENT_SUCCEEDED,
	DETACH_ELEMENT_SUCCEEDED,
	RESTORE_ELEMENT_SUCCEEDED,
	COMPONENT_PLACED_SUCCEEDED
} from '../elementActions';

//Get the slot name of the first item in a layout
const getDefaultSlotname = (layout) => {
	const items = get(layout, 'default.items') || [];
	const firstItem = items[0];
	if (firstItem) {
		return firstItem.slotName;
	} else {
		return null;
	}
};
export const compareLayouts = (a, b) => {
	const normalize = (obj) => {
		let parsed;
		try {
			parsed = JSON.parse(
				JSON.stringify(obj, (key, val) => {
					if (key !== 'id') {
						return val;
					}
				})
			);
		} catch (e) {
			parsed = {};
		}
		if (parsed && parsed.default) {
			const optionalNullkeys = ['root', 'children', 'styles', 'rules', 'items'];
			optionalNullkeys.forEach((key) => {
				if (key in parsed.default === false) {
					parsed.default[key] = null;
				}
			});
		}
		return parsed;
	};

	return isEqual(normalize(a), normalize(b));
};

const isDefaultLayout = (layout, getDefaultLayout) => {
	const defaultLayout = get(layout, 'default');
	if (defaultLayout) {
		const { items = [] } = normalizeRowColumnSlots(layout).default;
		const pageType = defaultLayout.type === 'rowColumn' ? 'landing' : 'screen';
		if (items.length !== 1) {
			return false;
		}

		return compareLayouts(layout, getDefaultLayout(pageType));
	} else {
		return false;
	}
};

const elementDetached = ({
	state,
	action,
	updateState,
	dispatch,
	updateProperties,
	properties
}) => {
	const { elementId: detachedElementId } = action.payload;
	const newRootElement = cloneDeep(getRootElementFromState(state));
	const { pageState } = properties;
	const parentElement = findParentElement(newRootElement, detachedElementId);
	if (!parentElement) {
		throwError('Cannot detach root element');
	}
	const detachedElements = findElements(
		getRootElementFromState(state),
		(elm) => elm.slot && elm.id === detachedElementId
	);
	const previousLayout = parentElement.layout;

	findElementById(newRootElement, detachedElementId).slot.name = null;
	dispatch(DETACH_ELEMENT_SUCCEEDED, { elementId: detachedElementId });
	updateProperties({
		pageState: createPageState(
			{
				rootElement: new Element(newRootElement)
			},
			pageState
		)
	});

	updateState({
		path: 'detachedElementData',
		operation: 'set',
		value: { previousLayout, detachedElements, detachedElementId }
	});
};

const detachedElementRelocated = ({
	state,
	action,
	updateState,
	updateProperties,
	dispatch
}) => {
	const {
		detachedElementData: { detachedElements, detachedElementId },
		properties: { pageState },
		getDefaultLayout
	} = state;
	const { slotName: newSlot, parentId } = action.payload;
	const newRootElement = cloneDeep(getRootElementFromState(state));
	const targetParentElement = findElementById(newRootElement, parentId);

	let detachedElementParent;
	detachedElements.length &&
		(detachedElementParent = findParentElement(
			newRootElement,
			detachedElements[0].id
		));
	if (
		detachedElementParent &&
		detachedElementParent.id !== targetParentElement.id
	) {
		detachedElements.forEach((detachedElement) =>
			deleteChildElement(detachedElementParent, detachedElement.id)
		);
		detachedElements.forEach((detachedElement) =>
			targetParentElement.children.push(detachedElement)
		);
	}

	let slotToPlaceNewElementIn = newSlot;

	if (!get(targetParentElement, 'layout.default.type')) {
		const defaultLayout = getDefaultLayout();
		slotToPlaceNewElementIn = getDefaultSlotname(defaultLayout);
		targetParentElement.layout = defaultLayout;
	}
	const detachedIndex = targetParentElement.children.findIndex(
		(elem) => elem.id === detachedElementId
	);
	targetParentElement.children[
		detachedIndex
	].slot.name = slotToPlaceNewElementIn;

	updateProperties({
		pageState: createPageState(
			{
				rootElement: new Element(newRootElement)
			},
			pageState
		)
	});
	dispatch(RELOCATE_ELEMENT_SUCCEEDED, { elementId: detachedElementId });

	updateState({ path: 'detachedElementData', operation: 'set', value: null });
};

const detachedElementRestored = ({
	state,
	updateProperties,
	updateState,
	dispatch
}) => {
	const {
		detachedElementData: {
			detachedElementId,
			detachedElements,
			previousLayout
		},
		properties: { pageState }
	} = state;

	if (!previousLayout) {
		updateState({
			path: 'detachedElementData',
			operation: 'set',
			value: null
		});

		return;
	}

	const newRootElement = cloneDeep(getRootElementFromState(state));

	if (detachedElements && detachedElements.length > 0) {
		const parentElement = findParentElement(
			newRootElement,
			detachedElements[0].id
		);
		parentElement.layout = previousLayout;
		dispatch(RESTORE_ELEMENT_SUCCEEDED, {
			elementIds: detachedElements.map((el) => el.id)
		});

		const detachedIndex = parentElement.children.findIndex(
			(elem) => elem.id === detachedElementId
		);
		parentElement.children[detachedIndex].slot.name = get(
			detachedElements,
			'[0].slot.name'
		);

		updateProperties({
			pageState: createPageState(
				{
					rootElement: new Element(newRootElement)
				},
				pageState
			)
		});
	}

	updateState({
		path: 'detachedElementData',
		operation: 'set',
		value: null
	});
};

const componentPlaced = ({ action, state, updateProperties, dispatch }) => {
	const { componentId, parentId, slotName } = action.payload;

	const {
		rawComponentList,
		properties: { pageState },
		getDefaultLayout
	} = state;

	const newRootElement = cloneDeep(getRootElementFromState(state));
	const parentElement = findElementById(newRootElement, parentId);

	const droppedComponent = rawComponentList.find(
		(component) => component.id === componentId
	);

	// Load component scripts dynamically
	// This is async, but shouldn't matter if the scripts are loaded after
	// the component tags have been rendered in the html
	getTemplates([droppedComponent.id]);

	const newElement = createElementFromComponent(
		droppedComponent,
		parentElement,
		false,
		getDefaultLayout('slotted-component')
	);

	const allElementIds = getAllElementIds(newRootElement);
	newElement.initialize(allElementIds);

	if (elementUsesSNLayout(parentElement)) {
		let slotToPlaceNewElementIn = slotName;
		if (!get(parentElement, 'layout.default.type')) {
			const defaultLayout = getDefaultLayout();
			slotToPlaceNewElementIn = getDefaultSlotname(defaultLayout);
			parentElement.layout = defaultLayout;
		}
		newElement.slot = new Slot({
			name: slotToPlaceNewElementIn,
			parentElement: parentElement
		});
	} else if (slotName) {
		newElement.slot = new Slot({
			name: slotName,
			parentElement: parentElement
		});
	}

	parentElement.children.push(newElement);

	updateProperties({
		pageState: createPageState(
			{
				rootElement: new Element(newRootElement)
			},
			pageState
		)
	});
	dispatch(COMPONENT_PLACED_SUCCEEDED, {
		elementId: newElement.id,
		componentId: droppedComponent.id
	});
};

const elementDeleted = ({ state, action, updateProperties }) => {
	const {
		properties: { pageState }
	} = state;

	const { elementId: deletedElementId } = action.payload;

	const newRootElement = cloneDeep(getRootElementFromState(state));

	if (newRootElement.id === deletedElementId) {
		// Don't allow deleting the root element
		return;
	}

	const parentElement = findParentElement(newRootElement, deletedElementId);
	if (parentElement) {
		const indexOfDeletedElem = parentElement.children.findIndex(
			(elem) => elem.id === deletedElementId
		);
		if (indexOfDeletedElem > -1) {
			parentElement.children.splice(indexOfDeletedElem, 1);
		}
	}

	updateProperties({
		pageState: createPageState(
			{
				rootElement: new Element(newRootElement)
			},
			pageState
		)
	});
};

export {
	componentPlaced,
	elementDetached,
	detachedElementRelocated,
	detachedElementRestored,
	elementDeleted,
	isDefaultLayout
};
