import { createCustomElement } from '@servicenow/ui-core';
import snabbdom, { createElement } from '@servicenow/ui-renderer-snabbdom';

function createProperties(properties) {
	return {
		...Object.entries(properties).reduce((acc, [key]) => {
			acc[key] = {
				selectable: true
			};
			return acc;
		}, {})
	};
}

export const createDataBrokerRuntimeElement = (dataBrokerInstance) => {
	const { properties: inputProperties, outputProperties } = dataBrokerInstance;
	// Lower case the tagname and remove characters other than alphanumeric, -, and _ to make it seismic-safe
	const tagName = `data-broker-${dataBrokerInstance.id
		.replace(/[^\w]/g, '')
		.toLowerCase()}`;

	createCustomElement(tagName, {
		renderer: {
			type: snabbdom,
			view: () => createElement('slot')
		},
		properties: {
			...createProperties(inputProperties),
			...createProperties(outputProperties),
			preview: {
				selectable: true,
				default: {}
			},
			executionTime: {
				selectable: true,
				default: -1
			}
		}
	});

	return tagName;
};
