import { filterBindings } from './bindings';
import { SN_UXF_CONTEXT_BINDINGS_REQUESTED } from './bindingActions';

const contextBindingsRequested = (coeffects) => {
	const {
		state: { allContextBindings },
		action,
		updateState,
		updateProperties
	} = coeffects;

	updateState({
		operation: 'set',
		path: 'contextBindingInfo',
		value: action.payload
	});

	updateProperties({
		bindings: filterBindings(allContextBindings, action.payload)
	});
};

export const bindingsHandlers = {
	[SN_UXF_CONTEXT_BINDINGS_REQUESTED]: contextBindingsRequested
};
