import cloneDeep from 'lodash/cloneDeep';
import get from 'lodash/get';
import {
	DASHBOARDS_CREATE_REQUEST,
	DASHBOARDS_CREATE_REQUEST_SUCCEEDED,
	DASHBOARDS_CREATE_REQUEST_FAILED,
	DASHBOARDS_CREATE_REQUEST_STARTED,
	DASHBOARDS_CREATE_TAB_FAILED,
	DASHBOARDS_CREATE_TAB_STARTED,
	DASHBOARDS_CREATE_TAB_REQUEST,
	DASHBOARDS_CREATE_TAB_SUCCEEDED,
	DASHBOARDS_CREATE_OVERRIDE_REQUEST,
	DASHBOARDS_CREATE_OVERRIDE_REQUEST_STARTED,
	DASHBOARDS_CREATE_OVERRIDE_REQUEST_SUCCEEDED,
	DASHBOARDS_CREATE_OVERRIDE_REQUEST_FAILED,
	DASHBOARDS_CREATE_OVERRIDE_HTTP_REQUESTED
} from './dashboardsActions';
import createPageState from '../../models/createPageState';
import processContextState from '../contextState/contextStateProcessor';
import { addNewPageReceived } from '../newPage/newPageUtils';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { generateOverrideCreateHttpEffect } from '../newPage/newPageVariantOverrideHandlers';

const dashboardsCreateEffect = createHttpEffect('/api/now/builder/dashboards', {
	method: 'POST',
	pathParams: [],
	dataParam: 'data',
	startActionType: DASHBOARDS_CREATE_REQUEST_STARTED,
	successActionType: DASHBOARDS_CREATE_REQUEST_SUCCEEDED,
	errorActionType: DASHBOARDS_CREATE_REQUEST_FAILED
});

const dashboardsCreateStarted = ({ updateProperties }) => {
	updateProperties({ isSavingPage: true });
};

const dashboardsCreateSucceeded = ({
	updateProperties,
	action,
	properties,
	updateState,
	state
}) => {
	updateProperties({ isSavingPage: false });

	const {
		result: { screen }
	} = action.payload;
	const { dataBrokerDefinitions, eventHandlers, pages } = properties;

	const [pageState, newPages] = addNewPageReceived(
		screen,
		pages,
		dataBrokerDefinitions,
		eventHandlers,
		state
	);

	const {
		ancestorDataBrokerInstances,
		parentDataBrokerInstances,
		ancestorPageProperties,
		parentPageProperties,
		ancestorElements,
		parentElements,
		appChromeSettings,
		parentEventHandlers,
		theme
	} = processContextState({
		contextState: get(action.payload, 'result.contextState', {}),
		updateState,
		properties
	});

	const updatedPageState = createPageState(
		{
			eventHandlers: parentEventHandlers
		},
		pageState
	);

	updateProperties({ pages: newPages });

	updateProperties({
		ancestorDataBrokerInstances,
		parentDataBrokerInstances,
		ancestorPageProperties,
		parentPageProperties,
		ancestorElements,
		appChromeSettings,
		parentElements,
		theme,
		isSavingPage: false,
		pageState: updatedPageState,
		pageStatePristine: cloneDeep(updatedPageState)
	});
};

const dashboardsCreateFailed = ({ updateProperties }) => {
	updateProperties({ isSavingPage: false });
};

const dashboardsTabCreateStarted = ({ updateProperties }) => {
	updateProperties({ isSavingPage: true });
};

const dashboardsTabCreateSucceeded = ({ updateProperties }) => {
	updateProperties({ isSavingPage: false });
};

const dashboardsTabCreateFailed = ({ updateProperties }) => {
	updateProperties({ isSavingPage: false });
};
const dashboardsCreateOverrideRequested = ({
	action,
	properties,
	dispatch
}) => {
	const { pageState } = properties;
	const sysId = pageState.sysId || '';
	dispatch(
		DASHBOARDS_CREATE_OVERRIDE_HTTP_REQUESTED,
		{
			...action.payload,
			sysId
		},
		action.meta
	);
};

const dashboardsCreateOverrideHttpRequested = generateOverrideCreateHttpEffect({
	successActionType: DASHBOARDS_CREATE_OVERRIDE_REQUEST_SUCCEEDED,
	startActionType: DASHBOARDS_CREATE_OVERRIDE_REQUEST_STARTED,
	errorActionType: DASHBOARDS_CREATE_OVERRIDE_REQUEST_FAILED
});

export default {
	[DASHBOARDS_CREATE_REQUEST]: dashboardsCreateEffect,
	[DASHBOARDS_CREATE_REQUEST_STARTED]: dashboardsCreateStarted,
	[DASHBOARDS_CREATE_REQUEST_SUCCEEDED]: dashboardsCreateSucceeded,
	[DASHBOARDS_CREATE_REQUEST_FAILED]: dashboardsCreateFailed,
	[DASHBOARDS_CREATE_TAB_REQUEST]: createHttpEffect(
		'api/now/builder/dashboards/subroute',
		{
			method: 'POST',
			dataParam: 'data',
			startActionType: DASHBOARDS_CREATE_TAB_STARTED,
			successActionType: DASHBOARDS_CREATE_TAB_SUCCEEDED,
			errorActionType: DASHBOARDS_CREATE_TAB_FAILED,
			batch: false
		}
	),
	[DASHBOARDS_CREATE_TAB_STARTED]: dashboardsTabCreateStarted,
	[DASHBOARDS_CREATE_TAB_SUCCEEDED]: dashboardsTabCreateSucceeded,
	[DASHBOARDS_CREATE_TAB_FAILED]: dashboardsTabCreateFailed,
	[DASHBOARDS_CREATE_OVERRIDE_REQUEST]: dashboardsCreateOverrideRequested,
	[DASHBOARDS_CREATE_OVERRIDE_HTTP_REQUESTED]: dashboardsCreateOverrideHttpRequested,
	[DASHBOARDS_CREATE_OVERRIDE_REQUEST_SUCCEEDED]: (coeffects) => {
		dashboardsCreateSucceeded({
			...coeffects,
			action: {
				...coeffects.action,
				payload: {
					...coeffects.action.payload,
					result: {
						screen: coeffects.action.payload.result
					}
				}
			}
		});
	}
};
