import {
	DASHBOARDS_EDIT_REQUEST,
	DASHBOARDS_EDIT_REQUEST_SUCCEEDED,
	DASHBOARDS_EDIT_REQUEST_FAILED,
	DASHBOARDS_EDIT_REQUEST_STARTED
} from './dashboardsActions';
import { createHttpEffect } from '@servicenow/ui-effect-http';

const dashboardsEditEffect = createHttpEffect(
	'/api/now/builder/dashboards/:dashboardId',
	{
		method: 'PATCH',
		pathParams: ['dashboardId'],
		dataParam: 'data',
		startActionType: DASHBOARDS_EDIT_REQUEST_STARTED,
		successActionType: DASHBOARDS_EDIT_REQUEST_SUCCEEDED,
		errorActionType: DASHBOARDS_EDIT_REQUEST_FAILED,
		batch: false
	}
);

const dashboardsEditStarted = ({ updateProperties }) => {
	updateProperties({ isSavingPage: true });
};

const dashboardsEditSucceeded = ({ updateProperties }) => {
	updateProperties({ isSavingPage: false });
};

const dashboardsEditFailed = ({ updateProperties }) => {
	updateProperties({ isSavingPage: false });
};

export default {
	[DASHBOARDS_EDIT_REQUEST]: dashboardsEditEffect,
	[DASHBOARDS_EDIT_REQUEST_STARTED]: dashboardsEditStarted,
	[DASHBOARDS_EDIT_REQUEST_SUCCEEDED]: dashboardsEditSucceeded,
	[DASHBOARDS_EDIT_REQUEST_FAILED]: dashboardsEditFailed
};
