import {
	ELEMENT_LAYOUT_CHANGED,
	SLOT_STYLES_CHANGED,
	SLOT_STYLES_PATCHED,
	UPDATE_LAYOUT_SUCCEEDED
} from './elementActions';
import { Element } from '../../models';
import createElementArray from './utils/createElementArray';
import isLayoutEmpty from './utils/isLayoutEmpty';
import reparentElementsToNewLayout from './utils/reparentElementsToNewLayout';
import createPageState from '../../models/createPageState';
import cloneDeep from 'lodash/cloneDeep';
import get from 'lodash/get';
import { throwError, normalizeRowColumnSlots } from '../../utils';

const addTemplateIdToLayout = (layout, templateId) => {
	if (layout.default) {
		layout.default.templateId = templateId;
	}
	return layout;
};

const elementLayoutChanged = ({
	action,
	properties,
	updateProperties,
	dispatch,
	getDefaultLayout
}) => {
	const { pageState, templatesDefinitions } = properties;
	const { elementId, layoutTemplateId, layout } = action.payload;
	const newRootElement = cloneDeep(pageState.rootElement);
	const elements = createElementArray(newRootElement, (element) => element);
	const updateElement = elements.find(({ id }) => id === elementId);

	let newLayout;
	if (layoutTemplateId) {
		const layoutTemplate = templatesDefinitions.find(
			(template) => layoutTemplateId === template.id
		);
		const rootMacroponentId = get(
			layoutTemplate,
			'macroponentInstance.rootMacroponentId'
		);
		newLayout = get(
			layoutTemplate,
			`macroponentInstance.macroponentDefinitions.${rootMacroponentId}.layout`,
			{}
		);
		addTemplateIdToLayout(newLayout, layoutTemplateId);
	} else {
		try {
			newLayout = JSON.parse(layout);
		} catch (er) {
			throwError('invalid layout JSON');
		}
	}

	const normalizedLayout = normalizeRowColumnSlots(newLayout);
	newLayout = isLayoutEmpty(normalizedLayout)
		? getDefaultLayout('slotted-component')
		: normalizedLayout;

	const reparentedComponents = reparentElementsToNewLayout(
		updateElement,
		newLayout
	);

	updateElement.children = reparentedComponents;
	updateElement.layout = newLayout;

	if (reparentedComponents.length > 0) {
		dispatch(UPDATE_LAYOUT_SUCCEEDED, {
			components: reparentedComponents
		});
	}

	updateProperties({
		pageState: createPageState(
			{
				rootElement: new Element(newRootElement)
			},
			pageState
		)
	});
};

const slotStylesPatched = ({ action, properties, updateProperties }) => {
	const { elementId, selectedSlotName, styles } = action.payload;
	const { pageState } = properties;
	const newRootElement = cloneDeep(pageState.rootElement);
	const elements = createElementArray(newRootElement, (element) => element);
	const updateElement = elements.find(({ id }) => id === elementId);
	const updateSlot = get(updateElement, 'layout.default.items', []).find(
		(item) => item.slotName === selectedSlotName
	);
	updateSlot.styles = {
		...updateSlot.styles,
		...styles
	};

	updateProperties({
		pageState: createPageState(
			{
				rootElement: new Element(newRootElement)
			},
			pageState
		)
	});
};

const slotStylesChanged = ({ action, properties, updateProperties }) => {
	const { elementId, selectedSlotName, styles } = action.payload;
	const { pageState } = properties;
	const newRootElement = cloneDeep(pageState.rootElement);
	const elements = createElementArray(newRootElement, (element) => element);
	const updateElement = elements.find(({ id }) => id === elementId);
	const updateSlot = get(updateElement, 'layout.default.items', []).find(
		(item) => item.slotName === selectedSlotName
	);

	updateSlot.styles = styles;

	updateProperties({
		pageState: createPageState(
			{
				rootElement: new Element(newRootElement)
			},
			pageState
		)
	});
};
export default {
	[ELEMENT_LAYOUT_CHANGED]: elementLayoutChanged,
	[SLOT_STYLES_CHANGED]: slotStylesChanged,
	[SLOT_STYLES_PATCHED]: slotStylesPatched
};
