import { findElementById } from './element';
import get from 'lodash/get';

/**
 * Finds the of quadrant of where an existing x,y coordinate.
 * @param {Object} rootElement - the element to recursively search throught.
 * @param {string} ownerId - cuid of the owning element.
 * @param {string} layoutRole - The layout role to use, this is currently just `default`.
 * @returns {string} - the layout type of the owner element or an empty string.
 */
export default function getLayoutType(
	rootElement,
	ownerId,
	layoutRole = 'default'
) {
	const owner = findElementById(rootElement, ownerId);
	return get(owner, `layout.${layoutRole}.type`, '');
}
