import '@devsnc/sn-layout';
import '@servicenow/now-uxf-page';
import { createCustomElement } from '@servicenow/ui-core';
// eslint-disable-next-line no-unused-vars
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';

import assign from 'lodash/assign';
import omit from 'lodash/omit';
import pick from 'lodash/pick';
import camelCase from 'lodash/camelCase';
import forEach from 'lodash/forEach';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import mapValues from 'lodash/mapValues';
import {
	DATA_NAMESPACE,
	DATA_OUTPUT_BINDING,
	JSON_LITERAL,
	DEFER
} from '../constants';
// eslint-disable-next-line no-unused-vars
import { getHostCSS, HostCss, hostAllowList, stripSlotStyles } from './utils';
import {
	matchBindingSyntax,
	resolvePropertyBinding,
	resolveBinding,
	getWithNullDefault,
	normalizeRowColumnSlots
} from '../utils';
import { UIB_VIEWPORT_RENDER_CONTENTS } from '../sn-uxf-builder-middleware/viewportHandlers/actions';
import { VIEWPORT_ROUTES } from '../sn-uxf-builder-middleware/element/reservedPropertyIds';
import {
	isViewportComponent,
	isViewportIsolatedEditingDisabled
} from '../sn-uxf-builder-middleware/viewportHandlers/utils';

const defaultElementRenderer = ({ children }) => <div>{children}</div>;

const isContainer = (tag) =>
	tag === 'sn-page' ||
	tag === 'sn-page-container' ||
	tag === 'now-uxf-page-container' ||
	tag === 'now-uxf-page-simple-container';

const partialRendererFn = (rendererFn, args) => (layoutArgs) =>
	rendererFn(assign(args, layoutArgs));

const nextFrame = (fn) =>
	requestAnimationFrame(() => {
		setTimeout(() => fn());
	});
const doesSlotSeeMount = (el, id) => {
	const slot = el.assignedSlot;
	const nodes = slot ? slot.assignedNodes() : [];
	return nodes.some((node) => node.element.id === id);
};
const reattachSlotName = (el, slotName) => {
	el.slot = 'foo';
	nextFrame(() => {
		el.slot = slotName;
	});
};

function elementHasNoDefaultSlot(element) {
	return element.component.slots.indexOf('@default') === -1;
}

function elementHasNamedSlots(element) {
	return (
		element.component.slots.filter((slot) => {
			return slot !== '@default';
		}).length > 0
	);
}
const NO_SLOT = 'NO_SLOT';

const transformChildren = (acc, child) => {
	if (!get(child, 'slot.name')) {
		const existingChildren = acc[NO_SLOT] || [];
		return { ...acc, [NO_SLOT]: [...existingChildren, child] };
	}
	const existingChildren = acc[child.slot.name] || [];
	return { ...acc, [child.slot.name]: [...existingChildren, child] };
};

const styleForUIB = (element, isHidden) => {
	if (isHidden) {
		return { display: 'none' };
	}
	return {};
};

const processChildren = (
	children,
	renderers,
	rootElement,
	element,
	bindings,
	hiddenElements
) => {
	return (
		<Fragment>
			{children.map((child) => {
				return (
					<sn-uxf-element-mount
						style={styleForUIB(child, hiddenElements[child.id])}
						element={child}
						renderers={renderers}
						key={child.id}
						slot={get(child, 'slot.name', null)}
						hook={{
							insert({ elm }) {
								nextFrame(() => {
									const slotConnected = doesSlotSeeMount(elm, child.id);
									if (!slotConnected) {
										reattachSlotName(elm, child.slot.name);
									}
								});
							},
							update(_, { elm }) {
								nextFrame(() => {
									const slotConnected = doesSlotSeeMount(elm, child.id);
									if (!slotConnected) {
										reattachSlotName(elm, child.slot.name);
									}
								});
							}
						}}
						parent={element.id}
						bindings={bindings}
						rootElement={rootElement}
						hiddenElements={hiddenElements}
					/>
				);
			})}
		</Fragment>
	);
};

// If element supports has not @default slot return
// if element is of type rowC
const renderChildren = (
	element,
	renderers,
	parent,
	bindings,
	rootElement,
	hiddenElements
) => {
	if (elementHasNoDefaultSlot(element) && !elementHasNamedSlots(element)) {
		return null;
	}

	const mountableChildren = element.children.filter((child) => !child.isModal);

	if (get(element, 'layout.default.type') === 'rowColumn') {
		const formattedModel = normalizeRowColumnSlots(element.layout);
		const layoutRendererParams = {
			isContainer: false,
			layout: get(element, 'layout.default', null),
			elementId: element.id
		};

		const obj = mountableChildren.reduce(transformChildren, {});

		const unslottedChildren = obj[NO_SLOT] || [];
		return (
			<sn-layout
				model={stripSlotStyles(element.layout)}
				renderers={{
					column: renderers.column
						? partialRendererFn(renderers.column, layoutRendererParams)
						: null,
					row: renderers.row
						? partialRendererFn(renderers.row, layoutRendererParams)
						: null
				}}>
				{formattedModel.default.items.map((item) => {
					const children = obj[item.slotName];
					if (children && children.length > 0) {
						return renderers.slotWrapper({
							children: processChildren(
								children,
								renderers,
								rootElement,
								element,
								bindings,
								hiddenElements
							),
							slotName: item.slotName,
							slotParent: element.id,
							styles: item.styles || {}
						});
					} else {
						return renderers.slotWrapper({
							children: renderers.emptyRegion
								? renderers.emptyRegion({
										elementId: element.id,
										parentElementId: parent || element.id,
										slotName: item.slotName,
										isModalDescendant:
											element.isModal || element.isModalDescendant
								  })
								: null,
							slotName: item.slotName,
							slotParent: element.id,

							styles: item.styles || {}
						});
					}
				})}
				{processChildren(
					unslottedChildren,
					renderers,
					rootElement,
					element,
					bindings,
					hiddenElements
				)}
			</sn-layout>
		);
	}
	if (getWithNullDefault(element, 'layout.default.items', []).length > 0) {
		if (mountableChildren.length > 0) {
			const obj = mountableChildren.reduce(transformChildren, {});

			/**
			 * Render a slot render for each for each item
			 */
			return (
				<sn-layout model={stripSlotStyles(element.layout)}>
					{element.layout.default.items.map((item) => {
						const children = obj[item.slotName];
						if (children && children.length > 0) {
							return renderers.slotWrapper({
								children: processChildren(
									children,
									renderers,
									rootElement,
									element,
									bindings,
									hiddenElements
								),
								slotName: item.slotName,
								slotParent: element.id,
								styles: item.styles || {}
							});
						} else {
							return renderers.slotWrapper({
								children: renderers.emptyRegion
									? renderers.emptyRegion({
											elementId: element.id,
											parentElementId: parent || element.id,
											slotName: item.slotName,
											isModalDescendant:
												element.isModal || element.isModalDescendant
									  })
									: null,
								slotName: item.slotName,
								slotParent: element.id,
								styles: item.styles || {}
							});
						}
					})}
				</sn-layout>
			);
		} else {
			return (
				<sn-layout model={stripSlotStyles(element.layout)}>
					{element.layout.default.items.map((item) =>
						renderers.slotWrapper({
							children: renderers.emptyRegion
								? renderers.emptyRegion({
										elementId: element.id,
										parentElementId: parent || element.id,
										slotName: item.slotName,
										isModalDescendant:
											element.isModal || element.isModalDescendant
								  })
								: null,

							slotName: item.slotName,
							slotParent: element.id,
							styles: item.styles || {}
						})
					)}
				</sn-layout>
			);
		}
	}

	if (elementHasNamedSlots(element)) {
		/**
		 * We do not call slotWrapper here because:
		 * - Until optional slot metadata is supported we don't want to pass default elements into slots
		 * - Since sn-layout is not used in named slots we cannot style the slots or insert
		 * 	near them there aren't many reasons to display them.
		 */
		if (element.children && element.children.length > 0) {
			return processChildren(
				element.children,
				renderers,
				rootElement,
				element,
				bindings,
				hiddenElements
			);
		} else {
			return null;
		}
	}

	return renderers.emptySlot
		? renderers.emptySlot({
				elementId: element.id,
				parentElementId: parent
		  })
		: null;
};

const rootNodeStyles = {
	minHeight: 'inherit',
	minWidth: 'inherit',
	width: 'inherit',
	height: 'inherit'
};
const view = (state, { dispatch, updateState }) => {
	const {
		hostCss,
		properties: {
			element,
			renderers,
			parent,
			rootElement,
			ancestorElements,
			bindings,
			ancestorPageProperties,
			dataBrokerInstances,
			ancestorDataBrokerInstances,
			clientScriptApi,
			hiddenElements,
			appChromeSettings,
			...dataProviderPropValues
		}
	} = state;

	if (!element) {
		return null;
	}
	renderers.slotWrapper = renderers.slotWrapper
		? renderers.slotWrapper
		: ({ children }) => {
				return <Fragment>{children}</Fragment>;
		  };

	const MountedComponent = element.component.tag;

	const propertyTypeMap = mapValues(element.properties, ({ type }) => type);

	const properties = {
		...mapValues(element.properties, ({ name, value }) =>
			!element.propertyValues || element.propertyValues[name] === undefined
				? value
				: element.propertyValues[name]
		),
		...dataProviderPropValues[camelCase(MountedComponent)]
	};

	if (!parent) {
		properties['component-id'] = element.id;
	}

	forEach(properties, (property, propName) => {
		/* 
			UX Runtime added a property called defer to enhance performance by
			requesting only the components which are visible to the user (helpful in
			case of modals etc). when an element is being mounted, we need to do 
			the same in UIB
		*/
		if (propName === DEFER) {
			properties[propName] = false;
		} else {
			properties[propName] = resolveBinding(property, {
				bindings,
				ancestorPageProperties,
				dataBrokerInstances,
				ancestorDataBrokerInstances,
				rootElement,
				ancestorElements,
				clientScriptApi,
				appChromeSettings,
				type: propertyTypeMap[propName]
			});
		}

		const processParentMcpBindingsForViewports = (mcp) => {
			if (!isEmpty(mcp.macroponentConfiguration)) {
				Object.keys(mcp.macroponentConfiguration).forEach((key) => {
					const val = mcp.macroponentConfiguration[key];
					const { type, binding } = val;

					if (type === DATA_OUTPUT_BINDING) {
						const dbPath =
							binding.address && [DATA_NAMESPACE, ...binding.address].join('.');
						const matchedValues = dbPath && matchBindingSyntax(dbPath);

						const value = resolvePropertyBinding({
							matchedValues,
							bindings,
							ancestorPageProperties,
							dataBrokerInstances,
							ancestorDataBrokerInstances,
							rootElement,
							ancestorElements,
							appChromeSettings
						});

						if (value) {
							mcp.macroponentConfiguration[key] = {
								type: JSON_LITERAL,
								value
							};
						}
					}
				});
			}
		};

		// the macroponent loader which is used by viewport only renders
		// the tab page and has no information regarding any of the ancestors.
		// In that case, since it would not have access to parent data brokers,
		// we need to resolve it into a static value in the configuration before
		// making it available to the viewport. The viewport then
		// continues to use this value and render it on the UI.

		if (propName === VIEWPORT_ROUTES && !isEmpty(property)) {
			for (const route in property) {
				const routeConfiguration = property[route];
				// TODO: change this 0 when we know more about
				// route variants
				const macroponents = get(routeConfiguration, `[0].macroponents`, []);
				macroponents.forEach(processParentMcpBindingsForViewports);
			}
		}
	});

	const renderViewportContents = ({ elm, ignoreCache }) => {
		if (isViewportComponent(element.component)) {
			dispatch(UIB_VIEWPORT_RENDER_CONTENTS, {
				host: elm,
				properties,
				ignoreCache
			});
		}
		const css = getHostCSS(elm);

		if (!isEmpty(css) && isEmpty(hostCss || {})) {
			updateState({ hostCss: css });
		}
	};

	const isContainerNode = isContainer(element.component.tag);

	return (
		<div style={rootNodeStyles}>
			<HostCss
				id={element.id}
				key={element.id}
				cssObj={{ ...hostCss, ...element.styles }}
			/>
			{(renderers.element || defaultElementRenderer)({
				children: (
					<MountedComponent
						id={element.id}
						component-id={element.id}
						style={{
							...omit(element.styles, hostAllowList),
							...(isContainerNode
								? pick(element.styles, ['width', 'height', 'display'])
								: pick(element.styles, ['width', 'height']))
						}}
						hook-insert={({ elm }) => {
							renderViewportContents({ elm, ignoreCache: true });
						}}
						hook-update={renderViewportContents}
						{...properties}>
						{renderChildren(
							element,
							renderers,
							parent,
							bindings,
							rootElement,
							hiddenElements
						)}
					</MountedComponent>
				),
				elementId: element.id,
				componentId: element.component.id,
				isContainer: isContainerNode,
				parentElementId: parent,
				componentIcon: element.component.icon,
				instanceName: element.instanceName,
				hasError: element.error !== null,
				allowIsolatedEditing:
					isViewportComponent(element.component) &&
					!isViewportIsolatedEditingDisabled(properties)
			})}
		</div>
	);
};

export default createCustomElement('sn-uxf-element-mount', {
	renderer: { type: snabbdom },
	view,
	properties: {
		element: {
			default: null
		},
		renderers: {
			default: {
				column: null,
				row: null,
				element: null,
				slotWrapper: ({ children }) => {
					return <Fragment>{children}</Fragment>;
				},
				emptySlot: null
			}
		},
		pageState: {
			default: '@uxf/pageState'
		},
		parent: {
			default: null
		},
		bindings: {
			default: {}
		},
		ancestorPageProperties: {
			default: '@uxf/ancestorPageProperties'
		},
		dataBrokerInstances: {
			default: '@uxf/dataBrokerInstances'
		},
		ancestorDataBrokerInstances: {
			default: '@uxf/ancestorDataBrokerInstances'
		},
		rootElement: {
			default: null
		},
		ancestorElements: {
			default: '@uxf/ancestorElements'
		},
		clientScriptApi: {
			default: '@uxf/clientScriptApi'
		},
		hiddenElements: {
			default: {}
		},
		appChromeSettings: {
			default: '@uxf/appChromeSettings'
		}
	}
});
