import cloneDeep from 'lodash/cloneDeep';
import { PAGE_LAYOUT_CHANGED } from './pageLayoutActions';
import { Element } from '../../models';
import createPageState from '../../models/createPageState';
import isLayoutEmpty from '../element/utils/isLayoutEmpty';
import { throwError, normalizeRowColumnSlots } from '../../utils';
import reparentElementsToNewLayout from '../element/utils/reparentElementsToNewLayout';

function getPageState(newRootElement, pageState) {
	return createPageState(
		{
			rootElement: new Element(newRootElement)
		},
		pageState
	);
}

const pageLayoutChanged = ({
	action,
	properties,
	updateProperties,
	getDefaultLayout
}) => {
	const { pageState } = properties;
	const newRootElement = cloneDeep(pageState.rootElement);
	const { layout } = action.payload;
	let newLayout;
	try {
		const normalizedParsedLayout = normalizeRowColumnSlots(JSON.parse(layout));
		newLayout = isLayoutEmpty(normalizedParsedLayout)
			? getDefaultLayout('page')
			: normalizedParsedLayout;
	} catch (er) {
		newLayout = newRootElement.layout;
		throwError('invalid page layout JSON');
	}
	newRootElement.children = reparentElementsToNewLayout(
		newRootElement,
		newLayout
	);
	newRootElement.layout = newLayout;
	updateProperties({
		pageState: getPageState(newRootElement, pageState)
	});
};

export default {
	[PAGE_LAYOUT_CHANGED]: pageLayoutChanged
};
