import Model from './model';

export default class ElementEvent extends Model {
	constructor({
		id = '', // string
		eventName = '', // string
		label = '', // string
		payloadShape = [], // Array
		apiName = '' // string
	}) {
		super();
		this.id = id;
		this.eventName = eventName;
		this.label = label || this.eventName;
		this.payloadShape = payloadShape;
		this.apiName = apiName;
	}
}
