/**
 * @typedef Element
 * @property {string} id
 * @property {Element[]} children
 * @property {boolean} isModal
 */
/**
 * a function that takes in a rootElement and returns the selected element, or
 * the element's closest modal parent if one exists
 * @param {Element} element
 * @param {string} selectedElementId
 * @return {Element|undefined}
 */
export const findSelectedElement = (element, selectedElementId) => {
	const parentStack = [];
	return search(parentStack, element, selectedElementId);
};

/**
 * a function which uses a stack to keep track of ancestral lineage while
 * it recurses the element's children to find the selectedElement
 * @param {Element[]} stack
 * @param {Element} element
 * @param {string} selectedElementId
 * @return {Element|undefined}
 */
const search = (stack, element, selectedElementId) => {
	if (!selectedElementId) {
		return undefined;
	}

	if (element.id === selectedElementId) {
		let el;
		while ((el = stack.pop())) {
			if (el.isModal) {
				return el;
			}
		}
		return element;
	}

	stack.push(element);

	for (const elem of element.children) {
		const e = search(stack, elem, selectedElementId);
		if (e) {
			return e;
		}
	}

	stack.pop();
	return undefined;
};

export const closeModal = (eventOrAction, dispatch) => {
	eventOrAction.stopPropagation();
	dispatch('UIB_ELEMENT#ELEMENT_SELECTED', {
		elementId: null,
		elementInstanceName: null
	});
};
