import ClientTransformScriptFactory from './client-transform-script';
import { SCRIPT_SHORTHAND_PATTERN, CLIENT_TRANSFORM_SCRIPT } from './constants';
import { processLinting } from '../utils';

export default async function VersionedClientTransformScriptFactory({
	model,
	shorthand
} = {}) {
	if (!shorthand && !model) {
		throw new TypeError(
			'Something went wrong initializing ClientTransformScriptFactory'
		);
	}

	const { getShorthand } = ClientTransformScriptFactory({
		model,
		shorthand
	});

	const scriptMatcher = getShorthand().match(SCRIPT_SHORTHAND_PATTERN);
	const scriptToLint = scriptMatcher ? scriptMatcher[1] : '';
	const isCompatibilityMode = !(scriptMatcher && scriptMatcher[2]);
	const errors = (await processLinting([scriptToLint]))[0];
	const fModel = {
		type: CLIENT_TRANSFORM_SCRIPT,
		script: {
			inlineScript: scriptToLint
		}
	};

	if (errors.length && isCompatibilityMode) {
		fModel.script.apiVersion = '1.0.0';
	} else {
		fModel.script.apiVersion = '2.0.0';
	}

	return {
		getModel() {
			return fModel;
		},
		getShorthand
	};
}
