import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	NEW_PAGE_VARIANT_CREATE_REQUEST,
	NEW_PAGE_VARIANT_CREATE_REQUEST_HTTP,
	NEW_PAGE_VARIANT_CREATE_STARTED,
	NEW_PAGE_VARIANT_CREATE_SUCCEEDED,
	NEW_PAGE_VARIANT_CREATE_FAILED
} from './newPageActions';
import cloneDeep from 'lodash/cloneDeep';
import { getTemplates } from '@devsnc/library-uxf';
import processContextState from '../contextState/contextStateProcessor';
import { pathParamsFromPayload, addNewPageReceived } from './newPageUtils';
import get from 'lodash/get';

const newPageVariantCreateHandler = ({ action, dispatch }) => {
	dispatch(
		NEW_PAGE_VARIANT_CREATE_REQUEST_HTTP,
		action.payload,
		pathParamsFromPayload(action.payload)
	);
};

const newPageVariantCreateStarted = ({ updateProperties }) => {
	updateProperties({ isSavingPage: true });
};
export const newPageVariantCreateSucceeded = ({
	updateProperties,
	updateState,
	action,
	properties,
	state
}) => {
	const { dataBrokerDefinitions, pages } = properties;

	const {
		result: {
			macroponentInstance: { rootMacroponentId },
			contextState
		}
	} = action.payload;

	const {
		ancestorDataBrokerInstances,
		parentDataBrokerInstances,
		ancestorPageProperties,
		parentPageProperties,
		ancestorElements,
		parentElements,
		appChromeSettings,
		parentEventHandlers,
		theme
	} = processContextState({
		contextState,
		updateState,
		properties: { dataBrokerDefinitions }
	});

	// Load component scripts dynamically
	// This is async, but shouldn't matter if the scripts are loaded after
	// the component tags have been rendered in the html
	if (rootMacroponentId) {
		getTemplates([rootMacroponentId]);
	}

	const variantId = get(properties, 'pageState.sysId', '');

	const [pageState, newPages] = addNewPageReceived(
		action.payload.result,
		pages,
		dataBrokerDefinitions,
		parentEventHandlers,
		state,
		variantId
	);

	updateProperties({ pages: newPages });

	updateProperties({
		ancestorDataBrokerInstances,
		parentDataBrokerInstances,
		ancestorPageProperties,
		parentPageProperties,
		ancestorElements,
		appChromeSettings,
		parentElements,
		theme,
		isSavingPage: false,
		pageState,
		pageStatePristine: cloneDeep(pageState)
	});
};
const newPageVariantCreateFailed = ({ updateProperties }) => {
	updateProperties({ isSavingPage: false, isLoadingPage: false });
};

const newPageVariantCreateHttpEffect = createHttpEffect(
	'/api/now/builder/:builderType/:appId/pages/:pageSysId/variants',
	{
		method: 'POST',
		pathParams: ['builderType', 'appId', 'pageSysId'],
		dataParam: 'data',
		startActionType: NEW_PAGE_VARIANT_CREATE_STARTED,
		successActionType: NEW_PAGE_VARIANT_CREATE_SUCCEEDED,
		errorActionType: NEW_PAGE_VARIANT_CREATE_FAILED,
		batch: false
	}
);

export default {
	[NEW_PAGE_VARIANT_CREATE_REQUEST]: newPageVariantCreateHandler,
	[NEW_PAGE_VARIANT_CREATE_REQUEST_HTTP]: newPageVariantCreateHttpEffect,
	[NEW_PAGE_VARIANT_CREATE_STARTED]: newPageVariantCreateStarted,
	[NEW_PAGE_VARIANT_CREATE_SUCCEEDED]: newPageVariantCreateSucceeded,
	[NEW_PAGE_VARIANT_CREATE_FAILED]: newPageVariantCreateFailed
};
