import Element from './element';
import Property from './property';
import { seismicCuid } from '../utils';
import BaseModel from './baseModel';
import { isViewportComponent } from '../sn-uxf-builder-middleware/viewportHandlers/utils';

export const getViewportElementIds = (element) => {
	const nodes = [element];
	const viewportElementIds = [];
	while (nodes.length) {
		const node = nodes.shift();
		if (isViewportComponent(node.component)) {
			viewportElementIds.push(node.id);
		} else {
			nodes.push(...node.children);
		}
	}
	return viewportElementIds;
};

function createElementPropertiesFromComponent(component) {
	return component.properties.reduce((acc, propertyConfig) => {
		acc[propertyConfig.columnName] = new Property(propertyConfig);
		return acc;
	}, {});
}

function createElementFromComponent(component) {
	return new Element({
		id: seismicCuid(),
		component,
		properties: createElementPropertiesFromComponent(component)
	});
}

export default class PageStateModel extends BaseModel {
	static attributes = {
		name: {
			default: ''
		},
		rootElement: {
			default: createElementFromComponent({
				name: 'Page',
				tag: 'sn-page',
				slots: [],
				icon: 'pencil-page-outline',
				properties: [
					new Property({
						columnName: 'label',
						label: 'Optional Page Title',
						type: 'String (Full UTF-8)'
					})
				]
			})
		},
		sysId: {
			default: null
		},
		scope: {
			default: ''
		},
		scopeName: {
			default: ''
		},
		domainId: {
			default: ''
		},
		domainName: {
			default: ''
		},
		applicationName: {
			default: 'Global'
		},
		type: {
			default: 'landing'
		},
		applications: {
			default: null
		},
		uiTypeParameters: {
			default: {}
		},
		pageStatus: {
			default: 'active'
		},
		sysPolicy: {
			default: ''
		},
		readOnly: {
			default: false
		},
		canCopy: {
			default: true
		},
		dataBrokerInstances: {
			default: []
		},
		eventHandlers: {
			default: []
		},
		clientStateParameters: {
			default: []
		},
		dispatchedEvents: {
			default: []
		},
		handledEvents: {
			default: []
		},
		screenEventMappings: {
			default: []
		},
		viewportElementIds: {
			default: {}
		},
		order: {
			default: 0
		},
		usingSharedPageDefinition: {
			default: false
		}
	};

	constructor(initialValues = {}) {
		super(initialValues);
		const { type } = initialValues;
		this.dataBrokersEnabled = type !== 'landing';
		this.viewportElementIds = getViewportElementIds(this.rootElement);
	}

	get pageDefinitionInSameScope() {
		return typeof this.rootElement.scope === 'string'
			? this.scope === this.rootElement.scope
			: true;
	}
}
