import { getComponentsByTagNames } from '@devsnc/library-uxf';

import { COMPONENT_SOURCES_REQUESTED } from './componentSourcesActions';

let configComponents = null;
let pageComponents = null;

const componentSourcesRequested = ({ action }) => {
	if (action.payload.configComponents) {
		configComponents = action.payload.configComponents;
	}

	if (action.payload.pageComponents) {
		pageComponents = action.payload.pageComponents;
	}

	if (configComponents && pageComponents) {
		getComponentsByTagNames(configComponents.concat(pageComponents));
	}
};

export default {
	[COMPONENT_SOURCES_REQUESTED]: componentSourcesRequested
};
