import { matchBindingSyntax } from '../../../utils';

export default function isCircular(
	sourceElement,
	allElements,
	currentElement,
	iterCount = 0
) {
	let currentDepth = iterCount;
	const currEl = currentElement || sourceElement;
	const boundElementIds = Object.values(currEl.properties).reduce(
		(acc, { value }) => {
			const matcher = matchBindingSyntax(value);
			if (matcher && matcher.namespace === '@elements') {
				return [...acc, matcher.id];
			}
			return acc;
		},
		[]
	);
	// Safety for call stack overflow
	if (iterCount > 100) {
		return false;
	}
	if (boundElementIds.length === 0) {
		return false;
	}
	if (boundElementIds.some((boundElId) => boundElId === sourceElement.id)) {
		return [
			sourceElement.instanceName || sourceElement.id,
			currEl.instanceName || currEl.id
		];
	}
	return boundElementIds.reduce((isCir, boundElId) => {
		const nextEl = allElements.find((el) => el.id === boundElId);
		if (isCir) {
			return [
				sourceElement.instanceName || sourceElement.id,
				currEl.instanceName || currEl.id
			];
		}
		if (!nextEl) {
			return false;
		}
		return isCircular(sourceElement, allElements, nextEl, ++currentDepth);
	}, false);
}
