const workerFn = () => {
	let scriptImported = false;
	self.onmessage = ({
		data: {
			name,
			payload: { id, origin, scripts }
		}
	}) => {
		if (name !== 'UXF#LINT_REQUESTED') {
			return;
		}

		if (!scriptImported) {
			// eslint-disable-next-line no-undef
			importScripts(
				`${origin}/scripts/externals/sn-uxf-client-scripts-eslint-bundle.min.js?sysparm_substitute=false`
			);
			scriptImported = true;
		}

		const lintConfig = {
			parserOptions: {
				ecmaVersion: '2018',
				sourceType: 'script',
				ecmaFeatures: {
					impliedStrict: true
				}
			},
			rules: {
				semi: 'warn',
				'no-undef': 'error'
			},
			globals: {
				console: 'readonly',
				Promise: 'readonly'
			}
		};

		// eslint-disable-next-line no-undef
		const uibLinter = new eslint.Linter();
		self.postMessage({
			name: 'UXF_LINT_WORKER#LINTING_COMPLETE',
			payload: {
				id,
				result: scripts.map((script) => uibLinter.verify(script, lintConfig))
			}
		});
	};
};

let instance;
export default () => {
	if (!instance) {
		instance = new Worker(
			window.URL.createObjectURL(new Blob([`(${workerFn.toString()})()`]))
		);
	}
	return instance;
};
