import { ELEMENT_PROPERTY_UPDATED } from './elementActions';
import { Element } from '../../models';
import { ELEMENT_INSTANCE_NAME, IS_HIDDEN } from './reservedPropertyIds';
import createElementArray from './utils/createElementArray';
import createPageState from '../../models/createPageState';

import cloneDeep from 'lodash/cloneDeep';

import isCircular from './utils/isCircular';

export const elementPropertyUpdatedHandler = ({
	action,
	properties,
	updateProperties
}) => {
	const { pageState, elementProperties } = properties;

	const {
		elementId,
		propertyId: payloadPropertyId,
		value: payloadValue,
		properties: payloadProperties
	} = action.payload;

	const propertiesToUpdate =
		payloadProperties && payloadProperties.length
			? payloadProperties
			: [
					{
						propertyId: payloadPropertyId,
						value: payloadValue
					}
			  ];

	const newRootElement = cloneDeep(pageState.rootElement);
	const elements = createElementArray(newRootElement, (element) => element);
	const updateElement = elements.find((el) => el.id === elementId);

	propertiesToUpdate.forEach(({ propertyId, value }) => {
		const updateProp =
			elementProperties[elementId].find((prop) => prop.id === propertyId) || {};

		if (updateProp.onChange) {
			updateProp.onChange(updateElement);
		}

		if (propertyId === ELEMENT_INSTANCE_NAME) {
			updateElement.instanceName = value;
		} else if (propertyId === IS_HIDDEN) {
			updateElement.isHidden = value;
		} else if (!updateElement.properties[propertyId].readOnly) {
			const checkElement = cloneDeep(updateElement);
			checkElement.properties[propertyId].value = value;
			const circularRefElements = isCircular(checkElement, elements);
			if (circularRefElements) {
				updateElement.properties[propertyId].error = {
					type: 'circular',
					elements: circularRefElements
				};
			} else {
				updateElement.properties[propertyId].value = value;
				updateElement.properties[propertyId].error = null;
			}
		} else {
			console.warn(
				`UXF Builder Middleware Error: Cannot update read-only property ${updateElement.properties[propertyId].columnName} of element ${elementId}`
			);
		}
	});

	updateProperties({
		pageState: createPageState(
			{
				rootElement: new Element(newRootElement)
			},
			pageState
		)
	});
};

export default {
	[ELEMENT_PROPERTY_UPDATED]: elementPropertyUpdatedHandler
};
