import computeContextDataBrokerInstances from './utils/dataBrokerUtils';
import computeContextPageProperties from './utils/pagePropertyUtils';
import computeContextElements from './utils/elementUtils';
import reduceAppChromeSettings from './utils/appChromeSettingsUtils';
import { createDataBrokerRuntimeElement } from '../dataBrokers/dataBrokerFactory';
import { createAncestorElementsRuntimeElements } from './ancestorElementFactory';
import { getDataBrokerInstances, addAllEventHandlers } from '../utils';
import { EventHandler } from '../../models';
import { isUxValueModel, getShorthandFromUxValue } from '../../factories';
import get from 'lodash/get';

export default function processContextState({
	contextState = {},
	updateState,
	properties
}) {
	const { dataBrokerDefinitions } = properties;

	const {
		ancestorDataBrokerInstances,
		parentDataBrokerInstances
	} = computeContextDataBrokerInstances(contextState, dataBrokerDefinitions);
	const transformedAncestorDataBrokerInstances = getDataBrokerInstances(
		ancestorDataBrokerInstances
	);
	const transformedParentDataBrokerInstances = getDataBrokerInstances(
		parentDataBrokerInstances
	);

	const {
		ancestorPageProperties,
		parentPageProperties
	} = computeContextPageProperties(contextState);

	const { pageProperties: appChromeSettings } = contextState;
	const transformedAppChromeSettings = reduceAppChromeSettings(
		appChromeSettings
	);

	const { ancestorElements, parentElements } = computeContextElements(
		contextState
	);

	const dataBrokerTags = {};
	ancestorDataBrokerInstances.forEach((dataBrokerInstance) => {
		if (dataBrokerInstance.definition.type !== 'CLIENT_STATE') {
			dataBrokerTags[dataBrokerInstance.id] = createDataBrokerRuntimeElement(
				dataBrokerInstance
			);
		}
	});

	const ancestorElementElements = createAncestorElementsRuntimeElements(
		ancestorElements
	);

	updateState({
		ancestorElementElements
	});

	const { parentHandledEvents = [], ancestorHandledEvents = [] } = contextState;
	const parentEventHandlers = [
		...parentHandledEvents,
		...ancestorHandledEvents
	].map((eventHandler) => {
		return new EventHandler({
			...eventHandler,
			ancestor: true
		});
	});

	const allParentEventHandlers = addAllEventHandlers(
		parentEventHandlers,
		true, // These event handlers are from ancestor contexts
		ancestorDataBrokerInstances,
		dataBrokerDefinitions
	);

	return {
		ancestorDataBrokerInstances: transformedAncestorDataBrokerInstances,
		parentDataBrokerInstances: transformedParentDataBrokerInstances,
		ancestorPageProperties,
		parentPageProperties,
		ancestorElements,
		parentElements,
		appChromeSettings: transformedAppChromeSettings,
		parentEventHandlers: allParentEventHandlers,
		ancestorDataBrokerTags: dataBrokerTags,
		theme: contextState.theme,
		parentInfo: {
			...contextState.parentInfo,
			elementProperties: convertToShorthand(
				get(contextState, 'parentInfo.elementProperties', {})
			)
		}
	};
}

export function convertToShorthand(properties) {
	const shorthand = {};
	for (const prop in properties) {
		const propVal = properties[prop];
		if (isUxValueModel(propVal)) {
			shorthand[prop] = getShorthandFromUxValue(propVal);
		}
	}
	return shorthand;
}
