import get from 'lodash/get';
import { UPDATED_TABLES_RESET_REQUESTED } from './updatedTablesActions';
import { messageInfo } from '../../utils';

export default {
	['SN_UXF#AMB_SUBSCRIPTION_SUCCEEDED:sys_ux_data_broker']: ({
		updateProperties,
		properties
	}) => {
		// eslint-disable-next-line no-console
		messageInfo('(AMB) listening for changes/updates on sys_ux_data_broker');
		updateProperties({
			updatedTables: {
				...properties.updatedTables,
				['sys_ux_data_broker']: null
			}
		});
	},
	['SN_UXF#AMB_MESSAGE_RECEIVED:sys_ux_data_broker']: ({
		action,
		updateProperties,
		properties
	}) => {
		const data = get(action.payload, 'data', {});
		// eslint-disable-next-line no-console
		messageInfo('(AMB) change detected on sys_ux_data_broker', data);
		updateProperties({
			updatedTables: {
				...properties.updatedTables,
				['sys_ux_data_broker']: data
			}
		});
	},
	[UPDATED_TABLES_RESET_REQUESTED]: ({
		action,
		properties,
		updateProperties
	}) => {
		const { table } = action.payload;
		updateProperties({
			updatedTables: {
				...properties.updatedTables,
				[table]: null
			}
		});
	}
};
