import { CLIENT_STATE_DATA_REQUESTED } from './clientStateActions';
import { processClientStateData } from '../utils';

const clientStateDataRequested = ({ action, properties, updateProperties }) => {
	const { params } = action.payload;
	const {
		pageState,
		ancestorPageProperties,
		dataBrokerInstances,
		ancestorDataBrokerInstances,
		ancestorElements,
		appChromeSettings
	} = properties;

	updateProperties({
		clientStateData: processClientStateData(
			params,
			pageState,
			ancestorPageProperties,
			dataBrokerInstances,
			ancestorDataBrokerInstances,
			ancestorElements,
			appChromeSettings
		)
	});
};

export default {
	[CLIENT_STATE_DATA_REQUESTED]: clientStateDataRequested
};
