import { createHttpEffect } from '@servicenow/ui-effect-http';
import get from 'lodash/get';
import set from 'lodash/set';
import createPageState from '../../models/createPageState';
import cloneDeep from 'lodash/cloneDeep';
import ElementEvent from '../../models/elementEvent';
import EventHandler from '../../models/eventHandler';
import createScreenEventMapping from './utils/createScreenEventMapping';
import { computeEventMappingTargetKeys } from '../page/utils/shared';
import {
	MCP_CONTRACT_EVENT_CREATED,
	MCP_CONTRACT_EVENT_CREATE_SUCCEEDED,
	MCP_CONTRACT_EVENT_CREATE_FAILED,
	MCP_CONTRACT_EVENT_FETCH_REQUESTED,
	MCP_CONTRACT_EVENT_UPDATED,
	MCP_CONTRACT_EVENT_UPDATE_SUCCEEDED,
	MCP_CONTRACT_EVENT_UPDATE_FAILED,
	MCP_CONTRACT_EVENT_DELETED,
	MCP_CONTRACT_EVENT_DELETE_REQUESTED
} from './dispatchedEventsActions';
import { PAGE_SAVE_HTTP_REQUESTED } from '../page/pageActions';
import { formatEventNameAndLabel } from '../elementEventMappings/utils';
import { throwError } from '../../utils';
import { removePrefixes } from '../../factories/util';

const macroponentContractEventCreated = createHttpEffect(
	'/api/now/table/sys_ux_event?sysparm_fields=sys_scope.scope,event_name,label,sys_id,props',
	{
		method: 'POST',
		batch: false,
		dataParam: 'event',
		queryParams: ['sysparm_transaction_scope'],
		successActionType: MCP_CONTRACT_EVENT_CREATE_SUCCEEDED,
		errorActionType: MCP_CONTRACT_EVENT_CREATE_FAILED
	}
);

const macroponentContractEventCreateSucceeded = ({
	action,
	properties,
	updateProperties,
	dispatch
}) => {
	const { pageState } = properties;
	const { result: rawResult } = action.payload;
	const {
		screenEventMappingHandler,
		savePayload,
		saveMeta,
		context: propName
	} = action.meta;

	let result = rawResult;
	if (Array.isArray(rawResult)) {
		if (rawResult.length === 0) {
			return;
		}
		result = rawResult[0];
	}

	const {
		event_name: eventName,
		props,
		label,
		sys_id: id,
		'sys_scope.scope': scopeName
	} = result;
	const apiName = `${scopeName}.${eventName}`;
	let payloadShape;
	try {
		payloadShape = JSON.parse(props);
	} catch (_) {
		payloadShape = [];
	}
	const newEvent = new ElementEvent({
		id,
		eventName,
		payloadShape,
		label,
		apiName
	});

	const { eventHandlers = [] } = pageState;
	const isRelay = eventHandlers.find((handler) => {
		const { name } = formatEventNameAndLabel(handler, pageState.rootElement.id);
		return name === eventName;
	});

	const newEventHandler = new EventHandler({
		id,
		apiName,
		label,
		type: 'EVENT',
		sourceName: pageState.rootElement.instanceName,
		sourceId: pageState.rootElement.id,
		name: eventName,
		payloadFields: payloadShape.map((prop) => {
			return {
				...prop,
				type: prop.fieldType
			};
		}),
		relay: !!isRelay
	});

	const clonedPageState = cloneDeep(pageState);
	let updatedPageState = clonedPageState;

	if (screenEventMappingHandler) {
		const newScreenEventMapping = createScreenEventMapping(
			id,
			apiName,
			screenEventMappingHandler
		);
		updatedPageState.screenEventMappings.unshift(newScreenEventMapping);
		updatedPageState = createPageState(
			{
				screenEventMappings: updatedPageState.screenEventMappings
			},
			updatedPageState
		);
	}

	updatedPageState[propName].unshift(newEvent);
	const newPageState = createPageState(
		{
			[propName]: updatedPageState[propName],
			eventHandlers: [...updatedPageState.eventHandlers, newEventHandler]
		},
		updatedPageState
	);

	updateProperties({
		pageState: newPageState
	});

	savePayload &&
		dispatch(
			PAGE_SAVE_HTTP_REQUESTED,
			{
				...savePayload,
				pageStateModel: newPageState
			},
			{ ...saveMeta }
		);
};

const macroponentContractEventUpdated = {
	effect: () => null,
	interceptors: [
		(context) => {
			const inputAction = context.coeffects.action;
			// Remove any i18n debugger prefixes on save
			// and dont patch event_name
			delete inputAction.payload.event.event_name;
			const existingLabel = get(inputAction, 'payload.event.label', '');
			set(inputAction, 'payload.event.label', removePrefixes(existingLabel));

			const url = '/api/now/table/sys_ux_event/:eventId';
			const options = {
				method: 'PATCH',
				batch: false,
				dataParam: 'event',
				pathParams: ['eventId'],
				successActionType: MCP_CONTRACT_EVENT_UPDATE_SUCCEEDED,
				errorActionType: MCP_CONTRACT_EVENT_UPDATE_FAILED
			};

			return {
				...context,
				effects: [
					{
						...createHttpEffect(url, options, context.coeffects),
						args: [url, options, context.coeffects]
					}
				]
			};
		}
	]
};

const macroponentContractEventUpdateSucceeded = ({
	action,
	updateProperties,
	properties,
	dispatch
}) => {
	const {
		result: { label, props, event_name: eventName, sys_id: id }
	} = action.payload;
	let payloadShape;
	try {
		payloadShape = JSON.parse(props);
	} catch (_) {
		payloadShape = [];
	}

	const {
		screenEventMappingHandler,
		savePayload,
		saveMeta,
		context: propName
	} = action.meta || {};

	const { pageState = {} } = properties;
	const clonedEvents = cloneDeep(pageState[propName] || []);
	const clonedEventHandlers = cloneDeep(pageState.eventHandlers || []);
	const clonedPageState = cloneDeep(pageState);
	const updatedPageState = clonedPageState;

	clonedEvents.forEach((event) => {
		if (event.id === id) {
			event.label = label;
			event.payloadShape = payloadShape;
			event.eventName = eventName;
		}
	});

	clonedEventHandlers.forEach((handler) => {
		if (handler.id === id) {
			handler.label = label;
			handler.name = label;
			handler.payloadFields = payloadShape.map((prop) => ({
				...prop,
				type: prop.fieldType
			}));
		}
	});

	const screenEventMappingToUpdate = updatedPageState.screenEventMappings.find(
		(eventMapping) => eventMapping.eventId === id
	);
	screenEventMappingToUpdate &&
		screenEventMappingHandler &&
		screenEventMappingToUpdate.targets.forEach((target) => {
			const { targetKey, valueKey, idKey } = computeEventMappingTargetKeys(
				target.type
			);
			if (target[targetKey][idKey] === screenEventMappingHandler.id) {
				target[targetKey][valueKey].forEach((prop) => {
					if (!prop.value) {
						prop.value = `@payload.${prop.name}`;
					}
				});
			}
		});

	const newPageState = createPageState(
		{
			[propName]: clonedEvents,
			eventHandlers: clonedEventHandlers
		},
		updatedPageState
	);

	updateProperties({
		pageState: newPageState
	});

	savePayload &&
		dispatch(
			PAGE_SAVE_HTTP_REQUESTED,
			{
				...savePayload,
				pageStateModel: newPageState
			},
			{ ...saveMeta }
		);
};

const macroponentContractEventUpdateFailed = ({ action }) => {
	const failureMessage = get(action.payload, 'options.data.error.detail', '');
	throwError(`Failed to update event: ${failureMessage}`);
};

const macroponentContractEventDeleteRequested = createHttpEffect(
	'/api/now/table/sys_ux_event/:eventId',
	{
		method: 'DELETE',
		batch: false,
		pathParams: ['eventId']
	}
);

const macroponentContractEventDeleted = ({
	action,
	dispatch,
	properties,
	updateProperties
}) => {
	const { context: propName, eventId } = action.payload || {};
	const { pageState = {} } = properties;

	const clonedEventHandlers = cloneDeep(pageState.eventHandlers);
	const clonedEvents = cloneDeep(pageState[propName]);
	const filteredHandlers = clonedEventHandlers.filter(
		(handler) => handler.id !== eventId
	);
	const filteredEvents = clonedEvents.filter((event) => event.id !== eventId);

	updateProperties({
		pageState: createPageState(
			{
				eventHandlers: filteredHandlers,
				[propName]: filteredEvents
			},
			cloneDeep(pageState)
		)
	});

	dispatch(MCP_CONTRACT_EVENT_DELETE_REQUESTED, { eventId });
};

const macroponentContractEventCreateFailed = ({ action, dispatch }) => {
	const failureMessage = get(action.payload, 'options.data.error.detail', '');
	const messageMatcher = failureMessage.match(/.+?\((.+)\)/);
	const targetEventLabel = messageMatcher ? messageMatcher[1] : undefined;
	if (targetEventLabel) {
		dispatch(
			MCP_CONTRACT_EVENT_FETCH_REQUESTED,
			{
				sysparm_query: `label=${targetEventLabel}`
			},
			action.meta
		);
	}
};

const macroponentContractEventFetchRequested = createHttpEffect(
	'/api/now/table/sys_ux_event',
	{
		method: 'GET',
		queryParams: ['sysparm_query'],
		batch: false,
		successActionType: MCP_CONTRACT_EVENT_CREATE_SUCCEEDED
	}
);

export default {
	[MCP_CONTRACT_EVENT_CREATED]: macroponentContractEventCreated,
	[MCP_CONTRACT_EVENT_CREATE_SUCCEEDED]: macroponentContractEventCreateSucceeded,
	[MCP_CONTRACT_EVENT_CREATE_FAILED]: macroponentContractEventCreateFailed,
	[MCP_CONTRACT_EVENT_FETCH_REQUESTED]: macroponentContractEventFetchRequested,
	[MCP_CONTRACT_EVENT_UPDATED]: macroponentContractEventUpdated,
	[MCP_CONTRACT_EVENT_UPDATE_SUCCEEDED]: macroponentContractEventUpdateSucceeded,
	[MCP_CONTRACT_EVENT_UPDATE_FAILED]: macroponentContractEventUpdateFailed,
	[MCP_CONTRACT_EVENT_DELETED]: macroponentContractEventDeleted,
	[MCP_CONTRACT_EVENT_DELETE_REQUESTED]: macroponentContractEventDeleteRequested
};
