import transformMacroponentToPageState from '../page/utils/transformMacroponentToPageState';

export const addNewPageReceived = (
	received,
	pages,
	dataBrokerDefinitions,
	eventHandlers,
	state,
	variantId
) => {
	const pageState = transformMacroponentToPageState(
		received,
		dataBrokerDefinitions,
		eventHandlers,
		[], // empty client state params array on new page creation
		state,
		variantId
	);

	return [
		pageState,
		[
			...pages,
			{
				name: pageState.name,
				sysId: pageState.sysId,
				type: pageState.type,
				pageStatus: pageState.pageStatus,
				scope: pageState.scope,
				uiTypeParameters: pageState.uiTypeParameters,
				readOnly: pageState.readOnly,
				sysPolicy: pageState.sysPolicy,
				screenType: received.screenType
			}
		]
	];
};

export const replacePageWithOverrideReceived = (
	received,
	pages,
	dataBrokerDefinitions,
	eventHandlers,
	state,
	variantId
) => {
	const pageState = transformMacroponentToPageState(
		received,
		dataBrokerDefinitions,
		eventHandlers,
		[],
		state,
		variantId
	);
	const index = pages.findIndex((page) => page.sysId === variantId);

	return [
		pageState,
		[
			...pages.slice(0, index),
			{
				name: pageState.name,
				sysId: pageState.sysId,
				type: pageState.type,
				pageStatus: pageState.pageStatus,
				scope: pageState.scope,
				uiTypeParameters: pageState.uiTypeParameters,
				readOnly: pageState.readOnly,
				sysPolicy: pageState.sysPolicy,
				screenType: received.screenType
			},
			...pages.slice(index + 1)
		]
	];
};

export const pathParamsFromPayload = ({
	appId,
	appsSysId,
	pageSysId,
	variantSysId,
	name
}) => ({
	appsSysId: appId || appsSysId,
	pageSysId,
	variantSysId,
	name
});
