import get from 'lodash/get';
import set from 'lodash/set';
import map from 'lodash/map';
import uniq from 'lodash/uniq';
import trim from 'lodash/trim';
import isString from 'lodash/isString';
import isArray from 'lodash/isArray';
import { TRANSLATE_SHORTHAND_PATTERN } from './constants';

const extractStringSchema = (oneOf) => {
	let obj = {};
	map(oneOf, (item) => {
		if (item.type === 'string') {
			obj = item;
		}
	});
	return obj;
};

export const isSchemaTranslatable = (schema, value) => {
	if (!schema) {
		return false;
	}
	const { oneOf } = schema;

	if (oneOf && typeof value === 'string') {
		const stringSchema = get(extractStringSchema(oneOf), 'translatable');
		if (stringSchema) {
			return true;
		}
	}

	return JSON.stringify(schema).indexOf(`"translatable":true`) > -1;
};

const TRANSLATION_PREFIXES = [
	/TRF:(.*)/,
	/MSG:(.*)/,
	/GMLD:(.*)/,
	/TRT:(.*)/,
	/CHC:(.*)/
];

export const removePrefixes = (str) => {
	for (const prefix of TRANSLATION_PREFIXES) {
		const match = str.match(prefix);
		if (match) {
			return trim(match[1]);
		}
	}
	return str;
};

export const extractTranslatablePaths = (schema, arr = [], path = '') => {
	const { type, translatable, properties, oneOf, anyOf, items } = schema;

	if (type === 'string' && translatable) {
		path.length && arr.push(path);
		path = '';
	}

	if (type === 'object' && properties) {
		map(properties, (val, key) => {
			const newPath = path.length ? `${path}.${key}` : key;
			extractTranslatablePaths(val, arr, newPath);
		});
	}

	if (type === 'array') {
		isArray(items)
			? items.map((item) => extractTranslatablePaths(item, arr, path))
			: extractTranslatablePaths(items, arr, path);
	}

	if (isArray(oneOf)) {
		oneOf.map((obj) => extractTranslatablePaths(obj, arr, path));
	}

	if (isArray(anyOf)) {
		anyOf.map((obj) => extractTranslatablePaths(obj, arr, path));
	}

	path = '';
	return uniq(arr);
};

const translate = (str) => {
	if (str.match(TRANSLATE_SHORTHAND_PATTERN)) {
		return str;
	}
	return `translate(${str})`;
};

const translatePropbyPath = (shorthand, path) => {
	const prop = get(shorthand, path);
	if (typeof prop === 'string') {
		set(shorthand, path, translate(prop));
		return true;
	}
	return false;
};

export const translateKeys = (shorthand, translatablePaths) => {
	map(translatablePaths, (path) => {
		const translated = translatePropbyPath(shorthand, path);
		if (!translated && isString(path)) {
			const splitPath = path.split('.');
			let temp = '';
			splitPath.map((prop, index) => {
				const newPath = `${temp}${prop}`;
				const value = get(shorthand, newPath);
				if (isArray(value)) {
					const newPaths = [];
					value.map((item, i) => {
						const newSplit = [...splitPath];
						newSplit[index] = `${newSplit[index]}[${i}]`;
						newPaths.push(newSplit.join('.'));
					});
					translateKeys(shorthand, newPaths);
				} else {
					temp = `${newPath}.`;
				}
			});
		}
	});
	return shorthand;
};
