function reduceElements(elements) {
	return elements.reduce(
		(acc, { elementId, elementLabel, propertyValues, macroponentType }) => {
			return {
				...acc,
				[elementId]: {
					label: elementLabel,
					propertyValues,
					category: macroponentType
				}
			};
		},
		{}
	);
}

export default (result) => {
	const { ancestorElements = [], parentElements = [] } = result || {};

	const aggregatedElements = [...ancestorElements, ...parentElements];

	return {
		ancestorElements: reduceElements(aggregatedElements),
		parentElements: reduceElements(parentElements)
	};
};
