import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
export default function (pageState) {
	const rootElement = get(pageState, 'rootElement', {});
	if (isEmpty(rootElement)) {
		return;
	}

	return (rootElement.macroponentProperties || []).reduce((acc, prop) => {
		return {
			...acc,
			[prop.name]: prop.testValue || prop.value
		};
	}, {});
}
