import { MACROPONENT_CATEGORY, SCREEN_TEMPLATE_SYS_ID } from '../../constants';
import cloneDeep from 'lodash/cloneDeep';
import uniqBy from 'lodash/uniqBy';
import { getTemplates } from '@devsnc/library-uxf';
import transformMacroponentToPageState from '../page/utils/transformMacroponentToPageState';
import get from 'lodash/get';

const getValuesForDefaultProperties = (screenTypes, screenTypeId) => (
	columnName
) => {
	try {
		return screenTypes
			.find(({ id }) => id === screenTypeId)
			[columnName].split(',')
			.filter((prop) => prop)
			.map((prop) => String(prop).trim());
	} catch (e) {
		console.warn(
			`UXF Builder Middleware Warning: no default page properties were configured from ${columnName}`
		);
		return [];
	}
};

const setupDefaultProperties = (screenTypes, screenTypeId) => {
	const getValuesFrom = getValuesForDefaultProperties(
		screenTypes,
		screenTypeId
	);
	const screenTypeFieldsAndParams = [
		...getValuesFrom('fields'),
		...getValuesFrom('optionalParameters')
	];

	const defaultProperties = uniqBy(
		screenTypeFieldsAndParams.map((name) => ({ name })),
		'name'
	);
	const defaultPropertyValues = screenTypeFieldsAndParams.reduce(
		(acc, prop) => {
			acc[prop] = '';
			return acc;
		},
		{}
	);

	return { defaultProperties, defaultPropertyValues };
};

const createLandingPage = (
	pageInformation,
	composition,
	layout,
	properties,
	propertyValues,
	dispatch,
	httpAction
) => {
	const {
		name,
		scope,
		sysPolicy,
		applicability: { applications },
		copyFrom //TODO: add support to copy from a given page sysId in the backend
	} = pageInformation;
	const applicablePage = (applications.length >= 1 && applications[0]) || '';
	const applicabilityType = applicablePage ? 'single' : 'any';

	dispatch(httpAction, {
		pageEndpoint: 'ws_landing_pages',
		sysparm_transaction_scope: scope,
		data: {
			isActive: false,
			applicability: { type: applicabilityType, applicablePage },
			macroponentInstance: {
				macroponent: {
					name,
					sysPolicy,
					composition,
					layout,
					properties,
					extendsSysId: SCREEN_TEMPLATE_SYS_ID,
					category: MACROPONENT_CATEGORY
				},
				propertyValues
			},
			copyFrom
		}
	});
};

const createScreen = (
	pageInformation,
	composition,
	layout,
	properties,
	propertyValues,
	dispatch,
	httpAction
) => {
	const {
		applicability: { appConfigId, screenTypeId },
		name,
		scope,
		sysPolicy,
		order,
		screenCondition,
		copyFrom //TODO: add support to copy from a given page sysId in the backend
	} = pageInformation;

	dispatch(httpAction, {
		pageEndpoint: 'screens',
		sysparm_transaction_scope: scope,
		data: {
			appConfig: appConfigId,
			screenType: screenTypeId,
			name,
			order,
			screenCondition,
			isActive: false,
			macroponentInstance: {
				macroponent: {
					name,
					sysPolicy,
					composition,
					layout,
					properties,
					extendsSysId: SCREEN_TEMPLATE_SYS_ID,
					category: MACROPONENT_CATEGORY
				},
				propertyValues
			},
			copyFrom
		}
	});
};

const pageCreateStarted = ({ updateProperties }) =>
	updateProperties({ isSavingPage: true });

const pageCreateSucceeded = ({
	action,
	state,
	properties,
	updateProperties
}) => {
	const { dataBrokerDefinitions, eventHandlers, pages } = properties;
	const variantId = get(properties, 'pageState.sysId', '');

	const {
		result: {
			macroponentInstance: { rootMacroponentId }
		}
	} = action.payload;

	// Load component scripts dynamically
	// This is async, but shouldn't matter if the scripts are loaded after
	// the component tags have been rendered in the html
	getTemplates([rootMacroponentId]);

	const pageState = transformMacroponentToPageState(
		action.payload.result,
		dataBrokerDefinitions,
		eventHandlers,
		[], // empty client state params array on new page creation
		state,
		variantId
	);
	const pageStatePristine = cloneDeep(pageState);

	updateProperties({
		isLoadingPage: false,
		isSavingPage: false,
		isPageReadOnly: pageState.readOnly && !!pageState.sysPolicy,
		isPageCopiable: pageState.canCopy,
		pageState,
		pageStatePristine,
		clientScriptsPristine: [],
		pages: pages.concat({
			name: pageState.name,
			sysId: pageState.sysId,
			type: pageState.type,
			pageStatus: pageState.pageStatus,
			scope: pageState.scope,
			uiTypeParameters: pageState.uiTypeParameters
		})
	});
};

const pageCreateFailed = ({ updateProperties }) => {
	updateProperties({
		isLoadingPage: false,
		isSavingPage: false
	});
};

export {
	setupDefaultProperties,
	createLandingPage,
	createScreen,
	pageCreateStarted,
	pageCreateSucceeded,
	pageCreateFailed
};
