import createElementArray from '../element/utils/createElementArray';
import get from 'lodash/get';
import cloneDeep from 'lodash/cloneDeep';
import reject from 'lodash/reject';
import { EVENT, DATABROKER_OP } from './constants';
import { UPDATE_CLIENT_STATE_HANDLER_ID } from '../../constants';
import { getProperty } from '../componentList/utils';
import { createExposedProperty, getDependentProperties } from '../utils';
import { computeEventMappingTargetKeys } from '../page/utils/shared';
import { EventMapping, EventMappingTarget } from '../../models';
import { seismicCuid } from '../../utils';

export const formatEventNameAndLabel = (source, rootId, screenName) => {
	if (source.type === DATABROKER_OP) {
		return {
			name: `${source.sourceId.toUpperCase()}_${source.name}_RELAY_${rootId}`,
			label: `${source.sourceName} ${source.label} Relay (${screenName})`
		};
	} else {
		return {
			name: `${source.name}_RELAY_${rootId}`,
			label: `${source.label} Relay (${screenName})`
		};
	}
};

const getInvalidTargetsByType = (target, parentEventHandler) => {
	if (target.type === EVENT) {
		return !!(
			get(target, 'event.sysId', null) === parentEventHandler.id ||
			get(target, 'event.apiName', null) === parentEventHandler.apiName
		);
	} else if (target.type === DATABROKER_OP) {
		if (
			get(target, 'operation.parentResourceId', null) ===
				parentEventHandler.sourceId &&
			get(target, 'operation.dataBrokerId', null) === parentEventHandler.id &&
			get(target, 'operation.operationName', null) === parentEventHandler.name
		) {
			return true;
		}
		return false;
	} else {
		return false;
	}
};

export const addInvalidHandlers = (
	eventMappings,
	parentEventHandlers,
	typeKey,
	typeValue,
	currentInvalidHandlers = []
) => {
	eventMappings.forEach((eventMapping) => {
		eventMapping.targets.forEach((target) => {
			parentEventHandlers.forEach((parentEventHandler) => {
				if (getInvalidTargetsByType(target, parentEventHandler)) {
					const invalidValue = {
						eventMappingId: eventMapping.eventMappingId,
						target,
						parentEventHandler,
						[typeKey]: typeValue
					};
					currentInvalidHandlers.push(invalidValue);
				}
			});
		});
	});
	return currentInvalidHandlers;
};

export const getInvalidEventMappingTargets = (
	rootElement,
	parentEventHandlers = [],
	dataBrokerInstances
) => {
	const { internalEventMappings } = rootElement;
	const elementArr = createElementArray(rootElement, (element) => ({
		id: element.id,
		eventMappings: element.eventMappings
	}));
	const invalidHandlers = [];
	elementArr.forEach((el) => {
		el.eventMappings &&
			addInvalidHandlers(
				el.eventMappings,
				parentEventHandlers,
				'elementId',
				el.id,
				invalidHandlers
			);
	});
	dataBrokerInstances.forEach((db) => {
		db.eventMappings &&
			addInvalidHandlers(
				db.eventMappings,
				parentEventHandlers,
				'dataBrokerId',
				db.id,
				invalidHandlers
			);
	});
	internalEventMappings &&
		internalEventMappings.length > 0 &&
		addInvalidHandlers(
			internalEventMappings,
			parentEventHandlers,
			'rootElementId',
			rootElement.id,
			invalidHandlers
		);
	return invalidHandlers;
};

export const dispatchedEventAndParentHandlerPayloadsCongruent = (
	existingDispatchedEvent,
	parentEventHandler
) => {
	const { payloadFields: parentEventHandlerPayloadDef } = parentEventHandler;
	const {
		payloadShape: existingDispatchedEventPayloadDef
	} = existingDispatchedEvent;
	return (
		parentEventHandlerPayloadDef.length ===
			existingDispatchedEventPayloadDef.length &&
		parentEventHandlerPayloadDef.every(({ name }) =>
			existingDispatchedEventPayloadDef.find((prop) => name === prop.name)
		)
	);
};

export const getReducedHandlerList = (
	invalidEventMappingTargets,
	existingDispatchedEvents,
	rootMacroponentId,
	invalidPredicateFn
) => {
	const handlerList = [];
	invalidEventMappingTargets.forEach((target) => {
		const { name } = formatEventNameAndLabel(
			target.parentEventHandler,
			rootMacroponentId
		);
		const existingDispatchedEvent = existingDispatchedEvents.find(
			(event) => event.eventName === name
		);
		if (
			invalidPredicateFn(
				existingDispatchedEvent,
				target.parentEventHandler,
				handlerList
			)
		) {
			handlerList.push({
				...target.parentEventHandler,
				existingDispatchedEvent
			});
		}
	});
	return handlerList;
};

export const getReducedInvalidHandlerList = (
	invalidEventMappingTargets,
	existingDispatchedEvents,
	rootMacroponentId
) => {
	return getReducedHandlerList(
		invalidEventMappingTargets,
		existingDispatchedEvents,
		rootMacroponentId,
		function (existingDispatchedEvent, targetParentEventHandler, handlerList) {
			return (
				!existingDispatchedEvent &&
				!handlerList.find(
					(eventHandler) => eventHandler.id === targetParentEventHandler.id
				)
			);
		}
	);
};

export const getReducedIncongruentHandlerList = (
	invalidEventMappingTargets,
	existingDispatchedEvents,
	rootMacroponentId
) => {
	return getReducedHandlerList(
		invalidEventMappingTargets,
		existingDispatchedEvents,
		rootMacroponentId,
		function (existingDispatchedEvent, targetParentEventHandler, handlerList) {
			return (
				existingDispatchedEvent &&
				!dispatchedEventAndParentHandlerPayloadsCongruent(
					existingDispatchedEvent,
					targetParentEventHandler
				) &&
				!handlerList.find(
					(eventHandler) => eventHandler.id === targetParentEventHandler.id
				)
			);
		}
	);
};

export const getNewRootElementAndElementToUpdate = (
	pageState,
	elementId = null
) => {
	const newRootElement = cloneDeep(pageState.rootElement);
	const elements = createElementArray(newRootElement, (element) => element);
	const elementToUpdate = elements.find((el) => el.id === elementId);

	return {
		newRootElement,
		elementToUpdate
	};
};

export const createEventMapping = (eventId, eventApiName) => {
	return new EventMapping({
		eventMappingId: seismicCuid(),
		eventId,
		eventApiName,
		targets: [new EventMappingTarget({})]
	});
};

export const createEventMappingHandler = (mappings, mappingId) => {
	const eventMappingToUpdate =
		mappings.find((mapping) => mapping.eventMappingId === mappingId) || {};
	eventMappingToUpdate.targets &&
		eventMappingToUpdate.targets.push(new EventMappingTarget({}));
	return eventMappingToUpdate;
};

export const deleteEventMappingHandler = (
	instance,
	mappings,
	mappingId,
	handlerId
) => {
	const eventMappingToUpdate =
		mappings.find((mapping) => mapping.eventMappingId === mappingId) || {};
	const { targets = [] } = eventMappingToUpdate;
	eventMappingToUpdate.targets = reject(targets, { targetId: handlerId });

	// if there was only one target for this event mapping, delete the whole mapping
	if (eventMappingToUpdate.targets.length === 0) {
		instance.eventMappings = reject(instance.eventMappings, {
			eventMappingId: mappingId
		});
	}
	return instance;
};

export const updateEventMappingHandler = (
	handlerToUpdate,
	eventHandlers,
	handlerTypeId,
	handlerValueId,
	value,
	scripted,
	clientStateParameters
) => {
	if (handlerTypeId) {
		const [typeId, instanceId, operationName] = handlerTypeId.split(':');
		const selectedHandler = operationName
			? eventHandlers.find(
					({ id, name, sourceId }) =>
						id === typeId && name === operationName && sourceId === instanceId
			  )
			: eventHandlers.find(({ id }) => id === typeId);
		const eventMappingHandlerType = selectedHandler.type;
		handlerToUpdate.type = eventMappingHandlerType;

		const {
			targetKey,
			valueKey,
			idKey,
			nonTargetKeys
		} = computeEventMappingTargetKeys(eventMappingHandlerType);

		handlerToUpdate[targetKey] = {
			[idKey]: typeId,
			[valueKey]: selectedHandler.payloadFields.map((field) =>
				getProperty({ ...field, fieldType: field.type })
			)
		};

		if (eventMappingHandlerType === EVENT) {
			handlerToUpdate[targetKey].apiName = selectedHandler.apiName;
		}

		if (eventMappingHandlerType === DATABROKER_OP) {
			handlerToUpdate[targetKey].operationName = selectedHandler.name;
			handlerToUpdate[targetKey].parentResourceId = selectedHandler.sourceId;
		}

		nonTargetKeys.forEach((key) => (handlerToUpdate[key] = null));
	} else if (handlerValueId) {
		// Changes one of the values for an event mapping handler
		let targetValueToUpdate = {};
		const eventMappingHandlerType = handlerToUpdate.type;

		const { targetKey, valueKey } = computeEventMappingTargetKeys(
			eventMappingHandlerType
		);
		targetValueToUpdate = handlerToUpdate[targetKey][valueKey].find(
			(actionValue) => actionValue.columnName === handlerValueId
		);

		const values = handlerToUpdate[targetKey][valueKey].map((value) =>
			createExposedProperty(value)
		);
		const propertyToUpdate = values.find((prop) => prop.id === handlerValueId);

		const dependees = getDependentProperties(propertyToUpdate, values);
		if (dependees.length) {
			// If there are dependent properties, clear out those property values.
			dependees.forEach((dependee) => {
				const { id } = dependee;
				const dependentTargetValue = handlerToUpdate[targetKey][valueKey].find(
					(value) => value.columnName === id
				);
				dependentTargetValue.value = null;
			});
		}

		targetValueToUpdate.value = value;

		if (
			handlerToUpdate.event &&
			handlerToUpdate.event.sysId === UPDATE_CLIENT_STATE_HANDLER_ID
		) {
			const {
				type: paramType,
				name: paramName,
				initialValue: paramInitialValue
			} = clientStateParameters.find((param) => param.name === value) || {};
			handlerToUpdate.event.payload.forEach((prop) => {
				if (prop.name === 'value' && paramName) {
					prop.type = paramType || 'string';
					prop.value = paramInitialValue;
				}
			});
		}
	} else if (scripted) {
		handlerToUpdate.clientTransformScript = value;
	}

	return handlerToUpdate;
};
