import { seismicCuid, throwError } from '../../../utils';
import Property from '../../../models/property';
import Element from '../../../models/element';
import flatMap from 'lodash/flatMap';
import get from 'lodash/get';
import set from 'lodash/set';

import isNil from 'lodash/isNil';
import isNaN from 'lodash/isNaN';
import { flattenLayout } from './layout';
import createElementArray from './createElementArray';

const componentUsesSNLayout = (component) => {
	return component.slots.indexOf('@default') > -1;
};
function elementUsesSNLayout(element) {
	return componentUsesSNLayout(element.component);
}

function getRootElementFromState(state) {
	return state.properties.pageState.rootElement;
}

function updateRootElementInState(state, newRootElement) {
	state.properties.pageState.rootElement = newRootElement;
}

function findElementById(element, elementId) {
	try {
		return findElements(element, (el) => el.id === elementId)[0];
	} catch (e) {
		return null;
	}
}

function findElements(element, callback) {
	if (typeof callback !== 'function') {
		throwError('Callback must be a function', TypeError);
	}

	const matchingElements = [];
	if (callback.call(null, element)) {
		matchingElements.push(element);
	}

	return matchingElements.concat(
		flatMap(element.children, (child) => findElements(child, callback))
	);
}

function findParentElement(element, elementId) {
	for (const child of element.children) {
		if (child.id === elementId) {
			return element;
		} else {
			const found = findParentElement(child, elementId);

			if (found) {
				return found;
			}
		}
	}

	return null;
}

function updateStaticProperty(element, changes) {
	for (const change in changes) {
		if (element.properties[change]) {
			element.properties[change].value = changes[change];
		}
	}
}

function updateDynamicProperty(element, prop, charterId, charterProp) {
	element.properties[prop].isStatic = !element.properties[prop].isStatic;
	if (charterId) {
		element.properties[prop].value = {
			charterId,
			charterProp
		};
	} else {
		element.properties[prop].value = null;
	}
}
function typedValue(type, value) {
	if (isNil(value)) {
		return null;
	}
	switch (type) {
		case 'boolean':
			if (typeof value === 'boolean') {
				return value;
			} else {
				return value.toString().toLowerCase() === 'true';
			}
		case 'number':
			if (typeof value === 'number') {
				return value;
			} else {
				const pv = parseFloat(value);
				return isNaN(pv) ? null : pv;
			}
		case 'string':
			return typeof value === 'string' ? value : '';
		default:
			return value;
	}
}

function createElementPropertiesFromComponent(component) {
	return component.properties.reduce((acc, propertyConfig) => {
		const { type, readOnly, columnName, defaultValue } = propertyConfig;
		let { value } = propertyConfig;

		if (readOnly) {
			value = isNil(value)
				? isNil(defaultValue)
					? null
					: defaultValue
				: typedValue(type, value);
		} else {
			value = typedValue(type, value);
		}
		const newProperty = new Property({
			...propertyConfig,
			value
		});

		acc[columnName] = newProperty;
		return acc;
	}, {});
}

function getAllElementIds(rootElement) {
	return createElementArray(rootElement, (element) => ({
		id: element.id
	})).map((el) => el.id);
}

function createElementFromComponent(
	component,
	parentElement,
	isModal = false,
	defaultLayout
) {
	let layout;
	const id = seismicCuid();
	if (componentUsesSNLayout(component)) {
		layout = defaultLayout;
	}

	// DEF0162800: If container, make sure it has 100% width
	const tagName = component.tag;
	const isContainer =
		tagName === 'now-uxf-page-simple-container' ||
		tagName === 'now-resizable-panes' ||
		tagName === 'now-uxf-page-container';
	return new Element({
		id,
		component,
		isModal,
		layout,
		isModalDescendant: parentElement.isModal || parentElement.isModalDescendant,
		properties: createElementPropertiesFromComponent(component),
		styles: isContainer ? { width: '100%' } : undefined
	});
}

function deleteChildElement(parentElement, deletedElementId) {
	return set(
		parentElement,
		'children',
		parentElement.children.filter((child) => child.id !== deletedElementId)
	);
}

function isOnlySlottedChild(elementId, rootElement) {
	const targetElement = findElementById(rootElement, elementId);
	const targetParentElement = findParentElement(rootElement, elementId);
	for (const i in get(targetParentElement, 'children', [])) {
		if (
			get(targetParentElement, `children[${i}].slot.name`) ===
				get(targetElement, 'slot.name') &&
			targetParentElement.children[i].id !== targetElement.id
		) {
			return false;
		}
	}
	return true;
}

function getEmptyLayoutSlots(containerElement) {
	const rootLayoutNode = get(containerElement, 'layout.default.root', null);
	if (!rootLayoutNode) {
		return [];
	}
	const flattenedLayout = flattenLayout(rootLayoutNode);

	const layoutSlots = Object.keys(flattenedLayout).reduce(
		(acc, layoutNodeId) => {
			const slotName = flattenedLayout[layoutNodeId].slotName;
			slotName && acc.push({ slot: slotName, id: layoutNodeId });
			return acc;
		},
		[]
	);

	return layoutSlots.reduce((acc, slotObj) => {
		const slottedElements = findElements(
			containerElement,
			(element) => element.slot && element.slot.name === slotObj.slot
		);
		if (!slottedElements.length) {
			acc.push(slotObj);
		}
		return acc;
	}, []);
}

const getParentLayoutType = (parentElement) =>
	get(parentElement, 'layout.default.type', 'rowColumn');

export {
	elementUsesSNLayout,
	createElementFromComponent,
	createElementPropertiesFromComponent,
	deleteChildElement,
	getRootElementFromState,
	findElements,
	findElementById,
	findParentElement,
	updateRootElementInState,
	updateStaticProperty,
	updateDynamicProperty,
	isOnlySlottedChild,
	getEmptyLayoutSlots,
	getParentLayoutType,
	getAllElementIds
};
