import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	NEW_PAGE_VARIANT_DELETE_REQUEST,
	NEW_PAGE_VARIANT_DELETE_STARTED,
	NEW_PAGE_VARIANT_DELETE_SUCCEEDED,
	NEW_PAGE_VARIANT_DELETE_FAILED,
	NEW_PAGE_VARIANT_DELETE_REQUEST_HTTP
} from './newPageActions';
import { pathParamsFromPayload } from './newPageUtils';

const newVariantDeletedHandler = ({ action, dispatch }) => {
	const {
		payload: { name, variantSysId, pageSysId, appId }
	} = action;

	dispatch(
		NEW_PAGE_VARIANT_DELETE_REQUEST_HTTP,
		{
			name,
			variantSysId,
			pageSysId,
			appId
		},
		pathParamsFromPayload(action.payload)
	);
};
const newPageVariantDeleteStarted = ({ updateProperties }) => {
	updateProperties({ isSavingPage: true });
};

const newVariantDeletedHttpHandler = createHttpEffect(
	'/api/now/builder/apps/:appId/pages/:pageSysId/variants/:variantSysId',
	{
		method: 'DELETE',
		pathParams: ['appId', 'pageSysId', 'variantSysId'],
		startActionType: NEW_PAGE_VARIANT_DELETE_STARTED,
		successActionType: NEW_PAGE_VARIANT_DELETE_SUCCEEDED,
		errorActionType: NEW_PAGE_VARIANT_DELETE_FAILED,
		batch: false
	}
);

const newPageVariantDeleteSucceeded = ({
	action,
	properties,
	updateProperties
}) => {
	const {
		meta: { deleteSysId }
	} = action;

	const pages = properties.pages.filter(({ sysId }) => sysId !== deleteSysId);

	updateProperties({
		pages,
		isSavingPage: false,
		pageState: null,
		pageStatePristine: null,
		isPageStateDirty: false
	});
};
const newPageVariantDeleteFailed = ({ updateProperties }) => {
	updateProperties({ isSavingPage: false, isLoadingPage: false });
};

export default {
	[NEW_PAGE_VARIANT_DELETE_REQUEST]: newVariantDeletedHandler,
	[NEW_PAGE_VARIANT_DELETE_REQUEST_HTTP]: newVariantDeletedHttpHandler,
	[NEW_PAGE_VARIANT_DELETE_STARTED]: newPageVariantDeleteStarted,
	[NEW_PAGE_VARIANT_DELETE_SUCCEEDED]: newPageVariantDeleteSucceeded,
	[NEW_PAGE_VARIANT_DELETE_FAILED]: newPageVariantDeleteFailed
};
