import { createHttpEffect } from '@servicenow/ui-effect-http';
import get from 'lodash/get';
import { Action, Component, ElementEvent } from '../../models';
import effectProxy from '../helpers/effectProxy';
import {
	COMPONENT_LIST_ALL_REQUESTED,
	COMPONENT_LIST_REQUESTED,
	COMPONENT_LIST_REQUEST_FAILED,
	COMPONENT_LIST_REQUEST_STARTED,
	COMPONENT_LIST_REQUEST_SUCCEEDED,
	COMPONENT_LIST_TYPE_REQUESTED
} from './componentListActions';
import { getProperty } from './utils';
const COMPONENT_STYLIZED_CONTAINER = '80653115e23949b501e719ae9b0b42a6';
const excludedComponents = [COMPONENT_STYLIZED_CONTAINER];

const componentListRequested = ({ action, dispatch }) => {
	const pageType = get(action, 'payload.pageType', null);

	if (pageType) {
		dispatch(COMPONENT_LIST_TYPE_REQUESTED, { pageType });
	} else {
		dispatch(COMPONENT_LIST_ALL_REQUESTED);
	}
};

export const typeComponentListRequested = createHttpEffect(
	'/api/now/builder/toolbox_components/:pageType',
	{
		method: 'GET',
		pathParams: ['pageType'],
		startActionType: COMPONENT_LIST_REQUEST_STARTED,
		errorActionType: COMPONENT_LIST_REQUEST_FAILED,
		successActionType: COMPONENT_LIST_REQUEST_SUCCEEDED,
		batch: false
	}
);

const allComponentListRequested = createHttpEffect(
	'/api/now/builder/toolbox_components',
	{
		method: 'GET',
		startActionType: COMPONENT_LIST_REQUEST_STARTED,
		errorActionType: COMPONENT_LIST_REQUEST_FAILED,
		successActionType: COMPONENT_LIST_REQUEST_SUCCEEDED,
		batch: false
	}
);

const fetchComponentData = (componentsResponse) => {
	if (!Array.isArray(componentsResponse)) {
		return [];
	}
	const components = [];
	componentsResponse.forEach(
		({
			macroponent: {
				id,
				name: macroponentName,
				category,
				properties = [],
				dispatchedEvents = [],
				rootElementDefinition: { availableSlots = [] } = {
					availableSlots: []
				},
				explodable = false
			},
			label,
			description = '',
			isEditable = false,
			externalConfigurationSources = [],
			icon,
			actions = [],
			rootComponentTag
		}) => {
			const propertyInstances = properties.map((property) => {
				return getProperty(property);
			});
			const actionInstances = actions.map(
				(action) => new Action({ name: action.name })
			);
			const eventInstances = dispatchedEvents.map((ev) => new ElementEvent(ev));
			const tag = rootComponentTag || `macroponent-${id}`;
			const componentInstance = new Component({
				id,
				name: label || macroponentName,
				description,
				icon,
				category,
				tag,
				externalConfigurationSources,
				actions: actionInstances,
				slots: availableSlots,
				properties: propertyInstances,
				dispatchedEvents: eventInstances,
				explodable,
				isEditable
			});

			components.push(componentInstance);
		}
	);

	return components;
};

const componentListRequestStarted = ({ updateProperties }) =>
	updateProperties({ isLoadingComponents: true });

const componentListRequestFailed = ({ updateProperties }) =>
	updateProperties({ isLoadingComponents: false });

const transformToolboxComponent = ({
	name,
	icon,
	id,
	category,
	description,
	externalConfigurationSources,
	isEditable,
	tag
}) => ({
	name,
	icon,
	id,
	category,
	description,
	externalConfigurationSources,
	isEditable,
	tag
});

const componentListRequestSucceeded = ({
	action,
	updateProperties,
	updateState,
	properties
}) => {
	const rawComponentList = fetchComponentData(action.payload.result);
	const { isLegacy } = properties;
	const filteredComponentList = isLegacy
		? rawComponentList.map(transformToolboxComponent)
		: rawComponentList
				.map(transformToolboxComponent)
				.filter(({ id }) => excludedComponents.indexOf(id) === -1);

	updateProperties({
		components: filteredComponentList,
		isLoadingComponents: false
	});
	updateState({
		path: 'rawComponentList',
		value: rawComponentList,
		operation: 'set'
	});
};

export default {
	[COMPONENT_LIST_REQUESTED]: componentListRequested,
	[COMPONENT_LIST_ALL_REQUESTED]: allComponentListRequested,
	[COMPONENT_LIST_TYPE_REQUESTED]: effectProxy(typeComponentListRequested),
	[COMPONENT_LIST_REQUEST_STARTED]: componentListRequestStarted,
	[COMPONENT_LIST_REQUEST_FAILED]: componentListRequestFailed,
	[COMPONENT_LIST_REQUEST_SUCCEEDED]: componentListRequestSucceeded
};
