import createContextBindings from './createContextBindings';
import { isContextBoundProperty, isDataBoundProperty } from '../../utils';
import { CONTEXT_NAMESPACE } from '../../../constants';

function isContextPropBinding(value) {
	const matcher = isContextBoundProperty(value);
	if (matcher && matcher[2] === 'props') {
		return matcher;
	}
	return null;
}

function isContextDataBinding(value) {
	const matcher = isContextBoundProperty(value);
	if (matcher && matcher[2] === 'data') {
		return matcher;
	}
	return null;
}

function iterateAndResolveValues(properties, name, value, onIntermediateValue) {
	let next = true,
		resolvedValue = value;
	while (next) {
		const contextMatcher = isContextPropBinding(resolvedValue);
		const dataMatcher = isContextDataBinding(resolvedValue);
		if (contextMatcher) {
			const pathSplit = contextMatcher[3].slice(1).split('.');
			const targetProp = pathSplit.shift();
			if (targetProp === name) {
				const parentKey = `${targetProp}.parent`;
				resolvedValue = properties[parentKey] || resolvedValue;
				onIntermediateValue(parentKey);
				break;
			}
			const targetValue = properties[targetProp];
			targetValue && onIntermediateValue(targetProp);
			next = !!targetValue;
			resolvedValue = !next ? resolvedValue : targetValue;
			resolvedValue +=
				next && pathSplit.length > 0 ? `.${pathSplit.join('.')}` : '';
		} else if (dataMatcher) {
			const dataExpression = resolvedValue.substring(
				`${CONTEXT_NAMESPACE}`.length + 1
			);
			const dataMatcher = isDataBoundProperty(`@${dataExpression}`);
			resolvedValue = dataMatcher ? dataMatcher[0] : resolvedValue;
			next = false;
		} else {
			next = false;
		}
	}
	return resolvedValue;
}

function resolveAncestorPropertyValues(properties) {
	const intermediateProps = [];
	const resolvedProps = Object.entries(properties).reduce(
		(acc, [propName, propValue]) => {
			if (!isContextBoundProperty(propValue)) {
				return {
					...acc,
					[propName]: propValue
				};
			}
			const resolvedValue = iterateAndResolveValues(
				properties,
				propName,
				propValue,
				(intermediateProp) => {
					intermediateProps.push(intermediateProp);
				}
			);
			return {
				...acc,
				[propName]: resolvedValue
			};
		},
		{}
	);
	intermediateProps.forEach((prop) => delete resolvedProps[prop]);
	return resolvedProps;
}

function mergePropertyValues(
	ancestorPageProperties = {},
	localPageProperties = {}
) {
	return Object.entries(ancestorPageProperties).reduce((acc, [name, value]) => {
		if (acc[name]) {
			return {
				...acc,
				[`${name}.parent`]: value
			};
		}
		return {
			...acc,
			[name]: value
		};
	}, localPageProperties);
}

export default function (pageState, ancestorPageProperties) {
	const allPageProperties = mergePropertyValues(
		ancestorPageProperties,
		createContextBindings(pageState)
	);
	return resolveAncestorPropertyValues(allPageProperties);
}
