import {
	CLIENT_STATE_PARAMETER_CREATED,
	CLIENT_STATE_PARAMETER_DELETED,
	CLIENT_STATE_PARAMETER_CHANGED
} from './clientStateActions';
import { ClientStateParameter } from '../../models';
import createPageState from '../../models/createPageState';
import { seismicCuid } from '../../utils';
import reject from 'lodash/reject';
import cloneDeep from 'lodash/cloneDeep';

const clientStateParameterCreated = ({ properties, updateProperties }) => {
	const { pageState } = properties;
	const newClientStateParameter = new ClientStateParameter({
		id: seismicCuid()
	});
	updateProperties({
		pageState: createPageState(
			{
				clientStateParameters: [
					...pageState.clientStateParameters,
					newClientStateParameter
				]
			},
			pageState
		)
	});
};

const clientStateParameterDeleted = ({
	action,
	properties,
	updateProperties
}) => {
	const { id } = action.payload;
	const { pageState } = properties;
	const newClientStateParameters = reject(pageState.clientStateParameters, {
		id: id
	});

	updateProperties({
		pageState: createPageState(
			{
				clientStateParameters: [...newClientStateParameters]
			},
			pageState
		)
	});
};

const clientStateParameterChanged = ({
	action,
	properties,
	updateProperties
}) => {
	const { id, value, context } = action.payload;
	const { pageState } = properties;
	const newClientStateParams = cloneDeep(pageState.clientStateParameters);

	const paramToUpdate = newClientStateParams.find((param) => param.id === id);
	let fieldToUpdate;
	switch (context) {
		case 'param-name':
			fieldToUpdate = 'name';
			break;
		case 'param-type':
			fieldToUpdate = 'type';
			break;
		case 'param-value':
			fieldToUpdate = 'initialValue';
			break;
	}
	if (fieldToUpdate) {
		if (
			fieldToUpdate === 'initialValue' &&
			paramToUpdate.type === 'number' &&
			typeof value === 'string' &&
			!value.startsWith('@')
		) {
			paramToUpdate[fieldToUpdate] = Number(value);
		} else {
			paramToUpdate[fieldToUpdate] = value;
		}

		if (fieldToUpdate === 'type') {
			paramToUpdate.initialValue = value === 'boolean' ? false : null;
		}
	}

	updateProperties({
		pageState: createPageState(
			{
				clientStateParameters: newClientStateParams
			},
			pageState
		)
	});
};

export default {
	[CLIENT_STATE_PARAMETER_CREATED]: clientStateParameterCreated,
	[CLIENT_STATE_PARAMETER_DELETED]: clientStateParameterDeleted,
	[CLIENT_STATE_PARAMETER_CHANGED]: clientStateParameterChanged
};
