import {
	ELEMENT_METADATA_UPDATE_REQUESTED,
	ELEMENT_ID_CHANGED,
	VIEWPORT_NAME_CHANGED
} from './elementActions';
import { Element } from '../../models';
import createElementArray from './utils/createElementArray';
import createPageState from '../../models/createPageState';
import cloneDeep from 'lodash/cloneDeep';
import { ELEMENT_NAMESPACE } from '../../constants';
import {
	removeEventReferencesToPreviousId,
	removePropertyReferencesToPreviousId
} from '../page/utils/shared';

const elementMetadataUpdatedHandler = ({
	action,
	properties,
	updateProperties,
	dispatch
}) => {
	const { pageState = {} } = properties;
	const { previousId, changes = {} } = action.payload;
	const newRootElement = cloneDeep(pageState.rootElement);
	const elements = createElementArray(newRootElement, (element) => element);
	const updateElement = elements.find((el) => el.id === previousId) || {};

	if (changes.instanceName) {
		updateElement.instanceName = changes.instanceName;

		if (
			updateElement.component &&
			updateElement.component.category === 'viewport'
		) {
			dispatch(VIEWPORT_NAME_CHANGED, {
				id: previousId,
				value: changes.instanceName
			});
		}
	}

	if (changes.id) {
		updateElement.id = changes.id;
		removePropertyReferencesToPreviousId(
			previousId,
			changes.id,
			elements,
			[], // elements can't be referenced in data broker properties
			[], // elements can't be referenced in client state parameters
			ELEMENT_NAMESPACE
		);
		removeEventReferencesToPreviousId(
			previousId,
			changes.id,
			elements,
			[], // event handlers can't reference element IDs
			[], // data broker event mappings can't reference element IDs
			[], // internal event mappings can't reference element IDs
			ELEMENT_NAMESPACE
		);
		dispatch(ELEMENT_ID_CHANGED, { id: changes.id });
	}

	updateProperties({
		pageState: createPageState(
			{
				rootElement: new Element(newRootElement)
			},
			pageState
		)
	});
};

export default {
	[ELEMENT_METADATA_UPDATE_REQUESTED]: elementMetadataUpdatedHandler
};
