import {
	DASHBOARDS_DELETE_REQUEST,
	DASHBOARDS_DELETE_REQUEST_HTTP,
	DASHBOARDS_DELETE_REQUEST_SUCCEEDED,
	DASHBOARDS_DELETE_REQUEST_FAILED,
	DASHBOARDS_DELETE_REQUEST_STARTED
} from './dashboardsActions';
import { createHttpEffect } from '@servicenow/ui-effect-http';

const dashboardsDeleteRequest = ({ state, action, dispatch }) => {
	const {
		properties: {
			pageState: { scope }
		}
	} = state;

	const {
		payload: { dashboardId },
		meta: { deletedDashboardId }
	} = action;

	dispatch(
		DASHBOARDS_DELETE_REQUEST_HTTP,
		{
			dashboardId,
			sysparm_transaction_scope: scope
		},
		{
			deletedDashboardId
		}
	);
};

const dashboardsDeleteEffect = createHttpEffect(
	'/api/now/builder/dashboards/:dashboardId',
	{
		method: 'DELETE',
		pathParams: ['dashboardId'],
		queryParams: ['sysparm_transaction_scope'],
		startActionType: DASHBOARDS_DELETE_REQUEST_STARTED,
		successActionType: DASHBOARDS_DELETE_REQUEST_SUCCEEDED,
		errorActionType: DASHBOARDS_DELETE_REQUEST_FAILED,
		batch: false
	}
);

const dashboardsDeleteStarted = ({ updateProperties }) => {
	updateProperties({ isSavingPage: true });
};

const dashboardsDeleteSucceeded = ({ updateProperties }) => {
	updateProperties({ isSavingPage: false });
};

const dashboardsDeleteFailed = ({ updateProperties }) => {
	updateProperties({ isSavingPage: false });
};

export default {
	[DASHBOARDS_DELETE_REQUEST]: dashboardsDeleteRequest,
	[DASHBOARDS_DELETE_REQUEST_HTTP]: dashboardsDeleteEffect,
	[DASHBOARDS_DELETE_REQUEST_STARTED]: dashboardsDeleteStarted,
	[DASHBOARDS_DELETE_REQUEST_SUCCEEDED]: dashboardsDeleteSucceeded,
	[DASHBOARDS_DELETE_REQUEST_FAILED]: dashboardsDeleteFailed
};
