import get from 'lodash/get';
import { resolveBinding } from '../../utils.js';
// @ts-check

// Temporarily using flatMap from lodash to be able to unit test.
import _flatMap from 'lodash/flatMap';
import _isEmpty from 'lodash/isEmpty';

/**
 * An Intellisense context binding.
 * A binding is composed of:
 * - a contextType (@element, @state, etc)
 * - optionally a context id (an element id )
 * - a propertyId (a key or id)
 *
 * @element.listNav.selectedList
 *  |       |       |
 *  |       |       └ propertyId
 *  |       └ contextId
 *  └ contextType
 *
 * @typedef Binding
 * @type {Object}
 * @property {!string} label  The actual binding string
 * @property {!string} bindingType  The binding type - a specialization of the context
 * @property {string=} contextId  The context id - some contexts will not have this
 * @property {!string} propertyId  The propertyId
 * @property {any=} value  The current value of this property
 * @property {any=} previewValue  A preview of what this property may look like
 * // LATER
 * // -----
 * // valueType {(string|object)} The string value of the type of the value,
 * //                             or an object with either an interfaceName or
 * //                             the json schema.
 */

/**
 * @constant {Object.<string, string>}
 */
const bindingTypes = {
	dataBroker: 'dataBroker',
	parentDataBroker: 'parentDataBroker',
	element: 'element',
	pageProperty: 'pageProperty',
	parentPageProperty: 'parentPageProperty',
	clientStateParam: 'clientStateParam',
	session: 'session',
	appChromeSettings: 'appChromeSettings'
};

/**
 * @constant {Object.<string, string>} contextType
 */
const contextTypes = {
	// `@data.<DATA BROKER INSTANCE ID>.<PROPERTY NAME>`
	dataBrokerInput: 'dataBrokerInput',
	// `@elements.<ELEMENT ID>.<PROPERTY NAME>`
	elementProperty: 'elementProperty',
	// `@context.props.<PROPERTY NAME>`
	pageProperty: 'pageProperty',
	// `@state.<PROPERTY NAME>`
	clientStateInitialValue: 'clientStateInitialValue'
};

/**
 * @constant {Object.<string, Array.<string>>} validBindingTypesByContextType
 */
const validBindingTypesForContextType = {
	[contextTypes.dataBrokerInput]: [
		bindingTypes.dataBroker,
		bindingTypes.pageProperty,
		bindingTypes.session,
		bindingTypes.clientStateParam,
		bindingTypes.appChromeSettings
	],
	[contextTypes.elementProperty]: [
		bindingTypes.pageProperty,
		bindingTypes.element,
		bindingTypes.dataBroker,
		bindingTypes.clientStateParam,
		bindingTypes.session,
		bindingTypes.appChromeSettings
	],
	[contextTypes.pageProperty]: [
		bindingTypes.parentPageProperty,
		bindingTypes.parentDataBroker,
		bindingTypes.session,
		bindingTypes.appChromeSettings
	],
	[contextTypes.clientStateInitialValue]: [
		bindingTypes.pageProperty,
		bindingTypes.parentDataBroker,
		bindingTypes.dataBroker,
		bindingTypes.session,
		bindingTypes.appChromeSettings
	]
};

const bindingTypesInContextType = {
	[contextTypes.dataBrokerInput]: [bindingTypes.dataBroker],
	[contextTypes.elementProperty]: [bindingTypes.element],
	[contextTypes.pageProperty]: [
		bindingTypes.parentPageProperty,
		bindingTypes.pageProperty
	],
	[contextTypes.clientStateInitialValue]: [bindingTypes.clientStateParam]
};

const sameContext = ({ contextId, binding }) => binding.contextId === contextId;

const samePropertyId = ({ propertyId, binding }) =>
	binding.propertyId === propertyId;

const samePropertyTestByContextType = {
	[contextTypes.dataBrokerInput]: sameContext,
	[contextTypes.elementProperty]: sameContext,
	[contextTypes.pageProperty]: samePropertyId,
	[contextTypes.clientStateInitialValue]: samePropertyId
};

const defaultBindingFilter = ({
	contextType: currentContextType,
	contextId,
	propertyId
}) => (b) => {
	const validBindingTypes = validBindingTypesForContextType[currentContextType];
	const inValidBindingTypes = validBindingTypes.includes(b.bindingType);
	const bindingTypesForContext = bindingTypesInContextType[currentContextType];
	const sameContextType = bindingTypesForContext.includes(b.bindingType);
	const test = samePropertyTestByContextType[currentContextType];
	const sameProperty =
		sameContextType && test({ contextId, propertyId, binding: b });

	return inValidBindingTypes && sameProperty === false;
};

/**
 *
 * @param {Array.<Object>} pageProperties
 * @returns {Array.<Binding>}
 */
const createPageBindings = (pageProperties, middlewareProperties) =>
	pageProperties.map(({ name, value, testValue }) => ({
		label: `@context.props.${name}`,
		value,
		previewValue: testValue || value,
		bindingType: bindingTypes.pageProperty,
		propertyId: name,
		resolvedBinding: resolveBinding(
			`@context.props.${name}`,
			middlewareProperties
		)
	}));

/**
 *
 * @returns {Array.<Binding>}
 */
const createSessionBindings = (middlewareProperties) => {
	return [
		{
			label: '@context.session',
			bindingType: bindingTypes.session,
			propertyId: 'session',
			resolvedBinding: resolveBinding('@context.session', middlewareProperties)
		}
	];
};

/**
 * @param {Object} appChromeSettings
 * @returns {Array.<Binding>}
 */
const createAppBindings = (appChromeSettings) =>
	(Object.entries(appChromeSettings) || []).map(([name, value]) => ({
		label: `@context.app.${name}`,
		value,
		bindingType: bindingTypes.appChromeSettings,
		propertyId: name
	}));

/**
 *
 * @param {Object} parentPageProperties
 * @returns {Array.<Binding>}
 */
const createParentPageBindings = (parentPageProperties, middlewareProperties) =>
	Object.keys(parentPageProperties).map((key) => ({
		label: `@context.props.${key}`,
		bindingType: bindingTypes.parentPageProperty,
		propertyId: key,
		resolvedBinding: resolveBinding(
			`@context.props.${key}`,
			middlewareProperties
		)
	}));

/**
 *
 * @param {Object} elementProperties
 * @param {Array.<Object>} elementArr
 * @returns {Array.<Binding>}
 */
const createElementBindings = (
	elementProperties,
	elementArr,
	middlewareProperties
) => {
	return _flatMap(Object.keys(elementProperties), (key) => {
		const element = elementArr.find((e) => e.id === key);
		const elementId = element ? element.id : key;
		return (elementProperties[key] || [])
			.filter(
				(prop) => prop.id && prop.id !== '__instance_name__' && prop.selectable
			)
			.map(({ id, value }) => ({
				label: `@elements.${elementId}.${id}`,
				value,
				bindingType: bindingTypes.element,
				contextId: elementId,
				propertyId: id,
				resolvedBinding: resolveBinding(
					`@elements.${elementId}.${id}`,
					middlewareProperties
				)
			}));
	});
};

/**
 *
 * @param {Array.<Object>} clientStateParameters
 * @returns {Array.<Binding>}
 */
const createStateBindings = (clientStateParameters, middlewareProperties) =>
	clientStateParameters.map((param) => {
		return {
			label: `@state.${param.name}`,
			bindingType: bindingTypes.clientStateParam,
			propertyId: param.name,
			resolvedBinding: resolveBinding(
				`@state.${param.name}`,
				middlewareProperties
			)
		};
	});

/**
 *
 * @param {{ simpleMutationBroker }} dataBroker
 * @returns {boolean}
 */
const isBindableDataBroker = ({ simpleMutationBroker }) =>
	simpleMutationBroker === false;

/**
 *
 * @param {Array.<Object>} dataBrokerInstances
 * @returns {Array.<Binding>}
 */
const createDataBrokerBindings = (
	dataBrokerInstances,
	middlewareProperties,
	isParent
) =>
	_flatMap(
		dataBrokerInstances.filter(isBindableDataBroker),
		({ id, outputProperties, definition }) => {
			const bindingType = isParent
				? bindingTypes.parentDataBroker
				: bindingTypes.dataBroker;
			if (get(definition, 'type', '') === 'CLIENT_STATE') {
				return {
					label: `@data.${id}`,
					bindingType,
					contextId: id,
					propertyId: id,
					resolvedBinding: get(
						middlewareProperties,
						`clientScriptApi.data.${id}`,
						{}
					)
				};
			}
			if (!_isEmpty(outputProperties)) {
				return Object.keys(outputProperties).map((key) => ({
					label: `@data.${id}.${key}`,
					bindingType,
					contextId: id,
					propertyId: key,
					resolvedBinding: resolveBinding(
						`@data.${id}.${key}`,
						middlewareProperties
					)
				}));
			} else {
				return [
					{
						label: `@data.${id}`,
						bindingType,
						contextId: id,
						resolvedBinding: resolveBinding(`@data.${id}`, middlewareProperties)
					}
				];
			}
		}
	);

/**
 * Collect the exhaustive list of all possible intellisense bindings. Not for direct use.
 * Should be filtered with `filterBindings` with the appropriate parameters.
 * @param {{
    pageProperties: Array.<import('../../models/macroponentProperty').MacroponentProperty>,
    elementProperties: Object.<string, Array.<import('../../models/macroponentProperty').MacroponentProperty>>,
    elementArr: Array.<import('../../models').Element>,
    dataBrokerInstances: Array.<import('../utils').dataBrokerInstance>,
    parentDataBrokerInstances: Array.<import('../utils').dataBrokerInstance>,
    parentPageProperties: Object.<string, Array.<import('../../models/macroponentProperty').MacroponentProperty>>,
    clientStateParameters: Array.<Object>
   }} param
 * @returns {Array.<Binding>}
 */
const collectAllBindings = ({
	pageProperties = [],
	elementProperties = {},
	elementArr = [],
	dataBrokerInstances = [],
	parentDataBrokerInstances = [],
	parentPageProperties = {},
	clientStateParameters = [],
	middlewareProperties = {},
	appChromeSettings = {}
}) => {
	const pageBindings = createPageBindings(pageProperties, middlewareProperties);
	const parentPageBindings = createParentPageBindings(
		parentPageProperties,
		middlewareProperties
	);
	const elementBindings = createElementBindings(
		elementProperties,
		elementArr,
		middlewareProperties
	);
	const dataBrokerBindings = createDataBrokerBindings(
		dataBrokerInstances,
		middlewareProperties,
		false
	);
	const parentDataBrokerBindings = createDataBrokerBindings(
		parentDataBrokerInstances,
		middlewareProperties,
		true
	);
	const clientStateBindings = createStateBindings(
		clientStateParameters,
		middlewareProperties
	);

	const sessionBindings = createSessionBindings(middlewareProperties);
	const appChromeBindings = createAppBindings(appChromeSettings);

	return [
		...pageBindings,
		...parentPageBindings,
		...elementBindings,
		...dataBrokerBindings,
		...parentDataBrokerBindings,
		...clientStateBindings,
		...sessionBindings,
		...appChromeBindings
	];
};

/**
 *
 * @param {Array.<Binding>} bindings
 * @param {{ contextType: string, contextId?: string, propertyId: string}} contextBindingInfo
 * @returns {Array.<Binding>}
 */
const filterBindings = (bindings, contextBindingInfo) => {
	if (!contextBindingInfo) {
		return bindings;
	}
	const { contextType, contextId, propertyId } = contextBindingInfo;
	const result = bindings.filter(
		defaultBindingFilter({ contextType, contextId, propertyId })
	);
	return result;
};

export { collectAllBindings, filterBindings, bindingTypes, contextTypes };
