import { createHttpEffect } from '@servicenow/ui-effect-http';

import {
	PAGE_LIST_REQUESTED,
	PAGE_LIST_HTTP_REQUESTED,
	PAGE_LIST_REQUEST_STARTED,
	PAGE_LIST_REQUEST_FAILED,
	PAGE_LIST_REQUEST_SUCCEEDED
} from './pageListActions';

import { CONFIGURATION_COMPONENT_BY_INTERFACE_REQUESTED } from '../configurationComponentByInterface/configurationComponentByInterfaceActions';

import {
	PAGE_REQUESTED_STARTED,
	PAGE_SAVE_REQUESTED
} from '../page/pageActions';
import { getPageStatus } from '../page/utils/pageStatus';
import effectProxy from '../helpers/effectProxy';

const appendToRequestPayload = (payload) => {
	if (!payload) {
		return {};
	}

	// For component builder
	if (payload.macroponentSysId) {
		return {
			sysparm_macroponent_id: payload.macroponentSysId
		};
	}

	return {
		sysparm_page_registry_id: payload.pageRegistrySysId,
		sysparm_app_config_id: payload.appConfigId
	};
};

const pageListRequested = ({ action, dispatch, updateState, properties }) => {
	const { payload } = action;

	updateState({
		operation: 'set',
		path: 'pagesLoaded',
		value: false
	});

	const sysparm_query_no_domain =
		payload && payload.sysparm_query_no_domain !== undefined
			? payload.sysparm_query_no_domain
			: properties.isDomainExpanded;

	dispatch(
		PAGE_LIST_HTTP_REQUESTED,
		{
			pageResource: 'screens',
			sysparm_query_no_domain,
			...appendToRequestPayload(payload)
		},
		action.meta
	);

	dispatch(CONFIGURATION_COMPONENT_BY_INTERFACE_REQUESTED);
};

export const pageListHttpRequested = createHttpEffect(
	'/api/now/uxframework/:pageResource',
	{
		method: 'GET',
		batch: false,
		pathParams: ['pageResource'],
		queryParams: [
			'sysparm_page_registry_id',
			'sysparm_app_config_id',
			'sysparm_macroponent_id',
			'sysparm_query_no_domain'
		],
		startActionType: PAGE_LIST_REQUEST_STARTED,
		successActionType: PAGE_LIST_REQUEST_SUCCEEDED,
		errorActionType: PAGE_LIST_REQUEST_FAILED
	}
);

const pageListRequestStarted = ({ updateProperties }) =>
	updateProperties({ isLoadingPages: true });

const pageListRequestSucceeded = ({
	action,
	state,
	updateProperties,
	updateState,
	dispatch
}) => {
	const { queuedPageRequestId } = state;

	const newPages = action.payload.result.map((pagePayload) => {
		const {
			name,
			id: sysId,
			scope,
			isActive,
			table,
			workspaceMasterConfigSysId,
			macroponentInstance: { rootMacroponentId, macroponentDefinitions },
			pageType,
			screenType,
			order,
			screenConditions
		} = pagePayload;

		const {
			readOnly,
			sysPolicy,
			scope: pageDefinitionScopeId
		} = macroponentDefinitions[rootMacroponentId];

		const page = {
			sysId,
			scope,
			readOnly,
			sysPolicy,
			name,
			pageStatus: getPageStatus(isActive),
			type: pageType,
			screenType,
			order,
			screenConditions,
			pageDefinitionScopeId
		};

		if (table) {
			page.uiTypeParameters = {
				table,
				workspaceMasterConfigSysId
			};
		}

		return page;
	});

	updateProperties({ isLoadingPages: false, pages: newPages });

	if (queuedPageRequestId) {
		dispatch(PAGE_REQUESTED_STARTED, {
			sysId: queuedPageRequestId,
			pages: newPages
		});
	}

	updateState({
		operation: 'set',
		path: 'queuedPageRequestId',
		value: null
	});
	updateState({
		operation: 'set',
		path: 'pagesLoaded',
		value: true
	});

	if (action.meta.isExplosion) {
		dispatch(PAGE_SAVE_REQUESTED);
	}
};

const pageListRequestFailed = ({ updateProperties }) =>
	updateProperties({ isLoadingPages: false });

export default {
	[PAGE_LIST_REQUESTED]: pageListRequested,
	[PAGE_LIST_HTTP_REQUESTED]: effectProxy(pageListHttpRequested),
	[PAGE_LIST_REQUEST_STARTED]: pageListRequestStarted,
	[PAGE_LIST_REQUEST_FAILED]: pageListRequestFailed,
	[PAGE_LIST_REQUEST_SUCCEEDED]: pageListRequestSucceeded
};
