import { createHttpEffect } from '@servicenow/ui-effect-http';
import cloneDeep from 'lodash/cloneDeep';
import get from 'lodash/get';
import {
	NEW_CREATE_PAGE,
	NEW_PAGE_CREATE_REQUEST,
	NEW_PAGE_CREATE_STARTED,
	NEW_PAGE_CREATE_SUCCEEDED,
	NEW_PAGE_CREATE_FAILED
} from './newPageActions';
import processContextState from '../contextState/contextStateProcessor';
import { addNewPageReceived, pathParamsFromPayload } from './newPageUtils';
import createPageState from '../../models/createPageState';

const newCreatePage = ({ action, dispatch }) => {
	dispatch(
		NEW_PAGE_CREATE_REQUEST,
		action.payload,
		pathParamsFromPayload(action.payload)
	);
};

const newPageCreateStarted = ({ updateProperties }) => {
	updateProperties({ isSavingPage: true });
};
const newPageCreateSucceeded = ({
	updateProperties,
	updateState,
	action,
	properties,
	state
}) => {
	const {
		dataBrokerDefinitions,
		eventHandlers,
		pages,
		pageState: oldPageState
	} = properties;

	const variantId = get(oldPageState, 'sysId');

	const [pageState, newPages] = addNewPageReceived(
		action.payload.result,
		pages,
		dataBrokerDefinitions,
		eventHandlers,
		state,
		variantId
	);

	const {
		ancestorDataBrokerInstances,
		parentDataBrokerInstances,
		ancestorPageProperties,
		parentPageProperties,
		ancestorElements,
		parentElements,
		parentEventHandlers,
		appChromeSettings,
		theme
	} = processContextState({
		contextState: get(action.payload, 'result.contextState', {}),
		updateState,
		properties
	});

	const updatedPageState = createPageState(
		{
			eventHandlers: parentEventHandlers
		},
		pageState
	);

	updateProperties({ pages: newPages });

	updateProperties({
		ancestorDataBrokerInstances,
		parentDataBrokerInstances,
		ancestorPageProperties,
		parentPageProperties,
		ancestorElements,
		parentElements,
		appChromeSettings,
		theme,
		isSavingPage: false,
		pageState: updatedPageState,
		pageStatePristine: cloneDeep(updatedPageState)
	});
};
const newPageCreateFailed = ({ updateProperties }) => {
	updateProperties({ isSavingPage: false, isLoadingPage: false });
};

export default {
	[NEW_CREATE_PAGE]: newCreatePage,
	[NEW_PAGE_CREATE_REQUEST]: createHttpEffect(
		'/api/now/builder/:builderType/:appId/pages',
		{
			method: 'POST',
			batch: false,
			pathParams: ['builderType', 'appId'],
			dataParam: 'data',
			startActionType: NEW_PAGE_CREATE_STARTED,
			successActionType: NEW_PAGE_CREATE_SUCCEEDED,
			errorActionType: NEW_PAGE_CREATE_FAILED
		}
	),
	[NEW_PAGE_CREATE_STARTED]: newPageCreateStarted,
	[NEW_PAGE_CREATE_SUCCEEDED]: newPageCreateSucceeded,
	[NEW_PAGE_CREATE_FAILED]: newPageCreateFailed
};
