import { createHttpEffect } from '@servicenow/ui-effect-http';
import get from 'lodash/get';
import {
	PAGE_COPY_REQUESTED,
	PAGE_COPY_HTTP_REQUESTED,
	PAGE_COPY_STARTED,
	PAGE_COPY_SUCCEEDED,
	PAGE_COPY_FAILED
} from './pageActions';

import {
	createLandingPage,
	createScreen,
	pageCreateStarted,
	pageCreateSucceeded,
	pageCreateFailed
} from './pageCreatedUtils';
import transformPageStateToMacroponent from './utils/transformPageStateToMacroponent';

const pageCopyRequested = async ({ state, action, dispatch }) => {
	const {
		properties: { pageState }
	} = state;

	if (!pageState) {
		console.warn(
			`UXF Builder Middleware Error: Must load a page before coping a page.`
		);
		return;
	}

	if (pageState.type === 'record') {
		// TODO: Record page logic should be removed
		return;
	}

	const copiedMacroponent = await transformPageStateToMacroponent(pageState);

	const layout = get(
		copiedMacroponent,
		'macroponentInstance.macroponent.layout',
		{}
	);
	const composition = get(
		copiedMacroponent,
		'macroponentInstance.macroponent.composition',
		[]
	);

	const propertyValues = get(
		copiedMacroponent,
		'macroponentInstance.propertyValues',
		{}
	);

	const properties = get(
		copiedMacroponent,
		'macroponentInstance.properties',
		[]
	);

	if (pageState.type === 'landing') {
		// TODO: Landing pages are deprecated.  Remove.
		createLandingPage(
			action.payload,
			composition,
			layout,
			properties,
			propertyValues,
			dispatch,
			PAGE_COPY_HTTP_REQUESTED
		);
	} else {
		createScreen(
			action.payload,
			composition,
			layout,
			properties,
			propertyValues,
			dispatch,
			PAGE_COPY_HTTP_REQUESTED
		);
	}
};

const pageCopyHttpEffect = createHttpEffect(
	'/api/now/uxframework/:pageEndpoint',
	{
		method: 'POST',
		pathParams: ['pageEndpoint'],
		queryParams: ['sysparm_transaction_scope'],
		dataParam: 'data',
		startActionType: PAGE_COPY_STARTED,
		successActionType: PAGE_COPY_SUCCEEDED,
		errorActionType: PAGE_COPY_FAILED,
		batch: false
	}
);

export default {
	[PAGE_COPY_REQUESTED]: pageCopyRequested,
	[PAGE_COPY_HTTP_REQUESTED]: pageCopyHttpEffect,
	[PAGE_COPY_STARTED]: pageCreateStarted,
	[PAGE_COPY_FAILED]: pageCreateFailed,
	[PAGE_COPY_SUCCEEDED]: pageCreateSucceeded
};
