import { JSON_LITERAL } from './constants';
import isUndefined from 'lodash/isUndefined';

export default function JSONLiteralFactory({
	model, // object
	shorthand // any
} = {}) {
	if (isUndefined(shorthand) && !model) {
		throw new TypeError('Something went wrong initializing JSONLiteralFactory');
	}

	// PRIVATE PROPERTIES
	const fType = JSON_LITERAL;
	const fShorthand = !isUndefined(shorthand)
		? shorthand
		: getShorthandFromModel(model);
	const fModel = model || getModelFromShorthand(shorthand);

	function getShorthandFromModel(model) {
		return model.value;
	}

	function getModelFromShorthand(shorthand) {
		return {
			type: fType,
			value: shorthand
		};
	}

	// PUBLIC API
	return {
		getShorthand() {
			return fShorthand;
		},

		getModel() {
			return fModel;
		}
	};
}
