import cloneDeep from 'lodash/cloneDeep';
import {
	getNewRootElementAndElementToUpdate,
	getInvalidEventMappingTargets,
	formatEventNameAndLabel
} from '../../elementEventMappings/utils';
import createPageState from '../../../models/createPageState';
import { EVENT, DATABROKER_OP } from '../../elementEventMappings/constants';

export default function convertAncestorEventMappingsToRelays(pageState) {
	let updatedPageState = cloneDeep(pageState);
	const {
		rootElement,
		eventHandlers = [],
		screenEventMappings = [],
		dataBrokerInstances = []
	} = updatedPageState;
	const parentEventHandlers = eventHandlers.filter(
		(handler) => handler.ancestor === true
	);
	const invalidEventMappingTargets = getInvalidEventMappingTargets(
		rootElement,
		parentEventHandlers,
		dataBrokerInstances
	);

	for (const invalidTarget of invalidEventMappingTargets) {
		let eventMappingToUpdate = null;
		const {
			newRootElement,
			elementToUpdate
		} = getNewRootElementAndElementToUpdate(
			updatedPageState,
			invalidTarget.elementId || null
		);

		if (invalidTarget.elementId) {
			eventMappingToUpdate = elementToUpdate.eventMappings.find(
				(eventMapping) =>
					eventMapping.eventMappingId === invalidTarget.eventMappingId
			);
		} else if (invalidTarget.dataBrokerId) {
			const dataBrokerInstanceToUpdate =
				dataBrokerInstances.find(
					(dataBrokerInstance) =>
						dataBrokerInstance.id === invalidTarget.dataBrokerId
				) || {};
			const { eventMappings = [] } = dataBrokerInstanceToUpdate;
			eventMappingToUpdate = eventMappings.find(
				(eventMapping) =>
					eventMapping.eventMappingId === invalidTarget.eventMappingId
			);
		} else if (invalidTarget.rootElementId) {
			eventMappingToUpdate = newRootElement.internalEventMappings.find(
				(internalEventMapping) =>
					internalEventMapping.eventMappingId === invalidTarget.eventMappingId
			);
		}

		if (eventMappingToUpdate) {
			const eventMappingTargetToUpdate = eventMappingToUpdate.targets.find(
				(target) => target.targetId === invalidTarget.target.targetId
			);

			const { name } = formatEventNameAndLabel(
				invalidTarget.parentEventHandler,
				newRootElement.id
			);
			const relayMapping = screenEventMappings.find((mapping) => {
				const [, eventName] = mapping.eventApiName.split('.');
				return eventName === name;
			});

			if (eventMappingTargetToUpdate && relayMapping) {
				const relayTarget = relayMapping.targets[0] || {};
				eventMappingTargetToUpdate.type = EVENT;
				if (relayTarget.type === DATABROKER_OP) {
					eventMappingTargetToUpdate.event = {};
					eventMappingTargetToUpdate.event.payload =
						invalidTarget.target.operation.payload;
					eventMappingTargetToUpdate.operation = null;
				}
				eventMappingTargetToUpdate.event.sysId = relayMapping.eventId;
				eventMappingTargetToUpdate.event.apiName = relayMapping.eventApiName;
			}
		}
		updatedPageState = createPageState(
			{
				rootElement: newRootElement
			},
			updatedPageState
		);
	}
	return updatedPageState;
}
