import { createHttpEffect } from '@servicenow/ui-effect-http';

import {
	PAGE_DELETE_REQUESTED,
	PAGE_DELETE_REQUESTED_HTTP,
	PAGE_DELETE_STARTED,
	PAGE_DELETE_FAILED,
	PAGE_DELETE_SUCCEEDED
} from './pageActions';
import pageEndpointFromType from './utils/pageEndpointFromType';
import { throwError } from '../../utils';

const pageDeletedHandler = ({ state, action, dispatch }) => {
	const {
		properties: { pages }
	} = state;

	const {
		payload: { sysId, scope }
	} = action;

	const pageToDelete = pages.find((page) => page.sysId === sysId);
	if (!pageToDelete) {
		throwError(`Cannot find requested page ${sysId}`);
	}

	dispatch(PAGE_DELETE_REQUESTED_HTTP, {
		sysId,
		sysparm_transaction_scope: scope,
		pagesEndpoint: pageEndpointFromType(pageToDelete.type)
	});
};

const pageDeletedHttpHandler = createHttpEffect(
	'/api/now/uxframework/:pagesEndpoint/:sysId',
	{
		method: 'DELETE',
		pathParams: ['pagesEndpoint', 'sysId'],
		queryParams: ['sysparm_transaction_scope'],
		startActionType: PAGE_DELETE_STARTED,
		successActionType: PAGE_DELETE_SUCCEEDED,
		errorActionType: PAGE_DELETE_FAILED,
		batch: false
	}
);

const addSysIdToActionMeta = (context) => {
	const { coeffects } = context;
	const { action } = coeffects;
	const { meta } = action;
	return {
		...context,
		coeffects: {
			...coeffects,
			action: {
				...action,
				meta: {
					...meta,
					deleteSysId: action.payload.sysId
				}
			}
		}
	};
};

const deletePageRequestStarted = ({ updateProperties }) =>
	updateProperties({ isDeletingPage: true });

const deletePageRequestSucceeded = ({
	action,
	properties,
	updateProperties
}) => {
	const {
		meta: { deleteSysId = '' }
	} = action;
	const pages = properties.pages.filter((p) => p.sysId !== deleteSysId);

	const updatedProperties = {
		pages,
		isDeletingPage: false
	};

	if (
		properties.pageMetadata &&
		deleteSysId === properties.pageMetadata.sysId
	) {
		updatedProperties.pageState = null;
		updatedProperties.pageStatePristine = null;
		updatedProperties.isPageStateDirty = false;
		updatedProperties.clientScripts = [];
		updatedProperties.clientScriptsPristine = [];
	}

	updateProperties(updatedProperties);
};

const deletePageRequestFailed = ({ updateProperties }) =>
	updateProperties({ isDeletingPage: false }); // TODO: Surface error?

export default {
	[PAGE_DELETE_REQUESTED]: pageDeletedHandler,
	[PAGE_DELETE_REQUESTED_HTTP]: {
		...pageDeletedHttpHandler,
		interceptors: [addSysIdToActionMeta]
	},
	[PAGE_DELETE_STARTED]: deletePageRequestStarted,
	[PAGE_DELETE_FAILED]: deletePageRequestFailed,
	[PAGE_DELETE_SUCCEEDED]: deletePageRequestSucceeded
};
