import Model from './model';

export default class Property extends Model {
	constructor({
		name = '', // string
		columnName = '', // string
		label = '', // string
		type = '', // string
		reference = null, // object
		referenceFilter = '', // string
		value = null, // any
		isStatic = true, // bool,
		choices = [],
		table = '',
		readOnly = false,
		order = 0, // integer
		interfaceApiName = null,
		emptyStateBehavior = undefined,
		selectable = false,
		description = '', // string,
		error = null,
		defaultValue = null,
		variant = null,
		translatable = false,
		schema = null
	}) {
		super();
		this.name = name;
		this.columnName = columnName;
		this.label = label;
		this.type = type;
		this.value = value;
		this.isStatic = isStatic;
		this.choices = choices;
		this.readOnly = readOnly;
		this.order = order;
		this.table = table;
		if (reference) {
			this.reference = reference;
			this.referenceFilter = referenceFilter;
		}
		this.interfaceApiName = interfaceApiName;
		this.emptyStateBehavior = emptyStateBehavior;
		this.selectable = selectable;
		this.description = description;
		this.error = error;
		this.defaultValue = defaultValue;
		this.variant = variant;
		this.translatable = translatable;
		this.schema = schema;
		/**
		 * isStatic,
		 * isSelectable,
		 * if dynamic...
		 *    id of element i am selecting
		 *    property that i am selecting
		 */
	}
}
