import cuid from 'cuid';
import Layout from '../../../../models/layout/layout';
import ColumnElement from '../../../../models/layout/columnElement';
import RowElement from '../../../../models/layout/rowElement';
import RowColumn from '../../../../models/layout/rowColumn';

import {
	insertAndGetUpdatedLayout,
	deleteAndGetUpdatedLayout,
	treeifyLayout,
	flattenLayout
} from '../layout';

function rowWithColumnsLayout(slotToPlaceNewElementIn) {
	return new Layout({
		default: new RowColumn(
			new RowElement({
				id: cuid(),
				columns: [
					new ColumnElement({
						size: '12',
						slotName: slotToPlaceNewElementIn,
						id: cuid()
					})
				]
			})
		)
	});
}

function rootIsALeaf(layout) {
	const {
		default: { root }
	} = layout;
	if (!root.columns && !root.rows) {
		return true;
	} else if (
		(root.columns && !root.columns.length) ||
		(root.rows && !root.rows.length)
	) {
		return true;
	}
	return false;
}

export const placeElementOnLayout = ({
	hasChildren,
	adjacentId,
	slotName,
	direction,
	layout
}) => {
	if (!layout || !layout.default || !hasChildren) {
		return rowWithColumnsLayout(slotName);
	}
	const flattenedInputLayout = flattenLayout(layout.default.root);
	const flattenedOutputLayout = insertAndGetUpdatedLayout(
		flattenedInputLayout,
		direction,
		adjacentId,
		slotName
	);
	return treeifyLayout(flattenedOutputLayout);
};

export const deleteElementFromLayout = ({ id, layout }) => {
	const flattenedInputLayout = flattenLayout(layout.default.root);

	const flattenedOutputLayout = deleteAndGetUpdatedLayout(
		flattenedInputLayout,
		id
	);
	const treeLayout = treeifyLayout(flattenedOutputLayout);
	if (rootIsALeaf(treeLayout)) {
		return null;
	}
	return treeLayout;
};

export const findLayoutElementIdFromSlotName = ({ slotName, layout }) => {
	const flattenedInputLayout = flattenLayout(layout.default.root);

	for (const key in flattenedInputLayout) {
		if (flattenedInputLayout[key].slotName === slotName) {
			return key;
		}
	}
};
