/**
 * Handy dandy proxy for helping keep effect functions mockable by sinon, example:
 *
 * export const unProxiedFetchEffect = createGraphQLEffect(someQuery, {
 *  startActionType,
 *  successActionType,
 *  errorActionType
 * })
 *
 * In actionHandlers -
 * import effectProxy from '../helpers/effectProxy'
 * ...
 * [ACTION_CONSTANT]: effectProxy(unProxiedFetchEffect)
 *
 * In tests -
 * import { unProxiedFetchEffect } from './component-file'
 * ...
 * beforeEach(() => {
 *  sandbox.stub(unProxiedFetchEffect, 'effect')
 * })
 */

export default (effect) => (coeffects) =>
	effect.effect(...effect.args, coeffects);
