import { createDataBrokerInstances } from '../../utils';

/**
 * @typedef ClientState
 * @property {Object[]} parentData
 * @property {Object[]} ancestorData
 * @property {Object[]} parentElements
 * @property {Object[]} ancestorElements
 * @property {Object[]} parentProperties
 * @property {Object[]} ancestorProperties
 */

/**
 * @typedef DataBrokerDefinition
 * @property {Object} definition
 * @property {Object} inputParams
 * @property {String} name
 * @property {String} type
 * @property {String} sysId
 * @property {String} description
 * @property {String} hydrationSysId
 * @property {Object} outputSchema
 * @property {Object[]} operations
 * @property {Boolean} simpleMutationBroker
 */

/**
 * @param {ClientState} result
 * @param {DataBrokerDefinition}
 */

const createDataBrokerInstancesFromContextState = (
	result,
	dataBrokerDefinitions
) => {
	const { ancestorData = [], parentData = [] } = result || {};
	const parentDataBrokerInstances = createDataBrokerInstances(
		parentData,
		dataBrokerDefinitions
	);
	const grandParentDataBrokerInstances = createDataBrokerInstances(
		ancestorData,
		dataBrokerDefinitions
	);

	return {
		ancestorDataBrokerInstances: [
			...parentDataBrokerInstances,
			...grandParentDataBrokerInstances
		],
		parentDataBrokerInstances
	};
};

export default createDataBrokerInstancesFromContextState;
