import ClientScript from '../../models/clientScript';
import cloneDeep from 'lodash/cloneDeep';
import flatMap from 'lodash/flatMap';
import get from 'lodash/get';
import { getTemplates } from '@devsnc/library-uxf';
import { Element, Slot } from '../../models';
import createPageState from '../../models/createPageState';
import processContextState from '../contextState/contextStateProcessor';
import { createDataBrokerRuntimeElement } from '../dataBrokers/dataBrokerFactory';

import {
	createSimpleLayout,
	createSlotsForEveryElement
} from '../element/utils/layoutMigration';
import transformMacroponentToPageState from './utils/transformMacroponentToPageState';
import {
	PAGE_COMPONENTS_REQUESTED,
	PAGE_REQUEST_HTTP_SUCCEEDED,
	PAGE_DATA_RECEIVED
} from './pageActions';
import { throwError, seismicCuid } from '../../utils';
import { getEventHandlersFromRootMacroponent } from '../utils';

import { COMPONENT_SOURCES_REQUESTED } from '../componentSources/componentSourcesActions';

function addIdsToLayoutElement(layoutElement) {
	layoutElement.id = layoutElement.id || seismicCuid();

	if (layoutElement.slotName) {
		return;
	}

	const childAccessor = layoutElement.type === 'row' ? 'columns' : 'rows';

	for (const childLayoutElement of layoutElement[childAccessor]) {
		addIdsToLayoutElement(childLayoutElement);
	}
}

export const addIdsToLayout = (layout) => {
	if (layout && layout.default) {
		const { root } = layout.default;

		addIdsToLayoutElement(root);
	}
};

function findElements(element, callback) {
	if (typeof callback !== 'function') {
		throwError('Callback must be a function', TypeError);
	}

	const matchingElements = [];
	if (callback.call(null, element)) {
		matchingElements.push(element);
	}

	return matchingElements.concat(
		flatMap(element.children, (child) => findElements(child, callback))
	);
}

const elementsWithOrphanedElements = (element) => {
	const hasNoLayout = element.layout && !element.layout.default;
	const hasChildren = element.children && element.children.length > 0;
	const hasNoNamedSlots =
		(get(element, 'component.slots') || []).filter(
			(name) => name !== '@default'
		).length === 0;
	return hasNoLayout && hasChildren && hasNoNamedSlots;
};

const pageRequestSucceeded = async ({
	action,
	dispatch,
	state,
	properties,
	updateProperties,
	updateState
}) => {
	const { dataBrokerDefinitions } = properties;

	const {
		result: {
			macroponentInstance: {
				rootMacroponentId,
				componentDefinitions,
				macroponentDefinitions,
				clientScripts = []
			},
			contextState
		}
	} = action.payload;
	dispatch(COMPONENT_SOURCES_REQUESTED, {
		pageComponents: Object.values(componentDefinitions)
	});

	const clientScriptsToUpdate = clientScripts.map(
		(script) => new ClientScript(script)
	);

	const mcpEventHandlers = getEventHandlersFromRootMacroponent(
		macroponentDefinitions,
		rootMacroponentId
	);

	const {
		ancestorDataBrokerInstances,
		parentDataBrokerInstances,
		ancestorPageProperties,
		parentPageProperties,
		ancestorElements,
		parentElements,
		parentEventHandlers,
		appChromeSettings,
		ancestorDataBrokerTags,
		theme,
		parentInfo
	} = processContextState({
		contextState,
		updateState,
		properties
	});

	const allEventHandlers = [...parentEventHandlers, ...mcpEventHandlers];

	const variantId = get(properties, 'pageState.sysId', null);

	const pageState = transformMacroponentToPageState(
		action.payload.result,
		dataBrokerDefinitions,
		allEventHandlers,
		[], // empty array for clientStateParameters on a fresh page request
		state,
		variantId
	);

	const { dataBrokerInstances } = pageState;

	if (pageState.dataBrokersEnabled) {
		// create data broker runtime elements.
		const dataBrokerTags = {};
		dataBrokerInstances.forEach((dataBrokerInstance) => {
			if (dataBrokerInstance.definition.type !== 'CLIENT_STATE') {
				dataBrokerTags[dataBrokerInstance.id] = createDataBrokerRuntimeElement(
					dataBrokerInstance
				);
			}
		});

		updateState({
			dataBrokerElements: {
				...state.dataBrokerElements,
				...ancestorDataBrokerTags,
				...dataBrokerTags
			}
		});
	}

	await getTemplates([rootMacroponentId]);

	dispatch(PAGE_COMPONENTS_REQUESTED);

	const newRootElement = cloneDeep(pageState.rootElement);
	const elementsWithLayout = findElements(
		newRootElement,
		(elem) => elem.layout
	);

	elementsWithLayout.forEach((element) => {
		if (get(element.layout, 'default.root', null)) {
			addIdsToLayout(element.layout);
			const elementsInLayout = element.children.map((child) => ({
				elementId: child.id,
				slotName: child.slot && child.slot.name
			}));
			const { newLayoutRoot, convertedSlotNames } = createSlotsForEveryElement(
				element.layout,
				elementsInLayout
			);
			if (newLayoutRoot) {
				element.layout.default.root = newLayoutRoot;
				element.children.forEach((child) => {
					if (convertedSlotNames[child.id]) {
						child.slot = new Slot({
							name: convertedSlotNames[child.id],
							parentElement: element
						});
					}
				});
			}
		}
		element.layout;
	});

	const elementsWithoutLayouts = findElements(
		newRootElement,
		elementsWithOrphanedElements
	);
	elementsWithoutLayouts.forEach(createSimpleLayout);

	const newPageState = createPageState(
		{
			rootElement: new Element(newRootElement)
		},
		pageState
	);

	updateProperties({
		dataBrokerInstances: newPageState.dataBrokerInstances,
		screenEventMappings: [],
		ancestorDataBrokerInstances,
		parentDataBrokerInstances,
		ancestorPageProperties,
		parentPageProperties,
		ancestorElements,
		appChromeSettings,
		parentElements,
		isLoadingPage: false,
		isPageReadOnly: pageState.readOnly && !!pageState.sysPolicy,
		isPageCopiable: pageState.canCopy,
		pageState: newPageState,
		pageStatePristine: cloneDeep(newPageState),
		clientScripts: clientScriptsToUpdate,
		clientScriptsPristine: cloneDeep(clientScriptsToUpdate),
		theme,
		parentInfo
	});
	dispatch(PAGE_DATA_RECEIVED);
};

export default {
	[PAGE_REQUEST_HTTP_SUCCEEDED]: pageRequestSucceeded
};
