import get from 'lodash/get';
import flow from 'lodash/flow';
import zip from 'lodash/zip';
import groupBy from 'lodash/groupBy';
import last from 'lodash/last';

const getAssignedComponents = (element) => {
	const { children = [] } = element;
	return children.reduce(
		(acc, current) => {
			const hasSlotName = !!get(current, 'slot.name', null);
			if (hasSlotName) {
				acc.orphanedComponents.push(current);
			} else {
				acc.nullSlottedChildren.push(current);
			}

			return acc;
		},
		{ orphanedComponents: [], nullSlottedChildren: [] }
	);
};
const PROCESSING = 'PROCESSING';
const DONE = 'DONE';
const ERROR = 'ERROR';

const gatherOrphanedComponents = ({ status, data }) => {
	if (status !== PROCESSING) {
		return { status, data };
	}
	const { ownerElement, newLayout, currentLayout } = data;
	const { orphanedComponents, nullSlottedChildren } = getAssignedComponents(
		ownerElement
	);
	if (orphanedComponents.length === 0) {
		return {
			status: DONE,
			data: { newLayout, ownerElement, children: nullSlottedChildren }
		};
	}
	return {
		status,
		data: {
			currentLayout,
			newLayout,
			orphanedComponents,
			ownerElement,
			children: nullSlottedChildren
		}
	};
};

const matchSlotNames = ({ status, data }) => {
	if (status !== PROCESSING) {
		return { status, data };
	}
	const { newLayout, orphanedComponents, children } = data;

	const { items = [] } = newLayout.default;
	const results = orphanedComponents.reduce(
		({ matched, remaining }, element) => {
			const { name } = element.slot;
			if (
				items.find((item) => {
					const { slotName } = item;
					return name === slotName;
				})
			) {
				matched.push(element);

				return {
					matched,
					remaining
				};
			}
			remaining.push(element);

			return {
				matched,
				remaining
			};
		},
		{ matched: [], remaining: [] }
	);

	if (results.remaining.length === 0) {
		return {
			status: DONE,
			data: {
				...data,
				children: [...children, ...results.matched],
				orphanedComponents: []
			}
		};
	}
	return {
		status,
		data: {
			...data,
			children: [...children, ...results.matched],
			orphanedComponents: results.remaining
		}
	};
};
const matchComponentsToSlots = (elements, slots) => {
	const matchedResults = zip(
		Object.values(groupBy(elements, 'slot.name')),
		slots
	).reduce(
		(acc, curr) => {
			const { matched, unmatched } = acc;
			const [elements, slot] = curr;
			if (!elements || elements.length === 0) {
				return acc;
			}
			if (!slot) {
				return {
					matched,
					unmatched: [...unmatched, ...elements]
				};
			}
			const newElements = elements.map((element) => {
				return {
					...element,
					slot: {
						name: slot.slotName
					}
				};
			});
			return {
				matched: [...matched, ...newElements],
				unmatched
			};
		},
		{
			matched: [],
			unmatched: []
		}
	);

	const matchedComponents = [
		...matchedResults.matched,
		...matchedResults.unmatched.map((comp) => {
			const lastItem = last(slots);
			const name = lastItem ? lastItem.slotName : comp.slot.name;
			return {
				...comp,
				slot: {
					name
				}
			};
		})
	];
	return matchedComponents;
};

const placeRemainingElements = ({ status, data }) => {
	if (status !== PROCESSING) {
		return { status, data };
	}
	const { newLayout, orphanedComponents, children, currentLayout } = data;

	const newItems = newLayout.default.items || [];

	const oldItems = get(currentLayout, 'default.items', []) || [];

	const matchedComponents = matchComponentsToSlots(orphanedComponents, [
		...newItems.filter(({ slotName }) => {
			return !oldItems.find(({ slotName: name }) => name === slotName);
		}),
		...newItems.filter(({ slotName }) => {
			return oldItems.find(({ slotName: name }) => name === slotName);
		})
	]);

	const remaningOrphans = orphanedComponents.filter(({ slot: orphanSlot }) => {
		return matchedComponents.find(({ slot }) => {
			slot.name === orphanSlot.name;
		});
	});
	const combinedChildren = [...children, ...matchedComponents];

	const newStatus = remaningOrphans.length > 0 ? ERROR : DONE;

	return {
		status: newStatus,
		data: {
			...data,
			orphanedComponents: remaningOrphans,
			children: combinedChildren
		}
	};
};

const unwrapData = (path) => ({ data }) => {
	return data[path];
};

const reparentElements = flow(
	gatherOrphanedComponents,
	matchSlotNames,
	placeRemainingElements,
	unwrapData('children')
);

const reparentElementsToNewLayout = (ownerElement, newLayout) => {
	const children = reparentElements({
		status: PROCESSING,
		data: { currentLayout: ownerElement.layout, newLayout, ownerElement }
	});
	return children;
};
export { matchComponentsToSlots };
export default reparentElementsToNewLayout;
