import get from 'lodash/get';
import {
	pageTypeLookup,
	DEFAULT_PAGE_TEMPLATE_ID,
	DEFAULT_CONTAINER_TEMPLATE_ID
} from './pageTypeLookup';
import { getLayoutFromMacroponent } from './getLayoutFromMacroponent';
import cuid from 'cuid';

const DEFAULT_LANDING_PAGE_LAYOUT = {
	default: {
		type: 'rowColumn',
		root: {
			type: 'row',
			columns: [
				{
					type: 'column',
					slotName: 'default-slot',
					size: '12',
					rows: [],
					id: cuid()
				}
			],
			id: cuid()
		}
	}
};
const FALLBACK_DEFAULT_PAGE_LAYOUT = {
	default: {
		type: 'flex',
		root: null,
		children: null,
		styles: null,
		rules: {
			'flex-direction': 'column'
		},
		items: [
			{
				slotName: 'default-slot',
				styles: {
					'flex-direction': 'column'
				}
			}
		],
		templateId: DEFAULT_PAGE_TEMPLATE_ID
	}
};

const FALLBACK_DEFAULT_CONTAINER_LAYOUT = {
	default: {
		type: 'flex',
		root: null,
		children: null,
		styles: null,
		rules: {
			'flex-direction': 'column'
		},
		items: [
			{
				slotName: 'default-slot',
				styles: {
					'flex-direction': 'column'
				}
			}
		],
		templateId: DEFAULT_CONTAINER_TEMPLATE_ID
	}
};

const fallbackTemplateLookup = {
	DEFAULT_PAGE_TEMPLATE_ID: FALLBACK_DEFAULT_PAGE_LAYOUT,
	DEFAULT_CONTAINER_TEMPLATE_ID: FALLBACK_DEFAULT_CONTAINER_LAYOUT,
	fallback: FALLBACK_DEFAULT_CONTAINER_LAYOUT
};

const addTemplateIdToLayout = (layout, templateId) => {
	if (layout && layout.default) {
		layout.default.templateId = templateId;
	}
	return layout;
};
const getDefaultLayout = (pageType, templatesDefinitions) => {
	const defaultTemplateId = get(
		pageTypeLookup,
		pageType,
		pageTypeLookup.fallback
	);
	if (!templatesDefinitions) {
		console.warn(
			'Failed to load template definitions falling back to hard coded layout'
		);
		return get(
			fallbackTemplateLookup,
			defaultTemplateId,
			fallbackTemplateLookup.fallback
		);
	}
	const defaultTemplate = templatesDefinitions.find((templateDefinition) => {
		return templateDefinition.id === defaultTemplateId;
	});
	const layout =
		defaultTemplate &&
		getLayoutFromMacroponent(defaultTemplate.macroponentInstance);
	addTemplateIdToLayout(layout, defaultTemplateId);
	return layout;
};
export const memoizeGetDefaultLayout = (templatesDefinitions) => {
	const layoutsCache = {
		landing: DEFAULT_LANDING_PAGE_LAYOUT
	};
	return (pageType) => {
		if (layoutsCache[pageType]) {
			return layoutsCache[pageType];
		} else {
			const defaultLayout = getDefaultLayout(pageType, templatesDefinitions);
			layoutsCache[pageType] = defaultLayout;
			return defaultLayout;
		}
	};
};
