import { createHttpEffect } from '@servicenow/ui-effect-http';
import get from 'lodash/get';
import cloneDeep from 'lodash/cloneDeep';
import {
	SCREEN_PROPERTIES_UPDATE_REQUESTED,
	SCREEN_PROPERTIES_UPDATE_STARTED,
	SCREEN_PROPERTIES_UPDATE_FAILED,
	SCREEN_PROPERTIES_UPDATE_SUCCEEDED
} from './screenPropertiesActions';
import { PAGE_REQUESTED } from '../page/pageActions';
import { updatePageState } from './utils';
import { CLIENT_SCRIPTS_UPDATE_REQUESTED } from '../clientScripts/clientScriptsActions';
import { getPageStatus } from '../page/utils/pageStatus';
import effectProxy from '../helpers/effectProxy';

export const screenPropertiesUpdateRequested = createHttpEffect(
	'/api/now/uxframework/screens/:sysId',
	{
		method: 'PATCH',
		pathParams: ['sysId'],
		queryParams: ['sysparm_transaction_scope'],
		dataParam: 'data',
		startActionType: SCREEN_PROPERTIES_UPDATE_STARTED,
		successActionType: SCREEN_PROPERTIES_UPDATE_SUCCEEDED,
		errorActionType: SCREEN_PROPERTIES_UPDATE_FAILED,
		batch: false
	}
);

const screenPropertiesUpdateStarted = ({
	properties,
	updateProperties,
	dispatch
}) => {
	updateProperties({ isSavingPage: true });
	dispatch(CLIENT_SCRIPTS_UPDATE_REQUESTED, { data: properties.clientScripts });
};

const screenPropertiesUpdateSucceeded = ({
	action,
	properties,
	updateProperties,
	dispatch
}) => {
	const { pages: currentPages = [], pageState, pageStatePristine } = properties;
	const updatedScreen = get(action.payload, 'result', {});
	const { name, isActive, order, screenCondition } = updatedScreen;
	const pageStatus = getPageStatus(isActive);
	const newPageState = cloneDeep(pageState);
	const pages = currentPages.map((page) => {
		if (page.sysId === updatedScreen.id) {
			return {
				...page,
				name,
				pageStatus
			};
		}
		return page;
	});

	//If updated screen is not current screen return

	const currentlyLoadedScreen = updatedScreen.id === pageState.sysId;
	if (!currentlyLoadedScreen) {
		return updateProperties({
			isSavingPage: false,
			isLoadingPage: false,
			pageState: newPageState,
			pages
		});
	} else {
		if (!action.meta.changePageDefinition) {
			updateProperties({
				isLoadingPage: false,
				isSavingPage: false,
				pageState: updatePageState(newPageState, {
					name,
					pageStatus,
					order,
					screenCondition
				}),
				pageStatePristine: updatePageState(pageStatePristine, {
					name,
					pageStatus,
					order,
					screenCondition
				}),
				pages
			});
		} else {
			dispatch(PAGE_REQUESTED, { sysId: updatedScreen.id });
			updateProperties({
				isLoadingPage: false,
				isSavingPage: false,
				pages
			});
		}
	}
};

const screenPropertiesUpdateFailed = ({ updateProperties }) => {
	updateProperties({
		isLoadingPage: false,
		isSavingPage: false
	});
};

export default {
	[SCREEN_PROPERTIES_UPDATE_REQUESTED]: effectProxy(
		screenPropertiesUpdateRequested
	),
	[SCREEN_PROPERTIES_UPDATE_STARTED]: screenPropertiesUpdateStarted,
	[SCREEN_PROPERTIES_UPDATE_FAILED]: screenPropertiesUpdateFailed,
	[SCREEN_PROPERTIES_UPDATE_SUCCEEDED]: screenPropertiesUpdateSucceeded
};
