import Model from './model';

export default class Component extends Model {
	constructor({
		id = '',
		name = '', // string,
		description = '', // string
		icon = '', // string
		tag = '', // string
		slots = [], // Array<Slot>
		properties = [], // Array<Property>
		dispatchedEvents = [], // Array<Event>
		actions = [], //Array<Action>
		configuration = null, //Object || null
		type = 'MACROPONENT', // string
		externalConfigurationSources = [],
		category,
		explodable = false,
		isEditable = false
	}) {
		super();
		this.id = id;
		this.name = name;
		this.description = description;
		this.icon = icon;
		this.tag = tag;
		this.slots = slots;
		this.properties = properties; // If this is a charter, which ones are selectable?
		this.dispatchedEvents = dispatchedEvents;
		this.actions = actions;
		this.configuration = configuration;
		this.type =
			category === 'viewport' || tag === 'now-uxf-modal-viewport'
				? 'MACROPONENT_VIEWPORT'
				: type;
		this.externalConfigurationSources = externalConfigurationSources;
		this.category = category;
		this.explodable = explodable;
		this.isEditable = isEditable;
	}
}
