import get from 'lodash/get';
import isNil from 'lodash/isNil';
import unionBy from 'lodash/unionBy';
import groupBy from 'lodash/groupBy';
import isEmpty from 'lodash/isEmpty';
import { t } from 'sn-translate';
import {
	Component,
	Element,
	ElementEvent,
	MacroponentProperty,
	PageStateModel,
	Property,
	Slot,
	ClientStateParameter
} from '../../../models';
import { getProperty } from '../../componentList/utils';
import { addAllEventHandlers, createDataBrokerInstances } from '../../utils';
import { getPageStatus } from './pageStatus';
import {
	coercePropertyValueToShorthand,
	generateEventMappings
} from './shared';
import { seismicCuid } from '../../../utils';
import {
	PARENT_COMPOSITION_ELEMENT_ID,
	DEFAULT_ROUTE
} from '../../element/reservedPropertyIds';
import {
	initializeViewportElementProperties,
	isViewportComponent,
	initializeDataDrivenViewportProperties
} from '../../viewportHandlers/utils';
import { isUxValueModel, getShorthandFromUxValue } from '../../../factories';
import { ELEMENT_TYPES } from '../../../constants';
import { ROOT_MACROPONENT_SOURCE_EVENTS } from '../constants';

const SHARED_PAGE_DEFINITION_SYS_ID = 'a094b3d2773210106e224a2a69106123';
import { TRANSLATION_LITERAL } from '../../../factories/constants';

const getComponent = (id, macroponentDefinitions, componentDefinitions) => {
	const {
		name: macroponentName,
		toolboxIcon: icon,
		toolboxLabel,
		properties,
		dispatchedEvents = [],
		rootElement = {},
		composition = [],
		rootElementDefinition,
		category,
		explodable = false,
		isEditable = false
	} = macroponentDefinitions[id];

	const availableSlots = rootElementDefinition
		? rootElementDefinition.availableSlots
		: [];

	// If a composition element is a "Facade macroponent", meaning that it has a Seismic component
	// as a root element and no composition, then we can use the tag of its root element.
	// Otherwise, we should use the web component generated by the macroponent factory.
	// This way, we can easily render a complex macroponent as a composition element.
	const tag =
		rootElement.definition.id && composition.length === 0
			? componentDefinitions[rootElement.definition.id]
			: `macroponent-${id}`;

	if (tag === 'now-uxf-page') {
		availableSlots.push('@default');
	}

	const actionInstances = []; // TODO: Get real actions at some point.
	const propertyInstances = properties.map((property) => {
		return getProperty(property);
	});
	const eventInstances = dispatchedEvents.map((ev) => new ElementEvent(ev));

	return new Component({
		id,
		name: toolboxLabel || macroponentName,
		icon,
		tag,
		category,
		actions: actionInstances,
		slots: availableSlots,
		properties: propertyInstances,
		dispatchedEvents: eventInstances,
		explodable,
		isEditable
	});
};

const getIsHiddenValue = (isHiddenValue) => {
	if (!isUxValueModel(isHiddenValue)) {
		return isHiddenValue;
	}
	return getShorthandFromUxValue(isHiddenValue);
};

function createElementPropertiesFromComponent(
	elementId,
	component,
	propertyValues,
	viewportConfigurations,
	state,
	variantId
) {
	const componentDerivedProperties = component.properties.reduce(
		(acc, propertyConfig) => {
			acc[propertyConfig.columnName] = new Property(propertyConfig);
			if (propertyValues.hasOwnProperty(propertyConfig.columnName)) {
				acc[propertyConfig.columnName].value = coercePropertyValueToShorthand(
					propertyValues[propertyConfig.columnName],
					propertyConfig.type
				);
			}

			return acc;
		},
		{}
	);

	const elementProperties = Object.keys(propertyValues).reduce(
		(acc, propertyName) => {
			const incomingValue = propertyValues[propertyName];
			let type = 'string';
			let finalValue = incomingValue;
			let translatable = false;
			if (isUxValueModel(incomingValue)) {
				const tempValue = getShorthandFromUxValue(incomingValue);
				type = typeof tempValue === 'object' ? 'json' : type;
				finalValue = coercePropertyValueToShorthand(incomingValue, type);
				translatable = incomingValue.type === TRANSLATION_LITERAL;
			}
			acc[propertyName] = new Property({
				value: finalValue,
				type,
				label: propertyName,
				translatable
			});
			return acc;
		},
		{}
	);

	let viewportComponentProperties = {};
	if (isViewportComponent(component)) {
		const viewportRoutes = viewportConfigurations[elementId];
		const currentDefaultRoute = get(
			componentDerivedProperties,
			'defaultRoute.value',
			''
		);
		if (viewportRoutes) {
			viewportComponentProperties = initializeViewportElementProperties(
				elementId,
				viewportRoutes,
				currentDefaultRoute,
				state,
				variantId,
				get(elementProperties, 'dynamicTabData.value', '')
			);
		} else {
			// If you have already saved data source in the composition and if there are no viewport routes,
			// we have to initialize/add the properties dynamicTabData and repeatedTabData
			// so the viewport component can be initialized and rendered properly
			// based on the data source which is in the composition.
			// Todo: remove this whenever the viewport behavior has the dynamicTabData property
			viewportComponentProperties = initializeDataDrivenViewportProperties(
				get(elementProperties, 'dynamicTabData.value', ''),
				state
			);

			// if there are no viewport routes, we need to reset the default route.
			// a user could go into the platform or the isolation mode and delete all routes
			// associated with a viewport component. We dont need to retain the
			// old defaultRoute in that case.
			if (currentDefaultRoute) {
				viewportComponentProperties[DEFAULT_ROUTE] = new Property({
					name: DEFAULT_ROUTE,
					columnName: DEFAULT_ROUTE,
					label: t('Default Route'),
					type: 'string',
					value: ''
				});
			}
		}
	}

	return {
		...elementProperties,
		...componentDerivedProperties,
		...viewportComponentProperties
	};
}

const createElementFromCompositionNode = (
	compositionNode,
	componentDefinitions,
	macroponentDefinitions,
	eventHandlers,
	isParentModal = false,
	viewportConfigurations,
	state,
	variantId,
	screenEventMappings,
	parentElement
) => {
	const {
		elementId,
		elementLabel = '',
		definition: { id, type },
		slot,
		propertyValues = {},
		isHidden,
		eventMappings = [],
		styles = {},
		overrides
	} = compositionNode;

	if (!macroponentDefinitions[id]) {
		// The element is missing so return the bare minimum of an Element with an error
		return new Element({
			id: elementId,
			instanceName: elementLabel,
			slot: new Slot({
				name: slot,
				parentElement
			}),
			component: new Component({
				tag: 'now-uxf-error-element'
			}),
			error: new Error(`UXF Error: Component ${id} not found.`)
		});
	}

	const component =
		type === 'MACROPONENT' || type === 'MACROPONENT_VIEWPORT'
			? getComponent(id, macroponentDefinitions, componentDefinitions)
			: {
					id,
					name: componentDefinitions[id],
					tag: componentDefinitions[id],
					type: 'SEISMIC_COMPONENT',
					properties: []
			  };

	const properties = createElementPropertiesFromComponent(
		elementId,
		component,
		propertyValues,
		viewportConfigurations,
		state,
		variantId
	);

	const EventMappings = generateEventMappings(
		eventMappings,
		eventHandlers,
		component.dispatchedEvents || [],
		screenEventMappings
	);

	const internalEventMappings =
		macroponentDefinitions[id].internalEventMappings;

	const isModal =
		get(macroponentDefinitions, `${component.id}.category`, null) ===
		ELEMENT_TYPES.MODAL;

	const isHiddenValue = getIsHiddenValue(isHidden);

	const element = new Element({
		id: elementId,
		instanceName: elementLabel,
		slot: new Slot({
			name: slot,
			parentElement
		}),
		children: [],
		component,
		properties,
		eventMappings: EventMappings,
		layout: {},
		styles,
		isModal,
		isModalDescendant: isParentModal,
		internalEventMappings,
		isHidden: isHiddenValue || ''
	});

	if (overrides) {
		const { layout = {}, composition = [] } = overrides;

		element.layout = layout;
		element.children = composition.map((node) =>
			createElementFromCompositionNode(
				node,
				componentDefinitions,
				macroponentDefinitions,
				eventHandlers,
				isModal,
				viewportConfigurations,
				state,
				null,
				screenEventMappings,
				element
			)
		);
	}

	return element;
};

const getMergedPropertiesFromHierarchy = (
	macroponent,
	macroponentDefinitions = {}
) => {
	let next = macroponent;
	const hierarchyItems = [];
	while (!isNil(next)) {
		hierarchyItems.push(
			get(next, 'properties', []).map((prop) => ({
				...prop,
				inherited: next !== macroponent
			}))
		);
		next = next.extendsSysId && macroponentDefinitions[next.extendsSysId];
	}

	return unionBy(...hierarchyItems, 'name');
};
const formatLayout = (layout, defaultLayout) => {
	if (!get(layout, 'default')) {
		return defaultLayout;
	} else {
		return layout;
	}
};

export const createElementFromMacroponent = (
	macroponent,
	propertyValues = {},
	propertyTestValues = {},
	componentDefinitions,
	macroponentDefinitions,
	eventHandlers,
	pageType,
	viewportConfigurations,
	state,
	variantId,
	screenEventMappings,
	internalEventMappings
) => {
	const {
		name,
		extendsSysId,
		composition = [],
		layout = {},
		id,
		sysPolicy
	} = macroponent;

	const { getDefaultLayout, properties } = state;
	const { isLegacy = false } = properties || {};
	const formattedLayout = formatLayout(layout, getDefaultLayout(pageType));

	const rootElement = extendsSysId
		? macroponentDefinitions[extendsSysId].rootElement
		: macroponent.rootElement;

	const macroThatHasProperties = extendsSysId // TODO: Rename this awful name.
		? macroponentDefinitions[extendsSysId]
		: macroponent;

	const hierarchyProperties = getMergedPropertiesFromHierarchy(
		macroponent,
		macroponentDefinitions
	);

	const componentProperties = macroThatHasProperties.properties.map((prop) => {
		const { fieldType, label, name, readOnly, selectable } = prop;
		return {
			columnName: name,
			label,
			type: fieldType,
			value: get(prop, 'defaultValue', ''), // TODO: This comes from the rootelement props, which may point back to the shell props
			readOnly,
			selectable
		};
	});

	const component = {
		name: macroThatHasProperties.name,
		tag: componentDefinitions[rootElement.definition.id],
		properties: componentProperties,
		slots: ['@default']
	};

	const elementProperties = componentProperties.reduce(
		(acc, propertyConfig) => {
			const isPropertyDefined = !isNil(
				propertyValues[propertyConfig.columnName]
			);
			propertyConfig.value = isPropertyDefined
				? propertyValues[propertyConfig.columnName]
				: propertyConfig.value;
			if (isLegacy && propertyConfig.columnName === 'bare') {
				propertyConfig.value = false;
			}
			acc[propertyConfig.columnName] = new Property(propertyConfig);
			return acc;
		},
		{}
	);

	const children = composition.map((node) =>
		createElementFromCompositionNode(
			node,
			componentDefinitions,
			macroponentDefinitions,
			eventHandlers,
			false,
			viewportConfigurations,
			state,
			variantId,
			screenEventMappings,
			rootElement
		)
	);

	// let's store things in pageState that closely resemble macroponents
	const rawProperties = hierarchyProperties.map((prop) => {
		const propertyValue = propertyValues[prop.name];

		const createdMacroponentProperty = MacroponentProperty.create({
			...prop,
			value: isUxValueModel(propertyValue)
				? getShorthandFromUxValue(propertyValue)
				: propertyValue,
			testValue: propertyTestValues[prop.name]
		});
		return createdMacroponentProperty;
	});

	const element = new Element({
		id,
		instanceName: name,
		children,
		component,
		properties: elementProperties,
		macroponentProperties: rawProperties,
		layout: formattedLayout,
		extendsSysId,
		propertyValues,
		propertyTestValues,
		internalEventMappings,
		scope: macroponent.scope,
		scopeName: macroponent.scopeName,
		domainId: macroponent.domainId,
		domainName: macroponent.domainName,
		sysPolicy
	});

	return element;
};

export const transformSubroutesToViewportConfig = (
	subroutes,
	entityMap = {}
) => {
	const subrouteConfigurations = groupBy(
		subroutes,
		PARENT_COMPOSITION_ELEMENT_ID
	);
	const viewportConfigurations = {};
	for (const viewportElementId in subrouteConfigurations) {
		const viewportConfig = subrouteConfigurations[viewportElementId];
		viewportConfigurations[viewportElementId] =
			viewportConfigurations[viewportElementId] || {};
		viewportConfig.forEach((route) => {
			viewportConfigurations[viewportElementId][route.routeType] =
				viewportConfigurations[viewportElementId][route.routeType] || [];
			viewportConfigurations[viewportElementId][route.routeType].push(route);
		});
	}
	if (isEmpty(entityMap)) {
		return viewportConfigurations;
	} else {
		const reMappedViewportConfig = {};
		Object.keys(viewportConfigurations).forEach((elem) => {
			const newKey = Object.keys(entityMap).find(
				(key) => entityMap[key] === elem
			);
			reMappedViewportConfig[newKey] = viewportConfigurations[elem];
		});
		return reMappedViewportConfig;
	}
};

export default function transformMacroponentToPageState(
	macroponent,
	dataBrokerDefinitions,
	eventHandlers = [],
	clientStateParameters = [],
	state,
	variantId
) {
	const {
		name,
		id: sysId,
		scope,
		scopeName,
		domainId,
		domainName,
		applicationName,
		isReadOnly: readOnly,
		canCopy,
		isActive,
		macroponentInstance: {
			componentDefinitions,
			macroponentDefinitions,
			rootMacroponentId,
			eventMappings = [],
			macroponentPublicHandledEvents = [],
			subroutes,
			clientScripts = []
		},
		pageType,
		order,
		sysPolicy,
		...uiTypeParameters
	} = macroponent;

	const rootMacroponent = macroponentDefinitions[rootMacroponentId];

	const dispatchedEvents = (rootMacroponent.dispatchedEvents || []).map(
		(event) => new ElementEvent(event)
	);
	const handledEvents = (rootMacroponent.handledEvents || []).map(
		(event) => new ElementEvent(event)
	);
	const internalEventMappingsSourceEvents = [
		...(handledEvents || []),
		...ROOT_MACROPONENT_SOURCE_EVENTS
	];

	const { data, stateProperties = [] } = rootMacroponent;

	const viewportConfigurations = transformSubroutesToViewportConfig(subroutes);

	const newClientStateParameters = stateProperties.map((stateParam) => {
		const matchingParam = clientStateParameters.find(
			(param) => param.name === stateParam.name
		);
		return new ClientStateParameter({
			id: matchingParam ? matchingParam.id : seismicCuid(),
			name: stateParam.name,
			type: stateParam.fieldType,
			initialValue: coercePropertyValueToShorthand(
				stateParam.initialValue,
				stateParam.fieldType
			)
		});
	});

	const initialDataBrokerInstances = createDataBrokerInstances(
		data,
		dataBrokerDefinitions
	);
	const updatedEventHandlers = addAllEventHandlers(
		eventHandlers,
		false, // These event handlers are from local context
		initialDataBrokerInstances,
		dataBrokerDefinitions,
		clientScripts,
		dispatchedEvents,
		rootMacroponent,
		macroponentPublicHandledEvents
	);

	const propertyValues = macroponent.macroponentInstance.propertyValues || {};
	const propertyTestValues =
		macroponent.macroponentInstance.propertyTestValues || {};

	const screenEventMappings = generateEventMappings(
		eventMappings || [],
		updatedEventHandlers,
		dispatchedEvents
	);
	const dataBrokerInstances = initialDataBrokerInstances.map((dbInstance) => {
		return {
			...dbInstance,
			eventMappings: generateEventMappings(
				dbInstance.eventMappings || [],
				updatedEventHandlers,
				dbInstance.dispatchedEvents || [],
				screenEventMappings
			)
		};
	});

	const rootMacroponentEventMappings =
		rootMacroponent.internalEventMappings || {};
	const transformedInternalEventMappings = Object.entries(
		rootMacroponentEventMappings
	).map(([eventApiName, targets]) => ({
		eventMappingId: eventApiName,
		sourceEventApiName: eventApiName,
		targets
	}));
	const internalEventMappings = generateEventMappings(
		transformedInternalEventMappings || [],
		updatedEventHandlers,
		internalEventMappingsSourceEvents,
		screenEventMappings
	);

	const rootElement = createElementFromMacroponent(
		rootMacroponent,
		propertyValues,
		propertyTestValues,
		componentDefinitions,
		macroponentDefinitions,
		updatedEventHandlers,
		pageType,
		viewportConfigurations,
		state,
		variantId,
		screenEventMappings,
		internalEventMappings
	);

	const associatedTypes = get(
		macroponentDefinitions[rootMacroponentId],
		'associatedTypes'
	);
	const usingSharedPageDefinition =
		associatedTypes &&
		!!associatedTypes.find((id) => id === SHARED_PAGE_DEFINITION_SYS_ID);

	return new PageStateModel({
		rootElement,
		name,
		sysId,
		scope,
		scopeName,
		domainId,
		domainName,
		applicationName,
		canCopy,
		pageStatus: getPageStatus(isActive),
		sysPolicy,
		readOnly,
		type: pageType,
		uiTypeParameters,
		dataBrokerInstances,
		eventHandlers: updatedEventHandlers,
		dispatchedEvents,
		handledEvents,
		clientStateParameters: newClientStateParameters,
		screenEventMappings,
		order,
		usingSharedPageDefinition
	});
}
