const supportedPropertyTypes = [
	'boolean',
	'choice',
	'condition_string',
	'field_list',
	'field',
	'html',
	'json',
	'number',
	'reference',
	'string',
	'table_name',
	'icon',
	'url',
	'variant',
	'css',
	'text'
];

// In place to maintain compatability with older Glide builds that return type in
// the wrong format.
const typeTranslationLookup = {
	'True/False': 'boolean',
	Choice: 'choice',
	Reference: 'reference',
	String: 'string',
	'Table Name': 'table_name'
};

const createExposedProperty = (rawProperty) => {
	const typeMeta = {};
	const requestedType =
		typeTranslationLookup[rawProperty.type] || rawProperty.type;
	const type = supportedPropertyTypes.includes(requestedType)
		? requestedType
		: 'string';

	if (type === 'choice') {
		typeMeta['choices'] = rawProperty.choices.map((choice) => ({
			label: choice.label,
			value: choice.value
		}));
		if (rawProperty.variant) {
			typeMeta['variant'] = rawProperty.variant;
		}
	} else if (type === 'reference') {
		typeMeta['reference'] = {
			table: rawProperty.reference,
			referenceFilter: rawProperty.referenceFilter
		};
	} else if (
		type === 'condition_string' ||
		type === 'field' ||
		type === 'field_list'
	) {
		typeMeta['table'] = rawProperty.table;
		if (type !== 'condition_string' && rawProperty.variant) {
			typeMeta['variant'] = rawProperty.variant;
		}
	}

	typeMeta['translatable'] = rawProperty.translatable;

	const propObj = {
		id: rawProperty.columnName,
		name: rawProperty.columnName,
		label: rawProperty.label,
		value: rawProperty.value,
		readOnly: rawProperty.readOnly,
		type,
		typeMeta,
		interfaceApiName: rawProperty.interfaceApiName,
		emptyStateBehavior: rawProperty.emptyStateBehavior,
		selectable: rawProperty.selectable,
		description: rawProperty.description,
		error: rawProperty.error,
		defaultValue: rawProperty.defaultValue,
		translatable: rawProperty.translatable
	};

	return propObj;
};

function getElementTreeIcon(tag, isRoot, isModal, isHidden) {
	if (isModal) {
		return !isHidden ? 'square-fill' : 'eye-slash-fill';
	}
	if (isRoot) {
		return 'grid-three-fill';
	}
	if (isHidden) {
		return 'eye-slash-fill';
	}
	switch (tag) {
		case undefined:
			return 'square-dashed-fill';
		case 'now-uxf-page-container':
		case 'now-uxf-page-simple-container':
			return 'record-activity-outline';
		case 'now-uxf-visualization-connected':
		case 'sn-kpi-details':
		case 'macroponent-3119f143532510102b54ddeeff7b126e': // Dashboard viewport
			return 'chart-spline-outline';
		case 'now-record-form-section-connected':
		case 'now-record-form-section-column-layout':
		case 'now-record-common-templates-connected':
		case 'macroponent-4815dd32c7401010d00628caf4c2604f': // form-tabset
			return 'score-list-outline';
		case 'sn-component-filter':
		case 'sn-tab-filter':
			return 'filter-outline';
		case 'now-email-client-composer-connected':
		case 'now-email-client-mini-composer-connected':
		case 'now-email-client-quick-messages':
		case 'now-email-client-viewer-connected':
			return 'envelope-outline';
		case 'now-contextual-sidebar':
		case 'now-uxf-tab-set':
			return 'tabset-outline';
		case 'now-link-set':
		case 'now-link-set-group':
		case 'now-text-link':
		case 'now-iframe':
			return 'link-outline';
		case 'now-avatar':
		case 'now-image':
		case 'now-icon':
			return 'image-outline';
		case 'now-stylized-text':
		case 'now-heading':
			return 'text-outline';
		case 'sn-search-results-container':
		case 'sn-search-facets':
		case 'sn-search-combobox':
			return 'magnifying-glass-fill';
		case 'now-record-list-connected-related':
		case 'now-record-list-connected':
		case 'now-record-list-connected-snapshot':
			return 'table-outline';
		case 'sn-catalog-action':
		case 'sn-catalog-builder-bridge-modal':
		case 'sn-cb-dashboard':
		case 'sn-catalog-builder-item-modal':
		case 'sn-catalog-builder-item-preview':
		case 'sn-catalog-description':
		case 'sn-catalog-form':
		case 'sn-catalog-iframe':
		case 'macroponent-afeef20635001010f877265d86f080df': // Catalog-item
		case 'macroponent-9301c99b73031010e12d1e3014f6a778': // Catalog item templates
		case 'sn-catalog-quantity':
		case 'sn-catalog-wizard':
		case 'sn-catalog-wizard-connected':
		case 'sn-catalog-form-connected':
			return 'shopping-cart-outline';
		default:
			return 'component-outline';
	}
}

// TODO: this fn assumes that layout.items matches DOM order...which may not be true
function sortChildrenUsingGrid(elementId, items, children) {
	const childLookup = children.reduce((acc, child) => {
		const key = child.slot && child.slot.name;
		const existingValue = acc[key] || [];
		return {
			...acc,
			[key]: [...existingValue, child]
		};
	}, {});

	return items.map((item) => ({
		id: `${elementId}-${item.slotName}`,
		elementId,
		slotName: item.slotName,
		instanceName: item.slotName,
		children: childLookup[item.slotName] ? childLookup[item.slotName] : [],
		component: {}
	}));
}

function sortChildrenUsingNamedSlots(elementId, slots, children) {
	const childLookup = children.reduce((acc, child) => {
		const key = child.slot && child.slot.name;
		const existingValue = acc[key] || [];
		return {
			...acc,
			[key]: [...existingValue, child]
		};
	}, {});

	return slots.map((slot) => ({
		id: `${elementId}-${slot}`,
		elementId,
		slotName: slot,
		instanceName: slot,
		children: childLookup[slot] ? childLookup[slot] : [],
		component: {}
	}));
}

function elementHasNamedSlots(element) {
	const slots = get(element, 'component.slots', []);

	return (
		slots.filter((slot) => {
			return slot !== '@default';
		}).length > 0
	);
}

function resolveDynamicValue(isHiddenValue, clientScriptApi) {
	if (!isHiddenValue || typeof isHiddenValue !== 'string') {
		return '';
	}
	const basicBindingMatcher = matchBindingSyntax(isHiddenValue);
	const stateParamBindingMatcher = matchStateBindingSyntax(isHiddenValue);
	const scriptBindingMatcher = matchScriptSyntax(isHiddenValue);

	let resolvedValue = '';
	if (stateParamBindingMatcher || basicBindingMatcher) {
		resolvedValue = get(clientScriptApi, isHiddenValue.slice(1));
	}

	if (scriptBindingMatcher) {
		return resolveScriptedPropertyValue({
			matchedScript: scriptBindingMatcher,
			clientScriptApi
		});
	}
	return resolvedValue;
}

function getHiddenElements(elementArr, clientScriptApi) {
	return elementArr.reduce((acc, cur) => {
		return {
			...acc,
			[cur.id]: cur.isHidden
				? !!resolveDynamicValue(cur.isHidden, clientScriptApi)
				: false
		};
	}, {});
}

function getElementTree(
	rootElement,
	hiddenElements,
	isModalDescendant = false,
	parentElement = null
) {
	if (!rootElement) {
		return {};
	}

	const {
		id,
		elementId,
		slotName,
		instanceName: label,
		children,
		component,
		layout,
		isModal
	} = rootElement;

	if (!id) {
		return {};
	}

	let sortedChildren = children;

	const layoutItems = getWithNullDefault(
		normalizeRowColumnSlots(layout),
		'default.items',
		[]
	);

	if (layoutItems.length > 0) {
		sortedChildren = sortChildrenUsingGrid(id, layoutItems, children);
		// concat modals since they are not assigned to a slot on the layout
		sortedChildren = sortedChildren.concat(
			...children.filter((child) => child.isModal)
		);
	}

	if (elementHasNamedSlots(rootElement)) {
		sortedChildren = sortChildrenUsingNamedSlots(
			id,
			get(rootElement, 'component.slots', []),
			children
		);

		// concat modals since they are not assigned to a slot on the layout
		sortedChildren = sortedChildren.concat(
			...children.filter((child) => child.isModal)
		);
	}

	const isRoot = parentElement === null;
	const tree = {
		id,
		elementId: elementId || id,
		slotName,
		label,
		isModal: !!isModal,
		isRoot,
		isModalDescendant: !!isModalDescendant,
		isNamedSlot: elementHasNamedSlots(parentElement),
		icon: getElementTreeIcon(
			component.tag,
			isRoot,
			isModal,
			!!hiddenElements[id]
		),
		children: sortedChildren.map((child) =>
			getElementTree(
				child,
				hiddenElements,
				!!isModal || !!isModalDescendant,
				rootElement
			)
		),
		component
	};

	if (isModal) {
		tree.layoutTree = new PageTreeModel({
			rootElement
		});
	}

	return tree;
}

function getElementStyles(elementArr) {
	return reduce(
		elementArr,
		(acc, cur) => {
			acc[cur.id] = cur.styles;
			return acc;
		},
		{}
	);
}

function addTemplateId(layout, templateId) {
	const layoutDefault = get(layout, 'default', null);
	if (layoutDefault) {
		layoutDefault.templateId = templateId;
	}
	return layout;
}
function getIsLayoutModified(element, templatesDefinitions) {
	const { layout } = element;
	if (layout) {
		const templateId = get(layout, 'default.templateId', null);

		const templateDefinition = templatesDefinitions.find(
			({ id }) => id === templateId
		);
		const templateLayout = getLayoutFromMacroponent(
			get(templateDefinition, 'macroponentInstance', null)
		);
		addTemplateId(templateLayout, templateId);

		const isLayoutModified = !compareLayouts(layout, templateLayout);
		return isLayoutModified;
	}
	return null;
}

function getElementProperties(elementArr) {
	return reduce(
		elementArr,
		(acc, cur) => {
			acc[cur.id] = cur.properties.map((property) => ({
				...property,
				readOnly: property.readOnly
			}));
			const isScripted =
				cur.isHidden && typeof cur.isHidden === 'string'
					? !!cur.isHidden.match(SCRIPT_SHORTHAND_PATTERN)
					: false;
			acc[cur.id].unshift({
				id: IS_HIDDEN,
				name: IS_HIDDEN,
				label: t('Hide component'),
				description: t('Set component visibility via data resource or script'),
				value: cur.isHidden || '@',
				readOnly: false,
				type: 'string',
				alwaysBound: true,
				isScripted,
				isStatic: false
			});
			acc[cur.id].unshift({
				id: ELEMENT_INSTANCE_NAME,
				name: ELEMENT_INSTANCE_NAME,
				label: 'Instance Name', //TODO: Translate me
				value: cur.instanceName,
				readOnly: false,
				type: 'string'
			});
			return acc;
		},
		{}
	);
}

function getDependentProperties(property, properties) {
	/**
	 * For each property, check whether there are any other properties
	 * whose typeMeta objects contain references to that property.
	 */
	const propertyReference = `@${property.id}`;
	const dependents = reduce(
		properties,
		(acc, prop) => {
			const { typeMeta } = prop;
			typeMeta &&
				Object.entries(typeMeta).forEach(([, data]) => {
					if (typeof data === 'string') {
						if (data === propertyReference) {
							acc.push({ id: prop.id });
						}
					} else if (data && typeof data === 'object') {
						Object.values(data).forEach((val) => {
							if (val === propertyReference) {
								acc.push({ id: prop.id });
							}
						});
					}
				});
			return acc;
		},
		[]
	);
	return dependents;
}

const getElementLayouts = (elementArr, templatesDefinitions) => {
	return reduce(
		elementArr,
		(acc, cur) => {
			const type = cur.elementUsesSNLayout ? CONTAINER_TYPE : LEAF_TYPE;
			acc[cur.id] = {
				layout: cur.layout,
				type,
				isLayoutModified: getIsLayoutModified(cur, templatesDefinitions)
			};
			return acc;
		},
		{}
	);
};

const getElementEvents = (elementArr) => {
	return reduce(
		elementArr,
		(acc, cur) => {
			acc[cur.id] = cur['dispatchedEvents'];
			return acc;
		},
		{}
	);
};

const getElementEventMappings = (elementArr) => {
	return elementArr.reduce((cumulativeEventMappings, { id, eventMappings }) => {
		return {
			...cumulativeEventMappings,
			[id]: eventMappings.map(({ eventId, eventMappingId }) => ({
				eventId,
				eventMappingId
			}))
		};
	}, {});
};

const getScreenEventMappings = (screenEventMappings) => {
	return screenEventMappings.map(({ eventId, eventMappingId }) => ({
		eventId,
		eventMappingId
	}));
};

const getInternalEventMappings = (rootElement) => {
	const mappings = get(rootElement, 'internalEventMappings', []);
	if (isEmpty(mappings)) {
		return [];
	} else {
		return mappings;
	}
};

const getElementToEventMappingHandlerMap = (
	elementArr,
	elementEventMappings,
	eventMappingHandlers
) => {
	const map = {};
	Object.values(eventMappingHandlers).forEach((handlers) => {
		handlers.forEach((handler) => {
			if (!map[handler.handlerTypeId]) {
				map[handler.handlerTypeId] = [];
			}
			map[handler.handlerTypeId].push(
				elementArr.find((element) => {
					return element.eventMappings.find((eventMapping) => {
						return eventMapping.targets.find(
							(target) => target.targetId === handler.handlerId
						);
					});
				})
			);
		});
	});
	return map;
};

function getDataBrokerInstanceProperties(dataBrokerInstances = []) {
	return dataBrokerInstances.reduce((acc, { id, properties = {} }) => {
		acc[id] = Object.values(properties || {}).map((prop) => {
			return createExposedProperty(prop);
		});
		return acc;
	}, {});
}

function setOnChangeForDependeeProperties(propertiesMap) {
	return Object.entries(propertiesMap || {}).reduce((acc, [id, props]) => {
		const transformedProperties = (props || []).map((prop = {}) => {
			const dependents = getDependentProperties(prop, props);
			// If there are dependent properties, create an onChange function for the
			// current property so that the dependees are updated when its value changes.
			if (dependents.length) {
				prop.onChange = (updateElement) => {
					dependents.forEach((dependent) => {
						const { id } = dependent;
						updateElement.properties[id].value = null;
					});
				};
			}

			return prop;
		});

		acc[id] = transformedProperties;
		return acc;
	}, {});
}

function getDataBrokerInstances(dataBrokerInstances) {
	return dataBrokerInstances.map(
		({
			id,
			instanceName,
			readEvaluationMode,
			definition,
			properties,
			selectableProps,
			simpleMutationBroker,
			outputProperties,
			clientStateDataBrokerTagName,
			dispatchedEvents,
			eventMappings
		}) => {
			const transformedPropertiesMap = Object.entries(properties).reduce(
				(acc, [key, value]) => {
					acc[key] = createExposedProperty(value);
					return acc;
				},
				{}
			);

			return {
				id,
				instanceName,
				readEvaluationMode,
				definition,
				properties: transformedPropertiesMap,
				selectableProps,
				simpleMutationBroker,
				selectablePropPreview: `@${id}/preview`,
				selectablePropExecutionTime: `@${id}/executionTime`,
				outputProperties,
				clientStateDataBrokerTagName,
				dispatchedEvents,
				eventMappings
			};
		}
	);
}

const getDataBrokerEventMappings = (dataBrokerInstances) => {
	return dataBrokerInstances.reduce(
		(cumulativeMappings, { id, eventMappings = [] }) => {
			return {
				...cumulativeMappings,
				[id]: eventMappings
			};
		},
		{}
	);
};

function getEventMappingTargetKey(
	declarativeAction,
	event,
	operation,
	clientScript,
	key
) {
	let targetKey;
	if (declarativeAction) {
		targetKey = computeEventMappingTargetKeys(DECLARATIVE_ACTION)[key];
	} else if (event) {
		targetKey = computeEventMappingTargetKeys(EVENT)[key];
	} else if (operation) {
		targetKey = computeEventMappingTargetKeys(DATABROKER_OP)[key];
	} else if (clientScript) {
		targetKey = computeEventMappingTargetKeys(CLIENT_SCRIPT)[key];
	} else {
		targetKey = null;
	}
	return targetKey;
}

function getHandlerTypeId(declarativeAction, event, operation, clientScript) {
	const targetIdKey =
		getEventMappingTargetKey(
			declarativeAction,
			event,
			operation,
			clientScript,
			'idKey'
		) || 'sysId';
	return get(
		declarativeAction || event || operation || clientScript,
		targetIdKey,
		null
	);
}

function getHandlerValuesCollection(
	declarativeAction,
	event,
	operation,
	clientScript
) {
	const targetValueKey =
		getEventMappingTargetKey(
			declarativeAction,
			event,
			operation,
			clientScript,
			'valueKey'
		) || 'payload';
	const values = get(
		declarativeAction || event || operation,
		targetValueKey,
		[]
	);
	return values.map((value) => createExposedProperty(value));
}

const getEventMappingHandlers = (elementArr) => {
	return elementArr.reduce((cumulativeHandlers, element) => {
		return {
			...cumulativeHandlers,
			...element.eventMappings.reduce((cumulativeMappings, mapping) => {
				return {
					...cumulativeMappings,
					[mapping.eventMappingId]: mapping.targets.map(
						({
							type,
							targetId,
							clientTransformScript,
							declarativeAction,
							event,
							operation,
							clientScript
						}) => {
							return {
								handlerType: type,
								handlerId: targetId,
								handlerTypeId: getHandlerTypeId(
									declarativeAction,
									event,
									operation,
									clientScript
								),
								sourceId: operation ? operation.parentResourceId : '',
								handlerName: operation ? operation.operationName : '',
								clientTransformScript,
								values: getHandlerValuesCollection(
									declarativeAction,
									event,
									operation,
									clientScript
								)
							};
						}
					)
				};
			}, {})
		};
	}, {});
};

const isContextBoundProperty = (property) =>
	property &&
	typeof property === 'string' &&
	property.match(
		new RegExp(`^(${CONTEXT_NAMESPACE})\\.(props|session|app)((\\.[\\w]+)+)$`)
	);

const isContextSessionBinding = (property) =>
	isContextBoundProperty(property) &&
	matchContextSessionBindingSyntax(property);

const isContextPropsBinding = (property) =>
	isContextBoundProperty(property) &&
	get(matchBindingSyntax(property), 'id', null) === 'props';

const isContextAppBinding = (property) =>
	isContextBoundProperty(property) &&
	get(matchBindingSyntax(property), 'id', null) === 'app';

const isDataBoundProperty = (property) =>
	property &&
	typeof property === 'string' &&
	property.match(
		new RegExp(`^(${DATA_NAMESPACE})\\.([\\w\\s]+)((\\.[\\w]+)+)$`)
	);

const getBoundValueFromClientScriptApi = (
	bindingExpression,
	clientScriptApi
) => {
	return get(clientScriptApi, bindingExpression.slice(1), null);
};

const getDataBrokerInputPropertiesFromPageState = (
	pageState,
	dataBrokerDataId,
	clientScriptApi
) => {
	const dataBrokerInstances = get(pageState, 'dataBrokerInstances', []);

	const dataBrokerInstance = find(dataBrokerInstances, {
		id: dataBrokerDataId
	});

	if (!dataBrokerInstance) {
		return {};
	}

	const pageProperties = createContextBindings(pageState);

	return Object.values(dataBrokerInstance.properties).reduce(
		(acc, property) => {
			if (property.value && isContextPropsBinding(property.value)) {
				// Context Prop Bindings
				const pagePropName = property.value.substring(
					`${CONTEXT_NAMESPACE}.props`.length + 1
				);

				if (pageProperties[pagePropName]) {
					acc[property.name || property.columnName] =
						pageProperties[pagePropName];
				} else {
					acc[property.name || property.columnName] = '';
				}
			} else if (property.value && isContextSessionBinding(property.value)) {
				// Context session bindings
				acc[
					property.name || property.columnName
				] = getBoundValueFromClientScriptApi(property.value, clientScriptApi);
			} else if (property.value && isContextAppBinding(property.value)) {
				// Context app bindings
				acc[
					property.name || property.columnName
				] = getBoundValueFromClientScriptApi(property.value, clientScriptApi);
			} else if (
				typeof property.value === 'string' &&
				matchStateBindingSyntax(property.value)
			) {
				// State bindings
				acc[
					property.name || property.columnName
				] = getBoundValueFromClientScriptApi(property.value, clientScriptApi);
			} else {
				acc[property.name || property.columnName] = property.value;
			}

			return acc;
		},
		{}
	);
};

function getPageMetadata(value) {
	return {
		type: value.type,
		name: value.name,
		sysId: value.sysId,
		uiTypeParameters: value.uiTypeParameters || {},
		pageStatus: value.pageStatus || PAGE_STATUS_ACTIVE
	};
}

function coerceJSONPagePropertyValue(value) {
	let valueToUse = value;

	if (typeof valueToUse === 'object') {
		try {
			valueToUse = JSON.stringify(valueToUse);
		} catch (_) {
			/*do nothing*/
		}
	}

	return valueToUse;
}

function getPageProperties(rootElement) {
	return rootElement.macroponentProperties.map(
		({
			name,
			id,
			value,
			testValue,
			inherited,
			fieldType,
			defaultValue,
			description,
			typeMetadata,
			label
		}) => {
			let valueToUse = value,
				testValueToUse = testValue;
			if (fieldType === 'json') {
				valueToUse = coerceJSONPagePropertyValue(value);
				testValueToUse = coerceJSONPagePropertyValue(testValue);
			}

			return {
				name,
				id,
				description,
				value: isNil(valueToUse) ? defaultValue : valueToUse,
				testValue: testValueToUse,
				inherited,
				fieldType,
				typeMetadata,
				label
			};
		}
	);
}

function stripViewportsProps(pageState) {
	if (!pageState.rootElement) {
		return pageState;
	}

	const viewportElementIds = pageState.viewportElementIds || [];

	const nodes = [pageState.rootElement];
	while (nodes.length) {
		const node = nodes.shift();
		if (viewportElementIds.includes(node.id)) {
			delete node.properties[ACTIVE_ROUTE];
			delete node.properties[ACTIVE_SCREEN];
			delete node.properties[REPEATED_VIEWPORT_TAB_DATA];
			delete node.properties[VIEWPORT_ROUTES];
		} else {
			nodes.push(...node.children);
		}
	}

	return {
		pageState
	};
}

function isPageStateDirty(pageState, pageStatePristine) {
	const { pageState: pageStateWoViewportTabs } = stripViewportsProps(
		cloneDeep(pageState)
	);
	const { pageState: pageStatePristineWoViewportTabs } = stripViewportsProps(
		cloneDeep(pageStatePristine)
	);

	return !isEqual(pageStateWoViewportTabs, pageStatePristineWoViewportTabs);
}

const addAllEventHandlers = (
	eventHandlers = [],
	isAncestor = false,
	dataBrokerInstances,
	dataBrokerDefinitions,
	clientScripts = [],
	dispatchedEvents = [],
	rootMacroponent = {},
	macroponentPublicHandledEvents = []
) => {
	let newHandlers = dataBrokerInstances.reduce((acc, dbInstance) => {
		const matchingHandler = eventHandlers.find(
			(handler) => handler.sourceId === dbInstance.id
		);
		if (matchingHandler) {
			return acc;
		}

		const definition = dataBrokerDefinitions.find(
			(def) => def.sysId === dbInstance.definition.id
		);
		const operations = (definition && definition.operations) || [];
		const operationsEventHandlers = operations.map((operation) => {
			return new EventHandler({
				id: definition.sysId,
				type: DATABROKER_OP,
				sourceName: dbInstance.instanceName,
				sourceId: dbInstance.id,
				label: operation.name,
				name: operation.name,
				ancestor: isAncestor,
				payloadFields: operation.eventProperties.map((prop) => {
					return {
						...prop,
						type: prop.fieldType
					};
				})
			});
		});
		return acc.concat(operationsEventHandlers);
	}, []);

	newHandlers = newHandlers.concat(
		clientScripts.reduce((acc, clientScript) => {
			const matchingHandler = eventHandlers.find(
				(handler) => handler.sourceId === clientScript.id
			);

			if (matchingHandler) {
				return acc;
			}

			acc.push(
				new EventHandler({
					id: clientScript.id,
					label: clientScript.name,
					includes: clientScript.includes,
					type: CLIENT_SCRIPT,
					sourceName: 'Client script',
					sourceId: clientScript.id,
					name: clientScript.name,
					ancestor: isAncestor,
					payloadFields: []
				})
			);

			return acc;
		}, [])
	);

	newHandlers = newHandlers.concat(
		macroponentPublicHandledEvents.reduce((acc, dispatchedEvent) => {
			const matchingHandler = eventHandlers.find(
				(handler) => handler.id === dispatchedEvent.id
			);

			if (matchingHandler) {
				return acc;
			}

			acc.push(
				new EventHandler({
					id: dispatchedEvent.id,
					apiName: dispatchedEvent.apiName,
					label: dispatchedEvent.label,
					type: 'EVENT',
					sourceName: rootMacroponent.name,
					sourceId: rootMacroponent.id,
					name: dispatchedEvent.eventName,
					ancestor: false,
					payloadFields: dispatchedEvent.payloadShape.map((prop) => {
						return {
							...prop,
							type: prop.fieldType
						};
					}),
					relay: false
				})
			);

			return acc;
		}, [])
	);

	newHandlers = newHandlers.concat(
		dispatchedEvents.reduce((acc, dispatchedEvent) => {
			const matchingHandler = eventHandlers.find(
				(handler) => handler.id === dispatchedEvent.id
			);

			if (matchingHandler) {
				return acc;
			}

			const isRelay = eventHandlers.find((handler) => {
				const { name } = formatEventNameAndLabel(handler, rootMacroponent.id);
				return name === dispatchedEvent.eventName;
			});

			acc.push(
				new EventHandler({
					id: dispatchedEvent.id,
					apiName: dispatchedEvent.apiName,
					label: dispatchedEvent.label,
					type: 'EVENT',
					sourceName: rootMacroponent.name,
					sourceId: rootMacroponent.id,
					name: dispatchedEvent.eventName,
					ancestor: isAncestor,
					payloadFields: dispatchedEvent.payloadShape.map((prop) => {
						return {
							...prop,
							type: prop.fieldType
						};
					}),
					relay: !!isRelay
				})
			);

			return acc;
		}, [])
	);

	return [...eventHandlers, ...newHandlers];
};

/**
 * generates data broker output & "normal" properties based on inputValues
 * @param {DataBrokerDefinition} dataBrokerDefinition
 * @param {Object} inputValues input values such as filter & graphqlData
 */
const generateDataBrokerProperties = (
	dataBrokerDefinition,
	inputValues = null
) => {
	const targetPropertyKey = inputValues
		? DATA_BROKER_INPUT_PROPERTIES_NAME
		: DATA_BROKER_OUTPUT_PROPERTIES_NAME;
	if (!dataBrokerDefinition[targetPropertyKey]) {
		return {};
	}
	return dataBrokerDefinition[targetPropertyKey].reduce(
		(acc, propertyConfig) => {
			acc[propertyConfig.name] = getProperty(propertyConfig);
			if (inputValues) {
				if (inputValues[propertyConfig.name]) {
					if (isUxValueModel(inputValues[propertyConfig.name])) {
						acc[propertyConfig.name].value = getShorthandFromUxValue(
							inputValues[propertyConfig.name]
						);
					} else {
						acc[propertyConfig.name].value = inputValues[propertyConfig.name];
					}
				} else {
					acc[propertyConfig.name].value = propertyConfig.defaultValue;
				}
			}
			return acc;
		},
		{}
	);
};

/**
 * @typedef DBData
 * @property {Object} definition
 * @property {Object} inputValues
 * @property {String} elementLabel
 * @property {String} elementId
 */

/**
 * @typedef DataBrokerDefinition
 * @property {Object} definition
 * @property {Object} inputParams
 * @property {String} name
 * @property {String} type
 * @property {String} sysId
 * @property {String} description
 * @property {String} hydrationSysId
 * @property {Object} outputSchema
 * @property {Object[]} operations
 * @property {Boolean} simpleMutationBroker
 */

/**
 * @typedef dataBrokerInstance
 * @property {String} id
 * @property {String} definition
 * @property {String} instanceName
 * @property {Object} properties
 * @property {*} selectableProps
 * @property {Boolean} simpleMutationBroker
 * @property {Object[]} operations
 * @property {String} clientStateDataBrokerTagName
 * @property {Object} outputProperties
 */

/**
 * this function creates DataBrokerInstances which will become part of the PageStateModel
 * @param {DBData[]} data information from the rootMacroponent associated with the data broker instance
 * @param {DataBrokerDefinition[]} dataBrokerDefinitions existing data broker definitions
 * @return {DataBrokerInstance[]}
 */
const createDataBrokerInstances = (data, dataBrokerDefinitions) => {
	return isEmpty(dataBrokerDefinitions)
		? []
		: data.map((datum) => {
				const definitionId = datum.definition.id;

				const dataBrokerDefinition = find(dataBrokerDefinitions, {
					sysId: definitionId
				});

				if (!dataBrokerDefinition) {
					return new DataBrokerInstance({
						id: datum.elementId,
						instanceName: datum.elementLabel,
						readEvaluationMode: datum.readEvaluationMode,
						definition: {
							...(datum.definition || {}),
							invalid: true
						}
					});
				} else {
					const dataBrokerInputProperties = generateDataBrokerProperties(
						dataBrokerDefinition,
						datum.inputValues
					);
					const dataBrokerOutputProperties = generateDataBrokerProperties(
						dataBrokerDefinition
					);
					const { dispatchedEvents = [] } = dataBrokerDefinition;
					const dataBrokerDispatchedEvents = dispatchedEvents.map(
						(ev) => new ElementEvent({ ...ev, id: ev.apiName })
					);
					return new DataBrokerInstance({
						id: datum.elementId,
						instanceName: datum.elementLabel,
						readEvaluationMode: datum.readEvaluationMode,
						definition: datum.definition,
						properties: dataBrokerInputProperties,
						selectableProps: dataBrokerDefinition.selectableProps,
						hydrationSysId: dataBrokerDefinition.hydrationSysId || null,
						clientStateDataBrokerTagName:
							dataBrokerDefinition.clientStateDataBrokerTagName,
						eventMappings: datum.eventMappings,
						outputProperties: dataBrokerOutputProperties,
						dispatchedEvents: dataBrokerDispatchedEvents
					});
				}
		  });
};

export const createDataBrokerInstancePropertiesFromDataBrokerDefinition = (
	dataBrokerDefinition,
	output = false
) => {
	const targetProperties = output
		? DATA_BROKER_OUTPUT_PROPERTIES_NAME
		: DATA_BROKER_INPUT_PROPERTIES_NAME;
	const instanceProperties = dataBrokerDefinition[targetProperties].reduce(
		(acc, param) => {
			acc[param.name] = getProperty(param);
			return acc;
		},
		{}
	);
	return instanceProperties;
};

const getStateParams = (params = []) => {
	return params.map(({ name, type, initialValue }) => {
		return { name, type, initialValue };
	});
};

const mergeArray = (currentArr, updatedArr, prop) => {
	return currentArr.map((currElem) => {
		const updatedElem = updatedArr.find(
			(item) => item[prop] === currElem[prop]
		);
		return { ...currElem, ...updatedElem };
	});
};

const getElementByProperty = (element, propertyValue, path) => {
	const nodes = [element];
	let parent = null;
	while (nodes.length && !parent) {
		const node = nodes.shift();
		if (get(node, path, null) === propertyValue) {
			parent = node;
		} else {
			if (!isEmpty(node.children)) {
				nodes.push(...node.children);
			}
		}
	}
	return parent;
};

const getClientScriptApi = ({
	elements = null,
	dataBrokerInstances = null,
	ancestorPageProperties = null,
	clientStateData = null,
	dataBrokerDefinitions = null,
	pageState = null,
	appChromeSettings = null
}) => {
	const api = {
		elements: null,
		data: null,
		context: { props: null, session: null }
	};

	// process data brokers
	api.data = dataBrokerInstances.reduce((acc, { id, definition }) => {
		if (definition.type === 'CLIENT_STATE') {
			const targetDefinition =
				dataBrokerDefinitions.find((def) => def.sysId === definition.id) || {};
			const selectableProps = get(targetDefinition, 'selectableProps', '[]');
			let reduceClientStateProps = {};
			try {
				reduceClientStateProps = JSON.parse(selectableProps).reduce(
					(selectablePropAcc, prop) => {
						return {
							...selectablePropAcc,
							[prop.name]: unstableResolvePropertySelectorValue(
								`@${id}/${prop.name}`
							)
						};
					},
					{}
				);
			} catch (_) {
				// do nothing
			}
			return {
				...acc,
				[id]: reduceClientStateProps
			};
		}
		return {
			...acc,
			[id]: unstableResolvePropertySelectorValue(`@${id}/preview`)
		};
	}, {});

	// process context props
	const pageProperties = reduceContextBindingsWithAncestors(
		pageState,
		ancestorPageProperties
	);
	api.context.props = Object.entries(pageProperties).reduce(
		(acc, [propName, propValue]) => {
			let valueToUse = propValue;
			if (!propName.match(/\.parent/)) {
				const bindingMatcher =
					typeof propValue === 'string' && matchBindingSyntax(propValue);
				if (bindingMatcher && bindingMatcher.namespace === DATA_NAMESPACE) {
					const { id, propName: output, path } = bindingMatcher;
					const dataBrokerValue = unstableResolvePropertySelectorValue(
						`@${id}/preview`
					);
					valueToUse = get(
						dataBrokerValue,
						`${output}${path}`,
						valueToUse || null
					);
				}
				return {
					...acc,
					[propName]: valueToUse
				};
			}
			return acc;
		},
		{}
	);

	// process context session
	api.context.session = unstableResolvePropertySelectorValue(`@session/output`);

	// process context app
	api.context.app = appChromeSettings || {};

	// process elements
	api.elements = elements.reduce((accElements, { id, properties }) => {
		return {
			...accElements,
			[id]: properties.reduce((accProps, prop) => {
				if (prop.selectable) {
					return {
						...accProps,
						[prop.id || prop.name]:
							unstableResolvePropertySelectorValue(
								`@${id}/${prop.id || prop.name}`
							) || prop.value
					};
				}
				return accProps;
			}, {})
		};
	}, {});

	// process clientState
	api.state = clientStateData;

	return api;
};

const createEventHandlerFromMcpEvent = (handledEvent, mcpId, mcpName) =>
	new EventHandler({
		ancestor: false,
		apiName: handledEvent.apiName,
		id: handledEvent.id,
		label: handledEvent.label,
		name: handledEvent.eventName,
		type: EVENT,
		sourceId: mcpId,
		sourceName: mcpName,
		payloadFields: handledEvent.payloadShape.map((prop) => ({
			defaultValue: prop.defaultValue,
			type: prop.fieldType,
			label: prop.label,
			name: prop.name,
			typeMetadata: prop.typeMetadata,
			interfaceApiName: prop.interfaceApiName,
			description: prop.description
		}))
	});

const getEventHandlersFromRootMacroponent = (
	macroponentDefinitions,
	rootMacroponentId
) => {
	const rootMacroponent = macroponentDefinitions[rootMacroponentId];
	const extensionParentId = rootMacroponent.extendsSysId;
	const extensionParent = macroponentDefinitions[extensionParentId];
	const eventHandlers = [];
	if (rootMacroponent) {
		rootMacroponent.handledEvents.forEach((handledEvent) => {
			eventHandlers.push(
				createEventHandlerFromMcpEvent(
					handledEvent,
					rootMacroponent.id,
					rootMacroponent.name
				)
			);
		});
	}
	if (extensionParentId && extensionParent) {
		extensionParent.handledEvents.forEach((handledEvent) => {
			eventHandlers.push(
				createEventHandlerFromMcpEvent(
					handledEvent,
					rootMacroponent.id,
					rootMacroponent.name
				)
			);
		});
	}
	return eventHandlers;
};

const processClientStateData = (
	params,
	pageState,
	ancestorPageProperties,
	dataBrokerInstances,
	ancestorDataBrokerInstances,
	ancestorElements,
	appChromeSettings
) => {
	const contextBindings = reduceContextBindingsWithAncestors(
		pageState,
		ancestorPageProperties
	);
	const { rootElement } = pageState;

	return params.reduce((acc, cur) => {
		const { name, initialValue, type } = cur;
		if (!name) {
			return acc;
		}

		if (type === 'json') {
			try {
				return { ...acc, [name]: JSON.parse(initialValue) };
			} catch (e) {
				return { ...acc, [name]: initialValue };
			}
		}

		const matchedValues =
			initialValue &&
			typeof initialValue === 'string' &&
			matchBindingSyntax(initialValue);
		if (matchedValues) {
			const resolved = resolvePropertyBinding({
				matchedValues,
				bindings: contextBindings,
				ancestorPageProperties: {},
				dataBrokerInstances,
				ancestorDataBrokerInstances,
				rootElement,
				ancestorElements,
				appChromeSettings
			});
			if (resolved === undefined) {
				acc[name] = '';
			} else {
				acc[name] = resolved;
			}
		} else {
			acc[name] = initialValue;
		}

		return acc;
	}, {});
};

// Autocomplete functions

function getTypeForValue(value) {
	if (value === null) {
		return 'null';
	}

	const type = typeof value;
	if (type === 'object') {
		// Use lodash to differentiate object vs. array
		if (isArray(type)) {
			return 'array';
		}
	}

	return type;
}

function addInterfaceFromObjectKeys(obj, interfaceName, interfaces) {
	if (!obj) {
		return;
	}

	const props = [];
	for (const name of Object.keys(obj)) {
		// Skip identifiers that start with a number, as they are invalid
		// for interface names and cause Monaco to ignore the entire interface.
		if (name[0] >= '0' && name[0] <= '9') {
			continue;
		}
		const entry = {
			name,
			type: getTypeForValue(obj[name])
		};
		props.push(entry);
	}

	interfaces.push({ name: interfaceName, props });
}

// Find an existing entry for the specified interface/prop and change its type
function overridePropType(interfaces, interfaceName, propName, newType) {
	const intf = find(interfaces, (i) => i.name === interfaceName);
	if (intf) {
		const prop = find(intf.props, (p) => p.name === propName);
		if (prop) {
			prop.type = newType;
			return true;
		}
	}
	return false;
}

function addTypesForNameValuePairs(obj, enumName, _interfaces, defs) {
	defs.push(`enum ${enumName}_enum {`);

	for (const entry of obj) {
		defs.push(`  "${entry.name.toUpperCase()}" = "${entry.name}",`);
	}
	defs.push('}');

	// Now add the type that references the enum
	defs.push(`type ${enumName} = {`);
	defs.push(`  [key in ${enumName}_enum]: any;`);
	defs.push('};');
}

function addTypesForNameObjectKeys(obj, enumName, _interfaces, defs) {
	defs.push(`enum ${enumName}_enum {`);

	for (const name of obj) {
		defs.push(`  "${name.toUpperCase()}" = "${name}",`);
	}
	defs.push('}');

	// Now add the type that references the enum
	defs.push(`type ${enumName} = {`);
	defs.push(`  [key in ${enumName}_enum]: any;`);
	defs.push('};');
}

function addInterfacesForContextSessionUser(
	user,
	interfaceName,
	interfaces,
	defs
) {
	if (!user) {
		return;
	}

	// Create an interface for the keys at this level that are not specifically overriden below
	addInterfaceFromObjectKeys(user, interfaceName, interfaces);

	// Add the interface for `preferences`
	addTypesForNameValuePairs(
		user.preferences,
		'api_context_session_user_preferences',
		interfaces,
		defs
	);

	// Override the type for `preferences`
	overridePropType(
		interfaces,
		'api_context_session_user',
		'preferences',
		'api_context_session_user_preferences'
	);

	// Add the enum for `roles`
	addTypesForNameObjectKeys(
		user.roles,
		'api_context_session_user_roles',
		interfaces,
		defs
	);

	// Override the type for `roles`
	overridePropType(
		interfaces,
		'api_context_session_user',
		'roles',
		'api_context_session_user_roles'
	);
}

function addInterfacesForContextSession(
	session,
	interfaceName,
	interfaces,
	defs
) {
	if (!session) {
		return;
	}

	// Create
	// enum Options {
	//   ONE = 'one.1',
	//   TWO = 'two',
	//   THREE = 'three'
	// }

	// type OptionRequirements = {
	//   [key in Options]: any; // Note that "key in".
	// };

	// Add the interface for `user`
	addInterfacesForContextSessionUser(
		session.user,
		'api_context_session_user',
		interfaces,
		defs
	);

	// Add the interface for `properties`
	addInterfaceFromObjectKeys(
		session.properties,
		'api_context_session_properties',
		interfaces
	);

	// Create an interface for the keys at this level that are not specifically overridden below
	addInterfaceFromObjectKeys(session, interfaceName, interfaces);

	// Override the type for `user`
	overridePropType(
		interfaces,
		'api_context_session',
		'user',
		'api_context_session_user'
	);

	// Override the type for `prooperties`
	overridePropType(
		interfaces,
		'api_context_session',
		'properties',
		'api_context_session_properties'
	);
}

function addInterfacesForContext(context, interfaceName, interfaces, defs) {
	if (!context) {
		return;
	}

	// Create an interface for context_app
	addInterfaceFromObjectKeys(context.app, 'api_context_app', interfaces);

	// Create an interface for context_props
	addInterfaceFromObjectKeys(context.props, 'api_context_props', interfaces);

	addInterfacesForContextSession(
		context.session,
		'api_context_session',
		interfaces,
		defs
	);

	// Add the hard-coded interface for 'context' itself
	const contextInterface = {
		name: interfaceName,
		props: [
			{ name: 'app', type: 'api_context_app' },
			{ name: 'props', type: 'api_context_props' },
			{ name: 'session', type: 'api_context_session' }
		]
	};
	interfaces.push(contextInterface);
}

// This is where we map paths to handlers
const apiPaths = [
	{
		srcPath: ['elements'], // Where do we read the props from
		apiPath: ['elements'], // Where in the API should the resulting interface live
		interfaceName: 'api_elements', // Name of the interface type that will be defined
		handler: addInterfaceFromObjectKeys
	},
	{
		srcPath: ['data'], // Where do we read the props from
		apiPath: ['data'], // Where in the API should the resulting interface live
		interfaceName: 'api_data', // Name of the interface type that will be defined
		handler: addInterfaceFromObjectKeys
	},
	{
		srcPath: ['state'], // Where do we read the props from
		apiPath: ['state'], // Where in the API should the resulting interface live
		interfaceName: 'api_state', // Name of the interface type that will be defined
		handler: addInterfaceFromObjectKeys
	},
	{
		srcPath: ['context'], // Where do we read the props from
		apiPath: ['context'], // Where in the API should the resulting interface live
		interfaceName: 'api_context', // Name of the interface type that will be defined
		handler: addInterfacesForContext
	}
];

function getClientScriptApiCompletions(api, isClientTransformScript = false) {
	const defs = ['/** UI Builder Client Script API **/'];

	// Each API entry is an object with string keys, which are the API names,
	// and a string value, which is the name of the data type.  This is either
	// a simple type like 'string', 'number' or 'boolean', or it is the name
	// of another interface.
	const interfaces = [];
	const apiInterface = {};

	// Iterate over the defined paths
	for (const pathEntry of apiPaths) {
		pathEntry.handler(
			get(api, pathEntry.srcPath, {}),
			pathEntry.interfaceName,
			interfaces,
			defs
		);

		// TODO: Do we need to allow for placing parts of the API in alternate nested locations?
		// TODO: If so, we need to expand the line below to take all of apiPath into account.
		apiInterface[pathEntry.apiPath[0]] = pathEntry.interfaceName;
	}

	// Serialize the interfaces to a final string
	for (const iface of interfaces) {
		defs.push(`interface ${iface.name} {`);
		for (const prop of iface.props) {
			defs.push(`  ${prop.name}: ${prop.type};`);
		}
		defs.push('}');
	}

	// Define interface for event
	defs.push('interface event {');
	defs.push('  elementId: string;');
	defs.push('  name: string;');
	defs.push('  payload: any;');
	defs.push('}');

	// Define interface for imports
	// TODO: This is a temporary placeholder.  Need a separate PR that injects the names of the available imports here.
	defs.push('interface imports {');
	defs.push(' [key: string]: any;');
	defs.push('}');

	// Serialize the main interface
	defs.push('interface api {');
	for (const propName of Object.keys(apiInterface)) {
		const propType = apiInterface[propName];
		defs.push(`  ${propName}: ${propType};`);
	}
	if (!isClientTransformScript) {
		defs.push('  /** Set current client state values */');
		defs.push('  setState(name: string, state: object): void;');
	}
	defs.push('}');

	defs.push('interface params {');
	defs.push('  api: api;');
	defs.push('  event: event;');
	defs.push('  imports: imports;');
	defs.push('}');

	return defs.join('\n');
}

const isAclViolation = (runtimeErrors) => {
	const ACL_ERROR_PATTERN = new RegExp('^.*(ACL).*$', 'i');
	if (Array.isArray(runtimeErrors)) {
		return runtimeErrors.some(
			(error) => typeof error === 'string' && error.match(ACL_ERROR_PATTERN)
		);
	}
};

const generateDataBrokerErrorObj = (errors) => {
	const errorObj = { errors };
	Object.defineProperty(errorObj, 'aclViolation', {
		enumerable: false,
		configurable: false,
		writable: false,
		value: isAclViolation(errors)
	});
	return errorObj;
};

