const clientStateParamRef = (param) => ({
	name: param.name,
	icon: 'address-card-outline',
	action: CLIENT_STATE_PARAMETER_CHANGED,
	payload: {
		id: param.id,
		value: '',
		context: 'param-value'
	}
});

const elementEventsRef = (elementName) => ({
	name: `${elementName} (Events)`,
	icon: 'component-outline'
	// no action or payload needed
});

const assessTarget = (target, item) => {
	const { targetKey, valueKey, idKey } = computeEventMappingTargetKeys(
		target.type
	);

	return {
		targetKey,
		valueKey,
		idKey,
		targetIsScriptOrOp:
			(targetKey === 'clientScript' && target[targetKey][idKey] === item) ||
			(targetKey === 'operation' &&
				target[targetKey].parentResourceId === item),
		properties: get(target, `[${targetKey}][${valueKey}]`, [])
	};
};

const isItemReferencedFn = (deleting, item) => (prop) => {
	let match;
	switch (deleting) {
		case 'pageProperty':
			match = matchBindingSyntax(prop);
			return match && match.propName === item;
		case 'dataBrokerInstance':
			match = matchBindingSyntax(prop);
			return match && match.id === item;
		case 'clientStateParameter':
			match = matchStateBindingSyntax(prop);
			return match && match.name === item;
		default:
			return false;
	}
};

/**
 * @param {string} deleting one of 'pageProperty', 'dataBrokerInstance', 'clientStateParameter', 'clientScript'
 * @param {string} item id (or name) of item to be deleted
 * @param {object} pageState @uxf/pageState
 * @returns {boolean} defaults to false
 */
function isReferenced(deleting = null, item = null, pageState = {}) {
	const {
		rootElement = {},
		dataBrokerInstances = [],
		clientStateParameters = []
	} = pageState;

	const refs = [];
	const isItemReferenced = isItemReferencedFn(deleting, item);

	// search root element internal event mappings
	(rootElement.internalEventMappings || []).forEach((mapping) => {
		mapping.targets.forEach((target) => {
			const { targetIsScriptOrOp, properties } = assessTarget(target, item);
			if (targetIsScriptOrOp) {
				refs.push(elementEventsRef('Page configuration'));
			} else {
				properties.forEach((property) => {
					if (isItemReferenced(property.value)) {
						refs.push(
							pageEventMappingRef(
								mapping.eventMappingId,
								target.targetId,
								property.name || property.id
							)
						);
					}
				});
			}
		});
	});

	// search element properties
	createElementArray(rootElement, (element) => {
		Object.keys(element.properties || {}).forEach((key) => {
			if (isItemReferenced(element.properties[key].value)) {
				refs.push(elementPropertyRef(element, key));
			}
		});
		(element.eventMappings || []).forEach((mapping) => {
			mapping.targets.forEach((target) => {
				const { targetIsScriptOrOp, properties } = assessTarget(target, item);
				if (targetIsScriptOrOp) {
					refs.push(elementEventsRef(element.instanceName));
				} else {
					properties.forEach((property) => {
						if (isItemReferenced(property.value)) {
							refs.push(
								elementEventMappingRef(
									element,
									mapping.eventMappingId,
									target.targetId,
									property.name || property.id
								)
							);
						}
					});
				}
			});
		});
	});

	// search data broker instances
	dataBrokerInstances.forEach((instance) => {
		Object.keys(instance.properties || {}).forEach((key) => {
			if (isItemReferenced(instance.properties[key].value)) {
				refs.push(dataBrokerInstanceRef(instance, key));
			}
		});
		(instance.eventMappings || []).forEach((mapping) => {
			mapping.targets.forEach((target) => {
				const { targetIsScriptOrOp, properties } = assessTarget(target, item);
				if (targetIsScriptOrOp) {
					refs.push({
						name: `${instance.instanceName} (Event)`,
						icon: 'database-outline'
					});
				} else {
					properties.forEach((property) => {
						if (isItemReferenced(property.value)) {
							refs.push(
								dataBrokerEventMappingRef(
									instance,
									mapping.eventMappingId,
									target.targetId,
									property.name || property.id
								)
							);
						}
					});
				}
			});
		});
	});

	// search client state parameters
	clientStateParameters.forEach((parameter) => {
		if (isItemReferenced(parameter.initialValue)) {
			refs.push(clientStateParamRef(parameter));
		}
	});

	return isEmpty(refs) ? false : refs;
}

const updateDeclarativeClientStateUpdateEventHandlers = (
	eventMappingHandlers,
	clientStateParamsToUse
) => {
	Object.values(eventMappingHandlers).forEach((handlers) => {
		handlers.forEach((handler) => {
			if (handler.handlerTypeId === UPDATE_CLIENT_STATE_HANDLER_ID) {
				handler.values.forEach((handlerValue) => {
					if (
						handlerValue.name === 'propName' ||
						handlerValue.columnName === 'propName'
					) {
						handlerValue.type = 'choice';
						handlerValue.typeMeta = {
							choices: clientStateParamsToUse.map((param) => ({
								value: param.name,
								label: param.name
							}))
						};
					}

					if (handlerValue.name === 'value') {
						const propNameProperty =
							handler.values.find((prop) => prop.name === 'propName') || {};
						const matchingClientStateParam =
							clientStateParamsToUse.find(
								(param) => param.name === propNameProperty.value
							) || {};
						handlerValue.type = matchingClientStateParam.type || 'string';
					}
				});
			}
		});
	});

	return eventMappingHandlers;
};

