import {
	CONTEXT_NAMESPACE,
	DATA_NAMESPACE,
	ELEMENT_NAMESPACE,
	STATE_NAMESPACE
} from './constants';

import { SCRIPT_SHORTHAND_PATTERN } from './factories/constants';
import cuid from 'cuid';
import { findElementById } from './sn-uxf-builder-middleware/element/utils/element';
import get from 'lodash/get';
import has from 'lodash/has';
import { unstableResolvePropertySelectorValue } from '@servicenow/ui-core';

import BindingFactory from './factories/binding';

import LintWorkerFactory from './lintWorker';

export const seismicCuid = () => cuidToSeismicFriendlyId(cuid());
export const getWithNullDefault = (obj, path, fallback) => {
	const result = get(obj, path, fallback);
	if (result === null) {
		return fallback;
	}
	return result;
};

const walkRowColumnTree = (node, acc = []) => {
	const childLookup = {
		column: 'rows',
		row: 'columns'
	};
	const { id, type, slotName } = node;
	const children = node[childLookup[type]] || [];
	if (slotName) {
		acc.push({ slotName, type, id });
	}
	children.forEach((child) => {
		walkRowColumnTree(child, acc);
	});
	return acc;
};
export const normalizeRowColumnSlots = (layout) => {
	const { type, root } = get(layout, 'default', {});
	if (type !== 'rowColumn') {
		return layout;
	}
	const items = walkRowColumnTree(root);
	return {
		default: {
			...layout.default,
			items
		}
	};
};

export const cuidToSeismicFriendlyId = (cuid) =>
	cuid.replace(/[0-9]/g, function (num) {
		const letterCode = String.fromCharCode(Number(num, 10) + 97);
		return letterCode + letterCode;
	});

export const getURLParamOverrides = () => {
	const params = new URL(window.location.href).searchParams;

	const urlParamsOverrides = {};
	for (const param of params.entries()) {
		urlParamsOverrides[param[0]] = param[1];
	}

	return urlParamsOverrides;
};

const ERROR_MESSAGE_PREFIX = 'UXF Middleware';
export const throwError = (msg, MiddlewareError = Error) => {
	const message = `${ERROR_MESSAGE_PREFIX} ${msg}`;
	throw new MiddlewareError(message);
};

export const messageInfo = (msg, data) => {
	// eslint-disable-next-line no-console
	console.info(`UXF Message: ${msg}`, data || null);
};

export const matchBindingSyntax = (value) => {
	const patternString = `^(${CONTEXT_NAMESPACE}|${ELEMENT_NAMESPACE}|${DATA_NAMESPACE})\\.([^\\.\\/]+){1}\\.?([^\\.\\/]+)*(\\.[^\\/]+)*$`;
	const matchedValues =
		typeof value === 'string' && (value || '').match(new RegExp(patternString));
	if (matchedValues) {
		return {
			namespace: matchedValues[1],
			id: matchedValues[2],
			propName: matchedValues[3],
			path: matchedValues[4] || null
		};
	}
	return null;
};

export const matchScriptSyntax = (value) => {
	const patternString = SCRIPT_SHORTHAND_PATTERN;
	const matchedScript =
		typeof value === 'string' && (value || '').match(new RegExp(patternString));
	if (matchedScript) {
		return matchedScript[1];
	} else {
		return null;
	}
};

export const matchContextAppBindingSyntax = (value) => {
	const appPatternString = `^(${CONTEXT_NAMESPACE}\\.app\\.)(.+)*$`;
	const matchedValues = value.match(new RegExp(appPatternString));
	if (matchedValues) {
		return { path: matchedValues[2] };
	}
	return null;
};

export const matchContextSessionBindingSyntax = (value) => {
	const pattern = `^${CONTEXT_NAMESPACE}\\.session(\\..+)*$`;
	const matchedValues =
		typeof value === 'string' && (value || '').match(new RegExp(pattern));
	if (matchedValues) {
		return {
			path: matchedValues[1]
		};
	}
	return null;
};

export const matchStateBindingSyntax = (value) => {
	const statePatternString = `^(${STATE_NAMESPACE})\\.(.+)`;
	const matchedValues =
		typeof value === 'string' &&
		(value || '').match(new RegExp(statePatternString));
	if (matchedValues) {
		const path = matchedValues[2];
		return { name: path };
	}
	return null;
};

export const deserializeAndExecuteScript = (inlineScript, api) => {
	const newFn = new Function('api', `return (${inlineScript})({api})`);
	let resolved;
	try {
		resolved = newFn.apply(this, [api]);
	} catch (_) {
		resolved = '';
	}
	return resolved;
};

export const resolveScriptedPropertyValue = ({
	matchedScript,
	clientScriptApi
}) => {
	return deserializeAndExecuteScript(matchedScript, clientScriptApi);
};

export const resolvePropertyBinding = ({
	matchedValues = {},
	bindings = {},
	ancestorPageProperties = {},
	dataBrokerInstances,
	ancestorDataBrokerInstances,
	rootElement,
	ancestorElements,
	appChromeSettings,
	pathToUse = '',
	pagePropertiesSeen = []
}) => {
	const { namespace, id, propName, path } = matchedValues;
	if (namespace === CONTEXT_NAMESPACE) {
		if (id === 'props') {
			if (has(bindings, propName)) {
				const boundPropertyValue = bindings[propName];

				const resolvedValues =
					typeof boundPropertyValue === 'string' &&
					matchBindingSyntax(boundPropertyValue);
				let dotWalkPath = '';
				dotWalkPath += path || '';
				dotWalkPath += pathToUse || '';

				if (typeof boundPropertyValue === 'object') {
					return get(boundPropertyValue, dotWalkPath.slice(1));
				}

				if (resolvedValues) {
					return resolvePropertyBinding({
						matchedValues: resolvedValues,
						bindings: ancestorPageProperties,
						ancestorPageProperties: {},
						dataBrokerInstances: ancestorDataBrokerInstances,
						ancestorDataBrokerInstances: [],
						rootElement,
						ancestorElements,
						appChromeSettings,
						pathToUse: dotWalkPath,
						pagePropertiesSeen
					});
				} else {
					return boundPropertyValue;
				}
			}
		} else if (id === 'session') {
			let dataExpression = `${DATA_NAMESPACE}.session.output`;
			dataExpression += propName ? `.${propName}` : '';
			dataExpression += propName && path ? path : '';
			return resolvePropertyBinding({
				matchedValues: matchBindingSyntax(dataExpression),
				bindings: {},
				appChromeSettings,
				dataBrokerInstances: [
					...dataBrokerInstances,
					...ancestorDataBrokerInstances
				]
			});
		} else if (id === 'app') {
			const boundPropertyValue = appChromeSettings[propName];

			let dotWalkPath = '';
			dotWalkPath += path || '';
			dotWalkPath += pathToUse || '';

			if (typeof boundPropertyValue === 'object') {
				return get(boundPropertyValue, dotWalkPath.slice(1));
			}

			return boundPropertyValue;
		}
	} else if (namespace === ELEMENT_NAMESPACE) {
		const boundEl = findElementById(rootElement, id) || ancestorElements[id];
		if (boundEl) {
			const resolvedPath = path ? `${propName}${path}` : propName;
			return `@${id}/${resolvedPath}${pathToUse}`;
		}
	} else if (namespace === DATA_NAMESPACE) {
		const boundDbInstance = dataBrokerInstances.find((db) => db.id === id);

		if (boundDbInstance) {
			const resolvedPath = path ? `${propName}${path}` : propName;

			try {
				const bindingFactory = BindingFactory({
					shorthand: `${namespace}.${id}.${resolvedPath}${pathToUse}`
				});

				const model = bindingFactory.getModel();

				if (boundDbInstance.definition.type === 'CLIENT_STATE') {
					const propData = unstableResolvePropertySelectorValue(
						`@${id}/${propName}`
					);

					return get(propData, model.binding.address.slice(2));
				}

				const fullData = unstableResolvePropertySelectorValue(`@${id}/preview`);

				return get(fullData, model.binding.address.slice(1));
			} catch (_) {
				return null;
			}
		}
	}
};

export const resolveBinding = (
	property,
	{
		bindings,
		ancestorPageProperties,
		dataBrokerInstances,
		ancestorDataBrokerInstances,
		rootElement,
		ancestorElements,
		clientScriptApi,
		appChromeSettings,
		type
	}
) => {
	const stateBinding =
		property &&
		typeof property === 'string' &&
		matchStateBindingSyntax(property);
	if (stateBinding) {
		return `@uxf/clientStateData.${stateBinding.name}`;
	}

	const matchedScript =
		property && typeof property === 'string' && matchScriptSyntax(property);
	let propertyToProcess = property;
	// Check to see if the property is either a binding or a script
	if (matchedScript) {
		propertyToProcess = resolveScriptedPropertyValue({
			matchedScript,
			clientScriptApi
		});
		return propertyToProcess;
	}

	const matchedValues =
		propertyToProcess &&
		typeof propertyToProcess === 'string' &&
		matchBindingSyntax(propertyToProcess);

	if (matchedValues) {
		const resolved = resolvePropertyBinding({
			matchedValues,
			bindings,
			ancestorPageProperties,
			dataBrokerInstances,
			ancestorDataBrokerInstances,
			rootElement,
			ancestorElements,
			appChromeSettings
		});
		propertyToProcess = resolved;
	}

	if (type === 'json' && typeof propertyToProcess === 'string') {
		try {
			return JSON.parse(propertyToProcess);
		} catch (error) {
			/*  */
		}
	}

	return propertyToProcess;
};

export const processLinting = (scripts) => {
	const id = seismicCuid();
	const lintWorker = LintWorkerFactory();

	lintWorker.postMessage({
		name: 'UXF#LINT_REQUESTED',
		payload: {
			scripts,
			origin: window.location.origin,
			id
		}
	});

	return new Promise((resolve, reject) => {
		setTimeout(() => {
			return reject();
		}, 5000);
		const onComplete = ({ data }) => {
			if (
				data.name === 'UXF_LINT_WORKER#LINTING_COMPLETE' &&
				data.payload.id === id
			) {
				lintWorker.removeEventListener('message', onComplete);
				return resolve(data.payload.result);
			}
		};
		lintWorker.addEventListener('message', onComplete);
	});
};
