import '@servicenow/now-uxf-page';
import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './sn-uxf-renderer.scss';
import get from 'lodash/get';
import createContextBindings from '../sn-uxf-builder-middleware/pageProperties/utils/createContextBindings';
import { t } from 'sn-translate';
import { installTheme } from '@devsnc/library-uxf';
import { findSelectedElement, closeModal } from './utils';
import resetTheme from './resetTheme.json';
import {
	isCustomModalComponent,
	isViewportModalComponent
} from '../sn-uxf-builder-middleware/modalhandlers/utils';
import './sn-uib-modal-content';
import '@servicenow/now-empty-state';

const renderBareModalContent = ({
	element,
	renderers,
	bindings,
	rootElement,
	hiddenElements
}) => {
	const component = get(element, 'component', {});
	if (isCustomModalComponent(component)) {
		return (
			<div
				className="modal-content"
				style={hiddenElements[element.id] ? { display: 'none' } : null}>
				<sn-uxf-element-mount
					element={element}
					renderers={renderers}
					bindings={bindings}
					rootElement={rootElement}
				/>
			</div>
		);
	}
	if (isViewportModalComponent(component)) {
		return (
			<div
				className="modal-content"
				style={hiddenElements[element.id] ? { display: 'none' } : null}>
				<now-empty-state
					variant="auto"
					icon="modal-outline"
					headerText={t('The content of this viewport modal are dynamic.')}
					bodyText={t('Use event binding to pass content in.')}
				/>
			</div>
		);
	}
};

const view = (state) => {
	const {
		properties: {
			page,
			renderers,
			selectedElementId,
			stageDimensions,
			theme,
			bindings,
			selectedSlotName,
			selectedSlotParent,
			hiddenElements
		}
	} = state;

	if (page) {
		const { rootElement } = page;

		const { top = 0, height = 1000 } = stageDimensions;

		const elementId = selectedSlotName ? selectedSlotParent : selectedElementId;

		const selectedElement =
			findSelectedElement(page.rootElement, elementId) || {};

		let themeAsJson;
		try {
			themeAsJson = JSON.parse(theme);
		} catch (er) {
			themeAsJson = {};
		}

		const resetThemeWithOverrides = {
			...themeAsJson,
			theme: {
				...resetTheme,
				...themeAsJson.theme
			}
		};
		const installResetThemeWithOverrides = (vnode) => {
			installTheme(
				resetThemeWithOverrides,
				vnode.elm,
				'reset-theme',
				'#sn-uxf-renderer-theme-with-overrides'
			);
		};

		const modalStyle = {
			top: `${top}px`,
			height: `${height}px`
		};

		hiddenElements[selectedElement.id] && (modalStyle.display = 'none');

		return (
			<div
				class={{
					'stage-renderer': true,
					'-modal': selectedElement && selectedElement.isModal
				}}>
				<div
					id="sn-uxf-renderer-theme-with-overrides"
					hook-insert={installResetThemeWithOverrides}>
					<sn-uxf-element-mount
						element={page.rootElement}
						renderers={renderers}
						bindings={bindings}
						rootElement={rootElement}
						hiddenElements={hiddenElements}
					/>

					{selectedElement && selectedElement.isModal ? (
						isCustomModalComponent(selectedElement.component) ||
						isViewportModalComponent(selectedElement.component) ? (
							<now-uxf-bare-modal
								style={
									hiddenElements[selectedElement.id]
										? { display: 'none' }
										: null
								}
								opened={true}
								size={get(selectedElement, 'properties.size.value', 'md')}
								overlayPositionAbsolute={true}
								disableDismiss={get(
									selectedElement,
									'properties.disableDismiss.value',
									false
								)}>
								{renderBareModalContent({
									element: selectedElement,
									renderers,
									bindings,
									rootElement,
									hiddenElements
								})}
							</now-uxf-bare-modal>
						) : (
							<div
								className="stage-modal-overlay"
								style={modalStyle}
								on-click={(event) => {
									event.stopPropagation();
									event.preventDefault();
									return false;
								}}>
								<sn-uib-modal-content
									element={selectedElement}
									renderers={renderers}
									bindings={bindings}
									rootElement={rootElement}
									className="modal-content-container"
								/>
							</div>
						)
					) : null}
				</div>
			</div>
		);
	} else {
		return null;
	}
};

export default createCustomElement('sn-uxf-renderer', {
	renderer: { type: snabbdom },
	view,
	styles,
	properties: {
		page: {
			default: null
		},
		bindings: {
			computed({ properties: { page } }) {
				if (page) {
					const { uiTypeParameters = {} } = page;
					const pageProperties = createContextBindings(page);
					const bindings = {
						...uiTypeParameters,
						...pageProperties
					};
					return bindings;
				}
				return {};
			}
		},
		selectedElementId: {
			default: ''
		},
		stageDimensions: {
			default: {}
		},
		renderers: {
			default: {
				column: null,
				row: null,
				element: null,
				emptySlot: null
			}
		},
		ancestorPageProperties: {
			default: '@uxf/ancestorPageProperties'
		},
		dataBrokerInstances: {
			default: '@uxf/dataBrokerInstances'
		},
		ancestorDataBrokerInstances: {
			default: '@uxf/ancestorDataBrokerInstances'
		},
		ancestorElements: {
			default: '@uxf/ancestorElements'
		},
		theme: {
			default: '@uxf/theme'
		},
		selectedSlotName: {
			default: null
		},
		selectedSlotParent: {
			default: null
		},
		selectedSlotParentComponent: {
			default: null
		},
		hiddenElements: {
			default: {}
		}
	},
	actionHandlers: {
		['NOW_BUTTON_ICONIC#CLICKED']: ({ action }) => action.stopPropagation(),
		['NOW_UXF_BARE_MODAL#DISMISSED']: ({ action, dispatch }) =>
			closeModal(action, dispatch)
	}
});
