import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	LAYOUT_TEMPLATE_CREATE_REQUESTED,
	LAYOUT_TEMPLATE_CREATE_HTTP_REQUESTED,
	LAYOUT_TEMPLATE_CREATE_STARTED,
	LAYOUT_TEMPLATE_CREATE_FAILED,
	LAYOUT_TEMPLATE_CREATE_SUCCEEDED
} from './layoutTemplatesActions';
import { TEMPLATES_REQUESTED } from '../templates/templatesActions';
import { MACROPONENT_CATEGORY, SCREEN_TEMPLATE_SYS_ID } from '../../constants';
import effectProxy from '../helpers/effectProxy';
import { throwError } from '../../utils';

const layoutTemplateCreateRequested = ({ action, properties, dispatch }) => {
	const { name: label, layout } = action.payload;
	const { templateTypes } = properties;

	const layoutType = (
		templateTypes.find((type) => type.name === 'layout') || {}
	).id;

	dispatch(LAYOUT_TEMPLATE_CREATE_HTTP_REQUESTED, {
		data: {
			label,
			associatedTypes: [layoutType],
			macroponentInstance: {
				macroponent: {
					name: label,
					category: MACROPONENT_CATEGORY,
					extendsSysId: SCREEN_TEMPLATE_SYS_ID,
					layout: JSON.parse(layout)
				}
			}
		}
	});
};

// TODO: figure out async and ui response
const layoutTemplateCreateStarted = ({ updateProperties }) =>
	updateProperties({ isSavingTemplate: true });

const layoutTemplateCreateSucceeded = ({ dispatch }) => {
	dispatch(TEMPLATES_REQUESTED);
};

const layoutTemplateCreateFailed = () =>
	throwError('Failed to save layout template');

export const layoutTemplateCreateHttpEffect = createHttpEffect(
	'/api/now/builder/templates',
	{
		method: 'POST',
		dataParam: 'data',
		startActionType: LAYOUT_TEMPLATE_CREATE_STARTED,
		successActionType: LAYOUT_TEMPLATE_CREATE_SUCCEEDED,
		errorActionType: LAYOUT_TEMPLATE_CREATE_FAILED,
		batch: false
	}
);

export default {
	[LAYOUT_TEMPLATE_CREATE_REQUESTED]: layoutTemplateCreateRequested,
	[LAYOUT_TEMPLATE_CREATE_HTTP_REQUESTED]: effectProxy(
		layoutTemplateCreateHttpEffect
	),
	[LAYOUT_TEMPLATE_CREATE_STARTED]: layoutTemplateCreateStarted,
	[LAYOUT_TEMPLATE_CREATE_FAILED]: layoutTemplateCreateFailed,
	[LAYOUT_TEMPLATE_CREATE_SUCCEEDED]: layoutTemplateCreateSucceeded
};
