import { MAP_CONTAINER } from './constants';
import { translateKeys, extractTranslatablePaths } from './util';
import { getUxValueFromShorthand, getShorthandFromUxValue } from '.';

export default function MapContainerFactory({
	shorthand = null, // object
	model = null, // object
	schema
} = {}) {
	if (
		(!shorthand && !model) ||
		(shorthand && (typeof shorthand !== 'object' || Array.isArray(shorthand)))
	) {
		throw new TypeError(
			'Something went wrong initializing MapContainerFactory'
		);
	}

	// PRIVATE PROPERTIES
	const fType = MAP_CONTAINER;
	const translatablePaths = schema ? extractTranslatablePaths(schema) : [];
	const fShorthand = shorthand || getShorthandFromModel(model);
	const fModel = model || getModelFromShorthand(shorthand);

	function getModelFromShorthand(shorthand) {
		shorthand = translateKeys(shorthand, translatablePaths);
		return {
			type: fType,
			container: {
				...Object.entries(shorthand).reduce((acc, [key, value]) => {
					schema = typeof value === 'object' ? schema : null;
					return {
						...acc,
						[key]: getUxValueFromShorthand(value, schema)
					};
				}, {})
			}
		};
	}

	function getShorthandFromModel(model) {
		return Object.entries(model.container).reduce((acc, [key, value]) => {
			return {
				...acc,
				[key]: getShorthandFromUxValue(value)
			};
		}, {});
	}

	// PUBLIC API
	return {
		getShorthand() {
			return fShorthand;
		},

		getModel() {
			return fModel;
		}
	};
}
