import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import get from 'lodash/get';
import isEqual from 'lodash/isEqual';
import map from 'lodash/map';
import PageTreeModel from '../models/pageTree';
import { componentListHandlers } from './componentList';
import { dataBrokerDefinitionsHandlers } from './dataBrokers/dataBrokerDefinitions';
import { dataBrokerInstancesHandlers } from './dataBrokers/dataBrokerInstances';
import { dataBrokerRuntimeHandlers } from './dataBrokers'; // TODO: Move instance and definition work here
import { DATA_BROKER_RUNTIME_EXECUTE_REQUESTED } from './dataBrokers/dataBrokerRuntimeActions';
import { elementHandlers } from './element';
import createElementArray from './element/utils/createElementArray';
import { isDefaultLayout } from './element/defaultLayoutElementHandlers/';
import { elementEventMappingsHandlers } from './elementEventMappings';
import { pageHandlers } from './page';
import { pageListHandlers } from './pageList';
import { screenPropertiesHandlers } from './screenProperties';
import { pagePropertiesHandlers } from './pageProperties';
import { pageLayoutHandlers } from './pageLayout';
import { templatesHandlers } from './templates';
import { pageTemplatesHandlers } from './pageTemplates';
import { layoutTemplatesHandlers } from './layoutTemplates';
import { clientScriptsHandlers } from './clientScripts';
import { clientStateHandlers, clientStateRuntimeHandlers } from './clientState';
import { dispatchedEventsHandlers } from './dispatchedEvents';
import { domReadyHandlers } from './domReady';
import { ambSubscribersHandlers } from './ambSubscribers';
import { updatedTablesHandlers } from './updatedTables';
import { CLIENT_STATE_DATA_REQUESTED } from './clientState/clientStateActions';
import {
	newPageCreateHandlers,
	newPageSaveHandlers,
	newPageDeleteHandlers,
	newPageVariantCreateHandlers,
	newPageVariantDeleteHandlers
} from './newPage';
import {
	createExposedProperty,
	getDataBrokerInputPropertiesFromPageState,
	getDataBrokerInstances,
	getDataBrokerInstanceProperties,
	setOnChangeForDependeeProperties,
	getElementTree,
	getElementEventMappings,
	getElementEvents,
	getElementProperties,
	getElementLayouts,
	getElementStyles,
	getEventMappingHandlers,
	getPageMetadata,
	getPageProperties,
	isPageStateDirty,
	getElementToEventMappingHandlerMap,
	getStateParams,
	getScreenEventMappings,
	getInternalEventMappings,
	getClientScriptApi,
	getHiddenElements,
	processClientStateData,
	getDataBrokerEventMappings,
	getClientScriptApiCompletions,
	generateDataBrokerErrorObj,
	updateDeclarativeClientStateUpdateEventHandlers
} from './utils';
import { memoizeGetDefaultLayout } from './templates/utils/getDefaultLayout';
import { elementUsesSNLayout } from './element/utils/element';
import { configurationComponentByInterfaceHandlers } from './configurationComponentByInterface';
import { componentSourcesHandlers } from './componentSources';
import modalhandlers from './modalhandlers';
import {
	filterBindings,
	collectAllBindings,
	bindingsHandlers
} from './bindings';
import viewportHandlers from './viewportHandlers';
import '@servicenow/now-modal';
import newPageVariantOverrideHandlers from './newPage/newPageVariantOverrideHandlers';
import { dashboardsHandlers } from './dashboards';
import { domainScopeHandlers } from './domainScope';
import configProperties from './configProperties';
import isEmpty from 'lodash/isEmpty';

export const PAGE_STATE_CLEARED = 'SN_UXF#PAGE_STATE_CLEARED';
export const PAGE_STATE_RESET = 'SN_UXF#PAGE_STATE_RESET';
export const SHOW_FAILURE_ALERT = 'SN_UXF#SHOW_FAILURE_ALERT';

const view = (state) => {
	const { dataBrokerData, dataBrokerElements, ancestorElementElements } = state;
	const {
		pageState,
		ancestorDataBrokerInstances,
		ancestorElements,
		clientScriptApi
	} = state.properties;

	const clientSideDataBrokers =
		pageState && pageState.dataBrokerInstances
			? pageState.dataBrokerInstances.filter((dataBrokerInstance) => {
					return dataBrokerInstance.definition.type === 'CLIENT_STATE';
			  })
			: [];

	const ancestorClientStateDataBrokers = ancestorDataBrokerInstances.filter(
		(dbinstance) => {
			return dbinstance.definition.type === 'CLIENT_STATE';
		}
	);
	const serverSideDataBrokers =
		pageState && pageState.dataBrokerInstances
			? [
					...ancestorDataBrokerInstances,
					...pageState.dataBrokerInstances
			  ].filter((dataBrokerInstance) => {
					return dataBrokerInstance.definition.type !== 'CLIENT_STATE';
			  })
			: [];

	return (
		<div>
			{Object.entries(dataBrokerElements).map(
				// eslint-disable-next-line no-unused-vars
				([dataBrokerInstanceId, DataBrokerElementName]) => {
					const targetData = dataBrokerData[dataBrokerInstanceId];
					const targetDataBrokerInstance = serverSideDataBrokers.find(
						(serverSideDataBroker) =>
							serverSideDataBroker.id === dataBrokerInstanceId
					);
					const outputs = targetDataBrokerInstance
						? Object.keys(
								targetDataBrokerInstance.outputProperties || {}
						  ).reduce((acc, outputKey) => {
								acc[outputKey] =
									get(targetData, `result.${outputKey}`, null) ||
									get(targetData, 'result.output', { data: [] });
								return acc;
						  }, {})
						: {};

					const dateBrokerProps = getDataBrokerInputPropertiesFromPageState(
						pageState,
						dataBrokerInstanceId,
						clientScriptApi
					);

					const dataBrokerRuntimeErrors = get(targetData, 'errors', []);

					const targetDataPreview = dataBrokerRuntimeErrors.length
						? generateDataBrokerErrorObj(dataBrokerRuntimeErrors)
						: get(targetData, 'result', {});
					const targetExecutionTime = get(targetData, 'executionTime', null);

					return (
						<DataBrokerElementName
							component-id={dataBrokerInstanceId}
							{...outputs}
							preview={targetDataPreview}
							executionTime={targetExecutionTime}
							{...dateBrokerProps}
						/>
					);
				}
			)}
			{clientSideDataBrokers.map((clientSideDataBroker) => {
				const TagName = clientSideDataBroker.clientStateDataBrokerTagName; // eslint-disable-line no-unused-vars
				const dataBrokerDataId = clientSideDataBroker.id;

				const dateBrokerProps = getDataBrokerInputPropertiesFromPageState(
					pageState,
					dataBrokerDataId,
					clientScriptApi
				);
				return (
					<TagName
						component-id={clientSideDataBroker.id}
						{...dateBrokerProps}
					/>
				);
			})}
			{ancestorClientStateDataBrokers.map((ancestorClientStateDataBroker) => {
				const TagName = // eslint-disable-line no-unused-vars
					ancestorClientStateDataBroker.clientStateDataBrokerTagName;
				const inputProps = Object.entries(
					ancestorClientStateDataBroker.properties
				).reduce((acc, [key, { value }]) => {
					return {
						...acc,
						[key]: value
					};
				}, {});
				return (
					<TagName
						component-id={ancestorClientStateDataBroker.id}
						{...inputProps}
					/>
				);
			})}
			{Object.entries(ancestorElementElements).map(
				// eslint-disable-next-line no-unused-vars
				([elementId, AncestorElementTagName]) => {
					return (
						<AncestorElementTagName
							component-id={elementId}
							{...get(ancestorElements, [elementId, 'propertyValues'], {})}
						/>
					);
				}
			)}
			<slot />
		</div>
	);
};

export default createCustomElement('sn-uxf-builder-middleware', {
	renderer: { type: snabbdom },
	view,
	initialState: {
		detachedElementData: null,
		dataBrokerElements: {},
		ancestorElementElements: {},
		dataBrokerData: {},
		contextBindingInfo: null,
		allContextBindings: [],
		activeViewportRoutes: {},
		activeViewportScreens: {},
		getDefaultLayout: memoizeGetDefaultLayout()
	},
	properties: configProperties,
	actionHandlers: {
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			action,
			properties,
			updateProperties,
			dispatch,
			updateState,
			state
		}) => {
			const {
				pageState,
				pageStatePristine,
				clientScripts,
				clientScriptsPristine,
				parentPageProperties,
				parentDataBrokerInstances,
				clientScriptApi,
				hiddenElements,
				dataBrokerDefinitions,
				ancestorDataBrokerInstances,
				ancestorPageProperties,
				ancestorElements,
				templatesDefinitions,
				appChromeSettings
			} = properties;
			const { getDefaultLayout } = state;
			const { name, value } = action.payload;

			const previousValue = action.payload.previousValue || {};

			// The all important object of changing data
			const selectablePropsToUpdate = {};
			// Whether to dispatch DATA_BROKER_RUNTIME_EXECUTE_REQUESTED, based on prop changes.
			let reexecuteDataBrokerRuntime = false;

			if (name === 'pageState' && value && value.rootElement) {
				let dataBrokerInstances, oldDataBrokerInstances;

				if (pageState.dataBrokersEnabled) {
					dataBrokerInstances = getDataBrokerInstances(
						value.dataBrokerInstances
					);
					oldDataBrokerInstances = previousValue.dataBrokerInstances
						? getDataBrokerInstances(previousValue.dataBrokerInstances)
						: [];
					if (!isEqual(oldDataBrokerInstances, dataBrokerInstances)) {
						selectablePropsToUpdate.dataBrokerInstances = dataBrokerInstances;
					}

					const dataBrokerProperties = getDataBrokerInstanceProperties(
						value.dataBrokerInstances
					);
					const oldDataBrokerProperties = getDataBrokerInstanceProperties(
						previousValue.dataBrokerInstances
					);

					if (!isEqual(oldDataBrokerProperties, dataBrokerProperties)) {
						reexecuteDataBrokerRuntime = true;
						const processedDataBrokerProperties = setOnChangeForDependeeProperties(
							dataBrokerProperties
						);
						selectablePropsToUpdate.dataBrokerProperties = processedDataBrokerProperties;
					}

					const dataBrokerEventMappings = getDataBrokerEventMappings(
						dataBrokerInstances
					);
					const previousDataBrokerEventMappings = getDataBrokerEventMappings(
						oldDataBrokerInstances
					);
					if (
						!isEqual(previousDataBrokerEventMappings, dataBrokerEventMappings)
					) {
						selectablePropsToUpdate.dataBrokerEventMappings = dataBrokerEventMappings;
					}

					const dataBrokerEventMappingHandlers = getEventMappingHandlers(
						dataBrokerInstances
					);
					const previousDataBrokerEventMappingHandlers = getEventMappingHandlers(
						oldDataBrokerInstances
					);
					if (
						!isEqual(
							previousDataBrokerEventMappingHandlers,
							dataBrokerEventMappingHandlers
						)
					) {
						selectablePropsToUpdate.eventMappingHandlers = {
							...(properties.eventMappingHandlers || {}),
							...dataBrokerEventMappingHandlers
						};
					}
				}

				if (!isEqual(previousValue.eventHandlers, value.eventHandlers)) {
					selectablePropsToUpdate.eventHandlers = value.eventHandlers;
				}

				const internalEventMappings = getInternalEventMappings(
					value.rootElement
				);
				const previousInternalEventMappings = getInternalEventMappings(
					previousValue.rootElement
				);
				if (!isEqual(previousInternalEventMappings, internalEventMappings)) {
					selectablePropsToUpdate.internalEventMappings = internalEventMappings;
				}

				const internalEventMappingHandlers = getEventMappingHandlers([
					{ eventMappings: internalEventMappings }
				]);
				const previousInternalEventMappingHandlers = getEventMappingHandlers([
					{ eventMappings: previousInternalEventMappings }
				]);
				if (
					!isEqual(
						previousInternalEventMappingHandlers,
						internalEventMappingHandlers
					)
				) {
					selectablePropsToUpdate.eventMappingHandlers = {
						...(properties.eventMappingHandlers || {}),
						...(selectablePropsToUpdate.eventMappingHandlers || {}),
						...internalEventMappingHandlers
					};
				}

				const elementArr = createElementArray(value.rootElement, (element) => ({
					id: element.id,
					instanceName: element.instanceName,
					isHidden: element.isHidden,
					tagName: element.component.tag,
					dispatchedEvents: element.component.dispatchedEvents,
					eventMappings: element.eventMappings,
					styles: element.styles,
					properties: map(element.properties, createExposedProperty),
					layout: elementUsesSNLayout(element) ? element.layout : null,
					elementUsesSNLayout: elementUsesSNLayout(element)
				}));

				const previousElementArr = previousValue.rootElement
					? createElementArray(previousValue.rootElement, (element) => ({
							id: element.id,
							instanceName: element.instanceName,
							isHidden: element.isHidden,
							tagName: element.component.tag,
							dispatchedEvents: element.component.dispatchedEvents,
							eventMappings: element.eventMappings,
							styles: element.styles,
							properties: map(element.properties, createExposedProperty),
							layout: elementUsesSNLayout(element) ? element.layout : null,
							elementUsesSNLayout: elementUsesSNLayout(element)
					  }))
					: [];

				const elementProperties = getElementProperties(elementArr, value);
				const previousElementProperties = getElementProperties(
					previousElementArr,
					value
				);

				if (!isEqual(previousElementProperties, elementProperties)) {
					const currentHiddenElements = getHiddenElements(
						elementArr,
						clientScriptApi
					);
					const elementTree = getElementTree(
						value.rootElement,
						currentHiddenElements
					);
					const processedElementProperties = setOnChangeForDependeeProperties(
						elementProperties
					);
					selectablePropsToUpdate.elementProperties = processedElementProperties;
					selectablePropsToUpdate.elementTree = elementTree;
					selectablePropsToUpdate.hiddenElements = currentHiddenElements;
				}

				const elementStyles = getElementStyles(elementArr, value);
				const previousElementStyles = getElementStyles(
					previousElementArr,
					value
				);

				if (!isEqual(previousElementStyles, elementStyles)) {
					selectablePropsToUpdate.elementStyles = elementStyles;
				}

				const elementLayouts = getElementLayouts(
					elementArr,
					templatesDefinitions
				);
				const previousElementLayouts = getElementLayouts(
					previousElementArr,
					templatesDefinitions
				);

				if (!isEqual(previousElementLayouts, elementLayouts)) {
					selectablePropsToUpdate.elementLayouts = elementLayouts;
				}

				const elementEvents = getElementEvents(elementArr);
				const previousElementEvents = getElementEvents(previousElementArr);
				if (!isEqual(previousElementEvents, elementEvents)) {
					selectablePropsToUpdate.elementEvents = elementEvents;
				}

				const elementEventMappings = getElementEventMappings(elementArr);
				const previousElementEventMappings = getElementEventMappings(
					previousElementArr
				);
				if (!isEqual(previousElementEventMappings, elementEventMappings)) {
					selectablePropsToUpdate.elementEventMappings = elementEventMappings;
				}

				const eventMappingHandlers = getEventMappingHandlers(elementArr);
				const previousEventMappingHandlers = getEventMappingHandlers(
					previousElementArr
				);
				if (!isEqual(previousEventMappingHandlers, eventMappingHandlers)) {
					selectablePropsToUpdate.eventMappingHandlers = {
						...(properties.eventMappingHandlers || {}),
						...(selectablePropsToUpdate.eventMappingHandlers || {}),
						...eventMappingHandlers
					};
				}

				const elementToEventMappingHandlerMap = getElementToEventMappingHandlerMap(
					elementArr,
					elementEventMappings,
					eventMappingHandlers
				);
				const previousElementToEventMappingHandlerMap = getElementToEventMappingHandlerMap(
					previousElementArr,
					previousElementEventMappings,
					previousEventMappingHandlers
				);
				if (
					!isEqual(
						previousElementToEventMappingHandlerMap,
						elementToEventMappingHandlerMap
					)
				) {
					selectablePropsToUpdate.eventHandlerElementMap = elementToEventMappingHandlerMap;
				}

				const dispatchedEvents = value.dispatchedEvents;
				const previousDispatchedEvents = previousValue.dispatchedEvents;
				if (!isEqual(dispatchedEvents, previousDispatchedEvents)) {
					selectablePropsToUpdate.dispatchedEvents = dispatchedEvents;
				}

				const handledEvents = value.handledEvents;
				const previousHandledEvents = previousValue.handledEvents;
				if (!isEqual(handledEvents, previousHandledEvents)) {
					selectablePropsToUpdate.handledEvents = handledEvents;
				}

				const screenEventMappings = getScreenEventMappings(
					value.screenEventMappings
				);
				const previousScreenEventMappings = getScreenEventMappings(
					previousValue.screenEventMappings || []
				);
				if (!isEqual(screenEventMappings, previousScreenEventMappings)) {
					selectablePropsToUpdate.screenEventMappings = screenEventMappings;
				}

				const screenEventMappingHandlers = getEventMappingHandlers([
					{ eventMappings: value.screenEventMappings }
				]);
				const previousScreenEventMappingHandlers = getEventMappingHandlers([
					{ eventMappings: previousValue.screenEventMappings || [] }
				]);
				if (
					!isEqual(
						screenEventMappingHandlers,
						previousScreenEventMappingHandlers
					)
				) {
					selectablePropsToUpdate.eventMappingHandlers = {
						...(properties.eventMappingHandlers || {}),
						...(selectablePropsToUpdate.eventMappingHandlers || {}),
						...screenEventMappingHandlers
					};
				}

				const pageTree = new PageTreeModel({
					rootElement: value.rootElement
				});

				const previousPageTree = new PageTreeModel({
					rootElement: previousValue.rootElement || {}
				});

				if (!isEqual(previousPageTree, pageTree)) {
					selectablePropsToUpdate.pageTree = pageTree;
				}

				const elementTree = getElementTree(value.rootElement, hiddenElements);
				const previousElementTree = getElementTree(
					previousValue.rootElement,
					hiddenElements
				);
				if (!isEqual(previousElementTree, elementTree)) {
					selectablePropsToUpdate.elementTree = elementTree;
				}

				const pageMetadata = getPageMetadata(value);
				const previousPageMetadata = getPageMetadata(previousValue);
				if (!isEqual(previousPageMetadata, pageMetadata)) {
					selectablePropsToUpdate.pageMetadata = pageMetadata;
				}

				const pageProperties = getPageProperties(value.rootElement);
				const previousPageProperties = previousValue.rootElement
					? getPageProperties(previousValue.rootElement)
					: [];
				if (!isEqual(previousPageProperties, pageProperties)) {
					if (pageState.dataBrokersEnabled) {
						reexecuteDataBrokerRuntime = true;
					}
					dispatch(CLIENT_STATE_DATA_REQUESTED, {
						params: value.clientStateParameters
					});
					selectablePropsToUpdate.pageProperties = pageProperties;
				}

				const pageLayout = get(value, 'rootElement.layout', {});
				const previousPageLayout = previousValue.rootElement
					? get(previousValue, 'rootElement.layout', {})
					: {};
				if (!isEqual(previousPageLayout, pageLayout)) {
					selectablePropsToUpdate.pageLayout = pageLayout;
				}

				const hasChildren = get(value, 'rootElement.children', []).length > 0;
				const isDefaultPageLayout =
					!hasChildren && isDefaultLayout(pageLayout, getDefaultLayout);
				selectablePropsToUpdate.isDefaultPageLayout = isDefaultPageLayout;

				const bindings = collectAllBindings({
					pageProperties,
					elementProperties,
					elementArr,
					dataBrokerInstances,
					parentDataBrokerInstances,
					parentPageProperties,
					clientStateParameters: value.clientStateParameters,
					middlewareProperties: properties,
					appChromeSettings
				});
				const previousBindings = collectAllBindings({
					previousPageProperties,
					previousElementProperties,
					previousElementArr,
					oldDataBrokerInstances,
					parentDataBrokerInstances,
					parentPageProperties,
					clientStateParameters: previousValue.clientStateParameters,
					middlewareProperties: properties,
					appChromeSettings
				});
				if (!isEqual(previousBindings, bindings)) {
					updateState({
						operation: 'set',
						path: 'allContextBindings',
						value: bindings
					});
					selectablePropsToUpdate.bindings = filterBindings(
						bindings,
						state.contextBindingInfo
					);
				}

				if (
					!isEqual(
						getStateParams(previousValue.clientStateParameters),
						getStateParams(value.clientStateParameters)
					)
				) {
					selectablePropsToUpdate.clientStateParameters =
						value.clientStateParameters;
					dispatch(CLIENT_STATE_DATA_REQUESTED, {
						params: value.clientStateParameters
					});
				}

				reexecuteDataBrokerRuntime === true &&
					dispatch(DATA_BROKER_RUNTIME_EXECUTE_REQUESTED);
			} else if (name === 'pageState' && value === null) {
				selectablePropsToUpdate;
				(selectablePropsToUpdate.dataBrokerInstances =
					configProperties.dataBrokerInstances.default),
					(selectablePropsToUpdate.dataBrokerEventMappings =
						configProperties.dataBrokerEventMappings.default),
					(selectablePropsToUpdate.eventMappingHandlers =
						configProperties.eventMappingHandlers.default),
					(selectablePropsToUpdate.eventHandlers =
						configProperties.eventHandlers.default),
					(selectablePropsToUpdate.internalEventMappings =
						configProperties.internalEventMappings.default),
					(selectablePropsToUpdate.elementProperties =
						configProperties.elementProperties.default),
					(selectablePropsToUpdate.elementTree =
						configProperties.elementTree.default),
					(selectablePropsToUpdate.hiddenElements =
						configProperties.hiddenElements.default),
					(selectablePropsToUpdate.elementStyles =
						configProperties.elementStyles.default),
					(selectablePropsToUpdate.elementLayouts =
						configProperties.elementLayouts.default),
					(selectablePropsToUpdate.elementEvents =
						configProperties.elementEvents.default),
					(selectablePropsToUpdate.elementEventMappings =
						configProperties.elementEventMappings.default),
					(selectablePropsToUpdate.eventHandlerElementMap =
						configProperties.eventHandlerElementMap.default),
					(selectablePropsToUpdate.dispatchedEvents =
						configProperties.dispatchedEvents.default),
					(selectablePropsToUpdate.handledEvents =
						configProperties.handledEvents.default),
					(selectablePropsToUpdate.screenEventMappings =
						configProperties.screenEventMappings.default),
					(selectablePropsToUpdate.pageTree =
						configProperties.pageTree.default),
					(selectablePropsToUpdate.pageMetadata =
						configProperties.pageMetadata.default),
					(selectablePropsToUpdate.pageProperties =
						configProperties.pageProperties.default),
					(selectablePropsToUpdate.clientScriptApi =
						configProperties.clientScriptApi.default),
					(selectablePropsToUpdate.pageLayout =
						configProperties.pageLayout.default),
					(selectablePropsToUpdate.isDefaultPageLayout =
						configProperties.isDefaultPageLayout.default),
					(selectablePropsToUpdate.bindings =
						configProperties.bindings.default),
					(selectablePropsToUpdate.clientStateParameters =
						configProperties.clientStateParameters.default);
			} else if (name === 'parentPageProperties') {
				// Get properties derived from pageState
				// for case when updated property is a parent prop
				const {
					pageProperties,
					elementProperties,
					elementArr,
					dataBrokerInstances,
					isIsolatedEditAndEmptyState,
					appChromeSettings
				} = properties;

				const bindings = collectAllBindings({
					pageProperties,
					elementProperties,
					elementArr,
					dataBrokerInstances,
					parentDataBrokerInstances,
					parentPageProperties,
					clientStateParameters: value.clientStateParameters,
					middlewareProperties: properties,
					appChromeSettings
				});
				const previousBindings = collectAllBindings({
					pageProperties,
					elementProperties,
					elementArr,
					dataBrokerInstances,
					parentDataBrokerInstances,
					parentPageProperties,
					clientStateParameters: value.clientStateParameters,
					middlewareProperties: properties,
					appChromeSettings
				});
				if (
					!isEqual(previousBindings, bindings) ||
					isIsolatedEditAndEmptyState
				) {
					if (isIsolatedEditAndEmptyState) {
						dispatch(DATA_BROKER_RUNTIME_EXECUTE_REQUESTED);
					}
					updateState({
						operation: 'set',
						path: 'allContextBindings',
						value: bindings
					});

					selectablePropsToUpdate.bindings = filterBindings(
						bindings,
						state.contextBindingInfo
					);
				}
			} else if (
				name === 'pageState' ||
				name === 'pageStatePristine' ||
				name === 'clientStateParameters' ||
				name === 'eventMappingHandlers'
			) {
				const { eventMappingHandlers, clientStateParameters } = properties;
				if (!pageState || !pageState.rootElement) {
					return;
				}

				if (clientStateParameters && clientStateParameters.length > 0) {
					const clientStateParamsToUse =
						name === 'clientStateParameters' ? value : clientStateParameters;
					selectablePropsToUpdate.eventMappingHandlers = updateDeclarativeClientStateUpdateEventHandlers(
						eventMappingHandlers,
						clientStateParamsToUse
					);
				}

				const elementArr = createElementArray(pageState.rootElement, (el) => ({
					id: el.id,
					properties: map(el.properties, createExposedProperty),
					isHidden: el.isHidden
				}));
				const clientScriptApi = getClientScriptApi({
					pageState,
					ancestorPageProperties,
					elements: elementArr,
					dataBrokerInstances: pageState.dataBrokerInstances,
					clientStateData: processClientStateData(
						clientStateParameters,
						pageState,
						ancestorPageProperties,
						pageState.dataBrokerInstances,
						ancestorDataBrokerInstances,
						ancestorElements,
						appChromeSettings
					),
					dataBrokerDefinitions,
					appChromeSettings
				});
				const currentHiddenElements = getHiddenElements(
					elementArr,
					clientScriptApi
				);
				const elementTree = getElementTree(
					pageState.rootElement,
					currentHiddenElements
				);

				const clientScriptApiCompletions = getClientScriptApiCompletions(
					clientScriptApi
				);

				const clientTransformScriptApiCompletions = getClientScriptApiCompletions(
					clientScriptApi,
					true
				);

				selectablePropsToUpdate.clientScriptApi = clientScriptApi;
				selectablePropsToUpdate.clientScriptApiCompletions = {
					clientScript: clientScriptApiCompletions,
					clientTransformScript: clientTransformScriptApiCompletions
				};
				selectablePropsToUpdate.elementTree = elementTree;
				selectablePropsToUpdate.hiddenElements = currentHiddenElements;
			} else if (name === 'clientStateData') {
				if (!isEqual(value, previousValue)) {
					dispatch(DATA_BROKER_RUNTIME_EXECUTE_REQUESTED);
				}
			}

			if (
				name === 'pageState' ||
				name === 'pageStatePristine' ||
				name === 'clientScripts' ||
				name === 'clientScriptsPristine'
			) {
				// This handles the case when the page has been deleted
				if (!pageState) {
					selectablePropsToUpdate.isPageStateDirty = false;
				} else {
					selectablePropsToUpdate.isPageStateDirty =
						isPageStateDirty(pageState, pageStatePristine) ||
						!isEqual(clientScripts, clientScriptsPristine);
				}
			}

			if (!isEmpty(selectablePropsToUpdate)) {
				updateProperties({ ...selectablePropsToUpdate });
			}
		},
		...domReadyHandlers,
		...componentListHandlers,
		...dataBrokerDefinitionsHandlers,
		...dataBrokerInstancesHandlers,
		...dataBrokerRuntimeHandlers,
		...elementHandlers,
		...elementEventMappingsHandlers,
		...pageHandlers,
		...pageListHandlers,
		...screenPropertiesHandlers,
		...pagePropertiesHandlers,
		...pageLayoutHandlers,
		...templatesHandlers,
		...pageTemplatesHandlers,
		...layoutTemplatesHandlers,
		...elementHandlers,
		...configurationComponentByInterfaceHandlers,
		...componentSourcesHandlers,
		...newPageCreateHandlers,
		...newPageSaveHandlers,
		...newPageDeleteHandlers,
		...newPageVariantCreateHandlers,
		...newPageVariantDeleteHandlers,
		...newPageVariantOverrideHandlers,
		...clientScriptsHandlers,
		...modalhandlers,
		...clientStateHandlers,
		...clientStateRuntimeHandlers,
		...dispatchedEventsHandlers,
		...bindingsHandlers,
		...ambSubscribersHandlers,
		...updatedTablesHandlers,
		...viewportHandlers,
		...dashboardsHandlers,
		...domainScopeHandlers,
		[actionTypes.COMPONENT_RENDER_REQUESTED]: ({
			action,
			properties,
			updateProperties,
			state,
			dispatch
		}) => {
			const { previousRenderState } = action.payload;
			const {
				pageState,
				clientStateParameters,
				ancestorDataBrokerInstances,
				ancestorPageProperties,
				dataBrokerInstances,
				ancestorElements,
				dataBrokerDefinitions,
				appChromeSettings
			} = properties;
			if (!isEqual(state.dataBrokerData, previousRenderState.dataBrokerData)) {
				if (!pageState || !pageState.rootElement) {
					return;
				}
				const elementArr = createElementArray(pageState.rootElement, (el) => ({
					id: el.id,
					properties: map(el.properties, createExposedProperty),
					isHidden: el.isHidden
				}));

				// Let the task queues clear so the data broker seismic elements
				// will be hydrated prior to resolving them for script api
				setTimeout(() => {
					const clientScriptApi = getClientScriptApi({
						pageState,
						ancestorPageProperties,
						elements: elementArr,
						dataBrokerInstances: dataBrokerInstances,
						clientStateData: processClientStateData(
							clientStateParameters,
							pageState,
							ancestorPageProperties,
							pageState.dataBrokerInstances,
							ancestorDataBrokerInstances,
							ancestorElements,
							appChromeSettings
						),
						dataBrokerDefinitions,
						appChromeSettings
					});

					const currentHiddenElements = getHiddenElements(
						elementArr,
						clientScriptApi
					);

					updateProperties({
						clientScriptApi,
						hiddenElements: currentHiddenElements
					});
					dispatch(CLIENT_STATE_DATA_REQUESTED, {
						params: properties.clientStateParameters
					});
				}, 0);
			}
		},
		[PAGE_STATE_CLEARED]: ({ updateProperties }) => {
			updateProperties({
				pageState: null,
				pageStatePristine: null,
				isPageStateDirty: false
			});
		},
		[PAGE_STATE_RESET]: ({ updateProperties, properties }) => {
			updateProperties({
				pageState: properties.pageStatePristine,
				isPageStateDirty: false
			});
		}
	}
});
