import cuid from 'cuid';
import get from 'lodash/get';

const pageTypeLookup = {
	landing(slot) {
		return {
			default: {
				type: 'rowColumn',
				root: {
					type: 'row',
					columns: [
						{
							type: 'column',
							slotName: slot,
							size: '12',
							rows: [],
							id: cuid()
						}
					],
					id: cuid()
				}
			}
		};
	},
	slottedComponent(slot) {
		return {
			default: {
				type: 'flex',
				root: null,
				children: null,
				styles: null,
				rules: {
					'flex-direction': 'column'
				},
				items: [
					{
						slotName: slot,
						styles: {
							'flex-direction': 'row'
						}
					}
				]
			}
		};
	},
	fallback(slot) {
		return {
			default: {
				type: 'flex',
				root: null,
				children: null,
				styles: null,
				rules: {
					'flex-direction': 'column'
				},
				items: [
					{
						slotName: slot,
						styles: {
							'flex-direction': 'column'
						}
					}
				]
			}
		};
	}
};

const createDefaultLayout = (slot, pageType) => {
	const fn = get(pageTypeLookup, pageType, pageTypeLookup.fallback);
	return fn(slot);
};
const DEFAULT_SLOT_NAME = 'default-slot';
export { DEFAULT_SLOT_NAME };
export default createDefaultLayout;
