import { LIST_CONTAINER } from './constants';
import { getUxValueFromShorthand, getShorthandFromUxValue } from '.';

export default function ListContainerFactory({
	shorthand = null, // object
	model = null, // object
	schema
} = {}) {
	if ((!shorthand && !model) || (shorthand && !Array.isArray(shorthand))) {
		throw new TypeError(
			'Something went wrong initializing ListContainerFactory'
		);
	}

	// PRIVATE PROPERTIES
	const fType = LIST_CONTAINER;
	const fSchema = schema;
	const fShorthand = shorthand || getShorthandFromModel(model);
	const fModel = model || getModelFromShorthand(shorthand);

	function getShorthandFromModel(model) {
		return model.container.map((element) => {
			return getShorthandFromUxValue(element);
		});
	}

	function getModelFromShorthand(shorthand) {
		return {
			type: fType,
			container: shorthand.map((element) => {
				return getUxValueFromShorthand(element, fSchema);
			})
		};
	}

	// PUBLIC API
	return {
		getModel() {
			return fModel;
		},

		getShorthand() {
			return fShorthand;
		}
	};
}
