import { createHttpEffect } from '@servicenow/ui-effect-http';
import cloneDeep from 'lodash/cloneDeep';
import {
	PAGE_UPDATE_REQUESTED,
	PAGE_UPDATE_STARTED,
	PAGE_UPDATE_FAILED,
	PAGE_UPDATE_SUCCEEDED
} from './pageActions';
import { CLIENT_SCRIPTS_UPDATE_REQUESTED } from '../clientScripts/clientScriptsActions';
import transformMacroponentToPageState from './utils/transformMacroponentToPageState';
import effectProxy from '../helpers/effectProxy';
import { deleteTemplateCache } from '../viewportHandlers/templateLoader';

export const pageUpdateRequested = createHttpEffect(
	'/api/now/uxframework/:pageEndpoint/:sysId',
	{
		method: 'PATCH',
		pathParams: ['pageEndpoint', 'sysId'],
		queryParams: ['sysparm_transaction_scope'],
		dataParam: 'data',
		startActionType: PAGE_UPDATE_STARTED,
		successActionType: PAGE_UPDATE_SUCCEEDED,
		errorActionType: PAGE_UPDATE_FAILED,
		batch: false
	}
);

const pageUpdateStarted = ({ properties, updateProperties, dispatch }) => {
	updateProperties({ isSavingPage: true });
	dispatch(CLIENT_SCRIPTS_UPDATE_REQUESTED, { data: properties.clientScripts });
};

const pageUpdateSucceeded = ({
	action,
	state,
	properties,
	updateProperties
}) => {
	const { isChangingPage } = action.meta || {};

	if (isChangingPage) {
		updateProperties({
			isLoadingPage: false,
			isSavingPage: false
		});
		return;
	}

	const {
		dataBrokerDefinitions,
		eventHandlers,
		pages: currentPages = [],
		clientStateParameters,
		pageState: { sysId: variantId, rootElement },
		clientScripts
	} = properties;

	if (rootElement && rootElement.id) {
		deleteTemplateCache([rootElement.id]);
	}

	const pageState = transformMacroponentToPageState(
		action.payload.result,
		dataBrokerDefinitions,
		eventHandlers,
		clientStateParameters,
		state,
		variantId
	);
	const pageStatePristine = cloneDeep(pageState);

	const pages = currentPages.map((page) => {
		if (page.sysId === pageState.sysId) {
			return {
				...page,
				name: pageState.name,
				pageStatus: pageState.pageStatus
			};
		}
		return page;
	});

	updateProperties({
		isLoadingPage: false,
		isPageStateDirty: false,
		isSavingPage: false,
		pageState,
		pageStatePristine,
		clientScriptsPristine: cloneDeep(clientScripts),
		pages
	});
};

const pageUpdateFailed = ({ updateProperties }) => {
	updateProperties({
		isLoadingPage: false,
		isSavingPage: false
	});
};

export default {
	[PAGE_UPDATE_REQUESTED]: effectProxy(pageUpdateRequested),
	[PAGE_UPDATE_STARTED]: pageUpdateStarted,
	[PAGE_UPDATE_FAILED]: pageUpdateFailed,
	[PAGE_UPDATE_SUCCEEDED]: pageUpdateSucceeded
};
