import cloneDeep from 'lodash/cloneDeep';
import { ELEMENT_RESIZED } from './elementActions';
import createPageState from '../../models/createPageState';
import { Element } from '../../models';

import { findElementById } from './utils/element';
import { getResizedRow, getClosestNeighbor } from './utils/layout';

const resizeLookup = (type = '') => {
	switch (type) {
		case 'SHRINK':
			return -1;
		case 'EXPAND':
			return 1;
		default:
			return 0;
	}
};

const elementResized = ({ action, state, updateProperties }) => {
	const {
		properties: { pageState }
	} = state;

	const { id, side, direction: directionString, elementId } = action.payload;
	const direction = directionString.toUpperCase();
	const newRootElement = cloneDeep(pageState.rootElement);
	const ownerElement = findElementById(newRootElement, elementId);
	const layout = ownerElement.layout;
	const row = getResizedRow(layout, { label: 'id', value: id });

	if (!row || row.length <= 1) {
		return;
	}

	const targetIdx = row.findIndex((part) => part.id === id);
	const target = row[targetIdx];
	const neighbor = getClosestNeighbor(row, targetIdx, side, direction);
	const neighborIndex = row.indexOf(neighbor);

	if (target.size <= 1 && direction === 'SHRINK') {
		return;
	}

	if (!neighbor) {
		return;
	}

	row[targetIdx] = {
		...target,
		size: parseInt(target.size, 10) + resizeLookup(direction)
	};

	row[neighborIndex] = {
		...neighbor,
		size: parseInt(neighbor.size, 10) - resizeLookup(direction)
	};

	updateProperties({
		pageState: createPageState(
			{
				rootElement: new Element(newRootElement)
			},
			pageState
		)
	});
};

export { elementResized };
export default {
	[ELEMENT_RESIZED]: elementResized
};
