import PageStateModel from './pageState';

const createPageState = (newProperties, sourcePageState) => {
	const spreadPageState = Object.keys(sourcePageState.attributes).reduce(
		(acc, cur) => {
			acc[cur] = sourcePageState[cur];
			return acc;
		},
		{}
	);

	return new PageStateModel({
		...spreadPageState,
		...newProperties
	});
};

export default createPageState;
