import {
	UIB_VIEWPORT_RENDER_CONTENTS,
	UIB_VIEWPORT_RENDER_REQUESTED
} from './actions';
import {
	renderContents,
	renderEmptyState,
	getViewportElementAndProperties,
	initializeDataDrivenViewportProperties,
	isDataDrivenTab
} from './utils';
import isEmpty from 'lodash/isEmpty';
import cloneDeep from 'lodash/cloneDeep';
import get from 'lodash/get';
import set from 'lodash/set';
import isEqual from 'lodash/isEqual';
import {
	REPEATED_VIEWPORT_TAB_DATA,
	DYNAMIC_VIEWPORT_TAB_DATA
} from '../element/reservedPropertyIds';
import { t } from 'sn-translate';

const calculateNewActiveRouteAndScreen = (
	dataDrivenTabInfo,
	parentId,
	id,
	viewportRoutes
) => {
	let routeId;

	if (!isEmpty(dataDrivenTabInfo)) {
		routeId = dataDrivenTabInfo.dataDrivenViewportParentId;
	} else {
		routeId = parentId || id;
	}

	const activeRoute = get(viewportRoutes, `${routeId}[0]`, {});
	let activeScreen = get(activeRoute, 'macroponents[0]', {});

	if (isEmpty(dataDrivenTabInfo) && parentId && activeRoute.macroponents) {
		activeScreen = activeRoute.macroponents.find((mcp) => mcp.id === id);
	}

	return {
		activeRoute,
		activeScreen
	};
};

export default {
	[UIB_VIEWPORT_RENDER_CONTENTS]: (coeffects) => {
		const {
			action: {
				payload: {
					host,
					properties: {
						activeRoute,
						activeScreen,
						viewportRoutes,
						dynamicTabData
					},
					ignoreCache = false
				}
			},
			state,
			properties: { pageState, pageProperties },
			updateState,
			updateProperties
		} = coeffects;

		const routeToRender = activeRoute || undefined;
		const screenToRender = activeScreen || undefined;
		const resolvedRepeatedTabs = dynamicTabData || [];
		const { activeViewportRoutes, activeViewportScreens } = state;
		const { sysId: variantId } = pageState;

		if (!variantId) {
			return;
		}
		const newPageState = cloneDeep(pageState);
		const {
			viewportElement,
			dynamicViewportTabData,
			repeatedViewportTabData
		} = getViewportElementAndProperties(newPageState, host.id);

		if (isEmpty(viewportRoutes)) {
			renderEmptyState(host, false);
			// When the new viewport component is dropped initially on the stage, it doesn't have dynamicTabData or repeatedTabData properties. The result is that the properties editor shows extra fields when the component is rendered or when it's properties are changed. So the below initializes those properties
			const newViewportProperties = initializeDataDrivenViewportProperties(
				null,
				state
			);
			let isViewportPropertySet = false;
			if (!dynamicViewportTabData) {
				set(
					viewportElement,
					`properties[${DYNAMIC_VIEWPORT_TAB_DATA}]`,
					newViewportProperties[DYNAMIC_VIEWPORT_TAB_DATA]
				);
				isViewportPropertySet = true;
			}
			if (!repeatedViewportTabData) {
				set(
					viewportElement,
					`properties[${REPEATED_VIEWPORT_TAB_DATA}]`,
					newViewportProperties[REPEATED_VIEWPORT_TAB_DATA]
				);
				isViewportPropertySet = true;
			}
			if (isViewportPropertySet) {
				isViewportPropertySet = false;
				updateProperties({ pageState: newPageState });
			}
		} else {
			if (!isEmpty(routeToRender) && !isEmpty(screenToRender)) {
				const currentlyRendered = {
					route: get(
						activeViewportRoutes,
						`${variantId}.${host.id}.routeType`,
						''
					),
					screen: get(activeViewportScreens, `${variantId}.${host.id}`, '')
				};

				if (!isEmpty(screenToRender) && isDataDrivenTab(routeToRender)) {
					// Add fields in the screen to render the data driven tabs and choose the first field in the list
					currentlyRendered.fields = get(
						activeViewportScreens,
						`${variantId}.${host.id}.fields`,
						{}
					);
					const renderedScreenDefaultFields =
						Array.isArray(resolvedRepeatedTabs) &&
						!isEmpty(resolvedRepeatedTabs)
							? resolvedRepeatedTabs[0].fields
							: {};
					screenToRender.fields = isEmpty(screenToRender.fields)
						? renderedScreenDefaultFields
						: screenToRender.fields;
				}

				if (
					currentlyRendered.route === routeToRender.routeType &&
					get(currentlyRendered, 'screen.id') === screenToRender.id &&
					isEqual(
						get(currentlyRendered, 'screen.macroponentConfiguration'),
						screenToRender.macroponentConfiguration
					) &&
					isEqual(currentlyRendered.fields, screenToRender.fields) &&
					!ignoreCache
				) {
					return;
				} else {
					renderContents(host, screenToRender, pageProperties);
				}
			} else if (!isEmpty(routeToRender) && isEmpty(screenToRender)) {
				renderEmptyState(
					host,
					!isEmpty(viewportRoutes),
					t('Please add a screen variant to {0}', routeToRender.name)
				);
			} else {
				renderEmptyState(host, !isEmpty(viewportRoutes), t('Unexpected Error'));
			}
		}

		// lodash#set sets the reference and not the value
		// we dont want state to get updated by reference
		set(
			activeViewportRoutes,
			`${variantId}.${host.id}`,
			cloneDeep(routeToRender)
		);
		set(
			activeViewportScreens,
			`${variantId}.${host.id}`,
			cloneDeep(screenToRender)
		);

		updateState({
			...state,
			activeViewportRoutes,
			activeViewportScreens
		});
	},
	[UIB_VIEWPORT_RENDER_REQUESTED]: (coeffects) => {
		const {
			action: {
				payload: { elementId, id, parentId, viewportRoutes, dataDrivenTabInfo }
			},
			properties: { pageState },
			updateProperties
		} = coeffects;

		const newPageState = cloneDeep(pageState);

		const { viewportElement } = getViewportElementAndProperties(
			newPageState,
			elementId
		);

		// TODO: handle this 0 when we know more about route variants
		let newActiveRoute = null;
		let newActiveScreen = null;

		const newRouteAndScreen = calculateNewActiveRouteAndScreen(
			dataDrivenTabInfo,
			parentId,
			id,
			viewportRoutes
		);
		newActiveRoute = newRouteAndScreen.activeRoute;
		newActiveScreen = newRouteAndScreen.activeScreen;

		if (!isEmpty(dataDrivenTabInfo)) {
			set(newActiveScreen, 'fields', dataDrivenTabInfo.fields);
		}

		set(viewportElement, 'properties.activeRoute.value', newActiveRoute);

		set(viewportElement, 'properties.activeScreen.value', newActiveScreen);

		updateProperties({
			pageState: newPageState
		});
	}
};
