import { createCustomElement } from '@servicenow/ui-core';
// eslint-disable-next-line no-unused-vars
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';
import get from 'lodash/get';
import styles from './sn-uxf-renderer.scss';
import { getModalSize } from '../sn-uxf-builder-middleware/modalhandlers/utils';
import './sn-uib-value-renderer';
import { resolveBinding } from '../utils';
import { closeModal } from './utils';

const CONFIRM_AND_DESTROY_MODAL_ID = '637fc6a75b230010b913cbd59b81c779';
const IFRAME_MODAL_ID = 'e80108425b101010b913cbd59b81c771';
const ALERT_MODAL_ID = '05d8b1515b230010b913cbd59b81c71a';

const resolveBindingValue = (
	stringVal,
	{
		bindings,
		ancestorPageProperties,
		dataBrokerInstances,
		ancestorDataBrokerInstances,
		rootElement,
		ancestorElements,
		clientScriptApi,
		appChromeSettings
	}
) => {
	return resolveBinding(stringVal, {
		bindings,
		ancestorPageProperties,
		dataBrokerInstances,
		ancestorDataBrokerInstances,
		rootElement,
		ancestorElements,
		clientScriptApi,
		appChromeSettings
	});
};

const renderModalHeader = (properties, dispatch) => {
	const { element } = properties;
	const headerLabel = get(element, 'properties.headerLabel.value', '');
	return (
		<Fragment>
			<h1 className="now-modal-heading">
				<sn-uib-value-renderer
					value={resolveBindingValue(headerLabel, properties)}
				/>
			</h1>
			<div on-click={(event) => closeModal(event, dispatch)}>
				<now-button-iconic
					icon="close-fill"
					size="md"
					variant="tertiary"
					bare={true}
				/>
			</div>
		</Fragment>
	);
};

const renderModalContent = (properties) => {
	const { element } = properties;
	const isIframeModal = get(element, 'component.id', '') === IFRAME_MODAL_ID;
	const content = get(element, 'properties.content.value', '');
	const iframeSrc = get(element, 'properties.src.value', '');

	const renderOOBModalContent = isIframeModal ? (
		<sn-uib-no-interact-wrapper>
			<iframe
				id="iframe-modal-id"
				title="iframe-modal"
				frameBorder="0"
				scrolling="no"
				src={iframeSrc}
				width="100%"
				height="100%"
			/>
		</sn-uib-no-interact-wrapper>
	) : (
		<sn-uib-value-renderer value={resolveBindingValue(content, properties)} />
	);

	return renderOOBModalContent;
};

const renderModalFooter = (properties) => {
	const { element } = properties;
	const componentId = get(element, 'component.id', '');
	const isConfirmDestroyModal = componentId === CONFIRM_AND_DESTROY_MODAL_ID;
	const isIframeModal = componentId === IFRAME_MODAL_ID;

	if (isIframeModal) {
		return;
	}

	const primaryButtonLabel =
		componentId === ALERT_MODAL_ID
			? get(element, 'properties.buttonLabel.value', '')
			: get(element, 'properties.primaryButtonLabel.value', '');
	const secondaryButtonLabel = get(
		element,
		'properties.secondaryButtonLabel.value',
		''
	);

	return (
		<Fragment>
			<now-button
				label={resolveBindingValue(primaryButtonLabel, properties)}
				className="footer-button-1"
				variant={isConfirmDestroyModal ? 'primary-negative' : 'primary'}
				size="md"
			/>

			{element.properties.secondaryButtonLabel ? (
				<now-button
					label={resolveBindingValue(secondaryButtonLabel, properties)}
					className="footer-button-2"
					variant="secondary"
					size="md"
				/>
			) : null}
		</Fragment>
	);
};

const view = ({ properties }, { dispatch }) => {
	const { element } = properties;
	return (
		<div
			class={{
				'stage-modal-container': true,
				[getModalSize(element.properties.size.value)]: true
			}}>
			<div className="now-modal-header">
				{renderModalHeader(properties, dispatch)}
			</div>
			<div className="now-modal-body">{renderModalContent(properties)}</div>
			<div className="now-modal-footer">{renderModalFooter(properties)}</div>
		</div>
	);
};

createCustomElement('sn-uib-modal-content', {
	renderer: { type: snabbdom },
	view,
	styles,
	properties: {
		element: {
			default: {}
		},
		renderers: {
			default: {}
		},
		bindings: {
			default: {}
		},
		rootElement: {
			default: {}
		},
		ancestorPageProperties: {
			default: '@uxf/ancestorPageProperties'
		},
		dataBrokerInstances: {
			default: '@uxf/dataBrokerInstances'
		},
		ancestorDataBrokerInstances: {
			default: '@uxf/ancestorDataBrokerInstances'
		},
		ancestorElements: {
			default: '@uxf/ancestorElements'
		},
		clientScriptApi: {
			default: '@uxf/clientScriptApi'
		},
		appChromeSettings: {
			default: '@uxf/appChromeSettings'
		}
	}
});
