import cloneDeep from 'lodash/cloneDeep';
import { getTemplates } from '@devsnc/library-uxf';
import createPageState from '../../../models/createPageState';

import {
	findElementById,
	findParentElement,
	getRootElementFromState,
	deleteChildElement,
	createElementFromComponent,
	getAllElementIds
} from '../utils/element';
import {
	RELOCATE_ELEMENT_SUCCEEDED,
	COMPONENT_PLACED_SUCCEEDED
} from '../elementActions';

import { Element } from '../../../models';
import Slot from '../../../models/slot';
import get from 'lodash/get';
const BEFORE = 'left';
const AFTER = 'right';
const directionLookup = {
	[BEFORE]: 0,
	[AFTER]: 1
};

const insertAt = (arr, item, index) => {
	const result = [...arr];
	result.splice(index, 0, item);
	return result;
};

const getInsertPosition = (parent, adjacent, direction) => {
	const formatted = (direction || '').toLowerCase();
	const { children = [] } = parent;
	const insertOffset = formatted
		? directionLookup[formatted]
		: directionLookup[AFTER];
	const adjacentIndex = children.findIndex((child) => {
		return child.id === adjacent.id;
	});

	const insertPosition =
		adjacentIndex !== -1 ? adjacentIndex + insertOffset : children.length - 1;
	return insertPosition;
};
const getSlot = (name, layout = {}) => {
	if (!name) {
		return null;
	}
	return (layout.items || []).find(({ slotName }) => slotName === name) || null;
};

const defaultSlotStylingToFlex = (adjacentElement, parentElement) => {
	const layoutSlot = getSlot(
		get(adjacentElement, 'slot.name', null),
		get(parentElement, 'layout.default', {})
	);

	if (layoutSlot) {
		if (!layoutSlot.styles) {
			layoutSlot.styles = {};
		}
		if (!layoutSlot.styles.display) {
			layoutSlot.styles.display = 'flex';
		}
	}
};
const componentCreatedInSlot = ({
	action,
	state,
	updateProperties,
	dispatch
}) => {
	const { componentId, elementId, direction } = action.payload;

	const {
		rawComponentList,
		properties: { pageState },
		getDefaultLayout
	} = state;
	const newRootElement = cloneDeep(getRootElementFromState(state));
	const adjacentElement = findElementById(newRootElement, elementId);
	const parentElement = findParentElement(newRootElement, adjacentElement.id);
	const droppedComponent = rawComponentList.find(
		(component) => component.id === componentId
	);

	// Load component scripts dynamically
	// This is async, but shouldn't matter if the scripts are loaded after
	// the component tags have been rendered in the html
	getTemplates([droppedComponent.id]);

	const newElement = createElementFromComponent(
		droppedComponent,
		parentElement,
		false,
		getDefaultLayout('slotted-component')
	);
	const allElementIds = getAllElementIds(newRootElement);
	newElement.initialize(allElementIds);

	newElement.slot = new Slot({
		name: adjacentElement.slot.name,
		parentElement: parentElement
	});
	parentElement.children = insertAt(
		parentElement.children,
		newElement,
		getInsertPosition(parentElement, adjacentElement, direction)
	);
	defaultSlotStylingToFlex(adjacentElement, parentElement);

	updateProperties({
		pageState: createPageState(
			{
				rootElement: new Element(newRootElement)
			},
			pageState
		)
	});
	dispatch(COMPONENT_PLACED_SUCCEEDED, {
		elementId: newElement.id,
		componentId: droppedComponent.id
	});
};
const componentMovedToSlot = ({
	action,
	state,
	updateProperties,
	updateState,
	dispatch
}) => {
	const { elementId, direction } = action.payload;

	const {
		detachedElementData: { detachedElements, detachedElementId },

		properties: { pageState }
	} = state;
	const newRootElement = cloneDeep(getRootElementFromState(state));
	const adjacentElement = findElementById(newRootElement, elementId);
	const targetParentElement = findParentElement(
		newRootElement,
		adjacentElement.id
	);

	let detachedElementParent;
	detachedElements.length &&
		(detachedElementParent = findParentElement(
			newRootElement,
			detachedElements[0].id
		));
	if (
		detachedElementParent &&
		detachedElementParent.id !== targetParentElement.id
	) {
		detachedElements.forEach((detachedElement) =>
			deleteChildElement(detachedElementParent, detachedElement.id)
		);
	} else {
		targetParentElement.children = targetParentElement.children.filter(
			(child) => {
				return child.id !== detachedElementId;
			}
		);
	}
	detachedElements.forEach(
		(detachedElement) =>
			(targetParentElement.children = insertAt(
				targetParentElement.children,
				detachedElement,
				getInsertPosition(targetParentElement, adjacentElement, direction)
			))
	);
	const detachedIndex = targetParentElement.children.findIndex(
		(elem) => elem.id === detachedElementId
	);
	targetParentElement.children[detachedIndex].slot.name =
		adjacentElement.slot.name;
	defaultSlotStylingToFlex(adjacentElement, targetParentElement);

	updateProperties({
		pageState: createPageState(
			{
				rootElement: new Element(newRootElement)
			},
			pageState
		)
	});
	dispatch(RELOCATE_ELEMENT_SUCCEEDED, { elementId: detachedElementId });

	updateState({ path: 'detachedElementData', operation: 'set', value: null });

	updateProperties({
		pageState: createPageState(
			{
				rootElement: new Element(newRootElement)
			},
			pageState
		)
	});
};

export { componentCreatedInSlot, componentMovedToSlot };
